// @jsx snabbdom.createElement
import get from 'lodash/get';
import { t } from 'sn-translate';
import snabbdom, { Fragment } from '@servicenow/ui-renderer-snabbdom'; // eslint-disable-line no-unused-vars
import { isChecked } from '../settings-helpers';
import { SOURCE_TYPE_TABLE } from '../../common/types/data-source';

export const SET_METRIC_LABEL_POSITION = 'SET_METRIC_LABEL_POSITION';

export function MetricLabelPosition(item, state) {
	const { chartModel } = state.properties;

	const value = get(chartModel, 'xenolithProperties.headingPosition');

	const options = [
		{ id: 'top', label: t('Above') },
		{ id: 'bottom', label: t('Below') },
	];

	if (
		!isChecked(item.id, chartModel) ||
		chartModel.dataSources[0].sourceType !== SOURCE_TYPE_TABLE
	) {
		return;
	}

	return (
		<Fragment>
			<div>
				<now-label-value-inline label={t('Position')} />
			</div>
			<now-dropdown
				id="metric-label-position"
				append-to-payload={{
					payloadActionType: SET_METRIC_LABEL_POSITION,
				}}
				items={options}
				select="single"
				selected-items={[value]}
				size="md"
				variant="tertiary"
				config-aria={{
					'aria-label': t('Metric label positioning'),
				}}
			/>
		</Fragment>
	);
}
