// @flow

import get from 'lodash/get';
import { actionTypes, type Coeffects } from '@servicenow/ui-core';
import { effectProxy } from '../helpers/seismic';
import { hasPropertyChanged } from '../helpers/props';
import { type State, type PredefinedFilter } from './state';
import { createGraphQLEffect } from '@servicenow/ui-effect-graphql';
import { createHttpEffect } from '@servicenow/ui-effect-http';
import { t } from 'sn-translate';
import {
	RETRIEVE_PREDEFINED_CONDITIONS,
	PREDEFINED_CONDITIONS_SUCCESS,
	REMOVE_PREDEFINED_CONDITION,
	TABLE_CONFIG_CHANGED,
	EDIT_FILTER,
	DISCARD_FILTER,
	UPDATE_TABLE_PREVIEW,
	ADD_CUSTOM_CONDITIONS,
	RETRIEVE_READABLE_QUERY,
	RETRIEVE_READABLE_QUERY_SUCCESS,
} from './constants';

const handleBootstrap = (coeffects: Coeffects<State, void>) => {
	const { tableInfo } = coeffects.state.properties;

	if (!tableInfo) {
		return;
	}

	onTableChange(coeffects);
};

const handlePropertyChange = (coeffects: Coeffects<State, any>) => {
	if (hasPropertyChanged(coeffects, 'tableInfo', ['tableName'])) {
		onTableChange(coeffects);
	}
};

const onTableChange = (coeffects: Coeffects<State, any>) => {
	const {
		tableName,
		filterQuery,
		isEditMode,
	} = coeffects.state.properties.tableInfo;

	coeffects.updateState({
		tableName,
		initialFilterQuery: filterQuery,
		isFilterLoading: true,
		isTableLoading: true,
		predefinedFilters: [],
		readableFilterQuery: '',
	});

	coeffects.dispatch(RETRIEVE_PREDEFINED_CONDITIONS, {
		queryConditions: `table=${tableName}`,
	});

	if (!isEditMode && filterQuery) {
		coeffects.dispatch(RETRIEVE_READABLE_QUERY, {
			table: tableName,
			query: filterQuery,
		});
	}
};

export const loadPredefinedConditions = createGraphQLEffect(
	`query($queryConditions: String) {
        GlideRecord_Query {
            sys_report_source(queryConditions: $queryConditions, pagination: {limit: 1000}) {
                _results {
					sys_id { value }
                    name { value }
                    filter { value displayValue }
                }
            }
        }
    }`,
	{
		variableList: ['queryConditions'],
		successActionType: PREDEFINED_CONDITIONS_SUCCESS,
	}
);

const getReadableQuery = (query: string): string => {
	if (!query) {
		return query;
	}

	const readableValue = query
		.replace(/\.and\./gi, 'and')
		.replace(/\.or\./gi, 'or');

	return readableValue;
};

const handlePredefinedConditionsSuccess = (
	coeffects: Coeffects<State, any>
) => {
	const results = get(
		coeffects.action.payload,
		'data.GlideRecord_Query.sys_report_source._results',
		[]
	);

	const predefinedFilters = results.map(result => {
		return {
			id: result.sys_id.value,
			name: result.name.value,
			query: result.filter.value,
			readableQuery: getReadableQuery(result.filter.displayValue),
		};
	});

	coeffects.updateState({
		predefinedFilters,
		isFilterLoading: false,
	});
};

const handlePredefinedConditionClicked = (coeffects: Coeffects<State, any>) => {
	if (
		coeffects.action.payload.payloadActionType ===
		'predefinedConditionTypeahead'
	) {
		const filter: PredefinedFilter = coeffects.action.payload.option;

		coeffects.dispatch(TABLE_CONFIG_CHANGED, {
			predefinedFilterId: filter.id,
		});
	}
};

export default {
	actionHandlers: {
		[actionTypes.COMPONENT_BOOTSTRAPPED]: handleBootstrap,
		[actionTypes.COMPONENT_PROPERTY_CHANGED]: handlePropertyChange,

		[RETRIEVE_PREDEFINED_CONDITIONS]: effectProxy(loadPredefinedConditions),
		[PREDEFINED_CONDITIONS_SUCCESS]: handlePredefinedConditionsSuccess,
		'DEVX_TYPEAHEAD#ITEM_CLICKED': handlePredefinedConditionClicked,
		'NOW_BUTTON#CLICKED': (coeffects: Coeffects<State, any>) => {
			switch (coeffects.action.payload.payloadActionType) {
				case UPDATE_TABLE_PREVIEW: {
					coeffects.dispatch(TABLE_CONFIG_CHANGED, {
						previewPredefinedFilterId:
							coeffects.state.properties.tableInfo.predefinedFilterId,
						previewFilterQuery:
							coeffects.state.properties.tableInfo.filterQuery,
					});
					if (coeffects.host.shadowRoot) {
						const buttonUpdatePreview = coeffects.host.shadowRoot.querySelector(
							'.btn-update-preview'
						);

						if (buttonUpdatePreview) {
							setTimeout(() => {
								buttonUpdatePreview.scrollIntoView({
									behavior: 'smooth',
								});
							}, 500);
						}
					}

					break;
				}
				case DISCARD_FILTER: {
					coeffects.dispatch(TABLE_CONFIG_CHANGED, {
						predefinedFilterId: undefined,
						filterQuery: undefined,
					});
					break;
				}
			}
		},
		'NOW_BUTTON_BARE#CLICKED': (coeffects: Coeffects<State, any>) => {
			switch (coeffects.action.payload.payloadActionType) {
				case EDIT_FILTER: {
					coeffects.dispatch(TABLE_CONFIG_CHANGED, {
						isEditMode: true,
						isExpanded: true,
					});
					break;
				}
				case REMOVE_PREDEFINED_CONDITION: {
					coeffects.dispatch(TABLE_CONFIG_CHANGED, {
						predefinedFilterId: undefined,
					});
					break;
				}
				case ADD_CUSTOM_CONDITIONS: {
					coeffects.dispatch(TABLE_CONFIG_CHANGED, {
						showCustomConditions: true,
					});
				}
			}
		},
		LIST_DATA_FETCH_COMPLETE: (coeffects: Coeffects<State, any>) => {
			// Table is loaded
			coeffects.updateState({
				isTableLoading: false,
			});
		},

		'NOW_CONDITION_BUILDER#ENCODED_QUERY_SET': (
			coeffects: Coeffects<State, any>
		) => {
			const filterQuery = coeffects.action.payload.value;

			coeffects.dispatch(TABLE_CONFIG_CHANGED, {
				filterQuery,
			});
		},

		[RETRIEVE_READABLE_QUERY]: createHttpEffect('/api/now/ui/breadcrumbs', {
			method: 'GET',
			queryParams: ['table', 'query'],
			successActionType: RETRIEVE_READABLE_QUERY_SUCCESS,
		}),

		[RETRIEVE_READABLE_QUERY_SUCCESS]: (coeffects: Coeffects<State, any>) => {
			const breadcrumbs = get(
				coeffects.action.payload,
				'result.breadcrumb',
				[]
			);

			let readableFilterQuery = '';

			if (breadcrumbs && breadcrumbs.length) {
				breadcrumbs.shift();
				readableFilterQuery = breadcrumbs.map(b => b.label).join(t(' and '));
			}

			coeffects.updateState({
				readableFilterQuery,
			});
		},
	},
};
