// @flow
import { createCustomElement } from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import view from './view';
import rtlBehavior from '@servicenow/behavior-rtl';
import styles from './styles.scss';
import { initialState } from './state';
import { type Breakdown } from '../common/types/indicator';
import { type IndicatorInfo } from '../common/types/indicator';
import { INDICATOR_TYPE } from '../common/types/data-source';

createCustomElement('viz-config-indicator-properties', {
	properties: {
		uuid: { default: {} },
		indicatorInfo: { default: {} },
		indicatorProps: {
			computed({ properties: { indicatorInfo } }) {
				return getPropertyDisplayValues(indicatorInfo);
			},
		},
	},
	...initialState,
	view,
	renderer: {
		type: snabbdom,
	},
	styles,
	behaviors: [{ behavior: rtlBehavior }],
});

const getCalculation = (indicatorInfo: IndicatorInfo): string => {
	const type = indicatorInfo.type && indicatorInfo.type.value;
	const aggregationValue =
		(indicatorInfo.script && indicatorInfo.script.name) || indicatorInfo.field;

	if (type == INDICATOR_TYPE.AUTOMATED && indicatorInfo.aggregate) {
		if (indicatorInfo.aggregate.name === 'COUNT')
			return indicatorInfo.aggregate.label;

		return `${indicatorInfo.aggregate.label} (${aggregationValue || ''})`;
	}

	return type == INDICATOR_TYPE.FORMULA
		? indicatorInfo.formula.displayValue
		: '';
};

const getBreakdowns = (breakdowns: Array<$Shape<Breakdown>> = []): string => {
	if (breakdowns.length <= 0) return '';

	return breakdowns.map(breakdown => breakdown.name).join(', ');
};

const getPropertyDisplayValues = (indicatorInfo: IndicatorInfo): any => {
	return {
		type: indicatorInfo.type && indicatorInfo.type.label,
		typeID: indicatorInfo.type && indicatorInfo.type.value,
		calculation: getCalculation(indicatorInfo),
		goal: indicatorInfo.direction && indicatorInfo.direction.label,
		unit: indicatorInfo.unit && indicatorInfo.unit.name,
		precision: indicatorInfo.precision,
		frequency: indicatorInfo.frequency && indicatorInfo.frequency.label,
		breakdowns: getBreakdowns(indicatorInfo.breakdowns),
	};
};
