// @flow
import { type Coeffects } from '@servicenow/ui-core';

type Effect = {
	effect(): any,
	args: any[],
};

/**
 This function wraps seismic effect into a function call
 which is useful when you want to mock your effect function.
 This proxy is needed because Seismic internally makes a copy of the pointer to
 original effect function making impossible to mock it later.
*/
export const effectProxy = (effect: Effect) => (
	coeffects: Coeffects<any, any>
) => effect.effect(...effect.args, coeffects);
