import { createCustomElement } from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import view from './view';
import styles from './styles.scss';
import { initialState } from './state';
import dotWalkingActions from './actions';

createCustomElement('viz-config-dot-walk', {
	renderer: { type: snabbdom },
	view,
	properties: {
		dotWalkPath: {
			default: '',
		},
		tableName: {
			default: '',
		},
		disabled: {
			default: false,
		},
		placeholder: {
			default: '',
		},
		label: {
			default: '',
		},
		errorMessage: {
			default: undefined,
		},
		transformMetadata: {},
	},
	styles,
	initialState,
	...dotWalkingActions,
});
