// @flow
// @jsx snabbdom.createElement
import '@servicenow/now-toggle';
import '@servicenow/now-dropdown';
import '@servicenow/now-accordion';

import snabbdom, { Fragment } from '@servicenow/ui-renderer-snabbdom'; // eslint-disable-line no-unused-vars
import { t } from 'sn-translate';
import { type State } from '../state';
import { SET_HIDE_HEADER } from '../actions/action-constants';
import { autoScrollToVisibility } from '../../helpers/now-accordion/auto-scroll-to-visibility';

export default (state: State) => {
	const { chartModel, accordionState } = state;

	const showHeaderPopOverContent = t(
		'Hiding the widget header also hides widget label and all the headers icons.'
	);

	return (
		<now-accordion-item
			id="component-header-accordion"
			dividers={{ top: false, bottom: true }}
			header={t('Component header')}
			expanded={accordionState.componentHeader}
			append-to-payload={{ accordionItem: 'componentHeader' }}
			on-click={autoScrollToVisibility}>
			<div className="now-accordion-content" slot="content">
				<div key="section-hide-header" className="section-hide-header">
					<viz-config-toggle
						id="show-header-toogle"
						append-to-payload={{ payloadActionType: SET_HIDE_HEADER }}
						checked={!chartModel.hideHeader}
						label={t('Show header')}
						infoTooltip={showHeaderPopOverContent}
					/>
				</div>
				{chartModel.hideHeader ? null : (
					<div className="section-prop-label">
						<now-input
							id="widget-label-input"
							label={t('Component title')}
							name="label"
							placeholder={t('Untitled')}
							value={state.chartModel.label}
							disabled={chartModel.hideHeader}
						/>
					</div>
				)}
			</div>
		</now-accordion-item>
	);
};
