//@flow
import {
	type AxisConfig,
	PRIMARY_AXIS,
	AXIS_PREFIX,
} from '../../common/types/axis';
import { CHART_TYPES } from '../../common/types/chart-type';
import { type DataSourceInfos } from '../../common/types/data-source';
import { type ChartModel } from '../../visualizations-config-panel/model/chart-model';
import { getMetricUnit } from '../metric';
import { t } from 'sn-translate';
import {
	TIMESERIES_CHART_TYPES,
	BAR_CHART_TYPES,
} from '../../common/chart-settings';

import { type FieldDefinitions } from '../../common/types/field';

export const getAxisConfigIndex = (axisConfig: AxisConfig) => {
	if (axisConfig.id === PRIMARY_AXIS) {
		return 0;
	} else {
		return Number(axisConfig.id.replace('axis', ''));
	}
};

export const getValidAxisId = (axisConfigs: AxisConfig[]) => {
	const lastAxisConfig = axisConfigs[axisConfigs.length - 1];

	const axisConfigIndex = getAxisConfigIndex(lastAxisConfig);

	return `${AXIS_PREFIX}${axisConfigIndex + 1}`;
};

export const sortAxisConfigs = (axisConfigs: AxisConfig[]) => {
	axisConfigs.sort((x1, x2) => getAxisConfigIndex(x1) - getAxisConfigIndex(x2));
};

export const getAxisUnit = (
	axisId: string,
	chartModel: ChartModel,
	dataSourceInfos: DataSourceInfos,
	fieldDefinitions: FieldDefinitions
) => {
	const { metrics, dataSources } = chartModel;

	const metric = metrics.find(m => m.axisId === axisId);

	if (metric) {
		return getMetricUnit(
			metric,
			dataSources,
			dataSourceInfos,
			fieldDefinitions
		);
	}

	return undefined;
};

export const axisLabels = [
	t('Main axis'),
	t('Secondary axis'),
	t('Tertiary axis'),
];

export const getAxisLabel = (axisIndex: number): string => {
	if (axisIndex > 2) {
		return t('Axis {0}', String(axisIndex + 1));
	}

	return axisLabels[axisIndex];
};

export const supportAxis = (chartModel: ChartModel) => {
	const { chartType } = chartModel;

	if (
		TIMESERIES_CHART_TYPES.includes(chartType) ||
		BAR_CHART_TYPES.includes(chartType)
	) {
		return true;
	}

	return false;
};

export const supportMultipleAxis = (chartModel: ChartModel) => {
	if (!supportAxis(chartModel)) {
		return false;
	}

	const { chartType, metrics } = chartModel;

	if (BAR_CHART_TYPES.includes(chartType)) {
		return false;
	}

	// Multiple axis is allowed when is timeseries chart and has at maximum one metric with column chart type
	const columnMetrics = metrics.filter(m => {
		const metricChartType = m.chartType || chartType;

		return metricChartType === CHART_TYPES.COLUMN;
	});

	return columnMetrics.length <= 1;
};
