// @flow
import {
	canCountDistinct,
	canGroup,
	canSum,
	canTrendBy,
	getNamedAttributes,
} from '../../helpers/field';

export type FieldBase = {
	attributes?: string,
	label: string,
	name: string,
	extendedTable?: boolean,
	type: string,
	internalType: string,
	reference: ?string,
	baseType?: string,
};

export type FieldMetadata = FieldBase & {
	canmatch: boolean,
	cansort: boolean,
	cangroup?: boolean,
	choiceType: number,
	choices: any[],
	columnType: string,
	default: string,
	filterable: boolean,
	hint: string,
	internalType: string,
	array: string,
	edgeEncrypted: boolean,
	multitext: boolean,
	isRange: boolean,
	label: string,
	mandatory: boolean,
	maxLength: number,
	maxUnit: string,
	name: string,
	operators: any[],
	readOnly: false,
};

export type FieldDefinition = FieldBase & {
	canCountDistinct: boolean,
	canGroupBy: boolean,
	canMatch?: boolean,
	canSort?: boolean,
	canSum: boolean,
	canTrendBy: boolean,
	isArray?: boolean,
	isEdgeEncrypted?: boolean,
	isMultiText?: boolean,
	isRange: boolean,
	unit: string,
};

export type FieldDefinitions = { [path: string]: FieldDefinition };

export type FieldDefinitionRequest = {|
	tableName: string,
	fieldName: string,
|};

export const FIELD_TYPES = {
	REFERENCE_TYPE: 'reference',
	CHOICE_TYPE: 'choice',
	GLIDE_DURATION: 'glide_duration',
	TIMER: 'timer',
	CURRENCY: 'currency',
	PRICE: 'price',
	FX_CURRENCY: 'currency2',
	INTEGER: 'integer',
};

export const createFieldDefFromMetadata: (a: any) => FieldDefinition = (
	metadata: FieldMetadata
) => {
	const fieldDefinition: $Shape<FieldDefinition> = {
		attributes: metadata.attributes,
		label: metadata.label,
		name: metadata.name,
		type: metadata.type,
		internalType: metadata.internalType,
		extendedTable: metadata.extendedTable,
		reference: metadata.reference,
		canMatch: metadata.canmatch,
		canSort: metadata.cansort,
		isArray: metadata.array === 'yes',
		isEdgeEncrypted: metadata.edgeEncrypted,
		isMultiText: metadata.multitext,
		isRange: !!metadata.isRange,
		unit: getUnit(metadata.internalType, metadata.attributes || ''),
	};

	fieldDefinition.canCountDistinct = canCountDistinct(fieldDefinition);
	fieldDefinition.canTrendBy = canTrendBy(fieldDefinition);
	fieldDefinition.canSum = canSum(fieldDefinition);
	fieldDefinition.canGroupBy =
		'cangroup' in metadata &&
		metadata.internalType === FIELD_TYPES.REFERENCE_TYPE
			? Boolean(metadata.cangroup)
			: canGroup(fieldDefinition);

	return fieldDefinition;
};

export const UNIT_DURATION = 'DURATION';
export const UNIT_CURRENCY = 'CURRENCY';
export const UNIT_FX_CURRENCY = 'FX_CURRENCY';
export const UNIT_DECIMAL = 'DECIMAL';

export const DATE_FIELD_TYPES = [
	'date',
	'datetime',
	'due_date',
	'glide_date',
	'glide_date_time',
];

export function isDurationField(type: string, attributes: string) {
	const namedAttributes = getNamedAttributes(attributes);
	const isGlideDurationFormat =
		namedAttributes['format'] === FIELD_TYPES.GLIDE_DURATION;

	return (
		FIELD_TYPES.GLIDE_DURATION === type ||
		FIELD_TYPES.TIMER === type ||
		(FIELD_TYPES.INTEGER === type && isGlideDurationFormat)
	);
}

export function isCurrencyField(type: string) {
	return FIELD_TYPES.PRICE === type || FIELD_TYPES.CURRENCY === type;
}

export function isFxCurrencyField(type: string) {
	return FIELD_TYPES.FX_CURRENCY === type;
}

export function getUnit(type: string, attributes: string) {
	if (isDurationField(type, attributes)) {
		return UNIT_DURATION;
	}

	if (isCurrencyField(type)) {
		return UNIT_CURRENCY;
	}

	if (isFxCurrencyField(type)) {
		return UNIT_FX_CURRENCY;
	}

	return UNIT_DECIMAL;
}
