// @flow
import { type FieldDefinition } from '../../common/types/field';
import { VARIABLE, VARIABLES, VARIABLE_CHOICE, GLIDE_LIST } from './constants';

import { isDateField } from './is-date-field';
import { getNamedAttributes } from './get-named-attributes';
import { canSort } from './can-sort';

/**
 * Verify if the field can be used for group by
 * Logic extracted from service.dotWalkingFilters.js: groupByFilter
 * @param {FieldDefinition} item
 * @return {boolean}
 */
export const canGroup = (item: FieldDefinition): boolean => {
	if (
		item.internalType === VARIABLE ||
		item.internalType === VARIABLES ||
		item.baseType === VARIABLE
	) {
		// For chart types pivot, list, box and trendbox should return false, but we don't support them now
		return true;
	}

	if (item.type === VARIABLE_CHOICE) {
		return true;
	}

	if (isDateField(item.internalType)) {
		return true;
	}

	const namedAttributes = getNamedAttributes(item.attributes);

	if (namedAttributes.can_group === 'true') {
		return true;
	}

	if (item.isEdgeEncrypted && item.canMatch) return true;

	if (item.extendedTable) return true;

	if (item.isMultiText || item.internalType === GLIDE_LIST || !canSort(item)) {
		return false;
	}

	return true;
};
