//@flow
import { t } from 'sn-translate';
import get from 'lodash/get';
import intersection from 'lodash/intersection';
import { type ChartModel } from '../model/chart-model';
import { type Breakdown } from '../../common/types/indicator';
import {
	type FieldDefinition,
	type FieldDefinitions,
	DATE_FIELD_TYPES,
	FIELD_TYPES,
} from '../../common/types/field';
import { type Option } from '../../common/types/option';
import {
	TREND_BY_NOT_SUPPORTED,
	TREND_BY_OPTIONAL,
	TIMESERIES_CHART_TYPES,
	getChartConfig,
} from '../../common/chart-settings';
import {
	SOURCE_TYPE_TABLE,
	type DataSource,
	type DataSourceInfo,
	type DataSourceInfos,
} from '../../common/types/data-source';
import {
	AGGREGATE_FUNCTION_COUNT,
	AGGREGATE_FUNCTION_SUM,
	AGGREGATE_FUNCTION_AVG,
	AGGREGATE_FUNCTION_DISTINCT,
	AGGREGATE_FUNCTION_MIN,
	AGGREGATE_FUNCTION_MAX,
} from '../../metric-configuration/constants';
import { getFieldDefinition } from '../../helpers/field';

export const getStackByOptions = (
	dataSource: DataSource,
	dataSourceInfos: DataSourceInfos
): Option[] => {
	const { sourceType, groupByField } = dataSource;

	let options: Option[] = [];

	if (sourceType === SOURCE_TYPE_TABLE) {
		const dataSourceInfo = dataSourceInfos[dataSource.id || ''];
		const tableFields: FieldDefinition[] = dataSourceInfo.tableFields || [];

		options = tableFields
			.filter(field => field.canGroupBy)
			.map(({ name, label }) => ({
				id: name,
				label,
			}));
	}

	options = groupByField ? options.filter(o => o.id !== groupByField) : options;

	return options;
};

export const hasStackByOptions = (
	dataSources: DataSource[],
	dataSourceInfos: DataSourceInfos
) => {
	const options = getStackByOptions(dataSources[0], dataSourceInfos);

	return options.length > 0;
};

export const hasGroupByOptions = (
	dataSources: DataSource[],
	dataSourceInfos: DataSourceInfos,
	fieldDefinitions: FieldDefinitions
) => {
	const options = getGroupByOptions(
		dataSources[0],
		dataSources,
		dataSourceInfos,
		fieldDefinitions
	);

	return options.length > 0;
};

export const getGroupByOptions = (
	dataSource: DataSource,
	dataSources: DataSource[],
	dataSourceInfos: DataSourceInfos,
	dotWalkFieldDefinition: { [path: string]: FieldDefinition }
): Option[] => {
	if (dataSource.sourceType === SOURCE_TYPE_TABLE) {
		return getGroupByOptionsTable(
			dataSource,
			dataSources,
			dataSourceInfos,
			dotWalkFieldDefinition
		);
	} else {
		return getGroupByOptionsIndicator(dataSources, dataSourceInfos);
	}
};

export const isFieldCompatible = (
	fieldDefinition1: FieldDefinition,
	fieldDefinition2: FieldDefinition
) => {
	if (
		DATE_FIELD_TYPES.includes(fieldDefinition1.internalType) &&
		DATE_FIELD_TYPES.includes(fieldDefinition2.internalType)
	) {
		return true;
	}

	if (
		fieldDefinition1.internalType === FIELD_TYPES.REFERENCE_TYPE &&
		fieldDefinition2.internalType === FIELD_TYPES.REFERENCE_TYPE
	) {
		return fieldDefinition1.reference === fieldDefinition2.reference;
	}

	if (fieldDefinition1.type === FIELD_TYPES.CHOICE_TYPE) {
		return (
			fieldDefinition1.name === fieldDefinition2.name &&
			fieldDefinition1.type === fieldDefinition2.type
		);
	}

	if (fieldDefinition1.internalType === fieldDefinition2.internalType) {
		return true;
	}

	return false;
};

export const getGroupByOptionsFilter = (
	dataSource: DataSource,
	dataSources: DataSource[],
	dataSourceInfos: DataSourceInfos,
	dotWalkFieldDefinition: { [path: string]: FieldDefinition } = {}
) => {
	const firstDataSource = dataSources[0];
	const isFirstDataSource = dataSource.id === firstDataSource.id;

	let filterGroupBy = (field: FieldDefinition) => field.canGroupBy;

	if (!isFirstDataSource) {
		const firstGroupByField = firstDataSource.groupByField || '';
		const firstDataSourceInfo = dataSourceInfos[firstDataSource.id || ''];
		const firstGroupByDefinition = getFieldDefinition(
			firstGroupByField,
			firstDataSourceInfo,
			firstDataSource,
			dotWalkFieldDefinition
		);

		filterGroupBy = (field: FieldDefinition) =>
			Boolean(
				field.canGroupBy &&
					firstGroupByDefinition &&
					isFieldCompatible(firstGroupByDefinition, field)
			);
	}

	return filterGroupBy;
};

export const getGroupByOptionsTable = (
	dataSource: DataSource,
	dataSources: DataSource[],
	dataSourceInfos: DataSourceInfos,
	dotWalkFieldDefinition: FieldDefinitions
): Option[] => {
	const dataSourceInfo = dataSourceInfos[dataSource.id || ''];
	const tableFields: FieldDefinition[] =
		(dataSourceInfo && dataSourceInfo.tableFields) || [];

	let filterGroupBy = getGroupByOptionsFilter(
		dataSource,
		dataSources,
		dataSourceInfos,
		dotWalkFieldDefinition
	);

	return tableFields.filter(filterGroupBy).map(({ name, label }) => ({
		id: name,
		label,
	}));
};

export const getGroupByOptionsIndicator = (
	dataSources: DataSource[],
	dataSourceInfos: DataSourceInfos
): Option[] => {
	const breakdownsPerIndicator = dataSources.map(ds => {
		if (!ds.id || !dataSourceInfos[ds.id]) {
			return [];
		}

		return getBreakdownsAvailableToGroupBy(ds, dataSourceInfos[ds.id]);
	});

	const breakdownsAvailableInAllIndicators = intersection(
		...breakdownsPerIndicator
	);

	const firstDataSource = get(dataSources, '[0].id');
	const breakdowns = dataSourceInfos[firstDataSource].breakdowns || [];

	return breakdowns
		.filter(b => breakdownsAvailableInAllIndicators.includes(b.sysId))
		.map(b => {
			return { id: b.sysId, label: b.name };
		});
};

const getBreakdownsAvailableToGroupBy = (
	dataSource: DataSource,
	dataSourceInfo: DataSourceInfo
): string[] => {
	const breakdowns: Breakdown[] = dataSourceInfo.breakdowns || [];

	const appliedBreakdowns = get(dataSource, 'uuid.breakdowns', []);

	const availableBreakdowns = breakdowns.reduce((acc, breakdown) => {
		const isApplied = appliedBreakdowns.find(b => b.sysId === breakdown.sysId);

		if (appliedBreakdowns.length < 2 || isApplied) {
			acc.push(breakdown.sysId);
		}

		return acc;
	}, []);

	return availableBreakdowns;
};

export const getTableReferenceFields = (
	chartModel: ChartModel,
	dataSourceInfo: DataSourceInfo
): FieldDefinition[] => {
	const { dataSources } = chartModel;

	if (dataSources[0].sourceType !== SOURCE_TYPE_TABLE) {
		return [];
	}

	return (dataSourceInfo.tableFields || []).filter(
		field => field.internalType === FIELD_TYPES.REFERENCE_TYPE
	);
};

export const getTrendByOptions = (
	chartModel: ChartModel,
	dataSourceInfo: DataSourceInfo
): Option[] => {
	const chartConfig = getChartConfig(chartModel.chartType);

	if (chartConfig.trendBy === TREND_BY_NOT_SUPPORTED) {
		return [];
	}

	const { dataSources } = chartModel;

	if (dataSources[0].sourceType !== SOURCE_TYPE_TABLE) {
		return [];
	}

	const tableFields: FieldDefinition[] = dataSourceInfo.tableFields || [];

	const options = tableFields
		.filter(field => field.canTrendBy)
		.map(({ name, label }) => ({
			id: name,
			label,
		}));

	if (chartConfig.trendBy === TREND_BY_OPTIONAL) {
		options.unshift({
			id: '',
			label: t('None'),
		});
	}

	return options;
};

export const aggregationFunctionFilters = {
	[AGGREGATE_FUNCTION_SUM]: (field: any) => field.canSum,
	[AGGREGATE_FUNCTION_AVG]: (field: any) => field.canSum,
	[AGGREGATE_FUNCTION_MIN]: (field: any) => field.canSum,
	[AGGREGATE_FUNCTION_MAX]: (field: any) => field.canSum,
	[AGGREGATE_FUNCTION_DISTINCT]: (field: any) => field.canCountDistinct,
};

export const getAggregateOptionsTable = (
	aggregateFunction: string,
	dataSourceInfo: DataSourceInfo
): Option[] => {
	if (aggregateFunction === AGGREGATE_FUNCTION_COUNT) {
		return [];
	}

	const tableFields: FieldDefinition[] = dataSourceInfo.tableFields || [];
	const filter = aggregationFunctionFilters[aggregateFunction];

	return tableFields.filter(filter).map(({ name, label }) => ({
		id: name,
		label,
	}));
};

export const getTimeSeriesAllowedFrequencies = (
	dataSources: DataSource[],
	dataSourceInfos: DataSourceInfos
): number[] => {
	const frequenciesPerIndicator = dataSources.map(ds => {
		if (!ds.id || !dataSourceInfos[ds.id]) {
			return [];
		}

		const dataSourceInfo = dataSourceInfos[ds.id];
		const frequencies: number[] = (dataSourceInfo.aggregates || []).map(
			a => a.frequencyValue
		);

		if (dataSourceInfo.frequencyValue) {
			frequencies.push(dataSourceInfo.frequencyValue);
		}

		return frequencies;
	});

	return intersection(...frequenciesPerIndicator);
};

export const getAggregateOptionsIndicator = (
	dataSource: ?string,
	chartModel: ChartModel,
	dataSourceInfos: DataSourceInfos,
	defaultId?: string
): Option[] => {
	const dataSourceInfo = dataSourceInfos[dataSource || ''];
	const isTimeSeries = TIMESERIES_CHART_TYPES.includes(chartModel.chartType);
	const timeSeriesAllowedFrequencies = getTimeSeriesAllowedFrequencies(
		chartModel.dataSources,
		dataSourceInfos
	);

	const { aggregates = [], frequency = '', frequencyValue } = dataSourceInfo;

	const options = aggregates.map(a => ({
		id: a.sysId,
		label: a.name,
		disabled: isTimeSeries
			? !timeSeriesAllowedFrequencies.includes(a.frequencyValue)
			: false,
	}));

	options.unshift({
		id: defaultId ? defaultId : '',
		label: frequency,
		disabled: isTimeSeries
			? !timeSeriesAllowedFrequencies.includes(frequencyValue)
			: false,
	});

	return options;
};
