// @flow
// @jsx snabbdom.createElement

import snabbdom, { Fragment } from '@servicenow/ui-renderer-snabbdom'; // eslint-disable-line no-unused-vars

import { type State } from './state';
import { t } from 'sn-translate';

import '@servicenow/now-popover';
import '@servicenow/now-button';
import '@servicenow/now-input';

import '@servicenow/now-accordion';
import 'sn-devx-components';
import './color-rule-popover';

import { type ColorRule } from '../common/types/color-rules';
import { type Metric } from '../common/types/metric';

import {
	BTN_ADD_COLOR_RULE,
	COLOR_RULE_EDIT,
	COLOR_RULE_DELETE,
	RULE_POPOVER,
} from './constants';

import { nowPopoverAllowedPositions } from '../common/popover-config';

const colorRuleLabel = (rule: ColorRule) => {
	let displayRule = '';

	switch (rule.operator) {
		case '<':
			displayRule = 'lower than';
			break;

		case '<=':
			displayRule = 'lower than or is';
			break;

		case '=':
			displayRule = 'is';
			break;

		case '>':
			displayRule = 'greater than';
			break;

		case '>=':
			displayRule = 'greater than or is';
			break;

		default:
			break;
	}

	if (rule.operator !== 'between') return displayRule + ' ' + rule.value1;

	return 'between ' + rule.value1 + ' and ' + (!rule.value2 ? '' : rule.value2);
};

const colorRulePillar = (rule: ColorRule, index: number) => {
	const contextMenuActions = [
		{
			id: 'editColorRule',
			children: [
				{
					icon: 'sliders-outline',
					id: COLOR_RULE_EDIT,
					label: t('Edit'),
				},
				{
					icon: 'sliders-outline',
					id: COLOR_RULE_DELETE,
					label: t('Delete'),
				},
			],
		},
	];

	return (
		<sn-par-pillar
			id={`color-rule-pillar-${index}`}
			append-to-payload={{ colorRuleIndex: index }}
			clickable
			context-menu-actions={contextMenuActions}>
			{colorRuleLabel(rule)}
		</sn-par-pillar>
	);
};

const renderColorRules = (metric: Metric) => {
	const { colorRules } = metric;

	if (!colorRules) return null;

	const pillars = colorRules.map((rule, index) => {
		return colorRulePillar(rule, index);
	});

	return pillars;
};

const colorRulePopover = (state: State) => {
	const { popoverOpened, popoverTarget } = state;

	return (
		<now-popover
			append-to-payload={{ type: RULE_POPOVER }}
			opened={popoverOpened}
			positions={nowPopoverAllowedPositions}
			positionTarget={popoverTarget}
			manage-opened>
			<viz-config-color-rule-popover slot="content" sharedState={state} />
		</now-popover>
	);
};

const addColorRuleButton = () => {
	return (
		<now-button
			id="btn-add-color-rule"
			append-to-payload={{
				id: BTN_ADD_COLOR_RULE,
			}}
			icon="plus-fill"
			label={t('Add rule')}
			size="sm"
			variant="secondary"
		/>
	);
};

export const colorRuleViewContent = (state: State) => {
	const {
		properties: { metric },
	} = state;

	if (!metric.colorRules || metric.colorRules.length === 0)
		return (
			<div className="viz-config-color-rule-empty-state">
				<div className="viz-config-color-rule-text-title">
					{t('Define a dynamic color')}
				</div>
				<div className="viz-config-color-rule-text">
					{t(
						'You can define specific colors based on rules like "Blue if metric is higher that 100"'
					)}
				</div>
				{addColorRuleButton()}
				{colorRulePopover(state)}
			</div>
		);

	return (
		<div className="viz-config-color-rule">
			{renderColorRules(state.properties.metric)}
			{addColorRuleButton()}
			{colorRulePopover(state)}
		</div>
	);
};

export const colorRuleView = (state: State) => {
	return (
		<div>
			<now-label-value-inline
				className="section-heading"
				label={t('Color rules')}
			/>
			<div className="viz-config-color-rule-container">
				{colorRuleViewContent(state)}
			</div>
		</div>
	);
};
