// @flow
import isArray from 'lodash/isArray';

import { actionTypes, type Coeffects } from '@servicenow/ui-core';
import { type State } from './state';

import { t } from 'sn-translate';

import {
	FIELD_LABEL,
	BTN_POPOVER_APPLY,
	BTN_POPOVER_CLOSE,
	INDICATOR_EMPTY_STRING_ID_REPLACEMENT,
	FIELD_AGGREGATE_FUNCTION,
	SET_METRICS,
	BTN_ADD_METRIC,
	PILLAR_ACTION_EDIT,
	PILLAR_ACTION_DELETE,
	SET_AGGREGATION_SOURCE,
	PILLAR_ACTION_ADD_NEW_AXIS,
	ADD_NEW_AXIS_TO_METRIC,
	METRIC_POPOVER,
	AGGREGATE_FUNCTION_COUNT,
	FIELD_AGGREGATE_FIELD,
} from './constants';

import {
	SN_PAR_PILLAR_CLICKED,
	SN_PAR_PILLAR_ACTION_CLICKED,
} from '@now-par-components/sn-par-pillar';

import { getValidMetric } from '../visualizations-config-panel/actions/model-validators';
import {
	getAggregateOptionsTable,
	getAggregateOptionsIndicator,
} from '../visualizations-config-panel/actions/model-options';
import {
	isTableMetric,
	isIndicatorMetric,
	getAggregateWithSameFrequency,
} from '../helpers/metric';
import { type Metric } from '../common/types/metric';
import { type DataSource } from '../common/types/data-source';
import { type Option } from '../common/types/option';
import { PRIMARY_AXIS, AXIS_PREFIX } from '../common/types/axis';

import { TIMESERIES_CHART_TYPES } from '../common/chart-settings';
import { DOT_WALK_PATH_UPDATED } from '../dot-walk/constants';
import { createFieldDefinitionFilter } from './aggregate-fields-view';

const getAggregateIndicatorRealValue = value =>
	value === INDICATOR_EMPTY_STRING_ID_REPLACEMENT ? '' : value;

const getAggregateOptions = (dataToApply: Metric, state: State) => {
	const {
		properties: { dataSourceInfos, chartModel },
	} = state;
	const { dataSources } = chartModel;
	const { aggregateFunction, dataSource } = dataToApply;
	const dataSourceInfo = dataSource && dataSourceInfos[dataSource];

	let localAggregateOptions = [];

	if (isTableMetric(dataToApply, dataSources)) {
		localAggregateOptions =
			aggregateFunction && dataSourceInfo
				? getAggregateOptionsTable(aggregateFunction, dataSourceInfo)
				: [];
	} else if (isIndicatorMetric(dataToApply, dataSources) && dataSource) {
		localAggregateOptions = getAggregateOptionsIndicator(
			dataSource,
			chartModel,
			dataSourceInfos,
			INDICATOR_EMPTY_STRING_ID_REPLACEMENT
		);
	}

	return localAggregateOptions;
};

const getDataSourceOptions = (dataSources: DataSource[]): Option[] => {
	return dataSources.reduce((acc, ds) => {
		if (ds.id) {
			acc.push({
				id: ds.id,
				label: ds.label,
			});
		}

		return acc;
	}, []);
};

const handleDropdownSelection = ({
	action,
	state,
	updateState,
}: Coeffects<State, any>) => {
	action.stopPropagation();

	const {
		properties: { dataSourceInfos, chartModel, dotWalkFieldDefinition },
		dataToApply: dataToApplyOld,
		typingTrack: typingTrackOld,
		errorMessages: errorMessagesOld,
		componentHost,
	} = state;

	const { dataSources } = chartModel;

	// DotWalk OR Dropdown seletion OR devx-typeahead
	const selectedValue =
		action.payload.dotWalkPath ||
		(isArray(action.payload.value)
			? action.payload.value[0]
			: action.payload.value);

	const field = action.payload.fieldName || action.payload.field;

	if (field) {
		const { aggregateFunction } = dataToApplyOld;
		const filter = createFieldDefinitionFilter(aggregateFunction);

		if (
			field === FIELD_AGGREGATE_FIELD &&
			action.payload.definition &&
			!filter(null, action.payload.definition.field)
		) {
			return updateState([
				{
					operation: 'set',
					path: `errorMessages[${field}]`,
					value: t('Selected field is invalid'),
				},
				{
					operation: 'set',
					path: `dataToApply[${field}]`,
					value: undefined,
				},
			]);
		}

		let dataToApply = {
			...dataToApplyOld,
			[field]: selectedValue,
		};

		if (isTableMetric(dataToApply, dataSources)) {
			dataToApply = getValidMetric(
				chartModel,
				dataSourceInfos,
				dotWalkFieldDefinition,
				dataToApply
			);
		}

		const typingTrack = {
			...typingTrackOld,
			[field]: undefined,
		};
		const errorMessages = {
			...errorMessagesOld,
			[field]: undefined,
		};

		if (field === FIELD_AGGREGATE_FUNCTION) {
			errorMessages.aggregateField = undefined;
			typingTrack.aggregateField = undefined;
		}

		updateState({
			dataToApply,
			typingTrack,
			errorMessages,
			localAggregateOptions: getAggregateOptions(dataToApply, state),
		});

		const parPopover: any =
			componentHost &&
			componentHost.shadowRoot &&
			componentHost.shadowRoot.querySelector('now-popover');

		setTimeout(() => {
			parPopover && parPopover.refit && parPopover.refit();
		}, 100);
	}
};

const handleInputBlured = ({
	action,
	state,
	updateState,
}: Coeffects<State, any>) => {
	switch (action.payload.field) {
		case FIELD_LABEL:
			action.stopPropagation();
			updateState({
				dataToApply: {
					...state.dataToApply,
					label: action.payload.value,
				},
			});
			break;
	}
};

const handlePillarAction = (coeffects: Coeffects<State, any>) => {
	switch (coeffects.action.payload.id) {
		case PILLAR_ACTION_EDIT:
			toggleMetricPopover(coeffects);
			break;
		case PILLAR_ACTION_DELETE:
			handleDeleteAction(coeffects);
			break;
		case PILLAR_ACTION_ADD_NEW_AXIS:
			coeffects.dispatch(
				ADD_NEW_AXIS_TO_METRIC,
				coeffects.action.payload.metricIndex
			);
			break;
		default: {
			handleDynamicActions(coeffects);
		}
	}
};

const handleDynamicActions = ({
	state,
	action,
	dispatch,
}: Coeffects<State, any>) => {
	const { metrics } = state.properties.chartModel;
	const { id = '' } = action.payload;
	const { metricIndex } = action.payload;

	const updatedMetrics = metrics.map((m, index) => {
		if (index === metricIndex) {
			const updateMetric = { ...m };
			const isValidAxisId =
				id === PRIMARY_AXIS || id.indexOf(AXIS_PREFIX) === 0;

			if (isValidAxisId) {
				updateMetric.axisId = id;
			} else {
				updateMetric.chartType = id;
			}

			return updateMetric;
		}

		return m;
	});

	dispatch(SET_METRICS, updatedMetrics);
};

const handleDeleteAction = ({
	state,
	action,
	dispatch,
}: Coeffects<State, any>) => {
	const { metricIndex } = action.payload;

	if (metricIndex === undefined) {
		return;
	}

	const metrics = state.properties.chartModel.metrics.filter(
		(m, i) => i !== metricIndex
	);

	dispatch(SET_METRICS, metrics);
};

const toggleMetricPopover = ({
	state,
	updateState,
	action,
}: Coeffects<State, any>) => {
	const {
		popoverOpened: popoverOpenedOld,
		popoverClosedAt,
		componentHost,
		properties: {
			chartModel: { metrics, dataSources },
		},
	} = state;

	const { metricIndex } = action.payload;
	const dataToApply =
		metricIndex === undefined
			? {
					dataSource: dataSources[0].id,
			  }
			: { ...metrics[metricIndex] };

	const selectorPopoverTarget =
		metricIndex === undefined
			? '#btn-add-metric'
			: `#metric-pillar-${metricIndex}`;

	const popoverOpened = !popoverOpenedOld;

	updateState({
		dataToApply,
		editMetricIndex: metricIndex,
		localAggregateOptions: getAggregateOptions(dataToApply, state),
		dataSourceOptions: getDataSourceOptions(dataSources),
		popoverClosedAt: popoverOpened ? new Date().getTime() : popoverClosedAt,
		popoverTarget:
			componentHost &&
			componentHost.shadowRoot &&
			componentHost.shadowRoot.querySelector(selectorPopoverTarget),
		...(!popoverOpened && { popoverOpened: false }),
	});

	if (popoverOpened) {
		setTimeout(() => {
			updateState({
				popoverOpened,
			});
		}, 100);
	}
};

const createCleanClosedPopoverState = () => ({
	popoverOpened: false,
	popoverClosedAt: new Date().getTime(),
	dataToApply: {},
	errorMessages: {},
	typingTrack: {},
});

const validateTypeaheads = (
	typingTrack: $PropertyType<State, 'typingTrack'>
) => {
	const errorMessages = {};
	const valid =
		!typingTrack.dataSource &&
		!typingTrack.aggregateFunction &&
		!typingTrack.aggregateField &&
		!typingTrack.aggregateIndicator;

	if (typingTrack.dataSource) {
		errorMessages.dataSource = t('Please select a data source');
	}

	if (typingTrack.aggregateFunction) {
		errorMessages.aggregateFunction = t('Please select a function');
	}

	if (typingTrack.aggregateField) {
		errorMessages.aggregateField = t('Please select a field');
	}

	if (typingTrack.aggregateIndicator) {
		errorMessages.aggregateIndicator = t('Please select an aggregation');
	}

	return { valid, errorMessages };
};

const getMetricsToBeApplied = (
	state: State,
	editMetricIndex?: number,
	metric: Metric
): Metric[] => {
	const {
		properties: {
			chartModel: { metrics, dataSources, chartType },
			dataSourceInfos,
		},
	} = state;

	const metricsToBeApplied = [...metrics];

	if (editMetricIndex === undefined) {
		metricsToBeApplied.push(metric);
	} else {
		metricsToBeApplied[editMetricIndex] = metric;
	}

	if (
		isIndicatorMetric(metric, dataSources) &&
		TIMESERIES_CHART_TYPES.includes(chartType)
	) {
		const newMetricIndex =
			editMetricIndex === undefined
				? metricsToBeApplied.length
				: editMetricIndex;

		// Validate if other metrics have same frequency
		return metricsToBeApplied.map((m, index) => {
			if (index === newMetricIndex) {
				return m;
			}

			return {
				...m,
				aggregateIndicator: getAggregateWithSameFrequency(
					metric,
					m,
					dataSourceInfos
				),
			};
		});
	}

	return metricsToBeApplied;
};

const handleApply = ({
	state,
	updateState,
	dispatch,
}: Coeffects<State, any>) => {
	const {
		properties: {
			chartModel: { metrics, dataSources },
		},
		dataToApply,
		typingTrack,
		editMetricIndex,
	} = state;

	const { errorMessages, valid } = validateTypeaheads(typingTrack);
	const validAggregationField =
		isIndicatorMetric(metrics[editMetricIndex || 0], dataSources) ||
		dataToApply.aggregateField ||
		dataToApply.aggregateFunction === AGGREGATE_FUNCTION_COUNT;

	if (!valid || !validAggregationField) {
		return updateState({ errorMessages });
	}

	const newMetrics = getMetricsToBeApplied(state, editMetricIndex, {
		...dataToApply,
		aggregateIndicator: getAggregateIndicatorRealValue(
			dataToApply.aggregateIndicator
		),
	});

	dispatch(SET_METRICS, newMetrics);

	updateState(createCleanClosedPopoverState());
};

const handleButtonClick = (coeffects: Coeffects<State, any>) => {
	switch (coeffects.action.payload.id) {
		case BTN_POPOVER_APPLY:
			handleApply(coeffects);
			break;
		case BTN_ADD_METRIC:
			toggleMetricPopover(coeffects);
			break;
	}
};

const updateDataToApplyNowDropdownItemSelected = ({
	action,
	state,
	updateState,
}: Coeffects<State, any>) => {
	const {
		properties: { dataSourceInfos, chartModel, dotWalkFieldDefinition },
		dataToApply: dataToApplyOld,
	} = state;

	const field = action.payload.field;

	if (!field) return;

	const { dataSources } = chartModel;
	const selectedValue = action.payload.value[0];

	let dataToApply = {
		...dataToApplyOld,
		[field]: selectedValue,
	};

	if (isTableMetric(dataToApply, dataSources))
		dataToApply = getValidMetric(
			chartModel,
			dataSourceInfos,
			dotWalkFieldDefinition,
			dataToApply
		);

	updateState({
		dataToApply,
		localAggregateOptions: getAggregateOptions(dataToApply, state),
	});
};

const handleNowDropdownItemSelected = (coeffects: Coeffects<State, any>) => {
	switch (coeffects.action.payload.payloadActionType) {
		case SET_AGGREGATION_SOURCE:
			updateDataToApplyNowDropdownItemSelected(coeffects);
			break;
		case FIELD_AGGREGATE_FUNCTION:
			handleDropdownSelection(coeffects);
			break;
	}
};

export default {
	actionHandlers: {
		[actionTypes.COMPONENT_BOOTSTRAPPED]: ({
			action,
			updateState,
		}: Coeffects<State, any>) => {
			updateState({ componentHost: action.payload.host });
		},

		[SN_PAR_PILLAR_ACTION_CLICKED]: handlePillarAction,
		[SN_PAR_PILLAR_CLICKED]: toggleMetricPopover,

		'NOW_SELECT#SELECTED_ITEM_SET': handleDropdownSelection,
		[DOT_WALK_PATH_UPDATED]: handleDropdownSelection,

		'NOW_BUTTON_ICONIC#CLICKED': ({
			action: { payload },
			updateState,
		}: Coeffects<State, any>) => {
			if (payload.id === BTN_POPOVER_CLOSE) {
				updateState(createCleanClosedPopoverState());
			}
		},

		'NOW_BUTTON#CLICKED': handleButtonClick,

		'NOW_POPOVER#OPENED_SET': ({
			updateState,
			action,
		}: Coeffects<State, any>) => {
			if (action.payload.type === METRIC_POPOVER && !action.payload.value) {
				updateState(createCleanClosedPopoverState());
			}
		},

		'DEVX_INPUT#INPUT_BLUR': handleInputBlured,

		'NOW_DROPDOWN#SELECTED_ITEMS_SET': handleNowDropdownItemSelected,
	},
};
