// @flow
// @jsx snabbdom.createElement
import '@servicenow/now-toggle';
import '@servicenow/now-accordion';
import '@servicenow/now-label-value';
import 'sn-devx-components';

import snabbdom, { Fragment } from '@servicenow/ui-renderer-snabbdom'; // eslint-disable-line no-unused-vars
import { t } from 'sn-translate';
import { type State } from '../state';
import { type Helpers } from '@servicenow/ui-core';
import {
	X_AXIS_NAME,
	Y_AXIS_NAME,
	AXIS_RANGE_FROM,
	AXIS_RANGE_TO,
	SET_Y_AXIS_POSITION_VALUE,
} from '../actions/action-constants';
import { Y_AXIS_POSITION_CHOICES } from './view-constants';
import {
	CHART_TYPES_CONFIG,
	TIMESERIES_CHART_TYPES,
} from '../../common/chart-settings';
import DateRangeSelectorView from './date-range-view';
import { INPUT_BLUR } from './view-constants';
import { CHART_TYPES } from '../../common/types/chart-type';
import { autoScrollToVisibility } from '../../helpers/now-accordion/auto-scroll-to-visibility';
import { getAxisLabel } from '../../helpers/axis';

export default (state: State, helpers: Helpers<State>) => {
	const {
		chartModel: { chartType, axisSettings, yAxisConfigs },
		accordionState,
	} = state;

	const { xAxisName } = axisSettings || {};

	const isAllowed = CHART_TYPES_CONFIG.find(
		e => e.id === chartType && (e.menuGroup === 'time' || e.menuGroup === 'bar')
	);

	if (!isAllowed || !axisSettings) {
		return null;
	}

	const xAxisLabel = t('X Axis');
	const yAxisLabel = t('Y Axis');

	return (
		<Fragment>
			<now-accordion-item
				id="y-axis-accordion"
				dividers={{ top: false, bottom: true }}
				header={
					chartType === CHART_TYPES.HORIZONTAL_BAR ? xAxisLabel : yAxisLabel
				}
				expanded={accordionState.yAxis}
				append-to-payload={{ accordionItem: 'yAxis' }}
				on-click={autoScrollToVisibility}>
				<div className="now-accordion-no-border-content" slot="content">
					{yAxisConfigs.map((yAxisConfig, index) => {
						return (
							<div key={`yAxisConfig${yAxisConfig.id}`}>
								{yAxisConfigs.length > 1 ? (
									<div className="axis-label-container">
										<label>{getAxisLabel(index)}</label>
									</div>
								) : null}
								<div className="axis-container">
									<devx-input
										id={`y-axis-title-${yAxisConfig.id}`}
										append-to-payload={{
											payloadActionType: Y_AXIS_NAME,
											axisId: yAxisConfig.id,
										}}
										label={t('Axis title (Optional)')}
										placeholder={t('Untitled')}
										inputValue={yAxisConfig.title}
										variant="secondary"
										transparent={true}
									/>
									<div className="axis-range">
										<span className="range-input">
											<now-label-value-inline label={t('Axis range')} />
											<input
												id={`y-axis-range-from-${yAxisConfig.id}`}
												aria-label={t('Axis range min value')}
												className="range-input-text"
												value={yAxisConfig.axisRangeFrom}
												type="number"
												onblur={e =>
													helpers.dispatch(INPUT_BLUR, {
														type: AXIS_RANGE_FROM,
														ref: e.target,
														axisId: yAxisConfig.id,
													})
												}
												placeholder={t('Dynamic')}
											/>
										</span>
										<span className="text-separator">to</span>
										<span className="range-input">
											<input
												id={`y-axis-range-to-${yAxisConfig.id}`}
												aria-label={t('Axis range max value')}
												className="range-input-text"
												value={yAxisConfig.axisRangeTo}
												type="number"
												onblur={e =>
													helpers.dispatch(INPUT_BLUR, {
														type: AXIS_RANGE_TO,
														ref: e.target,
														axisId: yAxisConfig.id,
													})
												}
												placeholder={t('Dynamic')}
											/>
										</span>
									</div>
									{TIMESERIES_CHART_TYPES.includes(chartType) ? (
										<div className="y-axis-position-container">
											<now-label-value-inline
												className="section-heading"
												label={t('Position')}
											/>
											<now-dropdown
												append-to-payload={{
													payloadActionType: SET_Y_AXIS_POSITION_VALUE,
													axisId: yAxisConfig.id,
												}}
												items={Y_AXIS_POSITION_CHOICES}
												select="single"
												selected-items={[yAxisConfig.orientation]}
												size="md"
												variant="tertiary"
												config-aria={{
													'aria-label': t('Show y axis position values'),
												}}
											/>
										</div>
									) : null}
								</div>
							</div>
						);
					})}
				</div>
			</now-accordion-item>
			<now-accordion-item
				id="x-axis-accordion"
				dividers={{ top: false, bottom: true }}
				header={
					chartType === CHART_TYPES.HORIZONTAL_BAR ? yAxisLabel : xAxisLabel
				}
				expanded={accordionState.xAxis}
				append-to-payload={{ accordionItem: 'xAxis' }}
				on-click={autoScrollToVisibility}>
				<div className="now-accordion-content" slot="content">
					<devx-input
						id="x-axis-title"
						append-to-payload={{
							payloadActionType: X_AXIS_NAME,
						}}
						label={t('Axis title (Optional)')}
						placeholder={t('Untitled')}
						inputValue={xAxisName}
						variant="secondary"
						transparent={true}
					/>
					{DateRangeSelectorView(state)}
				</div>
			</now-accordion-item>
		</Fragment>
	);
};
