//@flow
import { type Uuid } from './uuid';
import { type FieldDefinition } from './field';
import { type Aggregate, type Breakdown } from './indicator';

export type DataSourceType = 'table' | 'analytics' | '';

export const SOURCE_TYPE_INDICATOR = 'analytics';
export const SOURCE_TYPE_TABLE = 'table';
export const SOURCE_TYPE_TABLE_SOURCE = 'source';

/**
 This object used to store properties retrieved from dataConfiguration
 */
export type DataSource = {|
	id: string,
	label: string,
	sourceType: DataSourceType,
	tableName?: string,
	uuid?: Uuid,
	filterQuery?: string,
	predefinedFilterId?: string,
	groupByField?: string,
	stackByField?: string,
|};

export type PayloadAddDataSource = {
	addNewDataSource: boolean,
	dataSourceIndex?: number,
	dataSource: DataSource,
};

/**
 This object is used to cache and provide data loaded from API
*/
export type DataSourceInfo = {|
	displayLabel?: string,

	// Table-specific fields
	tableFields?: FieldDefinition[],
	isDatabaseView?: boolean,

	// Indicator-specific fields
	aggregates?: Aggregate[],
	breakdowns?: Breakdown[],
	scripted?: boolean,
	aggregate?: number,
	type?: IndicatorType,
	frequency?: string,
	frequencyValue?: number,
	isIndicatorRealtime?: boolean,
	unitId?: string,
|};

export type DataSourceInfos = { [string]: DataSourceInfo };

export const INDICATOR_TYPE = {
	AUTOMATED: '1',
	FORMULA: '2',
	MANUAL: '3',
	EXTERNAL: '4',
};

export const AGGREGATE_TYPE = {
	COUNT: 1,
	SUM: 2,
	AVERAGE: 3,
	MIN: 4,
	MAX: 5,
	COUNT_DISTINCT: 6,
};

export type IndicatorType = $Keys<typeof INDICATOR_TYPE>;
