// @flow
// @jsx snabbdom.createElement
import snabbdom from '@servicenow/ui-renderer-snabbdom'; // eslint-disable-line no-unused-vars
import { GROUP_BY_FIELD, STACK_BY_FIELD } from '../constants';

import {
	type DataSource,
	SOURCE_TYPE_TABLE,
} from '../../common/types/data-source';

export const getMissingGroupByIcon = (dataSources: DataSource[]) => {
	if (dataSources.some(ds => !ds.groupByField)) {
		return (
			<now-icon
				className="pillar-error-icon"
				icon="circle-exclamation-fill"></now-icon>
		);
	}

	return undefined;
};

export const extractTableFieldsFromDataToApply = (
	data: {
		[key: string]: string,
	},
	dataSources: DataSource[]
) => {
	const fields = [];

	for (const field in data) {
		const fieldName = data[field];

		// test to check if it's dot-walk
		if (!/\./.test(fieldName)) {
			continue;
		}

		let dataSourceIndex = -1;

		if (field.indexOf(GROUP_BY_FIELD) === 0) {
			dataSourceIndex = Number(field.replace(GROUP_BY_FIELD, ''));
		} else if (field.indexOf(STACK_BY_FIELD) === 0) {
			dataSourceIndex = Number(field.replace(STACK_BY_FIELD, ''));
		} else {
			continue;
		}

		if (!dataSources[dataSourceIndex]) {
			continue;
		}

		const tableName = dataSources[dataSourceIndex].tableName;

		if (dataSources[dataSourceIndex].sourceType === SOURCE_TYPE_TABLE) {
			fields.push({ tableName, fieldName });
		}
	}

	return fields;
};
