// @flow
import { type FieldDefinition } from '../../common/types/field';
import { GLIDE_DURATION, TIMER, CURRENCY, CURRENCY2, PRICE } from './constants';
import { isNumericField } from './is-numeric-field';

/**
 * Verify if the field can be used for sum aggregation
 * Logic extracted from service.dotWalkingFilters.js: sumFilter
 * @param {FieldDefinition} item
 * @return {boolean}
 */
export const canSum = (item: FieldDefinition): boolean => {
	if (
		isNumericField(item.internalType) ||
		item.internalType === GLIDE_DURATION ||
		item.internalType === TIMER ||
		item.internalType === CURRENCY ||
		item.internalType === PRICE ||
		item.internalType === CURRENCY2
	) {
		return true;
	}

	return false;
};
