// @flow

import { type Coeffects } from '@servicenow/ui-core';
import { type State } from './state';

import { dispatchCloseRequestEvent } from '@now-par-components/sn-par-popover';
import { actionTypesSourceConfiguration } from './../source-configuration/actions';

import {
	SN_PAR_PILLAR_CLICKED,
	SN_PAR_PILLAR_ACTION_CLICKED,
} from '@now-par-components/sn-par-pillar';
import { SN_PAR_POPOVER_CLOSE_REQUEST } from '@now-par-components/sn-par-popover';
import {
	ADD_DATA_SOURCE_SELECTED,
	ADD_NEW_DATA_SOURCE,
	CHANGE_DATA_SOURCE_LABEL,
	SAVE_LABEL,
} from './constants';
import { REMOVE_DATA_SOURCE } from '../visualizations-config-panel/actions/action-constants';

const handleAddDataSourceSelected = (coeffects: Coeffects<State, any>) => {
	let stateChanges = {};

	switch (coeffects.action.payload.payloadActionType) {
		case ADD_DATA_SOURCE_SELECTED:
		case ADD_NEW_DATA_SOURCE:
			stateChanges = {
				sourceConfigurationOpened: true,
				addNewDataSource: true,
			};
			break;
	}

	dispatchCloseRequestEvent(SN_PAR_POPOVER_CLOSE_REQUEST);
	coeffects.updateState(stateChanges);
};

const handlePillarSourceClick = (coeffects: Coeffects<State, any>) => {
	const { updateState, dispatch, action } = coeffects;
	const { id, dataSource, dataSourceIndex } = action.payload;

	switch (id) {
		case 'remove':
			dispatch(REMOVE_DATA_SOURCE, { dataSource, dataSourceIndex });
			break;

		case 'rename':
			updateState({
				labelEditingOnIndex: dataSourceIndex,
				labelEditingValue: undefined,
			});
			break;

		case 'save-label':
			handleSaveLabel(coeffects);
			break;

		default:
			dispatchCloseRequestEvent(SN_PAR_POPOVER_CLOSE_REQUEST);

			updateState({
				sourceConfigurationOpened: true,
				addNewDataSource: false,
				editDataSource: dataSource,
				editDataSourceIndex: dataSourceIndex,
				labelEditingOnIndex: undefined,
			});
	}
};

const handleSaveLabel = ({
	state,
	updateState,
	dispatch,
}: Coeffects<State, any>) => {
	const { labelEditingValue, labelEditingOnIndex } = state;

	updateState({
		labelEditingOnIndex: undefined,
	});

	if (labelEditingValue) {
		dispatch(CHANGE_DATA_SOURCE_LABEL, {
			labelEditingValue,
			labelEditingOnIndex,
		});
	}
};

const handleDevxInputChanged = ({
	updateState,
	action,
}: Coeffects<State, any>) =>
	updateState({
		labelEditingValue: action.payload.value,
	});

export default {
	actions: {},
	actionHandlers: {
		'NOW_BUTTON#CLICKED': handleAddDataSourceSelected,
		/**
		 * For now the only aciton is the "edit"
		 * which is also the same action when the user clicks in the entire pillar
		 * @TODO if needed, change the handler of actions to check the action id in case of multiple
		 */
		[SN_PAR_PILLAR_ACTION_CLICKED]: handlePillarSourceClick,
		[SN_PAR_PILLAR_CLICKED]: handlePillarSourceClick,

		'DEVX_INPUT#INPUT_CHANGED': handleDevxInputChanged,
		[SAVE_LABEL]: handleSaveLabel,

		[actionTypesSourceConfiguration.ADD_SOURCE]: (
			coeffects: Coeffects<State, any>
		) => {
			coeffects.updateState({ sourceConfigurationOpened: false });
		},

		[actionTypesSourceConfiguration.CANCEL]: (
			coeffects: Coeffects<State, any>
		) => {
			coeffects.updateState({ sourceConfigurationOpened: false });
		},
	},
};
