import { createCustomElement } from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import view from './view';
import rtlBehavior from '@servicenow/behavior-rtl';
import styles from './styles.scss';

createCustomElement('viz-config-toggle', {
	renderer: { type: snabbdom },
	view,
	properties: {
		checked: {
			default: false,
		},
		label: {
			default: '',
		},
		disabled: {
			default: false,
		},
		disableMargin: {
			default: false,
		},
		infoTooltip: {
			default: '',
		},
	},
	styles,
	behaviors: [{ behavior: rtlBehavior }],
	actionHandlers: {
		'NOW_TOGGLE#CHECKED_SET': ({ dispatch, action }) => {
			switch (action.payload.payloadActionType) {
				case 'VIZ_CONFIG_TOGGLE':
					dispatch('VIZ_CONFIG_TOGGLE#CHECKED_SET', {
						value: action.payload.value,
					});
					break;
			}
		},
	},
});
