//@flow
import { type ChartModel } from '../../visualizations-config-panel/model/chart-model';
import { type FieldDefinitionRequest } from '../../common/types/field';

import { getFieldDefinitionKey } from './get-field-definition-key';
import { getMetricDataSource } from '../metric';

/**
 * This function returns all fields that are dot-walk
 * and need extra metadata information to be loaded
 * @param {ChartModel} chartModel
 */
export function getFieldMetadataRequest(chartModel: ChartModel) {
	const cache = [];
	const fields = [];

	const isDotWalk = field => /\./.test(field);

	const pushToFields = (field: $Shape<FieldDefinitionRequest>) => {
		const key = getFieldDefinitionKey(field);

		if (!~cache.indexOf(key)) {
			cache.push(key);
			fields.push(field);
		}
	};

	chartModel.dataSources.forEach(ds => {
		if (ds.groupByField && isDotWalk(ds.groupByField)) {
			pushToFields({
				tableName: ds.tableName,
				fieldName: ds.groupByField,
			});
		}

		if (ds.stackByField && isDotWalk(ds.stackByField)) {
			pushToFields({
				tableName: ds.tableName,
				fieldName: ds.stackByField,
			});
		}
	});

	chartModel.metrics.forEach(metric => {
		if (metric.aggregateField && isDotWalk(metric.aggregateField)) {
			const ds = getMetricDataSource(metric, chartModel.dataSources);

			pushToFields({
				tableName: ds.tableName,
				fieldName: metric.aggregateField,
			});
		}

		if (metric.trendByField && isDotWalk(metric.trendByField)) {
			const ds = getMetricDataSource(metric, chartModel.dataSources);

			pushToFields({
				tableName: ds.tableName,
				fieldName: metric.trendByField,
			});
		}
	});

	return fields;
}
