export const METRIC_CHART_TYPES = {
	[CHART_TYPES.LINE]: 'line',
	[CHART_TYPES.COLUMN]: 'groupedBar',
	[CHART_TYPES.AREA]: 'area',
	[CHART_TYPES.STEP_LINE]: 'step',
	[CHART_TYPES.SPLINE]: 'spline',
};

export const getChartConfig = (chartType: string): ChartTypeConfig => {
	const chartConfig = CHART_TYPES_CONFIG.find(config => config.id == chartType);

	if (!chartConfig) {
		throw new Error('Cannot find chart config for ' + chartType);
	}

	return chartConfig;
};

export const isSameChartFamily = (chartType1: string, chartType2: string) => {
	if (!chartType1 || !chartType2) {
		return false;
	}

	return (
		getChartConfig(chartType1).menuGroup ===
		getChartConfig(chartType2).menuGroup
	);
};

export const STACKED = 'stacked';
export const SIDE_BY_SIDE = 'side_by_side';
