//@flow
import {
	type DataSource,
	type DataSourceInfos,
} from '../../common/types/data-source';

import { type Metric } from '../../common/types/metric';
import { type FieldDefinitions } from '../../common/types/field';

import { getAggregateOptionsTable } from '../../visualizations-config-panel/actions/model-options';
import { AGGREGATE_FUNCTION_COUNT } from '../../metric-configuration/constants';
import { getOptionLabel } from '../../common/options';

import {
	isTableMetric,
	isIndicatorMetric,
	getMetricDataSource,
} from './metric-datasource';
import { getFieldDefinitionKey } from '../field';

export const getMetricLabel = (
	dataSources: DataSource[],
	dataSourceInfos: DataSourceInfos,
	metric: Metric,
	fieldDefinitions: FieldDefinitions
): string => {
	if (metric.label) {
		return metric.label;
	}

	if (!metric.dataSource) {
		return '';
	}

	const dataSource = getMetricDataSource(metric, dataSources);
	const dataSourceInfo = dataSourceInfos[metric.dataSource];

	if (isTableMetric(metric, dataSources) && metric.aggregateFunction) {
		if (metric.aggregateFunction === AGGREGATE_FUNCTION_COUNT) {
			return dataSource.label;
		}

		if (/\./.test(metric.aggregateField || '')) {
			const key = getFieldDefinitionKey({
				tableName: dataSource.tableName,
				fieldName: metric.aggregateField,
			});

			if (!fieldDefinitions[key]) {
				throw new Error(`Field metadata not found for ${key}`);
			}

			return fieldDefinitions[key].label;
		}

		const aggregateOptions = getAggregateOptionsTable(
			metric.aggregateFunction || '',
			dataSourceInfo
		);

		return getOptionLabel(aggregateOptions, metric.aggregateField);
	} else if (isIndicatorMetric(metric, dataSources) && metric.dataSource) {
		return dataSource.label;
	}

	return '';
};
