import get from 'lodash/get';
import { CHART_TYPES } from '../common/types/chart-type';

export const isChecked = (id, chartModel) =>
	!!get(chartModel, ['additionalSettings', id]);

export const isDisabled = (id, { chartType, additionalSettings }) => {
	switch (id) {
		case 'include_change_percentage':
			return !additionalSettings.include_change;
		case 'include_gap_percentage':
			return !additionalSettings.include_targets;
		case 'showMetricLabel': {
			if (chartType === CHART_TYPES.SINGLE_SCORE) {
				return false;
			}

			return !additionalSettings.showTotal;
		}
		case 'include_forecast_range': {
			return !additionalSettings.include_forecast;
		}
	}

	return false;
};
