// @flow
import { type FieldDefinition } from '../../common/types/field';
import { REFERENCE, DOMAIN_ID, CHOICE, DOCUMENT_ID } from './constants';

/**
 * Verify if the field can be used for count distinct aggregation
 * Logic extracted from service.dotWalkingFilters.js: distinctFilter
 * @param {FieldDefinition} item
 * @return {boolean}
 */
export const canCountDistinct = (item: FieldDefinition): boolean => {
	if (
		item.internalType === REFERENCE ||
		item.internalType === DOMAIN_ID ||
		item.internalType === CHOICE ||
		item.internalType === DOCUMENT_ID ||
		item.name === 'sys_id'
	) {
		return true;
	}

	return false;
};
