export const convertUuidToProp = (
	uuid: Uuid,
	metric?: Metric
): UuidChartAPI => {
	const uuidProp: UuidChartAPI = {
		indicator: uuid.indicator,
		aggregate: metric && metric.aggregateIndicator,
		domain: uuid.domain,
	};

	if (uuid.breakdowns) {
		uuidProp.breakdowns = uuid.breakdowns.map(breakdown => ({
			sys_id: breakdown.sysId,
			elements_filter_sys_id: breakdown.elementsFilterSysId,
			element_sys_ids: breakdown.elementSysIds,
		}));
	}

	return uuidProp;
};

function getSourceTypeConfiguration(metric: Metric, dataSource: DataSource) {
	const config: $Shape<DataConfiguration> = {};

	if (dataSource.sourceType == SOURCE_TYPE_TABLE) {
		if (dataSource.predefinedFilterId) {
			config.source_type = SOURCE_TYPE_TABLE_SOURCE;
			config.source_id = dataSource.predefinedFilterId;
		} else {
			config.source_type = SOURCE_TYPE_TABLE;
			config.source_id = dataSource.tableName;
		}
	} else if (dataSource.sourceType == SOURCE_TYPE_INDICATOR) {
		if (!dataSource.uuid) {
			throw new Error('UUID model is not defined');
		}

		config.source_type = SOURCE_TYPE_INDICATOR;
		config.uuid = convertUuidToProp(dataSource.uuid, metric);
	}

	return config;
}

const isValidAxisRangeValue = (value: any): any => {
	if (
		typeof value === undefined ||
		value === null ||
		!(Number(value) || Number(value) === 0)
	)
		return false;

	return true;
};

const getAxisRange = (axisConfig: AxisConfig) => {
	const rangeFromValue = isValidAxisRangeValue(axisConfig.axisRangeFrom)
		? axisConfig.axisRangeFrom
		: DEFAULT_AXIS_RANGE;

	const rangeToValue = isValidAxisRangeValue(axisConfig.axisRangeTo)
		? axisConfig.axisRangeTo
		: DEFAULT_AXIS_RANGE;

	// Range is not defined
	if (
		rangeFromValue === DEFAULT_AXIS_RANGE &&
		rangeToValue === DEFAULT_AXIS_RANGE
	) {
		return undefined;
	}

	return [rangeFromValue, rangeToValue];
};

export const getExtent = (chartModel: ChartModel): ?ExtentsXenolith => {
	const { yAxisConfigs } = chartModel;

	if (yAxisConfigs.length === 1) {
		const axisRange = getAxisRange(yAxisConfigs[0]);

		if (axisRange) {
			return {
				y: axisRange,
			};
		}
	} else if (yAxisConfigs.length > 1) {
		const extents = {};

		yAxisConfigs.forEach(axisConfig => {
			const axisRange = getAxisRange(axisConfig);

			if (axisRange) {
				extents[axisConfig.id] = axisRange;
			}
		});
		return extents;
	}

	return undefined;
};

export const getYAxisConfig = (chartModel: ChartModel): ?AxisConfigXenolith => {
	const { yAxisConfigs } = chartModel;

	if (yAxisConfigs.length === 1) {
		return {
			title: yAxisConfigs[0].title,
			orientation: yAxisConfigs[0].orientation,
		};
	} else if (yAxisConfigs.length > 1) {
		const yAxisConfig = {};

		yAxisConfigs.forEach(axisConfig => {
			yAxisConfig[axisConfig.id] = {
				title: axisConfig.title,
				orientation: axisConfig.orientation,
			};
		});
		return yAxisConfig;
	}

	return undefined;
};

export const getSeriesConfig = (
	chartModel: ChartModel
): ?SeriesConfigXenolith => {
	const { chartType, metrics } = chartModel;

	if (!TIMESERIES_CHART_TYPES.includes(chartType)) {
		return undefined;
	}

	const seriesConfig = {};

	metrics.forEach((metric, index) => {
		const seriesConfigId = `data_${index}_0`;

		if (metric.axisId) {
			const metricChartType =
				metric.chartType && METRIC_CHART_TYPES[metric.chartType];

			seriesConfig[seriesConfigId] = {
				axis: metric.axisId,
				type: metricChartType,
			};
		}
	});

	return seriesConfig;
};

export const getAxisSettingsFromChartModelToProp = (
	model: ChartModel
): $Shape<StyleConfiguration> => {
	const isAllowed = CHART_TYPES_CONFIG.find(
		e =>
			e.id === model.chartType &&
			(e.menuGroup === 'time' || e.menuGroup === 'bar')
	);

	if (isEmpty(model.axisSettings) || !isAllowed) return {};

	return {
		xAxisConfig: {
			title: model.axisSettings && model.axisSettings.xAxisName,
		},
	};
};
function getStartTime({
	dateRangeShowMax,
	dateRangeAmount,
	dateRangeType,
}: AxisSettings) {
	if (!dateRangeAmount || dateRangeShowMax) {
		return;
	}

	return `P${dateRangeAmount}${dateRangeType || DATE_RANGE_TYPE.MONTHS}`;
}

function convertColorRuleToColorCondition(
	colorRule: ColorRule
): $Shape<ColorCondition> {
	return {
		operator: colorRule.operator,
		value1: colorRule.value1,
		value2: colorRule.value2,
		color_sys_id: colorRule.color,
		order: colorRule.order,
	};
}

function convertColorConditionToColorRule(
	colorCondition: ColorCondition
): $Shape<ColorRule> {
	return {
		operator: colorCondition.operator,
		value1: colorCondition.value1,
		value2: colorCondition.value2,
		color: colorCondition.color_sys_id,
		order: colorCondition.order,
	};
}

function buildPropsChartSettings(
	metric: Metric,
	{ chartSettings: modelChartSettings }: ChartModel
) {
	const chartSettings = {};

	if (metric.label) {
		chartSettings.main_series_label = metric.label;
	}

	if (modelChartSettings) {
		const color = modelChartSettings && modelChartSettings.color;

		if (color && color.type !== COLOR_TYPE_DEFAULT) {
			if (color.type === COLOR_TYPE_SINGLE && metric.colorSysID) {
				chartSettings.color = {
					type: color.type,
					sys_id: metric.colorSysID,
					conditions: !metric.colorRules
						? []
						: metric.colorRules.map(rule =>
								convertColorRuleToColorCondition(rule)
						  ),
				};
			} else if (color.type === COLOR_TYPE_PALETTE && color.sys_id) {
				chartSettings.color = {
					type: color.type,
					sys_id: color.sys_id,
				};
			} else if (color.type === COLOR_TYPE_FIXED) {
				chartSettings.color = {
					type: color.type,
				};
			}
		}
	}

	return isEmpty(chartSettings) ? undefined : chartSettings;
}

function buildModelChartSettings(
	modelChartSettings: PropsChartSettings | void
) {
	if (modelChartSettings && modelChartSettings.color) {
		if (modelChartSettings.color.type === COLOR_TYPE_PALETTE) {
			return {
				color: {
					type: modelChartSettings.color.type,
					sys_id: modelChartSettings.color.sys_id,
				},
			};
		} else if (modelChartSettings.color.type === COLOR_TYPE_SINGLE) {
			return {
				color: {
					type: modelChartSettings.color.type,
					sys_id: modelChartSettings.color.sys_id,
					colorRule: !modelChartSettings.color.conditions
						? []
						: modelChartSettings.color.conditions.map(condition =>
								convertColorConditionToColorRule(condition)
						  ),
				},
			};
		}

		return {
			color: {
				type: modelChartSettings.color.type,
			},
		};
	}

	return undefined;
}

function convertShowOverlappingLabels(styleConfig: StyleConfiguration) {
	if (
		styleConfig.dataLabelConfig &&
		styleConfig.dataLabelConfig.collisions === 'show'
	)
		return true;

	return false;
}

function convertMetricToDataConfiguration(
	model: ChartModel,
	metric: Metric
): DataConfiguration {
	const source = getMetricDataSource(metric, model.dataSources);

	const additional: AdditionalSettings = model.additionalSettings;
	const sourceTypeConfiguration = getSourceTypeConfiguration(metric, source);
	const start_time = model.axisSettings && getStartTime(model.axisSettings);
	const chart_settings = buildPropsChartSettings(metric, model);
	const displaySettings: DisplaySettings = model.displaySettings || {};

	return {
		__viz_config_id: source.id,
		__viz_config_label: source.label,
		aggregate_field: metric.aggregateField,
		aggregate_function: metric.aggregateFunction,
		aggregation_source: metric.aggregationSource,
		trend_field: metric.trendByField,
		trend_interval: model.trendByFrequency,
		stack_by_field: source.stackByField,
		filter_query: source.filterQuery,
		group_by: source.groupByField,
		number_of_groups: model.groupByMax,
		selected_groups:
			model.noOfGroupBasedOn === NO_OF_GROUP_BASED_ON_FIRST_METRIC
				? 'data_source_with_least_order'
				: undefined,
		show_other: model.showOthers,
		start_time,
		include_scores: additional.include_scores,
		include_benchmark: additional.include_benchmark,
		include_forecast: additional.include_forecast,
		include_forecast_range:
			additional.include_forecast === false
				? false
				: additional.include_forecast_range,
		include_global_targets: additional.include_targets,
		include_sparkline_global_targets: additional.include_targets,
		include_sparkline_personal_targets: additional.include_targets,
		include_target_review_date: additional.include_targets,
		include_targets: additional.include_targets,
		include_thresholds: additional.include_thresholds,
		include_change: additional.include_change,
		include_change_percentage:
			additional.include_change === false
				? false
				: additional.include_change_percentage,
		include_comments: additional.include_comments,
		include_initiatives: additional.include_initiatives,
		include_sparkline: additional.include_sparkline,
		include_gap_percentage:
			additional.include_targets === false
				? false
				: additional.include_gap_percentage,
		include_trend: additional.include_trend,
		include_band: additional.include_band,
		decimal_precision:
			model.dataLabelSettings && model.dataLabelSettings.decimalPrecision,
		sort_by: displaySettings.sortByField,
		sort_order: displaySettings.orderByField,
		display_zero: displaySettings.displayZero,
		chart_settings,
		...sourceTypeConfiguration,
	};
}

export function convertModelToProps(model: ChartModel): ChartProps {
	const additional: AdditionalSettings = model.additionalSettings;
	const filters: FiltersSettings = model.filtersSettings;
	const displaySettings: DisplaySettings = model.displaySettings || {};
	const xenolithProperties: xenolithProperties = model.xenolithProperties || {};
	const getAxisSettings = getAxisSettingsFromChartModelToProp(model);

	const props: ChartProps = {
		chartType: model.chartType,
		dataConfigurations: model.metrics.map(
			convertMetricToDataConfiguration.bind(null, model)
		),
		styleConfigurations: {
			...(model.isStacked === STACKED && { type: STACKED }),
			width: model.chartWidth,
			height: model.chartHeight,
			showDataLabels: !!(
				model.dataLabelSettings && model.dataLabelSettings.showDataLabels
			),
			...getAxisSettings,
			scoreSize: displaySettings.scoreSize,
			...pick(xenolithProperties, ['heading', 'headingPosition']),
			yAxisConfig: getYAxisConfig(model),
			seriesConfig: getSeriesConfig(model),
			extents: getExtent(model),
			canClick: model.canClick,
		},
		variant: model.variant,
		label: model.label,
		border: model.border,
		isRealtime: model.realtime,
		showRealtimeIndicator: model.showRealtimeIndicator,
		refreshInterval: model.refreshInterval,
		kpiDetailsNavigation: undefined, // TODO: Value will be dynamic in Quebec
		followFilters: filters.followFilters,
		hideFiltersInfo: filters.hideFiltersInfo,
		// This prop is just sent to UXF with value '@state.parFilters' based on followFilters boolean value, we do not read it back.
		filterConfigurations: filters.followFilters
			? '@state.parFilters'
			: undefined,
		hideHeader: model.hideHeader,
		refreshFrequency: model.refreshFrequency,
	};

	const isTimeSeriesChart = CHART_TYPES_CONFIG.find(
		e =>
			e.id === model.chartType &&
			e.menuGroup === 'time' &&
			model.chartType !== CHART_TYPES.COLUMN
	);

	const dataLabelConfig = {};

	if (DONUT_CHART_TYPES.includes(model.chartType)) {
		dataLabelConfig.showTotal = !!additional.showTotal;

		if (xenolithProperties.totalLabel) {
			dataLabelConfig.totalLabel = xenolithProperties.totalLabel;
		}
	}

	const dataLabelSettings = model.dataLabelSettings;

	if (dataLabelSettings && dataLabelSettings.showDataLabels !== undefined) {
		dataLabelConfig.align =
			dataLabelSettings.dataLabelPosition || DATA_LABEL_POSITION_MIDDLE;

		const showOverlappingLabel =
			dataLabelSettings.showOverlappingLabels === true ? 'show' : 'hide';

		dataLabelConfig.collisions = showOverlappingLabel || 'hide';

		dataLabelConfig.stacked =
			dataLabelSettings.stackedValue || DATA_LABEL_STACKED_INDIVIDUAL;

		dataLabelConfig.valueType =
			dataLabelSettings.showDataLabelAs || SHOW_DATA_LABEL_AS_VALUE_AND_PERCENT;
	}

	if (isTimeSeriesChart) {
		dataLabelConfig.type = 'peakValley';
	} else if (model.chartType === CHART_TYPES.COLUMN) {
		dataLabelConfig.type = 'all';
	}

	props.styleConfigurations.dataLabelConfig = dataLabelConfig;

	const legendSettings = model.legendSettings;

	if (legendSettings && legendSettings.showLegend !== undefined) {
		props.styleConfigurations.hideRegister = !legendSettings.showLegend;
		props.styleConfigurations.registerConfig = {
			legendOnly: !legendSettings.showLegendValue,
			showPercentages: legendSettings.showLegendPercentage,
		};
		props.styleConfigurations.registerPosition =
			legendSettings.legendPosition || LEGEND_POSITION_BOTTOM;
	}

	return props;
}

export const convertUuidToModel = (uuid: UuidChartAPI): Uuid => {
	const uuidModel: Uuid = {
		indicator: uuid.indicator,
		domain: uuid.domain,
		breakdowns: [],
	};

	if (uuid.breakdowns) {
		uuidModel.breakdowns = uuid.breakdowns.map(breakdown => ({
			sysId: breakdown.sys_id,
			elementsFilterSysId: breakdown.elements_filter_sys_id,
			elementSysIds: breakdown.element_sys_ids,
		}));
	}

	return uuidModel;
};

export const getSeriesConfigFromProps = (
	chartProps: ChartProps,
	dataConfigurationIndex: number
) => {
	const { chartType, styleConfigurations } = chartProps;

	let axisId;
	let metricChartType;

	if (BAR_CHART_TYPES.includes(chartType)) {
		axisId = PRIMARY_AXIS;
	}

	if (TIMESERIES_CHART_TYPES.includes(chartType)) {
		const seriesConfigId = `data_${dataConfigurationIndex}_0`;

		const seriesConfig =
			styleConfigurations.seriesConfig &&
			styleConfigurations.seriesConfig[seriesConfigId];

		axisId = (seriesConfig && seriesConfig.axis) || PRIMARY_AXIS;
		metricChartType = seriesConfig && seriesConfig.type;
	}

	return {
		axisId,
		chartType: metricChartType,
	};
};

function getMetricsFromProps(chartProps: ChartProps): $Shape<ChartModel> {
	const properties: $Shape<ChartModel> = {};
	const { dataConfigurations = [] } = chartProps;

	const dataSources: DataSource[] = [];
	const metrics: Metric[] = [];

	dataConfigurations.forEach((dataConfig, index) => {
		let dataSource = dataSources.find(
			ds => ds.id === dataConfig.__viz_config_id
		);

		if (!dataSource) {
			dataSource = getModelSourceTypeConfiguration(dataConfig, dataSources);
			dataSources.push(dataSource);
		}

		const metric: Metric = { dataSource: dataSource.id };

		if (dataSource.sourceType === SOURCE_TYPE_TABLE) {
			metric.aggregateField = dataConfig.aggregate_field;
			metric.aggregateFunction = dataConfig.aggregate_function;
			metric.aggregationSource = dataConfig.aggregation_source;
		} else if (dataSource.sourceType === SOURCE_TYPE_INDICATOR) {
			metric.aggregateIndicator = dataConfig.uuid && dataConfig.uuid.aggregate;
		}

		if (dataConfig.trend_field) {
			metric.trendByField = dataConfig.trend_field;
		}

		metric.label =
			dataConfig.chart_settings && dataConfig.chart_settings.main_series_label;
		const color = dataConfig.chart_settings && dataConfig.chart_settings.color;

		if (color && color.type === COLOR_TYPE_SINGLE) {
			metric.colorSysID = color.sys_id;
			metric.colorRules = !color.conditions
				? []
				: color.conditions.map(condition =>
						convertColorConditionToColorRule(condition)
				  );
		}

		const { axisId, chartType } = getSeriesConfigFromProps(chartProps, index);

		metric.axisId = axisId;

		const metricChartType = Object.keys(METRIC_CHART_TYPES).find(
			key => METRIC_CHART_TYPES[key] === chartType
		);

		metric.chartType = metricChartType;

		metrics.push(metric);
	});
	properties.dataSources = dataSources;
	properties.metrics = metrics;

	return properties;
}

export function getModelSourceTypeConfiguration(
	dataConfig: DataConfiguration,
	dataSources: DataSource[]
): DataSource {
	let dataSource: $Shape<DataSource> = {
		label: dataConfig.__viz_config_label,
		groupByField: dataConfig.group_by,
		stackByField: dataConfig.stack_by_field,
	};

	switch (dataConfig.source_type) {
		case SOURCE_TYPE_TABLE:
			Object.assign(dataSource, {
				sourceType: SOURCE_TYPE_TABLE,
				tableName: dataConfig.source_id,
				filterQuery: dataConfig.filter_query,
			});
			break;
		case SOURCE_TYPE_TABLE_SOURCE:
			Object.assign(dataSource, {
				sourceType: SOURCE_TYPE_TABLE,
				filterQuery: dataConfig.filter_query,
				predefinedFilterId: dataConfig.source_id,
			});
			break;
		case SOURCE_TYPE_INDICATOR: {
			if (!dataConfig.uuid) {
				throw new Error('UUID is missing from dataConfiguration');
			}

			Object.assign(dataSource, {
				sourceType: SOURCE_TYPE_INDICATOR,
				uuid: convertUuidToModel(dataConfig.uuid),
			});
			break;
		}
		default:
			dataSource = { id: undefined, label: undefined, sourceType: '' };
			break;
	}

	dataSource.id =
		dataConfig.__viz_config_id ||
		getDataSourceUniqueId(dataSource, dataSources);

	return dataSource;
}

function stripUndefined<T>(o: T): T {
	return omitBy((o: any), x => x === undefined);
}

function parseStarTime(period?: string): AxisSettings {
	if (!period) {
		return {
			dateRangeShowMax: true,
		};
	}

	const duration = moment.duration(period);
	const years = duration.asYears();
	const months = duration.asMonths();
	const days = duration.asDays();

	if (years >= 1 && Number.isInteger(years)) {
		return { dateRangeAmount: years, dateRangeType: DATE_RANGE_TYPE.YEARS };
	}

	if (months >= 1 && Number.isInteger(months)) {
		return { dateRangeAmount: months, dateRangeType: DATE_RANGE_TYPE.MONTHS };
	}

	return { dateRangeAmount: days, dateRangeType: DATE_RANGE_TYPE.DAYS };
}

export const getLegendOptions = (
	styleConfig: $Shape<StyleConfiguration>
): ?LegendSettings => {
	if (styleConfig.hideRegister !== undefined) {
		return {
			showLegend: !styleConfig.hideRegister,
			showLegendValue: !get(styleConfig, 'registerConfig.legendOnly'),
			showLegendPercentage: get(styleConfig, 'registerConfig.showPercentages'),
			legendPosition: styleConfig.registerPosition || LEGEND_POSITION_BOTTOM,
		};
	}

	return undefined;
};

export const getAxisRangeFromProps = (
	axisRange?: Array<?number | ?string>
): $Shape<AxisConfig> => {
	if (axisRange && axisRange.length > 1) {
		return {
			axisRangeFrom:
				axisRange[0] === DEFAULT_AXIS_RANGE ? undefined : axisRange[0],
			axisRangeTo:
				axisRange[1] === DEFAULT_AXIS_RANGE ? undefined : axisRange[1],
		};
	}

	return {
		axisRangeFrom: undefined,
		axisRangeTo: undefined,
	};
};

export const getYAxisConfigFromProps = (
	chartProps: ChartProps
): AxisConfig[] => {
	const { chartType, styleConfigurations } = chartProps;

	if (
		!BAR_CHART_TYPES.includes(chartType) &&
		!TIMESERIES_CHART_TYPES.includes(chartType)
	) {
		return [];
	}

	const hasAxisDefined =
		styleConfigurations.yAxisConfig &&
		styleConfigurations.yAxisConfig[PRIMARY_AXIS];

	if (hasAxisDefined) {
		const yAxisConfigs = [];

		for (const attribute in styleConfigurations.yAxisConfig) {
			const isValidAxisId =
				attribute === PRIMARY_AXIS || attribute.indexOf(AXIS_PREFIX) === 0;

			if (isValidAxisId) {
				const yAxisConfig = styleConfigurations.yAxisConfig[attribute];

				const axisRange =
					styleConfigurations.extents &&
					getAxisRangeFromProps(styleConfigurations.extents[attribute]);

				yAxisConfigs.push({
					id: attribute,
					title: yAxisConfig.title,
					orientation: yAxisConfig.orientation,
					...axisRange,
				});
			}
		}

		sortAxisConfigs(yAxisConfigs);

		return yAxisConfigs;
	}

	const yAxisRange =
		styleConfigurations.extents &&
		getAxisRangeFromProps(styleConfigurations.extents.y);

	return [
		{
			id: PRIMARY_AXIS,
			title:
				styleConfigurations.yAxisConfig &&
				styleConfigurations.yAxisConfig.title,
			orientation:
				(styleConfigurations.yAxisConfig &&
					styleConfigurations.yAxisConfig.orientation) ||
				AXIS_ORIENTATION_LEFT,
			...yAxisRange,
		},
	];
};

export const getAxisSettingsFromPropToModal = (
	styleConfig: $Shape<StyleConfiguration>
): AxisSettings => {
	return {
		xAxisName: styleConfig.xAxisConfig && styleConfig.xAxisConfig.title,
	};
};

export function convertPropsToModel(
	props: ChartProps,
	state: State
): ChartModel {
	const dataConfig: DataConfiguration = get(props, 'dataConfigurations[0]', {});
	const styleConfig: StyleConfiguration = props.styleConfigurations || {};
	const metricConfiguration = getMetricsFromProps(props);
	const legendOptions = getLegendOptions(styleConfig);
	const axisSettings = getAxisSettingsFromPropToModal(styleConfig);
	const axisTimeSettings = parseStarTime(dataConfig.start_time);
	const stateXAxisSettings: AxisSettings =
		state.chartModel && state.chartModel.axisSettings
			? state.chartModel.axisSettings
			: {};

	const showTotal = get(styleConfig, 'dataLabelConfig.showTotal');

	const additionalSettings: AdditionalSettings = stripUndefined({
		include_scores: dataConfig.include_scores,
		include_benchmark: dataConfig.include_benchmark,
		include_forecast: dataConfig.include_forecast,
		include_forecast_range:
			dataConfig.include_forecast === false
				? false
				: dataConfig.include_forecast_range,
		include_targets: dataConfig.include_targets,
		include_thresholds: dataConfig.include_thresholds,
		include_change: dataConfig.include_change,
		include_comments: dataConfig.include_comments,
		include_initiatives: dataConfig.include_initiatives,
		include_sparkline: dataConfig.include_sparkline,
		include_trend: dataConfig.include_trend,
		include_band: dataConfig.include_band,
		showTotal,
		include_gap_percentage:
			dataConfig.include_global_targets === false
				? false
				: dataConfig.include_gap_percentage,
		include_change_percentage:
			dataConfig.include_change === false
				? false
				: dataConfig.include_change_percentage,
	});

	const filtersSettings: FiltersSettings = {
		followFilters: props.followFilters,
		hideFiltersInfo: props.hideFiltersInfo,
	};

	const model: ChartModel = {
		chartType: props.chartType,

		trendByFrequency: dataConfig.trend_interval,
		isStacked: styleConfig.type,
		groupByMax: dataConfig.number_of_groups,
		noOfGroupBasedOn:
			dataConfig.selected_groups === 'data_source_with_least_order'
				? NO_OF_GROUP_BASED_ON_FIRST_METRIC
				: NO_OF_GROUP_BASED_ON_PER_METRIC,
		showOthers:
			dataConfig.selected_groups === 'data_source_with_least_order'
				? false
				: dataConfig.show_other,
		additionalSettings,
		filtersSettings,
		...metricConfiguration,
		chartWidth: styleConfig.width || 'auto',
		chartHeight: styleConfig.height || 'auto',
		dataLabelSettings: {
			showDataLabels: !!styleConfig.showDataLabels,
			decimalPrecision: dataConfig.decimal_precision,
			dataLabelPosition:
				get(styleConfig, 'dataLabelConfig.align') || DATA_LABEL_POSITION_MIDDLE,
			showOverlappingLabels: convertShowOverlappingLabels(styleConfig),
			stackedValue:
				get(styleConfig, 'dataLabelConfig.stacked') ||
				DATA_LABEL_STACKED_INDIVIDUAL,
			showDataLabelAs:
				get(styleConfig, 'dataLabelConfig.valueType') ||
				SHOW_DATA_LABEL_AS_VALUE_AND_PERCENT,
		},
		axisSettings: {
			...stateXAxisSettings,
			...axisSettings,
			...axisTimeSettings,
		},
		variant: props.variant,
		border: props.border,
		label: props.label,
		realtime: props.isRealtime,
		showRealtimeIndicator: props.showRealtimeIndicator,
		refreshInterval: props.refreshInterval,
		chartSettings: buildModelChartSettings(dataConfig.chart_settings),
		xenolithProperties: {
			heading: styleConfig.heading,
			headingPosition: styleConfig.headingPosition,
		},
		displaySettings: {
			sortByField: dataConfig.sort_by,
			orderByField: dataConfig.sort_order,
			displayZero: dataConfig.display_zero,
			scoreSize: styleConfig.scoreSize,
		},
		hideHeader: !!props.hideHeader,
		yAxisConfigs: getYAxisConfigFromProps(props),
		refreshFrequency: props.refreshFrequency,
		canClick: styleConfig.canClick,
	};

	if (
		(styleConfig.headingPosition &&
			styleConfig.heading &&
			props.chartType === CHART_TYPES.SINGLE_SCORE) ||
		(DONUT_CHART_TYPES.includes(props.chartType) &&
			get(styleConfig, 'dataLabelConfig.totalLabel') &&
			showTotal)
	) {
		model.additionalSettings.showMetricLabel = true;
	}

	if (legendOptions) {
		model.legendSettings = legendOptions;
	}

	return model;
}
