// @flow
import { type Coeffects } from '@servicenow/ui-core';
import { type State } from '../state';

import {
	SET_CHART_TYPE,
	CHART_TYPE_SELECTED,
	SET_SHOW_LEGEND,
	SET_SHOW_LEGEND_VALUE,
	SET_SHOW_LEGEND_PERCENTAGE,
	SET_LEGEND_POSITION,
	SET_DATA_LABEL_POSITION,
	SET_DATA_LABEL_SETTINGS,
	SET_SHOW_DATA_LABEL_AS,
	SET_DATA_LABEL_STACKED_VALUE,
	SET_OVERLAPPING_LABELS,
	SET_SORT_BY,
	SET_ORDER_BY,
	SET_AXIS_SETTINGS,
	SET_Y_AXIS_POSITION_VALUE,
	X_AXIS_NAME,
	Y_AXIS_NAME,
	AXIS_RANGE_FROM,
	AXIS_RANGE_TO,
	X_AXIS_TIME_TYPE_INPUT,
	X_AXIS_TIME_INPUT,
	X_AXIS_SHOW_MAX,
	SET_BORDER,
	SET_VARIANT,
	SET_REALTIME,
	SET_REALTIME_INDICATOR,
	SET_MODEL_PROPS,
	SET_DECIMAL_PRECISION,
	SET_AUTO_UPDATE,
	UPDATE_VISUALIZATION,
	SET_DISPLAY_ZERO,
	SET_HIDE_HEADER,
	SET_Y_AXIS_CONFIG,
	SET_SINGLE_SCORE_SIZE,
	SET_DATA_SOURCE_ERROR,
	SET_CONTAINER_STYLE,
	SET_CAN_CLICK,
} from './action-constants';
import {
	INPUT_BLUR,
	DEFAULT_AXIS_RANGE,
	DEFAULT_DECIMAL_PRECISION,
} from '../views/view-constants';

export const uiActionHandlers = {
	'NOW_DROPDOWN#SELECTED_ITEMS_SET': (coeffects: Coeffects<State, any>) => {
		switch (coeffects.action.payload.payloadActionType) {
			case X_AXIS_TIME_TYPE_INPUT:
				coeffects.dispatch(SET_AXIS_SETTINGS, {
					dateRangeType: coeffects.action.payload.value[0],
				});
				break;
			case CHART_TYPE_SELECTED: {
				const newChartType = coeffects.action.payload.value[0];

				coeffects.dispatch(SET_CHART_TYPE, {
					value: newChartType,
				});
				break;
			}
			case SET_LEGEND_POSITION: {
				const legendPosition = coeffects.action.payload.value[0];

				coeffects.dispatch(SET_LEGEND_POSITION, {
					legendPosition,
				});
				break;
			}
			case SET_SHOW_DATA_LABEL_AS: {
				coeffects.dispatch(SET_DATA_LABEL_SETTINGS, {
					showDataLabelAs: coeffects.action.payload.value[0],
				});
				break;
			}
			case SET_DATA_LABEL_POSITION: {
				const dataLabelPosition = coeffects.action.payload.value[0];

				coeffects.dispatch(SET_DATA_LABEL_POSITION, {
					dataLabelPosition,
				});
				break;
			}
			case SET_DATA_LABEL_STACKED_VALUE: {
				const stackedValue = coeffects.action.payload.value[0];

				coeffects.dispatch(SET_DATA_LABEL_POSITION, {
					stackedValue,
				});
				break;
			}
			case SET_Y_AXIS_POSITION_VALUE: {
				coeffects.dispatch(SET_Y_AXIS_CONFIG, {
					axisId: coeffects.action.payload.axisId,
					axisConfig: {
						orientation: coeffects.action.payload.value[0],
					},
				});
				break;
			}
			case SET_SORT_BY: {
				coeffects.action.stopPropagation();
				coeffects.dispatch(SET_SORT_BY, {
					sortByField: coeffects.action.payload.value[0],
				});
				break;
			}
			case SET_SINGLE_SCORE_SIZE: {
				coeffects.action.stopPropagation();
				coeffects.dispatch(SET_SINGLE_SCORE_SIZE, {
					scoreSize: coeffects.action.payload.value[0],
				});
				break;
			}
			case SET_VARIANT: {
				const {
					action: { payload },
				} = coeffects;

				let variant = coeffects.action.payload.value[0];

				if (payload.shouldReplaceStyle) {
					coeffects.dispatch(SET_CONTAINER_STYLE, {
						styles: {
							height: payload.items[0].value,
						},
					});

					variant = payload.items[0].id === 'auto' ? 'AUTO' : 'CUSTOM';
				}

				coeffects.dispatch(SET_MODEL_PROPS, {
					variant,
				});

				break;
			}
		}
	},
	'DEVX_INPUT#INPUT_CHANGED': {
		modifier: {
			name: 'debounce',
			delay: 300,
		},
		effect: (coeffects: Coeffects<State, any>) => {
			coeffects.action.stopPropagation();
			switch (coeffects.action.payload.payloadActionType) {
				case X_AXIS_TIME_INPUT: {
					const value = coeffects.action.payload.value.replace(/[^0-9.]/g, '');

					if (value === '' || Number.isNaN(Number(value))) {
						return;
					}

					coeffects.dispatch(SET_AXIS_SETTINGS, {
						dateRangeAmount: Number(value),
					});
					break;
				}
			}
		},
	},
	'NOW_TABS#SELECTED_ITEM_SET': (coeffects: Coeffects<State, any>) => {
		coeffects.updateState({
			selectedTab: coeffects.action.payload.value,
		});
	},
	'VIZ_CONFIG_TOGGLE#CHECKED_SET': (coeffects: Coeffects<State, any>) => {
		switch (coeffects.action.payload.payloadActionType) {
			case SET_BORDER: {
				coeffects.dispatch(SET_MODEL_PROPS, {
					border: coeffects.action.payload.value,
				});
				break;
			}
			case SET_REALTIME: {
				coeffects.dispatch(SET_MODEL_PROPS, {
					realtime: coeffects.action.payload.value,
				});
				break;
			}
			case SET_REALTIME_INDICATOR: {
				coeffects.dispatch(SET_MODEL_PROPS, {
					showRealtimeIndicator: coeffects.action.payload.value,
				});
				break;
			}
			case SET_SHOW_LEGEND: {
				coeffects.dispatch(SET_SHOW_LEGEND, {
					showLegend: coeffects.action.payload.value,
				});
				break;
			}
			case SET_SHOW_LEGEND_VALUE: {
				coeffects.dispatch(SET_SHOW_LEGEND_VALUE, {
					showLegendValue: coeffects.action.payload.value,
				});
				break;
			}
			case SET_SHOW_LEGEND_PERCENTAGE: {
				coeffects.dispatch(SET_SHOW_LEGEND_PERCENTAGE, {
					showLegendPercentage: coeffects.action.payload.value,
				});
				break;
			}
			case SET_DATA_LABEL_SETTINGS: {
				coeffects.dispatch(SET_DATA_LABEL_SETTINGS, {
					showDataLabels: coeffects.action.payload.value,
				});
				break;
			}
			case SET_OVERLAPPING_LABELS: {
				coeffects.dispatch(SET_OVERLAPPING_LABELS, {
					showOverlappingLabels: coeffects.action.payload.value,
				});
				break;
			}
			case X_AXIS_SHOW_MAX: {
				coeffects.dispatch(SET_AXIS_SETTINGS, {
					dateRangeShowMax: coeffects.action.payload.value,
				});
				break;
			}
			case SET_AUTO_UPDATE: {
				coeffects.dispatch(SET_AUTO_UPDATE, coeffects.action.payload);
				break;
			}
			case SET_DISPLAY_ZERO: {
				coeffects.dispatch(SET_DISPLAY_ZERO, {
					displayZero: coeffects.action.payload.value,
				});
				break;
			}
			case SET_HIDE_HEADER: {
				coeffects.dispatch(SET_MODEL_PROPS, {
					hideHeader: !coeffects.action.payload.value,
				});
				break;
			}
			case SET_CAN_CLICK: {
				coeffects.dispatch(SET_CAN_CLICK, {
					canClick: coeffects.action.payload.value,
				});
				break;
			}
		}
	},
	'DEVX_INPUT#INPUT_BLUR': (coeffects: Coeffects<State, any>) => {
		coeffects.action.stopPropagation();
		switch (coeffects.action.payload.payloadActionType) {
			case X_AXIS_NAME: {
				coeffects.dispatch(SET_AXIS_SETTINGS, {
					xAxisName: coeffects.action.payload.value,
				});
				break;
			}
			case Y_AXIS_NAME: {
				coeffects.dispatch(SET_Y_AXIS_CONFIG, {
					axisId: coeffects.action.payload.axisId,
					axisConfig: {
						title: coeffects.action.payload.value,
					},
				});
				break;
			}
			case X_AXIS_TIME_INPUT: {
				if (!coeffects.host.shadowRoot) {
					return;
				}

				const devxInput = coeffects.host.shadowRoot.querySelector(
					'.viz-config-x-axis-time-amount'
				);

				const input: any =
					devxInput && devxInput.shadowRoot
						? devxInput.shadowRoot.querySelector('input')
						: undefined;

				if (
					input &&
					(input.value === '' || Number.isNaN(Number(input.value)))
				) {
					input.value =
						coeffects.state.chartModel.axisSettings &&
						coeffects.state.chartModel.axisSettings.dateRangeAmount;
				}

				break;
			}
		}
	},
	[INPUT_BLUR]: (coeffects: Coeffects<State, any>) => {
		const elementRef = coeffects.action.payload.ref;

		let value = elementRef.value;

		switch (coeffects.action.payload.type) {
			case AXIS_RANGE_FROM: {
				if (value === '' || Number.isNaN(value)) {
					value = DEFAULT_AXIS_RANGE;
					elementRef.value = null;
				}

				coeffects.dispatch(SET_Y_AXIS_CONFIG, {
					axisId: coeffects.action.payload.axisId,
					axisConfig: {
						axisRangeFrom: value,
					},
				});
				break;
			}
			case AXIS_RANGE_TO: {
				if (value === '' || Number.isNaN(value)) {
					value = DEFAULT_AXIS_RANGE;
					elementRef.value = null;
				}

				coeffects.dispatch(SET_Y_AXIS_CONFIG, {
					axisId: coeffects.action.payload.axisId,
					axisConfig: {
						axisRangeTo: value,
					},
				});
				break;
			}
			case SET_DECIMAL_PRECISION: {
				if (value === '') {
					value = DEFAULT_DECIMAL_PRECISION;
					elementRef.value = DEFAULT_DECIMAL_PRECISION;
				}

				coeffects.dispatch(SET_DATA_LABEL_SETTINGS, {
					decimalPrecision: Number(value),
				});
				break;
			}
		}
	},
	'NOW_BUTTON#CLICKED': ({ action, dispatch }: Coeffects<State, any>) => {
		switch (action.payload.payloadActionType) {
			case UPDATE_VISUALIZATION:
				dispatch(UPDATE_VISUALIZATION, {});
				break;
		}
	},
	'NOW_BUTTON_STATEFUL#SELECTED_SET': (coeffects: Coeffects<State, any>) => {
		switch (coeffects.action.payload.payloadActionType) {
			case SET_ORDER_BY: {
				coeffects.action.stopPropagation();
				coeffects.dispatch(SET_ORDER_BY, {
					orderByField: coeffects.action.payload.value,
				});
				break;
			}
		}
	},
	'NOW_ACCORDION_ITEM#EXPANDED_SET': (coeffects: Coeffects<State, any>) => {
		const { accordionItem, value } = coeffects.action.payload;

		if (accordionItem) {
			coeffects.action.stopPropagation();
			coeffects.updateState({
				operation: 'set',
				path: `accordionState.${accordionItem}`,
				value: value,
			});
		}
	},

	'NOW_INPUT#VALUE_SET': (coeffects: Coeffects<State, any>) => {
		const { name, value } = coeffects.action.payload;

		coeffects.action.stopPropagation();
		if (name) {
			coeffects.dispatch(SET_MODEL_PROPS, {
				[name]: value,
			});
		}
	},

	[SET_DATA_SOURCE_ERROR]: ({
		updateState,
		action,
	}: Coeffects<State, { result: string }>) => {
		updateState({
			isLoadingDataSource: {},
			isInitializingDotWalkFields: false,
			dataSourceError: action.payload.result,
		});
	},
};
