// @flow
// @jsx snabbdom.createElement
import '@servicenow/now-tooltip';
import 'sn-devx-components';

import snabbdom, { Fragment } from '@servicenow/ui-renderer-snabbdom'; // eslint-disable-line no-unused-vars
import { t } from 'sn-translate';
import { type State } from '../state';
import {
	X_AXIS_TIME_INPUT,
	X_AXIS_TIME_TYPE_INPUT,
	X_AXIS_SHOW_MAX,
} from '../actions/action-constants';
import { X_AXIS_DATE_RANGE_TYPES } from './view-constants';
import { TIMESERIES_CHART_TYPES } from '../../common/chart-settings';
import { SOURCE_TYPE_INDICATOR } from '../../common/types/data-source';
import { getMetricFrequency } from '../../helpers/metric';

function XAxisDateRangeView(state: State) {
	const {
		chartModel: { axisSettings, metrics },
		dataSourceInfos,
	} = state;

	const frequencyValue = getMetricFrequency(metrics[0], dataSourceInfos) || 0;

	const filteredRangeTypes = X_AXIS_DATE_RANGE_TYPES.filter(
		type => type.frequencyRange >= frequencyValue
	);

	const xAxisDateRangePopOverContent =
		'Define the number of periods to show on the horizontal axis.';

	return (
		<Fragment>
			<div className="viz-config-section-divider">
				{t('Date range') + ' '}
				<now-popover interactionType="none">
					<now-icon slot="trigger" icon="info-circle-outline" />
					<viz-config-popover-content
						slot="content"
						popOverContent={xAxisDateRangePopOverContent}
					/>
				</now-popover>
			</div>
			<viz-config-toggle
				id="x-axis-show-max-range"
				append-to-payload={{ payloadActionType: X_AXIS_SHOW_MAX }}
				checked={axisSettings && axisSettings.dateRangeShowMax}
				label={t('Show maximum range')}
			/>
			<div className="viz-config-select-range">
				<devx-input
					id="x-axis-time"
					label={t('Specific date range')}
					append-to-payload={{
						payloadActionType: X_AXIS_TIME_INPUT,
					}}
					disabled={axisSettings && axisSettings.dateRangeShowMax}
					inputValue={axisSettings && axisSettings.dateRangeAmount}
					transparent={true}
					maxlength={2}
					variant="secondary"
					className="viz-config-x-axis-time-amount"
				/>
				<now-dropdown
					id="x-axis-time-type"
					append-to-payload={{
						payloadActionType: X_AXIS_TIME_TYPE_INPUT,
					}}
					disabled={axisSettings && axisSettings.dateRangeShowMax}
					items={filteredRangeTypes}
					select="single"
					selected-items={[axisSettings && axisSettings.dateRangeType]}
					size="md"
					variant="tertiary"
					config-aria={{
						'aria-label': t('Select date range period'),
					}}
				/>
			</div>
		</Fragment>
	);
}

export default (state: State) => {
	const {
		chartModel: { chartType, dataSources },
	} = state;

	const isIndicatorTimeseries =
		TIMESERIES_CHART_TYPES.includes(chartType) &&
		dataSources[0].sourceType === SOURCE_TYPE_INDICATOR;

	return isIndicatorTimeseries ? XAxisDateRangeView(state) : undefined;
};
