// @flow
// @jsx snabbdom.createElement
import snabbdom, { Fragment } from '@servicenow/ui-renderer-snabbdom'; // eslint-disable-line no-unused-vars
import { t } from 'sn-translate';
import { type State } from '../state';

import { autoScrollToVisibility } from '../../helpers/now-accordion/auto-scroll-to-visibility';
import { isAnyDataSourceGroupByReportRange } from '../../helpers/data-source/index';
import { doesShowSortBy } from '../../helpers/display-settings/helpers';

import { SET_SORT_BY, SET_ORDER_BY } from '../actions/action-constants';

import {
	SORT_BY_OPTIONS_RANGE,
	SORT_BY_OPTIONS_NON_RANGE,
	SORT_BY_RANGE,
	SORT_BY_VALUE,
	ORDER_ASC,
	ORDER_DESC,
} from './view-constants';

export default (state: State) => {
	const {
		chartModel: {
			chartType,
			displaySettings: { sortByField, orderByField },
			dataSources,
		},
		dataSourceInfos,
		accordionState,
	} = state;

	const showSortBy = doesShowSortBy(chartType);

	if (!showSortBy) return null;

	const groupByReportRange = isAnyDataSourceGroupByReportRange(
		dataSources,
		dataSourceInfos
	);

	const sortByItems = groupByReportRange
		? SORT_BY_OPTIONS_RANGE
		: SORT_BY_OPTIONS_NON_RANGE;

	const selectedSortByItem =
		sortByField && (groupByReportRange || sortByField !== SORT_BY_RANGE)
			? sortByField
			: SORT_BY_VALUE;

	return (
		<now-accordion-item
			id="sorting-accordion"
			dividers={{ top: false, bottom: true }}
			header={t('Sorting')}
			expanded={accordionState.sorting}
			append-to-payload={{ accordionItem: 'sorting' }}
			on-click={autoScrollToVisibility}>
			<div className="now-accordion-content" slot="content">
				<div className="sort-by-container">
					<div className="sort-by-column">
						<now-label-value-inline
							className="section-heading"
							label={t('Sorting by')}
						/>
						<now-dropdown
							id="sort-by-dropdown"
							append-to-payload={{
								payloadActionType: SET_SORT_BY,
							}}
							items={sortByItems}
							select="single"
							selected-items={[selectedSortByItem]}
							size="md"
							variant="tertiary"
							config-aria={{
								'aria-label': t('Sorting by'),
							}}
						/>
					</div>
					<div className="order-by-column">
						<now-label-value-inline
							className="section-heading"
							label={t('Direction')}
						/>
						<div className="order-by-buttons">
							<now-button-stateful
								id="sort-by-desc-button"
								manage-selected
								append-to-payload={{
									payloadActionType: SET_ORDER_BY,
									value: ORDER_DESC,
								}}
								variant="tertiary"
								config-role="button"
								tooltip-content={t('Sort Descending')}
								configAria={{
									'aria-label': t('Sort Descending'),
								}}
								icon="sort-descending-fill"
								size="md"
								selected={!orderByField || orderByField === ORDER_DESC}
							/>
							<now-button-stateful
								id="sort-by-asc-button"
								manage-selected
								append-to-payload={{
									payloadActionType: SET_ORDER_BY,
									value: ORDER_ASC,
								}}
								variant="tertiary"
								config-role="button"
								tooltip-content={t('Sort Ascending')}
								configAria={{
									'aria-label': t('Sort Ascending'),
								}}
								icon="sort-ascending-fill"
								size="md"
								selected={orderByField === ORDER_ASC}
							/>
						</div>
					</div>
				</div>
			</div>
		</now-accordion-item>
	);
};
