//@flow
import mapKeys from 'lodash/mapKeys';
import camelCase from 'lodash/camelCase';

import { type ChartModel } from '../../visualizations-config-panel/model/chart-model';

import { GROUP_BY_FIELD, STACK_BY_FIELD } from '../constants';

import {
	type FieldDefinition,
	createFieldDefFromMetadata,
} from '../../common/types/field';

import {
	type DataSource,
	SOURCE_TYPE_INDICATOR,
} from '../../common/types/data-source';

export const updateModelWithGroupByValues = (
	chartModel: ChartModel,
	dataToApply: { [fieldName: string]: string }
) => {
	let field;

	for (field in dataToApply) {
		const fieldValue = dataToApply[field];

		if (field.indexOf(GROUP_BY_FIELD) === 0) {
			const dataSourceIndex = Number(field.replace(GROUP_BY_FIELD, ''));
			const dataSource = chartModel.dataSources[dataSourceIndex];

			//Indicator should have same breakdown as groupBy
			if (dataSource.sourceType === SOURCE_TYPE_INDICATOR) {
				chartModel.dataSources.forEach(ds => {
					ds.groupByField = fieldValue;
				});
			} else {
				dataSource.groupByField = fieldValue;
			}
		} else if (field.indexOf(STACK_BY_FIELD) === 0) {
			const dataSourceIndex = Number(field.replace(STACK_BY_FIELD, ''));

			chartModel.dataSources[dataSourceIndex].stackByField = fieldValue;
		} else {
			chartModel[field] = fieldValue;
		}
	}

	return chartModel;
};

export const createTransformMetadataFn = (
	filter: (dataSource: DataSource, fieldDefinition: FieldDefinition) => boolean
) => (dataSource: DataSource, tables: any) => {
	Object.keys(tables).forEach(table =>
		Object.keys(tables[table]).forEach(field => {
			const camelCaseMapped = mapKeys(
				tables[table][field].fieldMetadata,
				(_, key) => camelCase(key)
			);

			const disablePropName = tables[table][field].isReference
				? 'disabled'
				: 'hidden';

			tables[table][field][disablePropName] = !filter(
				dataSource,
				createFieldDefFromMetadata(camelCaseMapped)
			);
		})
	);

	return tables;
};
