// @flow
import { t } from 'sn-translate';
import {
	type ChartModel,
	type AdditionalSettingName,
} from '../visualizations-config-panel/model/chart-model';
import {
	type DataSourceInfos,
	INDICATOR_TYPE,
} from '../common/types/data-source';
import { SOURCE_TYPE_INDICATOR } from '../common/types/data-source';

import { CHART_TYPES } from '../common/types/chart-type';
import {
	AGGREGATE_FUNCTION_COUNT,
	AGGREGATE_FUNCTION_SUM,
} from '../metric-configuration/constants';
import {
	TIMESERIES_CHART_TYPES,
	DONUT_CHART_TYPES,
} from '../common/chart-settings';
import {
	isTableMetric,
	isIndicatorMetric,
	getMetricDataSourceInfo,
} from '../helpers/metric';

type SettingMeta = {
	label: string,
	checkIsRelevant(model: ChartModel, dataSourceInfos: DataSourceInfos): boolean,
};

export const SETTINGS_METADATA: {
	[key: AdditionalSettingName]: SettingMeta,
} = {
	include_change: {
		label: t('Show change'),
		checkIsRelevant: ({ chartType, dataSources }) =>
			chartType === CHART_TYPES.SINGLE_SCORE &&
			dataSources[0].sourceType === SOURCE_TYPE_INDICATOR,
	},
	include_change_percentage: {
		label: t('Show change %'),
		checkIsRelevant: ({ chartType, dataSources }) =>
			chartType === CHART_TYPES.SINGLE_SCORE &&
			dataSources[0].sourceType === SOURCE_TYPE_INDICATOR,
	},
	include_targets: {
		label: t('Show target'),
		checkIsRelevant: ({ chartType, metrics, dataSources }) => {
			return (
				((TIMESERIES_CHART_TYPES.includes(chartType) &&
					metrics.length === 1 &&
					dataSources.length === 1) ||
					chartType === CHART_TYPES.SINGLE_SCORE) &&
				dataSources[0].sourceType === SOURCE_TYPE_INDICATOR &&
				!dataSources[0].groupByField
			);
		},
	},
	include_gap_percentage: {
		label: t('Show gap %'),
		checkIsRelevant: ({ chartType, dataSources }) => {
			return (
				chartType === CHART_TYPES.SINGLE_SCORE &&
				dataSources[0].sourceType === SOURCE_TYPE_INDICATOR
			);
		},
	},
	include_sparkline: {
		label: t('Show sparkline'),
		checkIsRelevant: ({ chartType, dataSources }) => {
			return (
				chartType === CHART_TYPES.SINGLE_SCORE &&
				dataSources[0].sourceType === SOURCE_TYPE_INDICATOR
			);
		},
	},
	include_forecast: {
		label: t('Show forecast'),
		checkIsRelevant: ({ chartType, dataSources }) => {
			return (
				TIMESERIES_CHART_TYPES.includes(chartType) &&
				dataSources[0].sourceType === SOURCE_TYPE_INDICATOR
			);
		},
	},
	include_forecast_range: {
		label: t('Show forecast range'),
		checkIsRelevant: ({ chartType, dataSources, metrics }) => {
			return (
				TIMESERIES_CHART_TYPES.includes(chartType) &&
				dataSources[0].sourceType === SOURCE_TYPE_INDICATOR &&
				metrics.length === 1 &&
				dataSources.length === 1 &&
				!dataSources[0].groupByField
			);
		},
	},
	include_trend: {
		label: t('Show trend'),
		checkIsRelevant: ({ chartType, metrics, dataSources }) => {
			return (
				TIMESERIES_CHART_TYPES.includes(chartType) &&
				metrics.length === 1 &&
				dataSources.length === 1 &&
				dataSources[0].sourceType === SOURCE_TYPE_INDICATOR &&
				!dataSources[0].groupByField
			);
		},
	},
	include_band: {
		label: t('Show confidence band'),
		checkIsRelevant: ({ chartType, metrics, dataSources }) => {
			return (
				TIMESERIES_CHART_TYPES.includes(chartType) &&
				metrics.length === 1 &&
				dataSources.length === 1 &&
				dataSources[0].sourceType === SOURCE_TYPE_INDICATOR &&
				!dataSources[0].groupByField
			);
		},
	},
	include_thresholds: {
		label: t('Show threshold'),
		checkIsRelevant: ({ chartType, metrics, dataSources }) => {
			return (
				TIMESERIES_CHART_TYPES.includes(chartType) &&
				metrics.length === 1 &&
				dataSources.length === 1 &&
				dataSources[0].sourceType === SOURCE_TYPE_INDICATOR &&
				!dataSources[0].groupByField
			);
		},
	},
	include_comments: {
		label: t('Show comment'),
		checkIsRelevant: ({ chartType, metrics, dataSources }) => {
			return (
				TIMESERIES_CHART_TYPES.includes(chartType) &&
				metrics.length === 1 &&
				dataSources.length === 1 &&
				dataSources[0].sourceType === SOURCE_TYPE_INDICATOR &&
				!dataSources[0].groupByField
			);
		},
	},
	showTotal: {
		label: t('Show total value'),
		checkIsRelevant: (chartModel, dataSourceInfos) => {
			const { chartType, dataSources } = chartModel;

			if (!DONUT_CHART_TYPES.includes(chartType)) {
				return false;
			}

			//Donut doesn't support multiple metrics
			const metric = chartModel.metrics[0];

			if (
				isTableMetric(metric, dataSources) &&
				(metric.aggregateFunction === AGGREGATE_FUNCTION_SUM ||
					metric.aggregateFunction === AGGREGATE_FUNCTION_COUNT)
			) {
				return true;
			}

			const dataSourceInfo = getMetricDataSourceInfo(metric, dataSourceInfos);

			if (
				isIndicatorMetric(metric, dataSources) &&
				((dataSourceInfo.type === INDICATOR_TYPE.AUTOMATED && // Automated
					(dataSourceInfo.aggregate === 2 || //  sum
						dataSourceInfo.aggregate === 1)) || // Count
					dataSourceInfo.type === INDICATOR_TYPE.MANUAL ||
					dataSourceInfo.type === INDICATOR_TYPE.EXTERNAL)
			) {
				return true;
			}

			return false;
		},
	},
	showMetricLabel: {
		label: t('Show metric label'),
		checkIsRelevant: (chartModel, dataSourceInfos) =>
			chartModel.chartType === CHART_TYPES.SINGLE_SCORE ||
			SETTINGS_METADATA.showTotal.checkIsRelevant(chartModel, dataSourceInfos),
	},
};

/**
 * This is the abstract of which settings to show in the addtional settings area
 * Later it's possible to add new boolean settings easily by just adding them to the lists above
 * It's also fine to add a new abstraction for new types of settings other than boolean
 * @param ChartModel chartModel
 */
export const getBooleanSettings = (
	chartModel: ChartModel,
	dataSourceInfos: DataSourceInfos
) => {
	return Object.keys(SETTINGS_METADATA).reduce(
		(availableSettings, key: AdditionalSettingName) => {
			const meta = SETTINGS_METADATA[key];

			if (meta.checkIsRelevant(chartModel, dataSourceInfos)) {
				availableSettings.push({
					label: meta.label,
					id: key,
				});
			}

			return availableSettings;
		},
		[]
	);
};
