//@flow
import get from 'lodash/get';
import find from 'lodash/find';

import { effectProxy } from '../../helpers/seismic';
import { type Coeffects } from '@servicenow/ui-core';
import { type State } from '../state';

import { createGraphQLEffect } from '@servicenow/ui-effect-graphql';

import {
	RETRIEVE_PREFERENCES_AND_SECURITY,
	RETRIEVE_PREFERENCES_AND_SECURITY_SUCCESS,
	RETRIEVE_PREFERENCES_AND_SECURITY_FAILURE,
	UPDATE_USER_PREFERENCES,
	UPDATE_USER_PREFERENCES_SUCCESS,
	UPDATE_USER_PREFERENCES_FAILURE,
} from './action-constants';

export const SYS_PROP_ALLOW_MANUAL_UPDATE =
	'par_vis_config.uibuilder.allow_manual_update';

export const SYS_PROP_MIN_REFRESH_FREQUENCY =
	'par_vis_config.live_refresh_rate_min_value';

export const handleDataInitialiationEffect = createGraphQLEffect(
	`query($preferences: [String]!){
		userData: GlideDomain_Query {
			user {
				preferences(name: $preferences) {
					name
					value
				}
				roles
			}
		}
		sysProps: GlideDomain_Query {
			isManualUpdateAllowed: property(name: "${SYS_PROP_ALLOW_MANUAL_UPDATE}") {
				name
				isDefined
				booleanValue
			}
			minRefreshFrequency: property(name: "${SYS_PROP_MIN_REFRESH_FREQUENCY}") {
				name
				isDefined
				value
			}
		}
	}`,
	{
		variableList: ['preferences'],
		successActionType: RETRIEVE_PREFERENCES_AND_SECURITY_SUCCESS,
		errorActionType: RETRIEVE_PREFERENCES_AND_SECURITY_FAILURE,
	}
);

const handleUpdatePreferences = createGraphQLEffect(
	`mutation($preferences: [GlideDomain_PreferenceArgument!]!) {
		GlideDomain_Mutation {
		  user {
			setPreferences(preferences: $preferences) {
			  name
			  value
			}
		  }
		}
	  }`,
	{
		variableList: ['preferences'],
		successActionType: UPDATE_USER_PREFERENCES_SUCCESS,
		errorActionType: UPDATE_USER_PREFERENCES_FAILURE,
	}
);

const handleDataInitialiationSuccess = ({
	action: { payload },
	updateState,
}: Coeffects<State, any>) => {
	const userPreferences = get(payload, 'data.userData.user.preferences', []);
	const roles = get(payload, 'data.userData.user.roles', []);
	const security = {
		isAdmin: roles && roles.indexOf('admin') > -1,
	};
	const sysProperties = [
		get(payload, 'data.sysProps.isManualUpdateAllowed') || {},
		get(payload, 'data.sysProps.minRefreshFrequency') || {},
	];

	updateState({ userPreferences, sysProperties, security });
};

const handleUpdatePreferencesSuccess = ({
	action: { payload },
	state: { userPreferences },
	updateState,
}: Coeffects<State, any>) => {
	const updatedValues = get(
		payload,
		'data.GlideDomain_Mutation.user.setPreferences',
		[]
	);

	const newPreferenceValues = userPreferences.map(pref => {
		const hasUpdated = find(updatedValues, ['name', pref.name]);

		return hasUpdated ? hasUpdated : pref;
	});

	updateState({ userPreferences: newPreferenceValues });
};

export const userDataSysPropertiesActionHandlers = {
	[RETRIEVE_PREFERENCES_AND_SECURITY]: effectProxy(
		handleDataInitialiationEffect
	),
	[RETRIEVE_PREFERENCES_AND_SECURITY_SUCCESS]: handleDataInitialiationSuccess,

	[UPDATE_USER_PREFERENCES]: handleUpdatePreferences,
	[UPDATE_USER_PREFERENCES_SUCCESS]: handleUpdatePreferencesSuccess,
	[UPDATE_USER_PREFERENCES_FAILURE]: handleUpdatePreferencesSuccess,
};
