// @flow
import { type Coeffects } from '@servicenow/ui-core';
import {
	ADDITIONAL_SETTINGS_CHANGE,
	SET_XENOLITH_PROPERTIES,
} from './constants';
import { SET_METRIC_LABEL_POSITION } from './sub-settings/metric-label-position';

export default {
	actions: {
		[ADDITIONAL_SETTINGS_CHANGE]: {},
	},
	actionHandlers: {
		'NOW_DROPDOWN#SELECTED_ITEMS_SET': ({
			dispatch,
			action,
		}: Coeffects<any, any>) => {
			switch (action.payload.payloadActionType) {
				case SET_METRIC_LABEL_POSITION:
					dispatch(SET_XENOLITH_PROPERTIES, {
						headingPosition: action.payload.value[0],
					});
					break;
			}
		},
		'VIZ_CONFIG_TOGGLE#CHECKED_SET': ({
			dispatch,
			action,
		}: Coeffects<any, any>) => {
			dispatch(ADDITIONAL_SETTINGS_CHANGE, { ...action.payload });
		},
	},
};
