// @flow
import { actionTypes, type Coeffects } from '@servicenow/ui-core';
import { createGraphQLEffect } from '@servicenow/ui-effect-graphql';
import get from 'lodash/get';
import { effectProxy } from '../helpers/seismic';
import {
	invalidateCache,
	getElementOptionsFromCache,
	setElementOptionsToCache,
} from './cache';

import {
	LOAD_REQUIRED_DATA,
	FETCH_ELEMENTS,
	FETCH_ELEMENTS_SUCCEEDED,
	BREAKDOWN_CHANGED,
	FETCH_ELEMENTS_FINISHED,
	BD_OPERATORS,
} from './constants';
import {
	type State,
	type BreakdownData,
	type BreakdownPropInfo,
	type OptionItem,
	createBreakdownAttrUpdateStatePatch,
} from './state';
import {
	type Breakdown,
	type ElementFilter,
	type IndicatorInfo,
} from '../common/types/indicator';
import { t } from 'sn-translate';

import { INDICATOR_TYPE, AGGREGATE_TYPE } from '../common/types/data-source';

function updateStateFromProps(
	coeffects: Coeffects<State, void>,
	force: boolean = false
) {
	const {
		indicator,
		properties: {
			selectedIndicatorInfo: { uuid },
			indicatorInfo,
		},
	} = coeffects.state;

	if (uuid.indicator === indicator && !force) {
		return;
	}

	const selectedBreakdowns = uuid.breakdowns || [];

	const breakdownOptions = getBreakdownOptions(coeffects);

	const allBreakdowns =
		get(coeffects, 'properties.indicatorInfo.breakdowns') || [];

	const breakdowns = selectedBreakdowns.map(br => ({
		sysId: br.sysId,
		elementSysIds: br.elementSysIds && [...br.elementSysIds],
		elementsFilterSysId: br.elementsFilterSysId,
		operator: br.elementsFilterSysId
			? BD_OPERATORS.DYNAMIC
			: br.elementSysIds && br.elementSysIds.length > 1
			? BD_OPERATORS.MULTI
			: BD_OPERATORS.DEFAULT,
		operatorOptions: getOperatorOptions(indicatorInfo, allBreakdowns, br.sysId),
		breakdownOptions,
	}));

	if (breakdowns.length == 0) {
		breakdowns.push({
			operator: BD_OPERATORS.DEFAULT,
			breakdownOptions,
		});
	}

	coeffects.updateState({
		indicator: uuid.indicator,
		breakdowns,
	});

	coeffects.dispatch(LOAD_REQUIRED_DATA);
}

function getBreakdownOptions(coeffects: Coeffects<State, any>) {
	const {
		properties: { indicatorInfo },
	} = coeffects.state;

	return indicatorInfo.breakdowns.map(x => ({
		label: x.name,
		id: x.sysId,
	}));
}

function getOperatorOptions(
	indicatorInfo: IndicatorInfo,
	breakDowns: Breakdown[],
	sysID: string
): OptionItem[] {
	const indicatorType = indicatorInfo.type.value;
	const aggregate = indicatorInfo.aggregate.value;
	const doMultiElementsApply =
		(indicatorType === INDICATOR_TYPE.AUTOMATED &&
			(aggregate === AGGREGATE_TYPE.COUNT ||
				aggregate === AGGREGATE_TYPE.SUM ||
				aggregate === AGGREGATE_TYPE.MIN ||
				aggregate === AGGREGATE_TYPE.MAX)) ||
		indicatorType === INDICATOR_TYPE.MANUAL ||
		indicatorType === INDICATOR_TYPE.EXTERNAL;
	const isElementFilterExists = getElementFilters(breakDowns, sysID).length > 0;
	const options = [{ id: BD_OPERATORS.DEFAULT, label: t('is') }];

	doMultiElementsApply &&
		options.push({ id: BD_OPERATORS.MULTI, label: t('is one of') });
	isElementFilterExists &&
		options.push({ id: BD_OPERATORS.DYNAMIC, label: t('is (dynamic)') });
	return options;
}

function getElementFilters(
	breakDowns: Breakdown[],
	sysID: string
): ElementFilter[] {
	const breakdown = breakDowns.find(bd => bd.sysId === sysID);

	if (!breakdown) return [];

	if (!breakdown.elementFilters || breakdown.elementFilters.length <= 0)
		return [];

	return breakdown.elementFilters;
}

function getElementFilterOptions(
	breakDowns: Breakdown[],
	sysID: string
): OptionItem[] {
	const elementFilters = getElementFilters(breakDowns, sysID);

	return elementFilters.map(x => ({
		label: x.title,
		id: x.sysId,
	}));
}

function handleFilters(
	coeffects: Coeffects<State, any>,
	newBreakdowns,
	keyUuid: string
) {
	let isSomethingRequired = false;

	const breakdownProps: BreakdownPropInfo[] = [];

	for (const br of newBreakdowns) {
		if (!br.sysId) {
			continue;
		}

		if (
			!br.elementsFilterSysId &&
			(!br.elementSysIds || br.elementSysIds.length === 0)
		) {
			isSomethingRequired = true;
			coeffects.updateState(
				createBreakdownAttrUpdateStatePatch(
					coeffects.state.breakdowns,
					br.sysId,
					'isElementRequired',
					true
				)
			);
			continue;
		}

		if (br.sysId && br.elementSysIds && br.elementSysIds.length > 0) {
			breakdownProps.push({
				sysId: br.sysId,
				elementSysIds: [...br.elementSysIds],
			});
		} else if (br.sysId && br.elementsFilterSysId) {
			breakdownProps.push({
				sysId: br.sysId,
				elementsFilterSysId: br.elementsFilterSysId,
			});
		}
	}

	if (!isSomethingRequired) {
		coeffects.dispatch(BREAKDOWN_CHANGED, {
			[keyUuid]: {
				breakdowns: breakdownProps,
				indicator:
					coeffects.state.properties.selectedIndicatorInfo.uuid.indicator,
			},
		});
	}
}

function handleButtonClicked(coeffects: Coeffects<State, any>) {
	const actionType: string = coeffects.action.payload.payloadActionType;

	switch (actionType) {
		case 'btnAddNewBreakdown': {
			coeffects.updateState({
				operation: 'push',
				path: `breakdowns`,
				value: {},
			});
			coeffects.dispatch(LOAD_REQUIRED_DATA);
			break;
		}
		case 'removeBreakdown': {
			const indexToDelete = coeffects.action.payload.breakdownIndex;
			const newBreakdowns = coeffects.state.breakdowns
				.map((br, i) =>
					i < indexToDelete
						? br
						: {
								// Reset breakdownOptions and elementOptions for dependent breakdowns
								sysId: br.sysId,
								elementSysIds: br.elementSysIds,
								elementsFilterSysId: br.elementsFilterSysId,
								operator: br.operator,
								operatorOptions: br.operatorOptions,
						  }
				)
				.filter((el, i) => i !== indexToDelete);

			if (newBreakdowns.length === 0) {
				newBreakdowns.push({});
			}

			coeffects.updateState({ breakdowns: newBreakdowns });
			coeffects.dispatch(LOAD_REQUIRED_DATA);

			handleFilters(coeffects, newBreakdowns, 'uuid');

			break;
		}
		case 'cancelFilter': {
			coeffects.updateState({ breakdowns: [{}] });
			coeffects.dispatch(LOAD_REQUIRED_DATA);
			coeffects.dispatch(BREAKDOWN_CHANGED, {
				uuid: {
					breakdowns: [],
					indicator:
						coeffects.state.properties.selectedIndicatorInfo.uuid.indicator,
				},
			});
			break;
		}
		case 'applyFilter': {
			const breakdowns = coeffects.state.breakdowns;

			handleFilters(coeffects, breakdowns, 'previewUuid');

			if (coeffects.host.shadowRoot) {
				const buttonUpdatePreview = coeffects.host.shadowRoot.querySelector(
					'.btn-update-preview'
				);

				if (buttonUpdatePreview) {
					setTimeout(() => {
						buttonUpdatePreview.scrollIntoView({
							behavior: 'smooth',
						});
					}, 500);
				}
			}

			break;
		}

		case 'editFilter': {
			coeffects.dispatch(BREAKDOWN_CHANGED, {
				isEditMode: true,
				isExpanded: true,
			});
			break;
		}
	}
}

function handleSelectElements(
	selectedElementWithType: any,
	coeffects: Coeffects<State, any>
) {
	const { state } = coeffects;
	const index = coeffects.action.payload.breakdownIndex;

	const brData: BreakdownData = {
		...state.breakdowns[index],
		...selectedElementWithType,
		isElementRequired: false,
	};

	coeffects.updateState({
		operation: 'set',
		path: `breakdowns[${index}]`,
		value: brData,
	});

	const breakdowns = [...coeffects.state.breakdowns];

	breakdowns[index] = brData;

	handleFilters(coeffects, breakdowns, 'uuid');
}

function handleTypeaheadMultiItemsSelected(coeffects: Coeffects<State, any>) {
	const actionType = coeffects.action.payload.payloadActionType;

	switch (actionType) {
		case 'elementSelectTypeahead': {
			const selectedElementWithType = {
				elementSysIds: coeffects.action.payload.value.map(value => value.id),
			};

			handleSelectElements(selectedElementWithType, coeffects);
			break;
		}

		default:
			break;
	}
}

export const fetchElementsEffect = createGraphQLEffect(
	`query($sysID: String!, $elementStart: Int, $elementCount: Int, $elementContains: String) {
            AppPARConfig_Query {
                breakdownDetails(
                    sysID: $sysID
                    elementStart: $elementStart
                    elementCount: $elementCount
                    elementContains: $elementContains
                ) {
                    breakdownSource {
                        elements {
                            sysID
                            label
                        }
                        unmatchedElement {
                            sysID
                            label
                        }
                    }
                    result
                }
            }
        }`,
	{
		variableList: ['sysID'],
		successActionType: FETCH_ELEMENTS_SUCCEEDED,
	}
);

export const actionHandlers = {
	[actionTypes.COMPONENT_BOOTSTRAPPED]: (coeffects: Coeffects<State, void>) => {
		updateStateFromProps(coeffects);
	},
	[actionTypes.COMPONENT_PROPERTY_CHANGED]: (
		coeffects: Coeffects<State, any>
	) => {
		updateStateFromProps(coeffects);
	},
	[actionTypes.COMPONENT_DISCONNECTED]: () => {
		invalidateCache();
	},
	[LOAD_REQUIRED_DATA]: (coeffects: Coeffects<State, any>) => {
		const { indicator, breakdowns } = coeffects.state;

		let baseUuid = indicator;

		for (const br of breakdowns) {
			if (!br.breakdownOptions) {
				const breakdownOptions = getBreakdownOptions(coeffects);

				coeffects.updateState(
					createBreakdownAttrUpdateStatePatch(
						coeffects.state.breakdowns,
						br.sysId,
						'breakdownOptions',
						breakdownOptions
					)
				);
			}

			if (
				(br.operator === BD_OPERATORS.DEFAULT ||
					br.operator === BD_OPERATORS.MULTI) &&
				!br.elementOptions &&
				br.sysId
			) {
				const optsFromCache = getElementOptionsFromCache(baseUuid, br.sysId);

				if (optsFromCache) {
					coeffects.updateState(
						createBreakdownAttrUpdateStatePatch(
							coeffects.state.breakdowns,
							br.sysId,
							'elementOptions',
							optsFromCache
						)
					);
					coeffects.dispatch(FETCH_ELEMENTS_FINISHED, {
						sysID: br.sysId,
					});
				} else {
					coeffects.dispatch(
						FETCH_ELEMENTS,
						{
							sysID: br.sysId,
						},
						{
							sysID: br.sysId,
						}
					);
				}
			} else if (br.operator === BD_OPERATORS.DYNAMIC && br.sysId) {
				const breakdowns =
					get(coeffects, 'properties.indicatorInfo.breakdowns') || [];
				const elementFilterOptions = getElementFilterOptions(
					breakdowns,
					br.sysId || ''
				);

				coeffects.dispatch(FETCH_ELEMENTS_FINISHED, {
					sysID: br.sysId,
				});

				coeffects.updateState(
					createBreakdownAttrUpdateStatePatch(
						coeffects.state.breakdowns,
						br.sysId,
						'elementOptions',
						elementFilterOptions
					)
				);
			}

			if (br.sysId && br.elementSysIds && br.elementSysIds.length > 0) {
				baseUuid = baseUuid + ':' + br.sysId + ':' + br.elementSysIds[0];
			}
		}
	},

	[FETCH_ELEMENTS]: effectProxy(fetchElementsEffect),

	[FETCH_ELEMENTS_SUCCEEDED]: (coeffects: Coeffects<State, any>) => {
		const elements = get(
			coeffects.action.payload,
			'data.AppPARConfig_Query.breakdownDetails.breakdownSource.elements'
		);

		const unMatchedElement = get(
			coeffects.action.payload,
			'data.AppPARConfig_Query.breakdownDetails.breakdownSource.unmatchedElement'
		);

		const { indicator, breakdowns } = coeffects.state;

		const sysID = coeffects.action.meta.sysID;
		const elementOptions = elements.map(x => ({
			label: x.label,
			id: x.sysID,
		}));

		if (unMatchedElement && unMatchedElement.label) {
			elementOptions.unshift({
				label: unMatchedElement.label,
				id: unMatchedElement.sysID,
			});
		}

		setElementOptionsToCache(indicator, sysID, elementOptions);
		coeffects.updateState(
			createBreakdownAttrUpdateStatePatch(
				breakdowns,
				sysID,
				'elementOptions',
				elementOptions
			)
		);
		coeffects.dispatch(FETCH_ELEMENTS_FINISHED, {
			sysID,
		});
	},
	'DEVX_TYPEAHEAD#ITEM_CLICKED': (coeffects: Coeffects<State, any>) => {
		const actionType: string = coeffects.action.payload.payloadActionType;
		const { state } = coeffects;

		switch (actionType) {
			case 'breakdownSelectTypeahead': {
				const index = coeffects.action.payload.breakdownIndex;
				const sysId = coeffects.action.payload.option.id;
				const breakdowns =
					get(coeffects, 'properties.indicatorInfo.breakdowns') || [];
				const indicatorInfo = get(coeffects, 'properties.indicatorInfo') || {};
				const operatorOptions = getOperatorOptions(
					indicatorInfo,
					breakdowns,
					sysId
				);
				const brData: BreakdownData = {
					...state.breakdowns[index],
					sysId,
					elementSysIds: [],
					operator: BD_OPERATORS.DEFAULT,
					operatorOptions,
					elementOptions: undefined,
				};

				coeffects.updateState({
					operation: 'set',
					path: `breakdowns[${index}]`,
					value: brData,
				});
				coeffects.dispatch(LOAD_REQUIRED_DATA);
				break;
			}
			case 'breakdownOperator': {
				const index = coeffects.action.payload.breakdownIndex;
				const operator = coeffects.action.payload.option.id;
				const brData: BreakdownData = {
					...state.breakdowns[index],
					operator,
					elementSysIds: [],
					elementsFilterSysId: undefined,
					elementOptions: undefined,
				};

				coeffects.updateState({
					operation: 'set',
					path: `breakdowns[${index}]`,
					value: brData,
				});

				coeffects.dispatch(LOAD_REQUIRED_DATA);
				break;
			}
			case 'elementSelectTypeahead': {
				const index = coeffects.action.payload.breakdownIndex;
				const elementId = coeffects.action.payload.option.id;
				const { operator } = state.breakdowns[index];

				const selectedElementWithType =
					operator === BD_OPERATORS.DEFAULT
						? {
								elementSysIds: [elementId],
						  }
						: {
								elementsFilterSysId: elementId,
						  };

				handleSelectElements(selectedElementWithType, coeffects);
			}
		}
	},
	'NOW_TYPEAHEAD_MULTI#SELECTED_ITEMS_SET': handleTypeaheadMultiItemsSelected,
	'NOW_BUTTON_ICONIC#CLICKED': handleButtonClicked,
	'NOW_BUTTON#CLICKED': handleButtonClicked,
	'NOW_BUTTON_BARE#CLICKED': handleButtonClicked,
};
