//@flow
import { type Uuid } from '../common/types/uuid';
import { type IndicatorInfo } from '../common/types/indicator';

export type State = {|
	breakdowns: BreakdownData[],
	indicator: string,
	properties: {
		selectedIndicatorInfo: {
			uuid: Uuid,
			previewUuid: Uuid,
			isExpanded: boolean,
			isEditMode: boolean,
		},
		indicatorInfo: IndicatorInfo,
		loadingBreakdownSelector: boolean,
	},
|};

export type BreakdownPropInfo = {|
	sysId: string,
	elementSysIds?: string[],
	elementsFilterSysId?: string,
|};

export type BreakdownData = {
	sysId?: string,
	elementSysIds?: string[],
	elementsFilterSysId?: string,
	operator?: BreakdownOperators,
	breakdownOptions?: OptionItem[],
	elementOptions?: OptionItem[],
	operatorOptions?: OptionItem[],
	isElementRequired?: boolean,
};

export type OptionItem = {
	id: string,
	label: string,
};

export const initialState: $Shape<State> = {
	breakdowns: [],
};

export function createBreakdownAttrUpdateStatePatch(
	breakdowns: BreakdownData[],
	sysId: string | void,
	attrName: string,
	value: any
) {
	const brIndex = breakdowns.findIndex(x => x.sysId === sysId);

	if (brIndex >= 0) {
		return {
			operation: 'set',
			path: `breakdowns[${brIndex}].${attrName}`,
			value,
		};
	}

	return {};
}

export type BreakdownOperators = 'default' | 'multi' | 'dynamic';
