// @flow
// @jsx snabbdom.createElement

import snabbdom, { Fragment } from '@servicenow/ui-renderer-snabbdom'; // eslint-disable-line no-unused-vars
import { type Helpers } from '@servicenow/ui-core';
import { jsxif } from '../helpers/seismic';
import { t } from 'sn-translate';
import { type State } from './state';
import { TableRecordsView } from './table-records-view';
import { FilterView } from './filter-view';

export default function(state: State, helpers: Helpers<State>) {
	if (state.tableName !== state.properties.tableInfo.tableName) {
		// State is not updated with latest props - skip render
		return;
	}

	return (
		// We want this always be rerendered when we change the table
		<div key={state.properties.tableInfo.tableName}>
			{jsxif(state.isFilterLoading || state.isTableLoading) && (
				<div
					key="loading-container"
					className="table-configuration-loading-container">
					<now-loader label={t('Loading...')} size="lg" />
				</div>
			)}

			{jsxif(!state.isFilterLoading && !state.isTableLoading) &&
				FilterView(state, helpers)}

			{jsxif(!state.isFilterLoading && !global.TEST_SKIP_RENDER_RECORDS) && (
				<div
					key="table-container"
					class={{
						'table-container': true,
						'is-loading': state.isTableLoading,
					}}>
					{TableRecordsView(state)}
				</div>
			)}
		</div>
	);
}
