// @flow
import { actionTypes, type Coeffects } from '@servicenow/ui-core';
import { type State } from '../state';
import { convertPropsToModel } from '../model/model-converter';
import { type ChartProps } from '../model/chart-props';

import { VIZ_CONFIG_PREFERENCES } from '../views/view-constants';
import {
	RETRIEVE_PREFERENCES_AND_SECURITY,
	LOAD_ALL_COLOR_OPTIONS,
	RETRIEVE_MULTI_DATA_SOURCE_OPTIONS,
} from './action-constants';

import { SOURCE_TYPE_TABLE } from '../../common/types/data-source';

import { getValidChartModelAndOptions } from './model-action-handlers';
import { isSameDataSource } from '../../helpers/data-source';
import { compareValidProperties } from '../../helpers/object-utils';

export const getChartProps = (properties: any): ChartProps => {
	return {
		chartType: properties.chartType,
		dataConfigurations: properties.dataConfigurations,
		styleConfigurations: properties.styleConfigurations,
		variant: properties.variant,
		border: properties.border,
		label: properties.label,
		isRealtime: properties.isRealtime,
		showRealtimeIndicator: properties.showRealtimeIndicator,
		refreshInterval: properties.refreshInterval,
		refreshFrequency: properties.refreshFrequency,
		kpiDetailsNavigation: properties.kpiDetailsNavigation,
		followFilters: properties.followFilters,
		hideHeader: properties.hideHeader,
		hideFiltersInfo: properties.hideFiltersInfo,
	};
};

export const lifeCycleActionHandlers = {
	[actionTypes.COMPONENT_BOOTSTRAPPED]: (coeffects: Coeffects<State, void>) => {
		const chartProps = getChartProps(coeffects.state.properties);
		const chartModel = convertPropsToModel(chartProps, coeffects.state);

		coeffects.updateState({ chartModel });
		coeffects.dispatch(RETRIEVE_PREFERENCES_AND_SECURITY, {
			preferences: VIZ_CONFIG_PREFERENCES,
		});

		coeffects.dispatch(LOAD_ALL_COLOR_OPTIONS);

		coeffects.dispatch(RETRIEVE_MULTI_DATA_SOURCE_OPTIONS, {
			dataSources: chartModel.dataSources,
		});
	},
	[actionTypes.COMPONENT_PROPERTY_CHANGED]: {
		modifier: { name: 'debounce', delay: 100 },
		effect: (coeffects: Coeffects<State, any>) => {
			const chartProps = getChartProps(coeffects.state.properties);

			let chartModel = convertPropsToModel(chartProps, coeffects.state);

			// During the model conversion of SOURCE_TYPE_TABLE_SOURCE the tableName is removed, the code below restore it
			chartModel.dataSources.forEach(ds => {
				if (
					ds.sourceType === SOURCE_TYPE_TABLE &&
					ds.predefinedFilterId &&
					!ds.tableName
				) {
					const oldDataSource = coeffects.state.chartModel.dataSources.find(
						oldDs => oldDs.predefinedFilterId === ds.predefinedFilterId
					);

					ds.tableName = oldDataSource && oldDataSource.tableName;
				}
			});

			if (!compareValidProperties(coeffects.state.chartModel, chartModel)) {
				const differentDataSources = chartModel.dataSources.map(
					(ds, index) =>
						!isSameDataSource(coeffects.state.chartModel.dataSources[index], ds)
				);

				if (differentDataSources.filter(Boolean).length > 0) {
					coeffects.dispatch(RETRIEVE_MULTI_DATA_SOURCE_OPTIONS, {
						dataSources: chartModel.dataSources,
						dataSourcesNeedsLoad: differentDataSources,
					});
				} else {
					const validatedModel = getValidChartModelAndOptions(
						chartModel,
						coeffects.state
					);

					chartModel = validatedModel.chartModel
						? validatedModel.chartModel
						: chartModel;
				}

				coeffects.updateState({ chartModel, hasChangesToUpdate: false });
			}
		},
	},
};
