// @flow
// @jsx snabbdom.createElement
import '@servicenow/now-toggle';
import '@servicenow/now-dropdown';
import '@servicenow/now-accordion';

import snabbdom, { Fragment } from '@servicenow/ui-renderer-snabbdom'; // eslint-disable-line no-unused-vars
import { t } from 'sn-translate';
import { type State } from '../state';

import {
	SET_SINGLE_SCORE_SIZE,
	SET_DISPLAY_ZERO,
	SET_BORDER,
	SET_VARIANT,
} from '../actions/action-constants';

import { autoScrollToVisibility } from '../../helpers/now-accordion/auto-scroll-to-visibility';

import { CHART_TYPES } from '../../common/types/chart-type';

import {
	doesShowDisplayZero,
	SINGLE_SCORE_SIZES,
	DEFAULT_SCORE_SIZE,
} from '../../helpers/display-settings/helpers';

import { jsxif } from '../../helpers/seismic';

const sizeItems = [
	{ id: 'auto', label: t('Auto'), value: 'auto' },
	{ id: 'SMALL', label: t('Small'), value: '200px' },
	{ id: 'MEDIUM', label: t('Medium'), value: '400px' },
	{ id: 'LARGE', label: t('Large'), value: '504px' },
	{ id: 'XL', label: t('Extra Large'), value: '676px' },
];

const getSelectedItem = (state: State, connectToStyle: boolean) => {
	const {
		properties: { elementId, elementStyles },
		chartModel,
	} = state;

	if (connectToStyle) {
		const currentStyles = elementStyles[elementId];

		return (
			sizeItems.find(item => item.value === currentStyles.height) ||
			sizeItems[0]
		);
	}

	return sizeItems.find(x => x.id === chartModel.variant);
};

const renderSizeSelector = (state: State) => {
	const {
		properties: { pageMetadata },
	} = state;

	const connectSizeSelectorToStyle =
		pageMetadata && pageMetadata.type !== 'landing';

	const selectedItem = getSelectedItem(state, connectSizeSelectorToStyle);

	return (
		<div className="section-label-dropdown">
			<now-label-value-inline
				className="section-heading"
				label={t('Chart size')}
			/>
			<now-dropdown
				id="size-variant-dropdown"
				append-to-payload={{
					payloadActionType: SET_VARIANT,
					shouldReplaceStyle: connectSizeSelectorToStyle,
				}}
				items={sizeItems}
				select="single"
				selected-items={[selectedItem ? selectedItem.id : 'auto']}
				size="md"
				variant="tertiary"
				config-aria={{
					'aria-label': t('Chart size'),
				}}
			/>
		</div>
	);
};

export default (state: State) => {
	const {
		chartModel: {
			chartType,
			displaySettings: { scoreSize, displayZero },
			dataSources,
		},
		accordionState,
	} = state;

	const showSingleScoreSize = chartType === CHART_TYPES.SINGLE_SCORE;
	const showDisplayZero = doesShowDisplayZero(chartType, dataSources);

	const displayZeroContent = () => (
		<viz-config-toggle
			id="display-zero-toggle"
			append-to-payload={{ payloadActionType: SET_DISPLAY_ZERO }}
			checked={displayZero}
			label={t('Show zero')}
		/>
	);

	const singleScoreSize = () => (
		<div className="section-label-dropdown">
			<now-label-value-inline
				className="section-heading"
				label={t('Score size')}
			/>
			<now-dropdown
				id="single-score-dropdown"
				append-to-payload={{
					payloadActionType: SET_SINGLE_SCORE_SIZE,
				}}
				items={SINGLE_SCORE_SIZES}
				select="single"
				selected-items={[scoreSize || DEFAULT_SCORE_SIZE]}
				size="md"
				variant="tertiary"
				config-aria={{
					'aria-label': t('Score size'),
				}}
			/>
		</div>
	);

	const borderToggle = (
		<div className="section-prop-border">
			<viz-config-toggle
				id="border-toggle"
				label={t('Show border')}
				append-to-payload={{ payloadActionType: SET_BORDER }}
				checked={
					state.chartModel.border === undefined ? true : state.chartModel.border
				}
			/>
		</div>
	);

	return (
		<now-accordion-item
			id="display-settings-accordion"
			dividers={{ top: false, bottom: true }}
			header={t('Display settings')}
			expanded={accordionState.displaySettings}
			append-to-payload={{ accordionItem: 'displaySettings' }}
			on-click={autoScrollToVisibility}>
			<div className="now-accordion-content" slot="content">
				{borderToggle}
				{jsxif(showDisplayZero) && displayZeroContent()}
				{renderSizeSelector(state)}
				{jsxif(showSingleScoreSize) && singleScoreSize()}
			</div>
		</now-accordion-item>
	);
};
