// @flow
import { actionTypes, type Coeffects } from '@servicenow/ui-core';
import { type State } from './state';

import {
	SET_GROUP_BY,
	SET_GROUP_BY_MAX,
	CLOSE_GROUP_BY_POPOVER,
	APPLY_GROUP_BY_POPOVER,
	PILLAR_GROUP_BY,
	CLICK_BUTTON_ADD_GROUP_BY,
	POPOVER_CONTENT_GROUP_BY,
	POPOVER_CONTENT_STACK_BY,
	PILLAR_ADD_ACTION,
	PILLAR_EDIT_ACTION,
	PILLAR_REMOVE_ACTION,
	GROUP_BY_MAX_NUMBER_ALL,
	GROUP_BY_FIELD,
	STACK_BY_FIELD,
	GROUP_BY_MAX,
	MAIN_POPOVER,
	SET_NO_OF_GROUP_BASED_ON,
	NO_OF_GROUP_BASED_ON,
	SET_CHART_VARIATION,
} from './constants';
import { DOT_WALK_PATH_UPDATED } from '../dot-walk/constants';
import { hasPropertyChanged } from '../helpers/props';

import get from 'lodash/get';
import cloneDeep from 'lodash/cloneDeep';

import {
	SN_PAR_PILLAR_CLICKED,
	SN_PAR_PILLAR_ACTION_CLICKED,
} from '@now-par-components/sn-par-pillar';

import { t } from 'sn-translate';
import {
	SOURCE_TYPE_TABLE,
	SOURCE_TYPE_INDICATOR,
} from '../common/types/data-source';

import {
	getGroupByOptions,
	getStackByOptions,
	getGroupByOptionsFilter,
} from '../visualizations-config-panel/actions/model-options';
import { getValidGroupByField } from '../visualizations-config-panel/actions/model-validators';

import { formatMultipleLabels } from '../helpers/string-utils';
import { RETRIEVE_FIELD_METADATA } from '../visualizations-config-panel/actions/action-constants';
import { extractTableFieldsFromDataToApply } from './helpers';
import { getFieldDefinition, getFieldDefinitionKey } from '../helpers/field';
import { type FieldDefinition } from '../common/types/field';
import { updateModelWithGroupByValues } from './helpers/transform-metadata';

const handleComponentBootstrap = (coeffects: Coeffects<State, any>) => {
	coeffects.updateState({ componentHost: coeffects.action.payload.host });

	loadFieldLabels(coeffects);
};

const handlePropertyChanged = (coeffects: Coeffects<State, any>) => {
	if (
		hasPropertyChanged(coeffects, 'chartModel', ['dataSources']) ||
		hasPropertyChanged(coeffects, 'dataSourceInfos')
	) {
		loadFieldLabels(coeffects);
	}
};

const handleNowSelectSelection = (coeffects: Coeffects<State, any>) => {
	coeffects.action.stopPropagation();
	const selectedValue = get(coeffects, 'action.payload.value');
	const fieldName: string = get(coeffects, 'action.payload.fieldName');

	if (fieldName) {
		const {
			dataToApply,
			localOptions,
		} = getDataToApplyAndOptionsAfterFieldChange(
			coeffects,
			fieldName,
			selectedValue
		);

		coeffects.updateState({
			dataToApply,
			localOptions,
			selectInvalid: {
				...coeffects.state.selectInvalid,
				[fieldName]: false,
			},
			errorMessages: {
				...coeffects.state.errorMessages,
				[fieldName]: undefined,
			},
		});
	}
};

const handleGroupByMaxSelection = (coeffects: Coeffects<State, any>) => {
	coeffects.action.stopPropagation();
	const groupByMax = coeffects.action.payload.value[0];
	const {
		dataToApply,
		localOptions,
	} = getDataToApplyAndOptionsAfterFieldChange(
		coeffects,
		GROUP_BY_MAX,
		groupByMax
	);

	const previousGroupByMax = coeffects.state.dataToApply.groupByMax;

	if (previousGroupByMax === GROUP_BY_MAX_NUMBER_ALL)
		dataToApply.showOthers = true;

	coeffects.updateState({
		dataToApply,
		localOptions,
	});
};

const handleNoOfGroupBasedOnSelection = (coeffects: Coeffects<State, any>) => {
	coeffects.action.stopPropagation();

	const noOfGroupBasedOn = coeffects.action.payload.value[0];

	const {
		dataToApply,
		localOptions,
	} = getDataToApplyAndOptionsAfterFieldChange(
		coeffects,
		NO_OF_GROUP_BASED_ON,
		noOfGroupBasedOn
	);

	coeffects.updateState({
		dataToApply,
		localOptions,
	});
};

const getDataToApplyAndOptionsAfterFieldChange = (
	coeffects: Coeffects<State, any>,
	fieldName: string,
	selectedValue: string,
	fieldDefinition?: FieldDefinition
) => {
	const {
		properties: { chartModel, dataSourceInfos },
		dataToApply,
		localOptions,
	} = coeffects.state;

	const newDataToApply = {
		...dataToApply,
		[fieldName]: selectedValue,
	};

	const newLocalOptions = { ...localOptions };

	const firstGroupByField = `${GROUP_BY_FIELD}0`;

	if (
		fieldName === firstGroupByField &&
		chartModel.dataSources[0].sourceType === SOURCE_TYPE_TABLE
	) {
		const clonedChartModel = cloneDeep(chartModel);

		const definitions = fieldDefinition
			? {
					...coeffects.state.properties.dotWalkFieldDefinition,
					[getFieldDefinitionKey({
						tableName: chartModel.dataSources[0].tableName,
						fieldName: selectedValue,
					})]: fieldDefinition,
			  }
			: coeffects.state.properties.dotWalkFieldDefinition;

		clonedChartModel.dataSources[0].groupByField = selectedValue;
		clonedChartModel.dataSources.forEach((ds, index) => {
			if (index === 0) {
				return;
			}

			const fieldName = `${GROUP_BY_FIELD}${index}`;

			newLocalOptions[fieldName] = getGroupByOptions(
				ds,
				clonedChartModel.dataSources,
				dataSourceInfos,
				definitions
			);

			newDataToApply[fieldName] = getValidGroupByField(
				ds,
				clonedChartModel,
				dataSourceInfos,
				definitions
			);
		});
	}

	return {
		dataToApply: newDataToApply,
		localOptions: newLocalOptions,
	};
};

const getInitialDataToApplyAndOptions = (
	state: State,
	isPillarGroupBy: boolean
): $Shape<State> => {
	const { chartModel, dataSourceInfos } = state.properties;
	const { dataSources, groupByMax, noOfGroupBasedOn, showOthers } = chartModel;
	const dataToApply = {};
	const localOptions = {};

	if (isPillarGroupBy) {
		dataToApply.groupByMax = groupByMax;
		dataToApply.noOfGroupBasedOn = noOfGroupBasedOn;
		dataToApply.showOthers = showOthers;
		dataSources.forEach((ds, index) => {
			if (index > 0 && ds.sourceType === SOURCE_TYPE_INDICATOR) {
				return;
			}

			const fieldName = `${GROUP_BY_FIELD}${index}`;
			const options = getGroupByOptions(
				ds,
				chartModel.dataSources,
				dataSourceInfos,
				state.properties.dotWalkFieldDefinition
			);

			dataToApply[fieldName] = ds.groupByField;
			localOptions[fieldName] = options;
		});
	} else {
		dataSources.forEach((ds, index) => {
			const fieldName = `${STACK_BY_FIELD}${index}`;
			const options = getStackByOptions(ds, dataSourceInfos);

			dataToApply[fieldName] = ds.stackByField;
			localOptions[fieldName] = options;
		});
	}

	return {
		dataToApply,
		localOptions,
	};
};

const handlePillarClicked = ({ state, action, updateState, dispatch }) => {
	const {
		payload: { id, payloadActionType },
	} = action;

	const {
		componentHost,
		properties: { chartModel },
	} = state;

	const isPillarGroupBy = payloadActionType === PILLAR_GROUP_BY;

	action.stopPropagation();

	if (id === PILLAR_REMOVE_ACTION) {
		updateState(createCleanPopoverState());
		const dataToApply: $PropertyType<State, 'dataToApply'> = {};

		chartModel.dataSources.forEach((ds, index) => {
			const groupByFieldName = `${GROUP_BY_FIELD}${index}`;
			const stackByFieldName = `${STACK_BY_FIELD}${index}`;

			if (isPillarGroupBy) {
				if (ds.stackByField) {
					dataToApply[groupByFieldName] = ds.stackByField;
					dataToApply[stackByFieldName] = undefined;
				} else {
					dataToApply[groupByFieldName] = undefined;
				}
			} else {
				dataToApply[stackByFieldName] = undefined;
			}
		});

		dispatch(SET_GROUP_BY, {
			...dataToApply,
		});
	} else {
		const popoverTarget = componentHost.shadowRoot.querySelector(
			`#${payloadActionType}`
		);
		const popoverContent = isPillarGroupBy
			? POPOVER_CONTENT_GROUP_BY
			: POPOVER_CONTENT_STACK_BY;

		updateState({
			popoverContent,
			popoverAction: PILLAR_EDIT_ACTION,
			popoverOpened: true,
			popoverTarget,
			...getInitialDataToApplyAndOptions(state, isPillarGroupBy),
		});
	}
};

const createCleanPopoverState = () => ({
	popoverOpened: false,
	popoverClosedAt: new Date().getTime(),
	popoverContent: undefined,
	popoverAction: undefined,
	dataToApply: {},
	selectInvalid: {},
	errorMessages: {},
});

const handleButtonClicked = ({
	state,
	action: { payload },
	updateState,
	dispatch,
}: Coeffects<State, any>) => {
	const {
		dataToApply,
		componentHost,
		properties: {
			chartModel: { dataSources },
		},
		selectInvalid,
	} = state;

	if (payload.payloadActionType === CLOSE_GROUP_BY_POPOVER) {
		updateState(createCleanPopoverState());
	} else if (payload.payloadActionType === APPLY_GROUP_BY_POPOVER) {
		const { errorMessages, valid } = validateTypeaheads(selectInvalid);

		if (!valid) {
			return updateState({ errorMessages });
		}

		updateState(createCleanPopoverState());

		if (dataSources.some(ds => ds.sourceType === SOURCE_TYPE_INDICATOR)) {
			return dispatch(SET_GROUP_BY, {
				...dataToApply,
			});
		}

		const fields = extractTableFieldsFromDataToApply(dataToApply, dataSources);

		dispatch(
			RETRIEVE_FIELD_METADATA,
			{ fields },
			{
				actionName: SET_GROUP_BY,
				payload: dataToApply,
			}
		);
	} else if (
		payload.payloadActionType === CLICK_BUTTON_ADD_GROUP_BY &&
		componentHost &&
		componentHost.shadowRoot
	) {
		const hasGroupBy = dataSources.some(ds => ds.groupByField);
		const popoverContent = hasGroupBy
			? POPOVER_CONTENT_STACK_BY
			: POPOVER_CONTENT_GROUP_BY;

		updateState({
			popoverContent,
			popoverAction: PILLAR_ADD_ACTION,
			dataToApply: {},
			popoverOpened: true,
			popoverTarget: componentHost.shadowRoot.querySelector(
				'#btn-add-group-by'
			),
			...getInitialDataToApplyAndOptions(state, !hasGroupBy),
		});
	}
};

const validateTypeaheads = (
	selectInvalid: $PropertyType<State, 'selectInvalid'>
) => {
	const errorMessages = {};

	for (const fieldName in selectInvalid) {
		if (selectInvalid[fieldName]) {
			errorMessages[fieldName] = t('Please select a value');
		}
	}

	return {
		valid: !Object.keys(errorMessages).length,
		errorMessages,
	};
};

const handleToggleCheckedSet = ({
	action,
	state,
	updateState,
}: Coeffects<State, any>) => {
	const { value, fieldName } = action.payload;

	if (fieldName) {
		updateState({
			dataToApply: {
				...state.dataToApply,
				[fieldName]: value,
			},
		});
	}
};

const handleDotWalkPathUpdated = (coeffects: Coeffects<State, any>) => {
	const {
		action,
		state: { dataToApply },
		properties: { chartModel, dataSourceInfos, dotWalkFieldDefinition },
	} = coeffects;

	const { dotWalkPath, fieldName, definition, dataSource } = action.payload;

	const patchedChartModel = updateModelWithGroupByValues(
		cloneDeep(chartModel),
		dataToApply
	);

	if (fieldName) {
		const groupByFieldFilter = getGroupByOptionsFilter(
			dataSource,
			patchedChartModel.dataSources,
			dataSourceInfos,
			dotWalkFieldDefinition
		);

		if (definition && !groupByFieldFilter(definition.field)) {
			return coeffects.updateState([
				{
					operation: 'set',
					path: `errorMessages[${fieldName}]`,
					value: t('Selected field is invalid'),
				},
				{
					operation: 'set',
					path: `selectInvalid[${fieldName}]`,
					value: true,
				},
			]);
		}

		const {
			dataToApply,
			localOptions,
		} = getDataToApplyAndOptionsAfterFieldChange(
			coeffects,
			fieldName,
			dotWalkPath,
			definition.field
		);

		coeffects.updateState({
			dataToApply,
			localOptions,
			selectInvalid: {
				...coeffects.state.selectInvalid,
				[fieldName]: false,
			},
			errorMessages: {
				...coeffects.state.errorMessages,
				[fieldName]: undefined,
			},
		});
	}
};

const getFieldLabel = (
	coeffects: Coeffects<State, any>,
	fieldName: string
): string => {
	const {
		chartModel: { dataSources },
		dataSourceInfos,
	} = coeffects.state.properties;

	const hasField = dataSources.some(ds => ds[fieldName]);

	if (!hasField) {
		return '';
	}

	const labels = dataSources.reduce((acc, ds) => {
		const fieldValue = ds[fieldName];

		if (!fieldValue || !ds.id || !dataSourceInfos[ds.id]) {
			return acc;
		}

		if (ds.sourceType === SOURCE_TYPE_TABLE && ds.tableName) {
			const field = getFieldDefinition(
				fieldValue,
				dataSourceInfos[ds.id],
				ds,
				coeffects.state.properties.dotWalkFieldDefinition
			);

			if (field) {
				acc.push(field.label);
			}
		} else if (ds.sourceType === SOURCE_TYPE_INDICATOR) {
			const breakdowns = dataSourceInfos[ds.id].breakdowns || [];
			const breakdown = breakdowns.find(b => b.sysId === fieldValue);

			if (breakdown) {
				acc.push(breakdown.name);
			}
		}

		return acc;
	}, []);

	return formatMultipleLabels(labels);
};

const loadFieldLabels = (coeffects: Coeffects<State, any>) => {
	let groupByLabel = getFieldLabel(coeffects, 'groupByField');
	let stackByLabel = getFieldLabel(coeffects, 'stackByField');

	coeffects.updateState({
		groupByLabel,
		stackByLabel,
	});
};

const handleNowDropdownItemSelected = (coeffects: Coeffects<State, any>) => {
	switch (coeffects.action.payload.payloadActionType) {
		case SET_GROUP_BY_MAX:
			handleGroupByMaxSelection(coeffects);
			break;

		case SET_NO_OF_GROUP_BASED_ON:
			handleNoOfGroupBasedOnSelection(coeffects);
			break;
		case SET_CHART_VARIATION: {
			coeffects.dispatch(SET_CHART_VARIATION, {
				isStacked: coeffects.action.payload.value[0],
			});
			break;
		}
	}
};

export default {
	actionHandlers: {
		[actionTypes.COMPONENT_BOOTSTRAPPED]: handleComponentBootstrap,
		[actionTypes.COMPONENT_PROPERTY_CHANGED]: handlePropertyChanged,
		[SN_PAR_PILLAR_ACTION_CLICKED]: handlePillarClicked,
		[SN_PAR_PILLAR_CLICKED]: handlePillarClicked,
		'NOW_BUTTON_ICONIC#CLICKED': handleButtonClicked,
		'NOW_BUTTON#CLICKED': handleButtonClicked,
		'VIZ_CONFIG_TOGGLE#CHECKED_SET': handleToggleCheckedSet,
		'NOW_SELECT#SELECTED_ITEM_SET': handleNowSelectSelection,
		'NOW_DROPDOWN#SELECTED_ITEMS_SET': handleNowDropdownItemSelected,
		'NOW_POPOVER#OPENED_SET': ({
			updateState,
			action,
		}: Coeffects<State, any>) => {
			if (action.payload.type === MAIN_POPOVER && !action.payload.value) {
				updateState(createCleanPopoverState());
			}
		},
		[DOT_WALK_PATH_UPDATED]: handleDotWalkPathUpdated,
	},
};
