// @flow
// @jsx snabbdom.createElement

import snabbdom, { Fragment } from '@servicenow/ui-renderer-snabbdom'; // eslint-disable-line no-unused-vars
import { t } from 'sn-translate';
import 'sn-devx-components';
import '../source-selector';
import '../table-configuration';
import '../indicator-configuration';
import '../empty-state';
import { type State } from './state';

import {
	SOURCE_TYPE_INDICATOR,
	SOURCE_TYPE_TABLE,
} from '../common/types/data-source';

export default function(state: State) {
	const {
		properties,
		sourceType,
		displayLabel = '',
		tableInfo,
		indicatorInfo,
	} = state;

	const isEditMode = Boolean(
		properties.dataSource && properties.dataSource.sourceType
	);
	const isIndicatorSelected = sourceType === SOURCE_TYPE_INDICATOR;
	const isTableSelected = sourceType === SOURCE_TYPE_TABLE;
	const isSourceSelected = isIndicatorSelected || isTableSelected;

	if (isEditMode && !isSourceSelected) {
		// State is not fully initialized - skip the render
		return undefined;
	}

	const resetModalFocus = ({ elm }) =>
		elm.focus && setTimeout(() => elm.focus(), 50);

	const footerActions = [
		{
			label: isEditMode ? t('Apply') : t('Add this source'),
			variant: 'primary',
			disabled: !isSourceSelected,
			clickActionType: 'NOW_MODAL#ADD_SOURCE',
		},
		{
			label: t('Cancel'),
			variant: 'secondary',
			disabled: false,
			clickActionType: 'NOW_MODAL#OPENED_SET',
		},
	];

	const renderDetailsPanel = () => {
		if (isIndicatorSelected && indicatorInfo && indicatorInfo.uuid) {
			return (
				<viz-config-indicator-configuration
					selectedIndicatorInfo={indicatorInfo}
				/>
			);
		}

		if (isTableSelected) {
			return <viz-config-table-configuration tableInfo={tableInfo} />;
		}

		return (
			<viz-config-empty-state illustration="unconfigured">
				<span slot="title">{t('Select a data source to start')}</span>
				<span slot="primary">
					{t(
						'Once you select a data source from the panel, optionally add conditions, then add your source.'
					)}
				</span>
			</viz-config-empty-state>
		);
	};

	const renderPanelHeader = () => {
		let label = '';

		if (isIndicatorSelected) {
			label = t('Selected indicator');
		} else if (isTableSelected) {
			label = t('Selected table');
		}

		if (label && displayLabel) {
			return (
				<now-heading
					className="heading"
					label={`${label}: ${displayLabel}`}
					level="3"
					variant="header-tertiary"
					wont-wrap
				/>
			);
		}
	};

	return (
		<div>
			<now-modal
				hook-insert={resetModalFocus}
				size="fullscreen"
				header-label={isEditMode ? t('Edit data source') : t('Add data source')}
				opened={true}
				manage-opened
				footer-actions={footerActions}>
				<div className="flex-grid">
					<div className="source-selector-container">
						<viz-config-source-selector
							id={
								(indicatorInfo &&
									indicatorInfo.uuid &&
									indicatorInfo.uuid.indicator) ||
								(tableInfo && tableInfo.tableName)
							}
							addNewDataSource={properties.operationMetadata.addNewDataSource}
							allowedSourceType={properties.operationMetadata.allowedSourceType}
							sourceType={sourceType}
						/>
					</div>
					<div className="source-configuration-panel">
						{renderPanelHeader()}
						<div className="source-configuration-panel-content">
							{renderDetailsPanel()}
						</div>
					</div>
				</div>
			</now-modal>
		</div>
	);
}
