//@flow
import { type Coeffects } from '@servicenow/ui-core';
import { type State } from './state';

import {
	COLOR_TYPE_REQUESTED,
	LOAD_COLOR_REQUESTED,
	SET_COLOR_REQUESTED,
} from './constants';

const handleSelectedItemSet = (coeffects: Coeffects<State, any>) => {
	switch (coeffects.action.payload.payloadActionType) {
		case COLOR_TYPE_REQUESTED: {
			coeffects.action.stopPropagation();
			coeffects.dispatch(SET_COLOR_REQUESTED, {
				color: {
					type: coeffects.action.payload.value[0],
				},
			});
			break;
		}

		case LOAD_COLOR_REQUESTED: {
			coeffects.action.stopPropagation();
			coeffects.dispatch(SET_COLOR_REQUESTED, {
				metricIndex: coeffects.action.payload.metricIndex,
				color: {
					sys_id: coeffects.action.payload.item.id,
					type: coeffects.action.payload.type,
				},
			});
			break;
		}
	}
};

export default {
	actionHandlers: {
		'NOW_SELECT#SELECTED_ITEM_SET': handleSelectedItemSet,
		'NOW_DROPDOWN#SELECTED_ITEMS_SET': handleSelectedItemSet,
	},
};
