// @flow
// @jsx snabbdom.createElement
import '@servicenow/now-toggle';
import '@servicenow/now-button';
import { type State } from './state';
import { type Coeffects } from '@servicenow/ui-core';
import snabbdom, { Fragment } from '@servicenow/ui-renderer-snabbdom'; // eslint-disable-line no-unused-vars
import './../common/popover-content';

export default (state: State, { dispatch }: Coeffects<State, any>) => {
	const {
		properties: { checked, label, disabled, disableMargin, infoTooltip },
	} = state;

	return (
		<div
			class={{
				'viz-config-toggle': true,
				'viz-config-toggle--no-margin': disableMargin,
			}}>
			<label
				on-click={e => {
					if (e.composedPath()[0].type !== 'button' && !disabled) {
						dispatch('VIZ_CONFIG_TOGGLE#CHECKED_SET', { value: !checked });
					}
				}}>
				<now-toggle
					size="sm"
					disabled={disabled}
					append-to-payload={{ payloadActionType: 'VIZ_CONFIG_TOGGLE' }}
					checked={checked}
					managed-checked
					config-aria={{ 'aria-label': label }}
				/>
				<span>{label}</span>
			</label>

			{infoTooltip ? (
				<now-popover interactionType="none">
					<now-icon slot="trigger" icon="info-circle-outline" />
					<viz-config-popover-content
						slot="content"
						popOverContent={infoTooltip}
					/>
				</now-popover>
			) : null}
		</div>
	);
};
