// @flow
// @jsx snabbdom.createElement
import snabbdom, { Fragment } from '@servicenow/ui-renderer-snabbdom'; // eslint-disable-line no-unused-vars
import { createCustomElement } from '@servicenow/ui-core';
import rtlBehavior from '@servicenow/behavior-rtl';
import styles from './styles.scss';
import { t } from 'sn-translate';

import '../dot-walk';
import '@servicenow/now-select';

import { type State } from './state';
import { type FieldDefinition } from '../common/types/field';

import {
	TREND_BY_SELECTED,
	FREQUENCY_OPTIONS,
	TREND_BY_FREQUENCY_SELECTED,
	BTN_APPLY_ID,
	BTN_TRENDBY_POPOVER_CLOSE_ID,
} from './constants';

import { SOURCE_TYPE_INDICATOR } from '../common/types/data-source';
import { createTransformMetadataFn } from '../group-by-selector/helpers/transform-metadata';

type SharedState = {
	properties: {
		sharedState: State,
		getOptionsForMetric: Function,
	},
};

createCustomElement('viz-config-trend-by-popover-content', {
	view: ({ properties: { sharedState, getOptionsForMetric } }: SharedState) => {
		const {
			properties: {
				chartModel: { dataSources, trendByFrequency, metrics },
			},
			dataToApply,
			errorMessages,
			localTrendByList,
		} = sharedState;

		const isIndicator = dataSources.some(
			dataSource => dataSource.sourceType === SOURCE_TYPE_INDICATOR
		);

		const getTrendByFieldValue = index =>
			metrics[index] && getOptionsForMetric(metrics[index]).length
				? metrics[index].trendByField
				: false;

		const getLocalTrendByField = index =>
			dataToApply && dataToApply.trendByField && dataToApply.trendByField[index]
				? dataToApply.trendByField[index]
				: getTrendByFieldValue(index);

		const localTrendByFrequency =
			dataToApply && dataToApply.trendByFrequency
				? dataToApply.trendByFrequency
				: trendByFrequency;

		const transformMetadata = createTransformMetadataFn(
			(_, field: FieldDefinition) => field.canTrendBy
		);

		return (
			<div className="viz-config-trendby-popover">
				<now-heading
					label={t('Trend by')}
					level="3"
					variant="title-secondary"
				/>
				<now-button-iconic
					bare
					icon="close-fill"
					size="md"
					variant="primary"
					append-to-payload={{
						id: BTN_TRENDBY_POPOVER_CLOSE_ID,
					}}
					config-aria={{ 'aria-label': t('Close') }}
				/>

				<now-label-value-inline label={t('Aggregation period')} />
				<now-dropdown
					id="aggregation-period-dropdown"
					append-to-payload={{
						payloadActionType: TREND_BY_FREQUENCY_SELECTED,
					}}
					items={FREQUENCY_OPTIONS}
					select="single"
					selected-items={[localTrendByFrequency]}
					size="md"
					variant="tertiary"
					config-aria={{
						'aria-label': t('Aggregation period'),
					}}
				/>

				{localTrendByList.map((data, index) => {
					const noOptions = data.options.length === 0;
					const noOptionsMessage = t('There are no date fields available');
					const value = getLocalTrendByField(index);

					const placeholder =
						(noOptions && isIndicator) ||
						(noOptions && !data.hasReferenceFields)
							? t('No date fields')
							: t('Select a field');

					const errorMessage = noOptions
						? noOptionsMessage
						: errorMessages &&
						  errorMessages.fieldError &&
						  errorMessages.fieldError[index];

					const messages = errorMessage
						? [{ status: 'critical', content: errorMessage }]
						: undefined;

					if (isIndicator) {
						return (
							<now-select
								key={index}
								className="viz-config-metric-data-source"
								label={t('Date field for {0}', data.label)}
								append-to-payload={{
									payloadActionType: TREND_BY_SELECTED,
									payloadActionMetricIndex: index,
								}}
								items={data.options}
								placeholder={placeholder}
								selected-item={value}
								search="contains"
								messages={messages}
							/>
						);
					}

					const dotWalkError =
						!value && data.hasReferenceFields
							? t('Please select a date field')
							: !value && !data.hasReferenceFields
							? noOptionsMessage
							: null;

					const dotWalkMessage = dotWalkError
						? dotWalkError
						: errorMessages.fieldError && errorMessages.fieldError[index];

					return (
						<Fragment>
							<viz-config-dot-walk
								key={`trendby-dot-walk-${index}`}
								className="viz-config-metric-data-source"
								label={t('Date field for {0}', data.label)}
								placeholder={placeholder}
								tableName={data.dataSource.tableName}
								dotWalkPath={dotWalkMessage ? undefined : value}
								disabled={noOptions && !data.hasReferenceFields}
								error-message={dotWalkMessage}
								transform-metadata={transformMetadata.bind(
									null,
									data.dataSource
								)}
								append-to-payload={{
									payloadActionType: TREND_BY_SELECTED,
									payloadActionMetricIndex: index,
								}}
							/>
						</Fragment>
					);
				})}

				<now-button
					label={t('Apply')}
					size="md"
					variant="primary"
					append-to-payload={{
						id: BTN_APPLY_ID,
					}}
				/>
			</div>
		);
	},
	properties: {
		sharedState: {
			default: {},
		},
		getOptionsForMetric: {},
	},
	styles,
	renderer: { type: snabbdom },
	behaviors: [{ behavior: rtlBehavior }],
});
