// @flow
// @jsx snabbdom.createElement
import snabbdom, { Fragment } from '@servicenow/ui-renderer-snabbdom'; // eslint-disable-line no-unused-vars
import { type State } from './state';
import { t } from 'sn-translate';

import '../dot-walk';

import { type FieldDefinition } from '../common/types/field';

import {
	isTableMetric,
	isAggregateFieldFxCurrency,
	isIndicatorMetric,
} from '../helpers/metric';

import {
	AGGREGATE_FUNCTION_COUNT,
	FIELD_AGGREGATE_FUNCTION,
	AGGREGATE_CONFIG,
	FIELD_AGGREGATE_FIELD,
	SET_AGGREGATION_SOURCE,
	FIELD_AGGREGATION_SOURCE,
	AGGREGATION_SOURCE_CONFIG,
	AGGREGATION_SOURCE_REFERENCE,
	FIELD_AGGREGATE_INDICATOR,
	INDICATOR_EMPTY_STRING_ID_REPLACEMENT,
} from './constants';

import {
	aggregationFunctionFilters,
	getTableReferenceFields,
} from '../visualizations-config-panel/actions/model-options';
import { createTransformMetadataFn } from '../group-by-selector/helpers/transform-metadata';

export const createFieldDefinitionFilter = (aggregateFunction?: string) => (
	_: any,
	field: FieldDefinition
) => {
	const filter =
		aggregateFunction && aggregationFunctionFilters[aggregateFunction]
			? aggregationFunctionFilters[aggregateFunction]
			: () => aggregateFunction !== AGGREGATE_FUNCTION_COUNT;

	// $FlowFixMe
	return filter(field);
};

export const getAggregateFields = (state: State) => {
	const {
		dataToApply,
		errorMessages,
		properties: { chartModel, dataSourceInfos, dotWalkFieldDefinition },
		localAggregateOptions,
	} = state;

	const {
		dataSource,
		aggregateFunction,
		aggregateField,
		aggregateIndicator,
		aggregationSource,
	} = dataToApply;

	const dataSourceData = chartModel.dataSources.find(
		ds => ds.id === dataSource
	);

	if (!dataSourceData) {
		return undefined;
	}

	if (isTableMetric(dataToApply, chartModel.dataSources)) {
		const isCount = aggregateFunction === AGGREGATE_FUNCTION_COUNT;
		const referenceFields = getTableReferenceFields(
			chartModel,
			dataSourceInfos[dataSource]
		);
		const noOptions = !localAggregateOptions.length && !referenceFields.length;

		const dotWalkError =
			!isCount && noOptions ? t('There are no fields available') : null;
		const dotWalkMessage = dotWalkError || errorMessages.aggregateField;

		const transformMetadata = createTransformMetadataFn(
			createFieldDefinitionFilter(aggregateFunction)
		).bind(null, dataSourceData);

		return (
			<Fragment>
				<div className="section-prop-size">
					<now-label-value-inline
						className="section-heading"
						label={t('Select an aggregation')}
					/>
					<now-dropdown
						className="viz-config-metric-function-dropdown"
						append-to-payload={{
							payloadActionType: FIELD_AGGREGATE_FUNCTION,
							field: FIELD_AGGREGATE_FUNCTION,
						}}
						items={AGGREGATE_CONFIG}
						select="single"
						placeholder={t('Select an option')}
						selected-items={aggregateFunction && [aggregateFunction]}
						size="md"
						variant="tertiary"
						config-aria={{
							'aria-label': t('Select an aggregation'),
						}}
					/>
				</div>
				<viz-config-dot-walk
					disabled={isCount || noOptions}
					className="viz-config-metric-field-typeahead"
					label={t('Field')}
					placeholder={isCount ? t('- Entire table -') : t('Select a field')}
					tableName={dataSourceData.tableName}
					dotWalkPath={aggregateField}
					transform-metadata={transformMetadata}
					error-message={dotWalkMessage}
					append-to-payload={{
						fieldName: FIELD_AGGREGATE_FIELD,
					}}
				/>
				{isAggregateFieldFxCurrency(
					aggregateField,
					dataSourceData,
					dataSourceInfos,
					dotWalkFieldDefinition
				) ? (
					<div className="section-prop-size">
						<now-label-value-inline
							className="section-heading"
							label={t('Show value in')}
						/>
						<now-dropdown
							append-to-payload={{
								payloadActionType: SET_AGGREGATION_SOURCE,
								field: FIELD_AGGREGATION_SOURCE,
							}}
							items={AGGREGATION_SOURCE_CONFIG}
							select="single"
							selected-items={[
								aggregationSource
									? aggregationSource
									: AGGREGATION_SOURCE_REFERENCE,
							]}
							size="md"
							variant="tertiary"
							placeholder={t('Select an option')}
							config-aria={{
								'aria-label': t('Show value in'),
							}}
						/>
					</div>
				) : null}
			</Fragment>
		);
	} else if (isIndicatorMetric(dataToApply, chartModel.dataSources)) {
		const hasOptionsAvailable =
			localAggregateOptions.filter(o => !o.disabled).length > 0;

		const errorMessage = hasOptionsAvailable
			? errorMessages && errorMessages.aggregateIndicator
			: t('There are no aggregations with same frequency');

		const aggregateIndicatorSelectMessages = errorMessage
			? [{ status: 'critical', content: errorMessage }]
			: undefined;

		return (
			<now-select
				className="viz-config-metric-function-typeahead"
				label={t('Time series aggregation')}
				append-to-payload={{
					field: FIELD_AGGREGATE_INDICATOR,
				}}
				items={localAggregateOptions}
				placeholder={
					hasOptionsAvailable ? t('Select a aggregation') : t('No matches')
				}
				selected-item={
					hasOptionsAvailable
						? aggregateIndicator || INDICATOR_EMPTY_STRING_ID_REPLACEMENT
						: undefined
				}
				messages={aggregateIndicatorSelectMessages}
				search="contains"
			/>
		);
	}

	return undefined;
};
