//@flow
import get from 'lodash/get';

import { createGraphQLEffect } from '@servicenow/ui-effect-graphql';
import { getFieldDefinitionKey } from '../../helpers/field';

import { type Coeffects } from '@servicenow/ui-core';
import { type State } from '../state';
import {
	type FieldDefinitionRequest,
	type FieldDefinition,
} from '../../common/types/field';

import {
	RETRIEVE_FIELD_METADATA,
	RETRIEVE_FIELD_METADATA_SUCCESS,
	RETRIEVE_FIELD_METADATA_FAILURE,
} from './action-constants';

const RETRIEVE_FIELD_METADATA_QUERY = `${RETRIEVE_FIELD_METADATA}#DONT_USE`;

const handleFieldMetadataRetrievalMemo = ({
	state,
	action,
	dispatch,
	updateState,
}: Coeffects<State, { fields: FieldDefinitionRequest[] }>) => {
	const { actionName, payload } = action.meta;

	const cache = state.dotWalkFieldDefinition;

	const fieldsPayload = action.payload.fields || [];

	const fields = fieldsPayload.filter(
		field => !cache[getFieldDefinitionKey(field)]
	);

	/**
	 * Keep the continuity even if theres nothing to load
	 */
	if (!fields.length) {
		updateState({
			isInitializingDotWalkFields: false,
		});

		return actionName && dispatch(actionName, payload);
	}

	dispatch(
		RETRIEVE_FIELD_METADATA_QUERY,
		{ fields: JSON.stringify(fields) },
		{ fields, ...action.meta }
	);
};

const handleFieldMetadataRetrieval = createGraphQLEffect(
	`query($fields: String!){
		AppPARConfig_Query {
			fieldDefinitions(fields: $fields) {
				value {
					field {
						label
						name
						type
						internalType
						reference
						canCountDistinct
						canGroupBy
						canSum
						canTrendBy
						isRange
						unit
					}
					result
				}
				result
			}
		}
	}`,
	{
		variableList: ['fields'],
		successActionType: RETRIEVE_FIELD_METADATA_SUCCESS,
		errorActionType: RETRIEVE_FIELD_METADATA_FAILURE,
	}
);

const handleFieldMetadataRetrievalSuccess = (
	coeffects: Coeffects<State, any>
) => {
	const updates = [];

	const { fields, actionName, payload } = coeffects.action.meta;

	const definitions: { field: FieldDefinition, result: string }[] = get(
		coeffects.action.payload,
		'data.AppPARConfig_Query.fieldDefinitions.value'
	);

	for (let i = 0; i < definitions.length; i++) {
		const key = getFieldDefinitionKey(fields[i]);

		if (definitions[i].result !== 'SUCCESS') {
			continue;
		}

		updates.push({
			operation: 'merge',
			path: 'dotWalkFieldDefinition',
			value: { [key]: definitions[i].field },
		});
	}

	coeffects.updateState(updates);

	coeffects.updateState({
		isInitializingDotWalkFields: false,
	});

	/**
	 * This is the continuity of some previous action that depends
	 * on the result of the field metadata
	 */
	if (actionName) {
		coeffects.dispatch(actionName, payload);
	}
};

export const fieldsActionHandlers = {
	[RETRIEVE_FIELD_METADATA]: handleFieldMetadataRetrievalMemo,
	[RETRIEVE_FIELD_METADATA_QUERY]: handleFieldMetadataRetrieval,
	[RETRIEVE_FIELD_METADATA_SUCCESS]: handleFieldMetadataRetrievalSuccess,
};
