//@flow
import { type ChartType } from '../../common/types/chart-type';
import { type UuidChartAPI } from '../../common/types/uuid';

export type ChartProps = {|
	chartType: ChartType,
	dataConfigurations: DataConfiguration[],
	styleConfigurations: StyleConfiguration,
	variant?: string,
	border?: boolean,
	label?: string,
	isRealtime?: boolean,
	showRealtimeIndicator?: boolean,
	refreshInterval?: string,
	kpiDetailsNavigation?: boolean,
	followFilters?: boolean,
	hideFiltersInfo?: boolean,
	// This prop is just sent to UXF with value '@state.parFilters' based on followFilters boolean value, we do not read it back.
	filterConfigurations?: string,
	hideHeader?: boolean,
	refreshFrequency?: string,
|};

export type PropsChartSettings = {|
	main_series_label: string,
	color?: ColorConfiguration,
|};

export type ColorConfiguration = {
	type?: string,
	sys_id?: string,
	conditions: ColorCondition[],
};

export type ColorCondition = {
	operator: string,
	value1: number,
	value2: number,
	order: number,
	color_sys_id: string,
};

export type DataConfiguration = {|
	__viz_config_id: ?string,
	__viz_config_label: string,
	source_type: 'table' | 'analytics' | 'source',
	source_id?: string,
	uuid?: UuidChartAPI,
	group_by?: string,
	number_of_groups?: number,
	selected_groups?: string,
	show_other?: boolean,
	trend_field?: string,
	trend_interval?: string,
	stack_by_field?: string,
	aggregate_field?: string,
	aggregate_function?: string,
	aggregation_source?: string,
	start_time?: string, // https://en.wikipedia.org/wiki/ISO_8601 Durations | PnYnMnDTnHnMnS
	end_time?: string,
	include_scores?: boolean,
	include_benchmark?: boolean,
	include_forecast?: boolean,
	include_forecast_range?: boolean,
	include_global_targets?: boolean,
	include_targets?: boolean,
	include_target_review_date?: boolean,
	include_sparkline_global_targets?: boolean,
	include_sparkline_personal_targets?: boolean,
	include_thresholds?: boolean,
	include_change?: boolean,
	include_change_percentage?: boolean,
	include_comments?: boolean,
	include_initiatives?: boolean,
	include_sparkline?: boolean,
	include_gap_percentage?: boolean,
	include_trend?: boolean,
	include_band?: boolean,
	realtime?: boolean,
	filter_query?: string,
	decimal_precision?: number,
	sort_by?: string,
	sort_order?: string,
	display_zero?: boolean,
	chart_settings?: PropsChartSettings | void,
|};

export type StyleConfiguration = {
	width?: string,
	height?: string,
	accessibilityMode?: boolean,
	accessibilityTable?: boolean,
	type?: string,
	dataLabelConfig?: {
		showTotal?: boolean,
		type?: string,
		align?: string,
		collisions?: string,
		valueType?: string,
	},
	hideRegister?: boolean,
	registerConfig?: {
		legendOnly?: boolean,
		showPercentages?: boolean,
	},
	registerPosition?: string,
	showDataLabels?: boolean,
	extents?: ExtentsXenolith,
	xAxisConfig?: {
		title?: string,
	},
	yAxisConfig?: AxisConfigXenolith,
	seriesConfig?: SeriesConfigXenolith,
	colors?: {
		type: string,
		sys_id: string,
	},
	displayZero?: boolean,
	canClick?: boolean,
	heading?: string,
	scoreSize?: string,
	headingPosition?: 'top' | 'bottom',
};

export type AxisConfigXenolith = {
	title?: string,
	orientation?: string,
	[string]: {
		title?: string,
		orientation: string,
	},
};

export type SeriesConfigXenolith = {
	[string]: {
		axis?: string,
		type?: string,
	},
};

export type ExtentsXenolith = {
	[string]: Array<?number | ?string>,
};
