// @flow
// @jsx snabbdom.createElement

import { createCustomElement } from '@servicenow/ui-core';
import snabbdom, { Fragment } from '@servicenow/ui-renderer-snabbdom'; // eslint-disable-line no-unused-vars
import styles from '../visualizations-config-panel/views/main-view.scss';

createCustomElement('viz-config-popover-content', {
	renderer: { type: snabbdom },
	properties: {
		popOverContent: { type: 'string' },
	},
	view(popOverContent) {
		const popoverText = popOverContent.properties.popOverContent;

		return <div className="viz-config-popover-content">{popoverText}</div>;
	},
	styles,
});
