//@flow
import get from 'lodash/get';
import { t } from 'sn-translate';
import {
	type DataSource,
	type DataSourceInfo,
	type DataSourceInfos,
	SOURCE_TYPE_INDICATOR,
	SOURCE_TYPE_TABLE,
} from '../../common/types/data-source';

export const uuidPropsOrder = [
	'aggregate',
	'breakdowns',
	'domain',
	'indicator',
	'sysId',
	'elementSysIds',
	'elementsFilterSysId',
];

export const shallowDataSourceCompare = (
	dataSource: DataSource,
	otherDataSource: DataSource
) =>
	(dataSource.sourceType === SOURCE_TYPE_INDICATOR &&
		get(dataSource, 'uuid.indicator') ===
			get(otherDataSource, 'uuid.indicator')) ||
	(dataSource.sourceType === SOURCE_TYPE_TABLE &&
		dataSource.tableName === otherDataSource.tableName);

export const isSameDataSource = (
	currentDataSource: DataSource,
	newDataSource: DataSource
) =>
	(currentDataSource.sourceType === SOURCE_TYPE_INDICATOR &&
		JSON.stringify(currentDataSource.uuid, uuidPropsOrder) ===
			JSON.stringify(newDataSource.uuid, uuidPropsOrder)) ||
	(currentDataSource.sourceType === SOURCE_TYPE_TABLE &&
		currentDataSource.tableName === newDataSource.tableName);

export const getUniqueDataSourceLabel = (
	disDisplayLabel: string = t('Data source'),
	dataSources: DataSource[] = [],
	iteration: number = 0
) => {
	const label = disDisplayLabel + (iteration === 0 ? '' : ` ${iteration}`);

	const findSameLabel = dataSources.find(ds => ds.label === label);

	if (findSameLabel) {
		return getUniqueDataSourceLabel(
			disDisplayLabel,
			dataSources,
			iteration + 1
		);
	}

	return label;
};

export const getDataSourceUniqueId = (
	dataSource: DataSource,
	dataSources: DataSource[] = []
) => {
	const timestamp = new Date().getTime();

	const id = btoa(
		dataSource.sourceType === SOURCE_TYPE_TABLE
			? `${dataSource.sourceType}${get(dataSource, 'tableName')}${timestamp}`
			: `${dataSource.sourceType}${get(
					dataSource,
					'uuid.indicator'
			  )}${timestamp}`
	);

	const findSameId = dataSources.find(ds => ds.id === id);

	if (findSameId) {
		return getDataSourceUniqueId(dataSource, dataSources);
	}

	return id;
};

export const isFieldReportRange = (
	fieldName?: string,
	dataSourceInfo: DataSourceInfo
) => {
	if (!dataSourceInfo.tableFields) return false;

	const fieldDefinition = dataSourceInfo.tableFields.filter(
		info => info.name === fieldName
	);

	return !fieldDefinition || fieldDefinition.length === 0
		? false
		: fieldDefinition[0].isRange;
};

export const isDataSourceGroupByReportRange = (
	dataSource: DataSource,
	dataSourceInfos: DataSourceInfos
) => {
	if (dataSource.sourceType === SOURCE_TYPE_INDICATOR) return false;

	const dataSourceInfo = dataSourceInfos[dataSource.id];

	if (!dataSourceInfo) return false;

	return isFieldReportRange(dataSource.groupByField, dataSourceInfo);
};

export const isAnyDataSourceGroupByReportRange = (
	dataSources: DataSource[],
	dataSourceInfos: DataSourceInfos
) => {
	return dataSources.length === 0
		? false
		: isDataSourceGroupByReportRange(dataSources[0], dataSourceInfos);
};

export const isFieldReportRangeForDataSource = (
	field: string,
	dataSource: DataSource,
	dataSourceInfos: DataSourceInfos
) => {
	const dataSourceInfo = dataSourceInfos[dataSource.id];

	if (!dataSourceInfo || dataSource.sourceType === SOURCE_TYPE_INDICATOR)
		return false;

	return isFieldReportRange(field, dataSourceInfo);
};
