// @flow
// @jsx snabbdom.createElement
import snabbdom, { Fragment } from '@servicenow/ui-renderer-snabbdom'; // eslint-disable-line no-unused-vars
import { t } from 'sn-translate';
import 'sn-devx-components';
import '../toggle';
import { type State } from './state';
import '@servicenow/now-accordion';
import '@servicenow/now-popover';

import './group-by-popover';
import './stack-by-popover';

import {
	GROUP_BY_NOT_SUPPORTED,
	STACK_BY_NOT_SUPPORTED,
	GROUP_BY_MANDATORY,
	TIMESERIES_CHART_TYPES,
	PIES_CHART_TYPES,
} from '../common/chart-settings';

import { SOURCE_TYPE_INDICATOR } from '../common/types/data-source';

import { type ChartModel } from '../visualizations-config-panel/model/chart-model';
import {
	getGroupBySupport,
	getStackBySupport,
} from '../visualizations-config-panel/actions/model-validators';
import {
	hasGroupByOptions,
	hasStackByOptions,
} from '../visualizations-config-panel/actions/model-options';

import {
	GROUP_BY_FIELD,
	GROUP_BY_MAX_NUMBER_ALL,
	PILLAR_GROUP_BY,
	PILLAR_STACK_BY,
	CLICK_BUTTON_ADD_GROUP_BY,
	POPOVER_CONTENT_GROUP_BY,
	POPOVER_CONTENT_STACK_BY,
	PILLAR_EDIT_ACTION,
	PILLAR_REMOVE_ACTION,
	MAIN_POPOVER,
	CHART_VARIATIONS,
	SET_CHART_VARIATION,
} from './constants';

import { SIDE_BY_SIDE } from '../common/chart-settings';

import { doesShowChartVariation } from '../helpers/display-settings/helpers';

import { autoScrollToVisibility } from '../helpers/now-accordion/auto-scroll-to-visibility';
import { nowPopoverAllowedPositions } from '../common/popover-config';

import { isFieldReportRangeForDataSource } from '../helpers/data-source/index';
import { getMissingGroupByIcon } from './helpers';

const preventClickFromOpenPopover = e => e.stopPropagation();

const getNoMatchingBreakdownsError = () => {
	return (
		<div slot="content" className="now-accordion-content">
			<div className="error-message">
				<now-icon icon="circle-exclamation-outline" size="md"></now-icon>
				<label>
					{t('There are no matching breakdowns for the selected indicators.')}
				</label>
			</div>
		</div>
	);
};

const getAccordionContent = (state: State, groupBySupport: string) => {
	const {
		properties: { chartModel, dataSourceInfos },
		stackByLabel,
	} = state;

	const { dataSources, isStacked } = chartModel;
	const stackBySupport = getStackBySupport(chartModel);
	const isIndicator = dataSources.some(
		ds => ds.sourceType === SOURCE_TYPE_INDICATOR
	);

	const hasGroupBy = dataSources.some(ds => ds.groupByField);

	let allowedElements = 2;
	let infoMessage = '';
	let selectedElements = 0;
	let pillarActions = [];
	let hasStackBy = false;

	if (isTimeseriesWithMultipleMetrics(chartModel)) {
		infoMessage = t(
			'Group by is not available for time series visualizations with more than one metric.'
		);
		allowedElements = 0;
	} else if (
		!hasGroupByOptions(
			dataSources,
			dataSourceInfos,
			state.properties.dotWalkFieldDefinition
		)
	) {
		if (dataSources.length === 1 || !isIndicator) {
			infoMessage = isIndicator
				? t('This data source doesnt have breakdowns to group by.')
				: t('This data source doesnt have fields to group by.');
			allowedElements = 0;
		} else {
			return getNoMatchingBreakdownsError();
		}
	} else if (stackBySupport === STACK_BY_NOT_SUPPORTED) {
		allowedElements = 1;
	} else if (!hasStackByOptions(dataSources, dataSourceInfos)) {
		infoMessage = t('This data source has only one field to group by.');

		allowedElements = 1;
	}

	if (hasGroupBy) {
		pillarActions.push({
			icon: 'pencil-outline',
			id: PILLAR_EDIT_ACTION,
			label: t('Edit group by'),
		});

		if (
			!(
				groupBySupport === GROUP_BY_MANDATORY &&
				stackBySupport === STACK_BY_NOT_SUPPORTED
			)
		) {
			pillarActions.push({
				icon: 'circle-close-outline',
				id: PILLAR_REMOVE_ACTION,
				disabled: groupBySupport === GROUP_BY_MANDATORY && !stackByLabel,
				label: t('Remove group by'),
			});
		}

		hasStackBy = dataSources.some(ds => ds.stackByField);

		selectedElements++;

		if (hasStackBy) {
			selectedElements++;
		}
	}

	const allowGroupBy = selectedElements < allowedElements;

	const groupBySelectorClass =
		stackBySupport === STACK_BY_NOT_SUPPORTED
			? 'group-by-selector'
			: 'group-by-selector pillar-box';

	const showChartVariation = doesShowChartVariation(chartModel);

	return (
		<div slot="content" className="now-accordion-content">
			<div className={groupBySelectorClass}>
				{getGroupByPopover(state, pillarActions, allowGroupBy && !hasGroupBy)}
				{getStackByPopover(state, allowGroupBy && !hasStackBy && hasGroupBy)}
				{infoMessage ? (
					<now-button
						id="btn-add-group-by"
						append-to-payload={{
							payloadActionType: CLICK_BUTTON_ADD_GROUP_BY,
						}}
						icon="plus-fill"
						label={t('Add')}
						size="sm"
						disabled={!!infoMessage}
						variant="secondary"></now-button>
				) : null}
			</div>
			{showChartVariation ? (
				<div className="chart-variation">
					<now-label-value-inline
						className="section-heading"
						label={t('Chart variation')}
					/>
					<now-dropdown
						id="chart-variation-dropdown"
						append-to-payload={{
							payloadActionType: SET_CHART_VARIATION,
						}}
						items={CHART_VARIATIONS}
						select="single"
						selected-items={[isStacked || SIDE_BY_SIDE]}
						size="md"
						variant="tertiary"
						config-aria={{
							'aria-label': t('Chart variation'),
						}}
					/>
				</div>
			) : (
				''
			)}
			{infoMessage ? (
				<div className="info-message">
					<now-icon icon="circle-info-outline" size="md"></now-icon>
					<label>{infoMessage}</label>
				</div>
			) : null}
		</div>
	);
};

const getGroupByPopover = (
	state: State,
	pillarActions: any[],
	showButton: boolean
) => {
	const {
		properties: { chartModel, dataSourceInfos, dotWalkFieldDefinition },
		popoverContent,
		popoverOpened,
		popoverClosedAt,
		popoverTarget,
		groupByLabel,
		dataToApply,
		errorMessages,
		localOptions,
	} = state;

	const { dataSources } = chartModel;

	const isFirstGroupByReportRange = isFieldReportRangeForDataSource(
		dataToApply[`${GROUP_BY_FIELD}${0}`],
		dataSources[0],
		dataSourceInfos
	);

	let triggerContent;

	if (
		isFirstGroupByReportRange &&
		dataToApply.groupByMax !== GROUP_BY_MAX_NUMBER_ALL
	)
		dataToApply.groupByMax = GROUP_BY_MAX_NUMBER_ALL;

	if (isFirstGroupByReportRange) dataToApply.showOthers = false;

	if (dataSources.some(ds => ds.groupByField)) {
		triggerContent = (
			<sn-par-pillar
				slot="trigger"
				id={PILLAR_GROUP_BY}
				append-to-payload={{
					payloadActionType: PILLAR_GROUP_BY,
				}}
				clickable
				ellipsis
				actions={pillarActions}
				on-click={preventClickFromOpenPopover}>
				{getMissingGroupByIcon(dataSources)}
				{groupByLabel}
			</sn-par-pillar>
		);
	} else if (showButton) {
		triggerContent = (
			<now-button
				slot="trigger"
				id="btn-add-group-by"
				append-to-payload={{
					payloadActionType: CLICK_BUTTON_ADD_GROUP_BY,
				}}
				icon="plus-fill"
				label={t('Add')}
				size="sm"
				variant="secondary"
			/>
		);
	}

	return (
		<now-popover
			append-to-payload={{ type: MAIN_POPOVER }}
			opened={popoverOpened && popoverContent === POPOVER_CONTENT_GROUP_BY}
			positions={nowPopoverAllowedPositions}
			position-target={popoverTarget}
			manage-opened>
			{triggerContent}
			<viz-config-group-by-popover-content
				slot="content"
				shared-state={{
					chartModel,
					dataSourceInfos,
					popoverClosedAt,
					dataToApply,
					errorMessages,
					localOptions,
					dotWalkFieldDefinition,
				}}
			/>
		</now-popover>
	);
};

const getStackByPopover = (state: State, showButton: boolean) => {
	const {
		properties: {
			chartModel: { dataSources },
		},
		stackByLabel,
		popoverContent,
		popoverOpened,
		popoverTarget,
	} = state;

	let triggerContent;

	if (dataSources.some(ds => ds.stackByField)) {
		triggerContent = (
			<sn-par-pillar
				slot="trigger"
				id={PILLAR_STACK_BY}
				append-to-payload={{
					payloadActionType: PILLAR_STACK_BY,
				}}
				clickable
				ellipsis
				on-click={preventClickFromOpenPopover}
				actions={[
					{
						icon: 'pencil-outline',
						id: PILLAR_EDIT_ACTION,
						label: t('Edit group by'),
					},
					{
						icon: 'circle-close-outline',
						id: PILLAR_REMOVE_ACTION,
						label: t('Remove group by'),
					},
				]}>
				{stackByLabel}
			</sn-par-pillar>
		);
	} else if (showButton) {
		triggerContent = (
			<now-button
				slot="trigger"
				id="btn-add-group-by"
				append-to-payload={{
					payloadActionType: CLICK_BUTTON_ADD_GROUP_BY,
				}}
				icon="plus-fill"
				label={t('Add')}
				size="sm"
				variant="secondary"
			/>
		);
	}

	return (
		<now-popover
			append-to-payload={{ type: MAIN_POPOVER }}
			opened={popoverOpened && popoverContent === POPOVER_CONTENT_STACK_BY}
			positions={nowPopoverAllowedPositions}
			position-target={popoverTarget}
			manage-opened>
			{triggerContent}
			<viz-config-stack-by-popover-content
				slot="content"
				shared-state={state}
			/>
		</now-popover>
	);
};

const isTimeseriesWithMultipleMetrics = (chartModel: ChartModel) =>
	TIMESERIES_CHART_TYPES.includes(chartModel.chartType) &&
	chartModel.metrics.length > 1;

export const GroupByView = (state: State) => {
	const {
		properties: { chartModel, expanded },
	} = state;

	const groupBySupport = getGroupBySupport(chartModel);

	// Timeseries with multiple metrics don't support support but needs to display error message

	if (
		groupBySupport === GROUP_BY_NOT_SUPPORTED &&
		!isTimeseriesWithMultipleMetrics(chartModel)
	) {
		return;
	}

	const groupByAccordionLabel = PIES_CHART_TYPES.includes(chartModel.chartType)
		? t('Group by (Slices)')
		: t('Group by');

	return (
		<now-accordion-item
			dividers={{ top: false, bottom: true }}
			header={groupByAccordionLabel}
			expanded={expanded}
			append-to-payload={{ accordionItem: 'groupBy' }}
			on-click={autoScrollToVisibility}>
			{getAccordionContent(state, groupBySupport)}
		</now-accordion-item>
	);
};
