// @flow
import isEqual from 'lodash/isEqual';
import isPlainObject from 'lodash/isPlainObject';
import setWith from 'lodash/setWith';
import clone from 'lodash/clone';
import get from 'lodash/get';
import { type LoadingItem, type ContentTreeItem } from './state';

const unwrapPath = (path: any) => (isPlainObject(path) ? path.path : path);

export const hasPath = (list: LoadingItem[], path: string[]) =>
	list.findIndex(p => isEqual(unwrapPath(p), unwrapPath(path))) > -1;
export const removePath = (
	list: LoadingItem[],
	path: string[]
): LoadingItem[] => list.filter(p => !isEqual(unwrapPath(p), unwrapPath(path)));
const addPath = (list: LoadingItem[], path: string[]): LoadingItem[] => [
	...list,
	{ path },
];

export const togglePath = (
	list: LoadingItem[],
	path: string[]
): LoadingItem[] =>
	hasPath(list, path) ? removePath(list, path) : addPath(list, path);

const toLodashPath = (items: ContentTreeItem[], path: string[]) => {
	let ids = [...path];
	let next: ContentTreeItem[] = items;
	let result = [];

	while (ids.length) {
		const id = ids.shift();
		const index = next.findIndex(item => item.id === id);

		if (ids.length === 0) {
			return [...result, index];
		}

		result = [...result, index, 'children'];
		if (next[index].children) {
			next = next[index].children;
		}
	}

	return undefined;
};

const setItem = (
	items: ContentTreeItem[],
	path: string[],
	fn
): ContentTreeItem[] => {
	const lodashPath = toLodashPath(items, path);
	const item = get(items, lodashPath);

	return setWith(clone(items), lodashPath, clone(fn(item)), clone);
};

export const setItemChildren = (
	items: ContentTreeItem[],
	path: string[],
	children: ContentTreeItem[],
	childrenCount: number
): ContentTreeItem[] =>
	setItem(items, path, item => ({
		...item,
		children: [...(item.children || []), ...children],
		childrenAvailable: (item.children || []).length + childrenCount,
	}));

export const clearItemChildren = (
	items: ContentTreeItem[],
	path: string[]
): ContentTreeItem[] =>
	setItem(items, path, item => ({
		...item,
		children: [],
		childrenAvailable: 0,
	}));
