export const getFirstOption = (options: Option[]) => {
	const firstGroupByOption = get(options, '[0].id', '');

	return firstGroupByOption === DEFAULT_DROPDOWN_VALUE
		? ''
		: firstGroupByOption;
};

export const getValidChartType = (
	chartModel: ChartModel,
	dataSourceInfos: DataSourceInfos,
	fieldDefinitions: FieldDefinitions
) => {
	const { chartType, dataSources } = chartModel;

	if (!chartType) {
		return CHART_TYPES.SINGLE_SCORE;
	}

	const chartConfig = getChartConfig(chartType);

	if (
		dataSources.length === 1 &&
		dataSources[0].sourceType === SOURCE_TYPE_INDICATOR &&
		(chartConfig.menuGroup === 'bar' || chartConfig.menuGroup === 'pie') &&
		!hasGroupByOptions(dataSources, dataSourceInfos, fieldDefinitions)
	) {
		return CHART_TYPES.SINGLE_SCORE;
	}

	return chartModel.chartType;
};

export const getValidDataSources = (chartModel: ChartModel, state: State) => {
	const { dataSources } = chartModel;

	dataSources.forEach(dataSource => {
		dataSource.groupByField = getValidGroupByField(
			dataSource,
			chartModel,
			state.dataSourceInfos,
			state.dotWalkFieldDefinition
		);

		dataSource.stackByField = getValidStackBy(
			dataSource,
			chartModel,
			state.dataSourceInfos
		);
	});

	return dataSources;
};

export const getGroupBySupport = ({
	chartType,
	dataSources,
	metrics,
}: ChartModel) => {
	if (
		PIES_CHART_TYPES.includes(chartType) ||
		BAR_CHART_TYPES.includes(chartType)
	) {
		return GROUP_BY_MANDATORY;
	}

	if (
		TIMESERIES_CHART_TYPES.includes(chartType) &&
		dataSources.length === 1 &&
		metrics.length === 1
	) {
		return GROUP_BY_OPTIONAL;
	}

	return GROUP_BY_NOT_SUPPORTED;
};

export const getValidGroupByField = (
	dataSource: DataSource,
	chartModel: ChartModel,
	dataSourceInfos: DataSourceInfos,
	dotWalkFieldDefinition: FieldDefinitions
) => {
	const groupBySupport = getGroupBySupport(chartModel);

	if (groupBySupport === GROUP_BY_NOT_SUPPORTED) return undefined;

	const { groupByField } = dataSource;
	const { dataSources } = chartModel;

	const groupByOptions = getGroupByOptions(
		dataSource,
		dataSources,
		dataSourceInfos,
		dotWalkFieldDefinition
	);

	const hasGroupByField = groupByOptions.find(c => c.id === groupByField);

	const isFirstDataSource = dataSource.id === dataSources[0].id;
	const firstGroupByField = dataSources[0].groupByField || '';
	const firstDataSourceInfo = dataSourceInfos[dataSources[0].id];
	const isDotWalkField = /\./.test(groupByField || '');

	if (isDotWalkField) {
		if (isFirstDataSource) {
			return groupByField;
		}

		const firstDefinition = getFieldDefinition(
			firstGroupByField,
			firstDataSourceInfo,
			dataSources[0],
			dotWalkFieldDefinition
		);

		const currentDefinition =
			dotWalkFieldDefinition[
				`${dataSource.tableName || ''}:${groupByField || ''}`
			];

		if (!currentDefinition || !firstDefinition) {
			throw new Error(
				`Field definition for ${groupByField || ''} does not exists.`
			);
		}

		if (isFieldCompatible(firstDefinition, currentDefinition)) {
			return groupByField;
		}
	}

	if (hasGroupByField) {
		return groupByField;
	}

	if (groupBySupport === GROUP_BY_OPTIONAL) {
		return undefined;
	}

	if (
		!isFirstDataSource &&
		groupByOptions.find(c => c.id === firstGroupByField)
	) {
		return firstGroupByField;
	}

	return getFirstOption(groupByOptions);
};

export const getValidGroupByMax = (
	chartModel: ChartModel,
	dataSourceInfos: DataSourceInfos
) => {
	const { dataSources } = chartModel;
	const groupBySupport = getGroupBySupport(chartModel);

	if (groupBySupport === GROUP_BY_NOT_SUPPORTED) return undefined;

	if (isAnyDataSourceGroupByReportRange(dataSources, dataSourceInfos))
		return GROUP_BY_MAX_NUMBER_ALL;

	return chartModel.groupByMax ? chartModel.groupByMax : GROUP_BY_DEFAULT_MAX;
};

export const getValidNoOfGroupBasedOn = (chartModel: ChartModel) => {
	const groupBySupport = getGroupBySupport(chartModel);

	if (groupBySupport === GROUP_BY_NOT_SUPPORTED) return undefined;

	return chartModel.noOfGroupBasedOn && chartModel.dataSources.length > 1
		? chartModel.noOfGroupBasedOn
		: NO_OF_GROUP_BASED_ON_PER_METRIC;
};

export const getValidShowOthers = (chartModel: ChartModel) => {
	const groupBySupport = getGroupBySupport(chartModel);

	if (
		groupBySupport === GROUP_BY_NOT_SUPPORTED ||
		chartModel.groupByMax === GROUP_BY_MAX_NUMBER_ALL
	)
		return undefined;

	if (
		chartModel.dataSources.length > 1 &&
		getValidNoOfGroupBasedOn(chartModel) === NO_OF_GROUP_BASED_ON_FIRST_METRIC
	)
		return false;

	return chartModel.showOthers;
};

export const getStackBySupport = ({
	chartType,
	dataSources,
	metrics,
}: ChartModel) => {
	if (
		BAR_CHART_TYPES.includes(chartType) &&
		dataSources.length === 1 &&
		metrics.length === 1 &&
		dataSources[0].sourceType === SOURCE_TYPE_TABLE
	) {
		return STACK_BY_OPTIONAL;
	}

	return STACK_BY_NOT_SUPPORTED;
};

export const getValidStackBy = (
	dataSource: DataSource,
	chartModel: ChartModel,
	dataSourceInfos: DataSourceInfos
) => {
	const stackBySupport = getStackBySupport(chartModel);

	if (stackBySupport === STACK_BY_NOT_SUPPORTED) return undefined;

	const stackByOptions = getStackByOptions(dataSource, dataSourceInfos);
	const { groupByField, stackByField } = dataSource;

	if (dataSource.sourceType === SOURCE_TYPE_INDICATOR) {
		const hasStackByField = stackByOptions.find(c => c.id === stackByField);

		if (hasStackByField) {
			return stackByField;
		}
	} else {
		if (stackByField && stackByField !== groupByField) {
			return stackByField;
		}
	}

	if (stackBySupport === STACK_BY_OPTIONAL) {
		return undefined;
	}

	return getFirstOption(stackByOptions);
};

export const getValidIsStacked = (chartModel: ChartModel) => {
	if (!doesShowChartVariation(chartModel)) {
		return undefined;
	} else if (chartModel.isStacked === undefined) {
		return SIDE_BY_SIDE;
	}

	return chartModel.isStacked;
};

export const getValidMetrics = (
	chartModel: ChartModel,
	dataSourceInfos: DataSourceInfos,
	fieldDefinitions: FieldDefinitions
) => {
	const { dataSources } = chartModel;

	//Remove metrics with invalid datasource
	const metrics: Metric[] = chartModel.metrics.filter(metric => {
		return !!dataSources.find(dt => dt.id === metric.dataSource);
	});

	//Verify each datasource has at least one metric
	dataSources.forEach(dataSource => {
		const metricFound = metrics.find(m => m.dataSource === dataSource.id);

		if (dataSource.id && !metricFound) {
			metrics.push({
				dataSource: dataSource.id,
			});
		}
	});

	metrics.forEach((metric, index) => {
		getValidMetric(
			chartModel,
			dataSourceInfos,
			fieldDefinitions,
			metric,
			index
		);

		metric.trendByField = getValidTrendByField(
			chartModel,
			getTrendByOptions(
				chartModel,
				metric.dataSource ? dataSourceInfos[metric.dataSource] : {}
			),
			metric,
			fieldDefinitions
		);

		const timeseriesMultipleMetrics =
			TIMESERIES_CHART_TYPES.includes(chartModel.chartType) &&
			metrics.length > 1;

		if (!timeseriesMultipleMetrics) {
			metric.chartType = undefined;
		}
	});

	return metrics;
};

export const getValidMetric = (
	chartModel: ChartModel,
	dataSourceInfos: DataSourceInfos,
	fieldDefinitions: FieldDefinitions,
	metric: Metric,
	metricIndex: ?number
) => {
	const { dataSources } = chartModel;

	if (isTableMetric(metric, dataSources) && metric.dataSource) {
		const dataSourceInfo = dataSourceInfos[metric.dataSource];
		const aggregateFunction = getValidAggregateFunction(metric);
		const aggregateOptions = getAggregateOptionsTable(
			aggregateFunction,
			dataSourceInfo
		);

		metric.aggregateFunction = aggregateFunction;
		metric.aggregateField = getValidAggregateField(
			aggregateFunction,
			metric.aggregateField,
			aggregateOptions
		);
		metric.aggregationSource = getValidAggregationSource(
			metric,
			getMetricDataSource(metric, chartModel.dataSources),
			fieldDefinitions
		);
		metric.aggregateIndicator = undefined;
	} else if (isIndicatorMetric(metric, dataSources)) {
		metric.aggregateFunction = undefined;
		metric.aggregateField = undefined;
		metric.aggregateIndicator = getValidAggregateIndicator(
			metricIndex,
			metric,
			chartModel,
			dataSourceInfos
		);
	}

	return metric;
};

export const getValidYAxisConfigs = (
	chartModel: ChartModel,
	dataSourceInfos: DataSourceInfos,
	fieldDefinitions: FieldDefinitions
): AxisConfig[] => {
	const { chartType, metrics, dataSources } = chartModel;

	if (!supportAxis(chartModel)) {
		metrics.forEach(metric => (metric.axisId = undefined));
		return [];
	}

	let { yAxisConfigs } = chartModel;

	if (supportMultipleAxis(chartModel)) {
		metrics.forEach((metric, index) => {
			if (index === 0) {
				// First Metric - If no axis, then set primary
				if (!metric.axisId || !yAxisConfigs.length) {
					metric.axisId = PRIMARY_AXIS;

					yAxisConfigs.splice(0, yAxisConfigs.length); // Remove all elements from array
					yAxisConfigs.push({
						id: PRIMARY_AXIS,
						orientation: AXIS_ORIENTATION_LEFT,
					});
				}
			} else {
				const metricUnit = getMetricUnit(
					metric,
					dataSources,
					dataSourceInfos,
					fieldDefinitions
				);

				// Validate if current axis is valid
				if (metric.axisId) {
					const axisUnit = getAxisUnit(
						metric.axisId,
						chartModel,
						dataSourceInfos,
						fieldDefinitions
					);

					if (metricUnit !== axisUnit) {
						metric.axisId = undefined;
					}
				}

				// Try to find axis with same unit
				if (!metric.axisId) {
					const axisConfigFound = yAxisConfigs.find(
						ac =>
							getAxisUnit(
								ac.id,
								chartModel,
								dataSourceInfos,
								fieldDefinitions
							) === metricUnit
					);

					if (axisConfigFound) {
						metric.axisId = axisConfigFound.id;
					}
				}

				// If axis with same unit not found, create a new one
				if (!metric.axisId) {
					const validAxisId = getValidAxisId(yAxisConfigs);

					metric.axisId = validAxisId;
					yAxisConfigs.push({
						id: validAxisId,
						orientation: AXIS_ORIENTATION_RIGHT,
					});
				}
			}
		});
	} else {
		metrics.forEach(metric => (metric.axisId = PRIMARY_AXIS));
		if (!yAxisConfigs.length) {
			yAxisConfigs = [
				{
					id: PRIMARY_AXIS,
					orientation: undefined,
				},
			];
		}
	}

	// Remove axis without metrics
	yAxisConfigs = yAxisConfigs.filter(axisConfig => {
		return metrics.find(m => m.axisId === axisConfig.id);
	});

	// Reorder because we always need a primary axis and assign orientation
	yAxisConfigs.forEach((axisConfig, index) => {
		const idealAxisId = index === 0 ? PRIMARY_AXIS : `${AXIS_PREFIX}${index}`;

		if (axisConfig !== idealAxisId) {
			metrics.forEach(metric => {
				if (metric.axisId === axisConfig.id) {
					metric.axisId = idealAxisId;
				}
			});
			axisConfig.id = idealAxisId;
		}

		if (BAR_CHART_TYPES.includes(chartType)) {
			axisConfig.orientation = undefined;
		} else if (!axisConfig.orientation) {
			axisConfig.orientation =
				index === 0 ? AXIS_ORIENTATION_LEFT : AXIS_ORIENTATION_RIGHT;
		}
	});

	return yAxisConfigs;
};

export const getValidAggregateField = (
	aggregateFunction: string,
	aggregateField: string = '',
	aggregateOptions: Option[]
) => {
	if (aggregateFunction !== AGGREGATE_FUNCTION_COUNT) {
		if (/\./.test(aggregateField)) {
			return aggregateField;
		}

		const hasAggregateField = aggregateOptions.find(
			c => c.id === aggregateField
		);

		if (hasAggregateField) {
			return aggregateField;
		}

		return getFirstOption(aggregateOptions);
	}

	return undefined;
};

export const getValidAggregateFunction = (metric: Metric) => {
	if (metric.aggregateFunction) {
		return metric.aggregateFunction;
	}

	return AGGREGATE_FUNCTION_COUNT;
};

export const getValidAggregationSource = (
	metric: Metric,
	dataSource: DataSource,
	fieldDefinitions: FieldDefinitions
) => {
	if (metric.aggregationSource) {
		return metric.aggregationSource;
	}

	const fieldDefinition =
		fieldDefinitions[
			getFieldDefinitionKey({
				tableName: dataSource.tableName,
				fieldName: metric.aggregateField,
			})
		];

	if (fieldDefinition && isFxCurrencyField(fieldDefinition.internalType)) {
		return AGGREGATION_SOURCE_REFERENCE;
	}

	return undefined;
};

export const getValidAggregateIndicator = (
	metricIndex: ?number,
	metric: Metric,
	chartModel: ChartModel,
	dataSourceInfos: DataSourceInfos
) => {
	const { chartType, metrics } = chartModel;

	if (metricIndex !== 0 && TIMESERIES_CHART_TYPES.includes(chartType)) {
		return getAggregateWithSameFrequency(metrics[0], metric, dataSourceInfos);
	} else {
		const aggregateOptions = getAggregateOptionsIndicator(
			metric.dataSource,
			chartModel,
			dataSourceInfos
		);

		const hasAggregateIndicator = aggregateOptions.find(
			o => o.id === metric.aggregateIndicator && !o.disabled
		);

		if (hasAggregateIndicator) {
			return hasAggregateIndicator.id;
		}

		return getFirstOption(aggregateOptions.filter(o => !o.disabled));
	}
};

export const getValidTrendByField = (
	chartModel: ChartModel,
	trendByOptions: Option[],
	metric: Metric,
	fieldDefinitions: FieldDefinitions
) => {
	const { chartType, dataSources } = chartModel;

	if (dataSources[0].sourceType !== SOURCE_TYPE_TABLE) {
		return undefined;
	}

	const chartConfig = getChartConfig(chartType);

	if (chartConfig.trendBy === TREND_BY_NOT_SUPPORTED) return undefined;

	const trendByField = metric.trendByField || '';
	const dataSource = getMetricDataSource(metric, chartModel.dataSources);

	if (
		fieldDefinitions[
			getFieldDefinitionKey({
				tableName: dataSource.tableName,
				fieldName: trendByField,
			})
		]
	) {
		return trendByField;
	}

	const hasTrendyByField = trendByOptions.find(c => c.id === trendByField);

	if (hasTrendyByField) {
		return trendByField;
	}

	return get(trendByOptions, '[0].id', '');
};

export const getValidTrendByFrequency = (chartModel: ChartModel) => {
	const { chartType, dataSources } = chartModel;

	if (dataSources[0].sourceType !== SOURCE_TYPE_TABLE) {
		return undefined;
	}

	const chartConfig = getChartConfig(chartType);

	if (chartConfig.trendBy === TREND_BY_NOT_SUPPORTED) return undefined;

	if (chartModel.trendByFrequency) {
		return chartModel.trendByFrequency;
	}

	return FREQUENCY_OPTIONS[0].id;
};

export const getValidAdditionalSettings = (
	chartModel: ChartModel,
	dataSourceInfos: DataSourceInfos
) => {
	const { chartType, dataSources } = chartModel;
	const allRelevant = getBooleanSettings(chartModel, dataSourceInfos);
	const clonedSettings = cloneDeep(chartModel.additionalSettings);

	const isSingleScoreIndicator =
		chartType === CHART_TYPES.SINGLE_SCORE &&
		dataSources[0].sourceType === SOURCE_TYPE_INDICATOR;

	const newSettings = allRelevant.reduce((acc, { id }) => {
		acc[id] = clonedSettings[id];

		if (
			acc[id] === undefined &&
			isSingleScoreIndicator &&
			DEFAULT_TRUE_SETTINGS.includes(id)
		) {
			acc[id] = true;
		} else if (acc[id] === undefined) {
			acc[id] = false;
		}

		if (id === 'include_forecast') {
			acc.include_forecast = acc[id];

			if (!acc[id]) {
				clonedSettings.include_forecast_range = false;
			}
		}

		if (id === 'include_change') {
			acc.include_change_percentage = acc[id];

			if (!acc[id]) {
				clonedSettings.include_change_percentage = false;
			}
		}

		if (id === 'include_targets' && acc[id] === false) {
			acc.include_gap_percentage = false;
			clonedSettings.include_gap_percentage = false;
		}

		return acc;
	}, {});

	return newSettings;
};

export const getValidLegendOptions = (
	chartModel: ChartModel
): LegendSettings | void => {
	const { chartType, legendSettings } = chartModel;

	if (chartType === CHART_TYPES.SINGLE_SCORE) {
		return undefined;
	}

	if (!legendSettings) {
		return {
			showLegend: true,
			showLegendValue: false,
			legendPosition: LEGEND_POSITION_BOTTOM,
		};
	}

	const {
		showLegend,
		showLegendValue,
		showLegendPercentage,
		legendPosition,
	} = legendSettings;

	return {
		showLegend,
		showLegendValue: showLegend && showLegendValue,
		showLegendPercentage:
			Boolean(showLegendValue === true && showLegendPercentage === undefined) ||
			showLegendPercentage,
		legendPosition,
	};
};

export const getValidXAxisSettings = (
	{ chartType, axisSettings, metrics }: ChartModel,
	{ chartType: previousChartType }: ChartModel,
	dataSourceInfos: DataSourceInfos
): AxisSettings => {
	const frequency = getMetricFrequency(metrics[0], dataSourceInfos) || 0;

	const filteredRangeTypes = X_AXIS_DATE_RANGE_TYPES.filter(
		type => type.frequencyRange >= frequency
	);

	const dateRangeTypeDefault =
		filteredRangeTypes.length >= 3
			? DATE_RANGE_TYPE.MONTHS
			: filteredRangeTypes[0].id;

	const dateRangeType =
		(axisSettings && axisSettings.dateRangeType) || dateRangeTypeDefault;

	const newSettings = {
		...axisSettings,
		dateRangeShowMax: Boolean(axisSettings && axisSettings.dateRangeShowMax),
		dateRangeAmount:
			(axisSettings && axisSettings.dateRangeAmount) || DATE_RANGE_AMOUNT_DFT,
		dateRangeType,
	};

	const isChangingToTimeseries =
		TIMESERIES_CHART_TYPES.includes(chartType) &&
		!TIMESERIES_CHART_TYPES.includes(previousChartType);

	const isCurrentTypeStillAvailable = !!filteredRangeTypes.find(
		type => type.id === dateRangeType
	);

	/**
	 * Regarding `!isCurrentTypeStillAvailable`
	 * If the user change the indicator, and this new indicator has a different frequency range
	 * we need to check if the current selected frequency is correct, otherwise fallback to default
	 *
	 * Regarding `isChangingToTimeseries`
	 * If the user change the type to timeseries (from not-timeseries) then we apply the default
	 * To make sure the default is applied in some specific cases (i.e.: empty state)
	 */
	if (isChangingToTimeseries || !isCurrentTypeStillAvailable) {
		newSettings.dateRangeShowMax = false;
		newSettings.dateRangeAmount = DATE_RANGE_AMOUNT_DFT;
		newSettings.dateRangeType = dateRangeTypeDefault;
	}

	return newSettings;
};

export const getValidDecimalPrecision = (
	chartModel: ChartModel,
	dataSourceInfos: DataSourceInfos,
	fieldDefinitions: FieldDefinitions
): number | void => {
	if (
		shouldEnableDecimalPrecision(chartModel, dataSourceInfos, fieldDefinitions)
	) {
		const decimalPrecision =
			chartModel.dataLabelSettings &&
			chartModel.dataLabelSettings.decimalPrecision;

		return decimalPrecision === undefined
			? DEFAULT_DECIMAL_PRECISION
			: decimalPrecision;
	}

	return undefined;
};

export const shouldEnableDecimalPrecision = (
	chartModel: ChartModel,
	dataSourceInfos: DataSourceInfos,
	fieldDefinitions: FieldDefinitions
): boolean => {
	return chartModel.metrics.some(metric => {
		return (
			isTableMetric(metric, chartModel.dataSources) &&
			!isFXCurrencyMetric(
				metric,
				chartModel.dataSources,
				dataSourceInfos,
				fieldDefinitions
			) &&
			metric.aggregateFunction !== AGGREGATE_FUNCTION_COUNT &&
			metric.aggregateFunction !== AGGREGATE_FUNCTION_DISTINCT
		);
	});
};

export const getValidRealtime = (
	chartModel: ChartModel,
	dataSourceInfos: DataSourceInfos
): boolean | void => {
	if (isRealtimeRelevant(chartModel, dataSourceInfos)) {
		return chartModel.realtime;
	}

	return undefined;
};

export const getValidShowRealtimeIndicator = (
	chartModel: ChartModel,
	dataSourceInfos: DataSourceInfos
): boolean | void => {
	if (getValidRealtime(chartModel, dataSourceInfos))
		return (
			chartModel.showRealtimeIndicator === undefined ||
			chartModel.showRealtimeIndicator
		);

	return undefined;
};

export function isRealtimeRelevant(
	chartModel: ChartModel,
	dataSourceInfos: DataSourceInfos
) {
	if (chartModel.chartType === CHART_TYPES.SINGLE_SCORE) {
		return chartModel.metrics.some(metric => {
			if (!metric.dataSource || !dataSourceInfos[metric.dataSource]) {
				return false;
			}

			const dataSourceInfo = dataSourceInfos[metric.dataSource];

			return (
				(isIndicatorMetric(metric, chartModel.dataSources) &&
					dataSourceInfo.isIndicatorRealtime) ||
				(isTableMetric(metric, chartModel.dataSources) &&
					metric.aggregateFunction === AGGREGATE_FUNCTION_COUNT &&
					!dataSourceInfo.isDatabaseView)
			);
		});
	}

	return false;
}

export function getValidRefreshFrequency(
	isAdmin: boolean,
	chartModel: ChartModel,
	dataSourceInfos: DataSourceInfos
) {
	if (isRefreshFrequencyRelevant(isAdmin, chartModel, dataSourceInfos))
		return chartModel.refreshFrequency;

	return undefined;
}

export function isRefreshFrequencyRelevant(
	isAdmin: boolean,
	chartModel: ChartModel,
	dataSourceInfos: DataSourceInfos
) {
	if (!isAdmin) return false;

	if (chartModel.chartType === CHART_TYPES.SINGLE_SCORE) {
		return chartModel.metrics.some(metric => {
			if (!metric.dataSource || !dataSourceInfos[metric.dataSource]) {
				return false;
			}

			const dataSourceInfo = dataSourceInfos[metric.dataSource];

			return (
				isTableMetric(metric, chartModel.dataSources) &&
				!(
					metric.aggregateFunction === AGGREGATE_FUNCTION_COUNT &&
					!dataSourceInfo.isDatabaseView
				)
			);
		});
	}

	return false;
}

export const getValidChartSettings = (
	chartModel: ChartModel,
	singleColors: Option[],
	paletteColors: Option[]
): ModelChartSettings | void => {
	const chartSettings = chartModel.chartSettings || {};

	let colorType =
		(chartSettings.color && chartSettings.color.type) || COLOR_TYPE_DEFAULT;
	let colorSysId = chartSettings.color && chartSettings.color.sys_id;

	const validColorChoices = COLOR_CHOICES.filter(type => {
		return !type.extraVerification || type.extraVerification(chartModel);
	}).map(p => p.id);

	if (!validColorChoices.includes(colorType)) {
		colorType = validColorChoices[0];
	}

	if (colorType === COLOR_TYPE_PALETTE) {
		const colorFound = paletteColors.find(o => o.id === colorSysId);
		const newColorSysId = colorFound ? colorFound.id : paletteColors[0].id;

		chartSettings.color = { type: colorType, sys_id: newColorSysId };
	} else if (colorType === COLOR_TYPE_SINGLE) {
		chartModel.metrics.forEach(metric => {
			const colorFound =
				metric.colorSysID && singleColors.find(o => o.id === metric.colorSysID);
			const newColorSysId = colorFound ? colorFound.id : singleColors[0].id;

			metric.colorSysID = newColorSysId;
		});
	} else {
		chartSettings.color = { type: colorType };
	}

	return chartSettings;
};

export const getValidDisplaySettings = (
	chartModel: ChartModel,
	dataSourceInfos: DataSourceInfos
): DisplaySettings => {
	const { displaySettings, chartType, dataSources } = chartModel;

	if (!dataSources || !dataSources.length) return {};

	// Display settings do not support multiple dataset yet
	const { stackByField } = dataSources[0];
	const showSortBy = doesShowSortBy(chartType);
	const showDisplayZero = doesShowDisplayZero(chartType, dataSources);

	if (
		chartType !== CHART_TYPES.SINGLE_SCORE &&
		!stackByField &&
		!showSortBy &&
		!showDisplayZero
	)
		return {};

	if (chartType !== CHART_TYPES.SINGLE_SCORE) {
		delete displaySettings.scoreSize;
	}

	if (showDisplayZero)
		return !displaySettings || displaySettings.displayZero === undefined
			? { displayZero: true }
			: displaySettings;

	if (
		displaySettings &&
		displaySettings.sortByField === SORT_BY_RANGE &&
		!isAnyDataSourceGroupByReportRange(dataSources, dataSourceInfos)
	)
		return {
			...displaySettings,
			sortByField: SORT_BY_VALUE,
		};

	return displaySettings || {};
};

export const getValidXenolithProperties = (
	chartModel: ChartModel,
	dataSourceInfos: DataSourceInfos,
	fieldDefinitions: FieldDefinitions
): XenolithProperties => {
	const props = {};

	if (
		DONUT_CHART_TYPES.includes(chartModel.chartType) &&
		chartModel.additionalSettings.showMetricLabel
	) {
		props.totalLabel = getMetricLabel(
			chartModel.dataSources,
			dataSourceInfos,
			chartModel.metrics[0],
			fieldDefinitions
		);
	}

	if (
		chartModel.additionalSettings.showMetricLabel &&
		chartModel.chartType === CHART_TYPES.SINGLE_SCORE
	) {
		props.heading = getMetricLabel(
			chartModel.dataSources,
			dataSourceInfos,
			chartModel.metrics[0],
			fieldDefinitions
		);

		props.headingPosition = get(
			chartModel,
			'xenolithProperties.headingPosition',
			'top'
		);
	}

	return props;
};
