//@flow
import get from 'lodash/get';
import { type Coeffects } from '@servicenow/ui-core';
import { type State } from '../state';
import { createGraphQLEffect } from '@servicenow/ui-effect-graphql';
import { effectProxy } from '../../helpers/seismic';
import {
	LOAD_ALL_COLOR_OPTIONS,
	LOAD_ALL_COLORS_SUCCESS,
} from './action-constants';

export const loadAllColorOptions = createGraphQLEffect(
	`query {
        GlideRecord_Query {
            sys_report_color(queryConditions: "ORDERBYname", pagination: {limit: 1000}) {
                _results {
					name { displayValue }
					sys_id { value }
                }
            }
			pa_chart_color_schemes(queryConditions: "ORDERBYname", pagination: {limit: 1000}) {
                _results {
					name { displayValue }
					sys_id { value }
                }
            }
        }
    }`,
	{
		successActionType: LOAD_ALL_COLORS_SUCCESS,
	}
);

const handleAllColorsSuccess = (coeffects: Coeffects<State, any>) => {
	const singleColors = get(
		coeffects.action.payload,
		'data.GlideRecord_Query.sys_report_color._results',
		[]
	).map(result => {
		return {
			id: result.sys_id.value,
			label: result.name.displayValue,
		};
	});
	const paletteColors = get(
		coeffects.action.payload,
		'data.GlideRecord_Query.pa_chart_color_schemes._results',
		[]
	).map(result => {
		return {
			id: result.sys_id.value,
			label: result.name.displayValue,
		};
	});

	coeffects.updateState({
		singleColors,
		paletteColors,
	});
};

export const colorPaletteActionHandlers = {
	[LOAD_ALL_COLOR_OPTIONS]: effectProxy(loadAllColorOptions),
	[LOAD_ALL_COLORS_SUCCESS]: handleAllColorsSuccess,
};
