//@flow

let cache: { [key: string]: string } = {};

export function getFieldLabelFromCache(table: string, field: string) {
	return cache[table + '->' + field];
}

export function setFieldLabelToCache(
	table: string,
	field: string,
	label: string
) {
	return (cache[table + '->' + field] = label);
}
