// @flow
import { t } from 'sn-translate';
import get from 'lodash/get';

import { type Coeffects, actionTypes } from '@servicenow/ui-core';
import { type State, type TrendbyTypeTrack } from './state';
import { SN_PAR_POPOVER_OPENED_SET } from '@now-par-components/sn-par-popover';
import {
	SN_PAR_PILLAR_CLICKED,
	SN_PAR_PILLAR_ACTION_CLICKED,
} from '@now-par-components/sn-par-pillar';
import {
	TREND_BY_SELECTED,
	SET_TREND_BY,
	TREND_BY_FREQUENCY_SELECTED,
	BTN_APPLY_ID,
	BTN_TRENDBY_POPOVER_CLOSE_ID,
	TREND_BY_POPOVER,
} from './constants';

import { getMetricDataSource } from '../helpers/metric/metric-datasource';
import { getMetricLabel } from '../helpers/metric';
import {
	getTrendByOptions,
	getTableReferenceFields,
} from '../visualizations-config-panel/actions/model-options';
import { DOT_WALK_PATH_UPDATED } from '../dot-walk/constants';

const createCleanPopoverState = () => ({
	popoverOpened: false,
	popoverClosedAt: new Date().getTime(),
	dataToApply: undefined,
	errorMessages: {},
	typingTrack: {},
});

const toggleTrendbyPopover = ({
	state: { popoverOpened, popoverClosedAt, componentHost, properties },
	updateState,
}) => {
	const { chartModel } = properties;

	const {
		chartModel: { metrics, dataSources },
		dataSourceInfos,
		fieldDefinitions,
	} = properties;

	const normalizeMetrics = (metric, dataSource) => ({
		dataSource,
		label:
			getMetricLabel(dataSources, dataSourceInfos, metric, fieldDefinitions) ||
			dataSource.id ||
			'',
		options: getTrendByOptions(
			chartModel,
			dataSourceInfos[metric.dataSource || '']
		),
		hasReferenceFields:
			getTableReferenceFields(
				chartModel,
				dataSourceInfos[metric.dataSource || '']
			).length > 0,
	});

	updateState({
		dataToApply: undefined,
		localTrendByList: metrics.map(metric =>
			normalizeMetrics(metric, getMetricDataSource(metric, dataSources))
		),
	});

	setTimeout(() => {
		updateState({
			popoverOpened: !popoverOpened,
			popoverClosedAt: popoverOpened ? new Date().getTime() : popoverClosedAt,
			popoverTarget: componentHost.shadowRoot.querySelector('sn-par-pillar'),
		});
	}, 100);
};

const handleDropdownSelection = ({
	action,
	updateState,
}: Coeffects<State, any>) => {
	action.stopPropagation();

	switch (action.payload.payloadActionType) {
		case TREND_BY_SELECTED: {
			const index = action.payload.payloadActionMetricIndex;

			/**
			 * If the user clicks on dot-walk breadcrumb, this validation needs to happen
			 * otherwise invalid fields can be selecteed
			 */
			if (
				action.payload.definition &&
				!get(action.payload, 'definition.field.canTrendBy')
			) {
				return updateState([
					{
						operation: 'set',
						path: `dataToApply.trendByField[${index}]`,
						value: undefined,
					},
					{
						operation: 'set',
						path: `errorMessages.fieldError[${index}]`,
						value: t('Selected field is invalid for trend by'),
					},
				]);
			}

			updateState([
				{
					operation: 'set',
					path: `dataToApply.trendByField[${index}]`,
					value: action.payload.dotWalkPath || action.payload.value,
				},
				{
					operation: 'set',
					path: `errorMessages.fieldError[${index}]`,
					value: undefined,
				},
			]);
			break;
		}
	}
};

const validateTrendbyValues = (typingTrack: TrendbyTypeTrack) => {
	const errorMessages = {};
	const valid = !typingTrack.fieldTyped && !typingTrack.frequencyTyped;

	if (typingTrack.fieldTyped) {
		errorMessages.fieldError = typingTrack.fieldTyped.map(() =>
			t('Please select a trend by field')
		);
	}

	if (typingTrack.frequencyTyped) {
		errorMessages.frequencyError = t('Please select a frequency');
	}

	return { valid, errorMessages };
};

const handleDropdownSelectedItem = ({
	state: { dataToApply },
	action,
	updateState,
}: Coeffects<State, any>) => {
	switch (action.payload.payloadActionType) {
		case TREND_BY_FREQUENCY_SELECTED: {
			const trendByFrequency = action.payload.value[0];

			updateState({
				dataToApply: { ...dataToApply, trendByFrequency },
			});
			break;
		}
	}
};

export default {
	actionHandlers: {
		[actionTypes.COMPONENT_BOOTSTRAPPED]: ({
			action,
			updateState,
		}: Coeffects<State, any>) =>
			updateState({ componentHost: action.payload.host }),

		[SN_PAR_POPOVER_OPENED_SET]: ({ updateState }: Coeffects<State, any>) =>
			updateState(createCleanPopoverState()),

		[SN_PAR_PILLAR_ACTION_CLICKED]: toggleTrendbyPopover,
		[SN_PAR_PILLAR_CLICKED]: toggleTrendbyPopover,

		[DOT_WALK_PATH_UPDATED]: handleDropdownSelection,

		'NOW_SELECT#SELECTED_ITEM_SET': handleDropdownSelection,

		'NOW_DROPDOWN#SELECTED_ITEMS_SET': handleDropdownSelectedItem,
		'NOW_BUTTON#CLICKED': ({
			action: { payload },
			state: { dataToApply, typingTrack },
			dispatch,
			updateState,
		}: Coeffects<State, any>) => {
			if (payload.id === BTN_APPLY_ID) {
				const { valid, errorMessages } = validateTrendbyValues(typingTrack);

				if (!valid) {
					return updateState({ errorMessages });
				}

				if (dataToApply) {
					dispatch(SET_TREND_BY, dataToApply);
				}

				updateState(createCleanPopoverState());
			}
		},

		'NOW_POPOVER#OPENED_SET': ({
			updateState,
			action,
		}: Coeffects<State, any>) => {
			if (action.payload.type === TREND_BY_POPOVER && !action.payload.value) {
				updateState(createCleanPopoverState());
			}
		},

		'NOW_BUTTON_ICONIC#CLICKED': ({
			action: { payload },
			updateState,
		}: Coeffects<State, any>) => {
			if (payload.id === BTN_TRENDBY_POPOVER_CLOSE_ID) {
				updateState(createCleanPopoverState());
			}
		},
	},
};
