const notifyUIB = (newProps, oldProps, dispatch) => {
	const properties = [];

	for (let [newPropKey, newPropValue] of Object.entries(newProps)) {
		let oldPropValue = oldProps[newPropKey];

		const isObject = typeof newPropValue === 'object';

		const compare = isObject
			? !compareValidProperties(newPropValue, oldPropValue)
			: newPropValue !== oldPropValue;

		if (compare) {
			properties.push({
				propertyId: newPropKey,
				value: isObject ? JSON.stringify(newPropValue) : newPropValue,
			});
		}
	}

	if (properties.length) {
		dispatch(globalActionTypes.propsChange, {
			properties,
		});
	}
};

const dispatchPropsChange = (
	coeffects: Coeffects<State, any>,
	chartModel: ChartModel
) => {
	const newProps = convertModelToProps(chartModel);

	notifyUIB(newProps, coeffects.state.properties, coeffects.dispatch);
};

const updateChartModel = (coeffects: Coeffects<State, any>) => {
	let chartModel: ChartModel = cloneDeep(coeffects.state.chartModel);

	switch (coeffects.action.type) {
		case SET_CHART_TYPE: {
			const newChartType = coeffects.action.payload.value;

			if (!isSameChartFamily(chartModel.chartType, newChartType)) {
				chartModel.additionalSettings = {};
			}

			chartModel.chartType = newChartType;
			chartModel.metrics.forEach(m => {
				m.chartType = undefined;
			});
			break;
		}
		case SET_GROUP_BY: {
			updateModelWithGroupByValues(chartModel, coeffects.action.payload);
			break;
		}
		case SET_CHART_VARIATION:
			chartModel = {
				...chartModel,
				...coeffects.action.payload,
			};
			break;
		case SET_TREND_BY:
			if (coeffects.action.payload.trendByFrequency) {
				chartModel.trendByFrequency = coeffects.action.payload.trendByFrequency;
			}

			if (coeffects.action.payload.trendByField) {
				coeffects.action.payload.trendByField.forEach((field, index) => {
					if (field) {
						chartModel.metrics[index].trendByField = field;
					}
				});
			}

			break;
		case SET_SORT_BY:
		case SET_ORDER_BY:
		case SET_DISPLAY_ZERO:
		case SET_SINGLE_SCORE_SIZE:
			chartModel.displaySettings = {
				...chartModel.displaySettings,
				...coeffects.action.payload,
			};
			break;
		case SET_COLOR_REQUESTED: {
			const {
				metricIndex,
				color: { type, sys_id },
			} = coeffects.action.payload;
			const color: ColorConfiguration = {
				type,
			};

			if (type === COLOR_TYPE_SINGLE && metricIndex !== undefined && sys_id) {
				chartModel.metrics[metricIndex].colorSysID = sys_id;
			} else if (type === COLOR_TYPE_PALETTE && sys_id) {
				color.sys_id = sys_id;
			}

			chartModel.chartSettings = {
				...chartModel.chartSettings,
				color,
			};
			break;
		}
		case SET_SHOW_LEGEND:
		case SET_SHOW_LEGEND_PERCENTAGE:
		case SET_LEGEND_POSITION:
			chartModel.legendSettings = {
				...chartModel.legendSettings,
				...coeffects.action.payload,
			};
			break;
		case SET_SHOW_LEGEND_VALUE:
			chartModel.legendSettings = {
				...chartModel.legendSettings,
				...coeffects.action.payload,
				showLegendPercentage: undefined,
			};
			break;
		case SET_Y_AXIS_CONFIG: {
			const { axisId, axisConfig } = coeffects.action.payload;
			const yAxisConfig = chartModel.yAxisConfigs.find(a => a.id === axisId);

			if (yAxisConfig) {
				Object.assign(yAxisConfig, axisConfig);
			}

			break;
		}

		case SET_DATA_LABEL_SETTINGS:
		case SET_OVERLAPPING_LABELS:
		case SET_DATA_LABEL_STACKED_VALUE:
		case SET_DATA_LABEL_POSITION:
		case SET_SHOW_DATA_LABEL_AS:
			chartModel.dataLabelSettings = {
				...chartModel.dataLabelSettings,
				...coeffects.action.payload,
			};
			break;
		case SET_AXIS_SETTINGS:
			chartModel.axisSettings = {
				...chartModel.axisSettings,
				...coeffects.action.payload,
			};
			break;

		case SET_METRICS: {
			chartModel.metrics = coeffects.action.payload;
			break;
		}

		case SET_CAN_CLICK:
			chartModel.canClick = coeffects.action.payload.canClick;
			break;

		case ADD_NEW_AXIS_TO_METRIC: {
			const metricIndex = coeffects.action.payload;

			if (metricIndex !== undefined) {
				const validAxisId = getValidAxisId(chartModel.yAxisConfigs);

				chartModel.metrics[metricIndex].axisId = validAxisId;
				chartModel.yAxisConfigs.push({
					id: validAxisId,
					orientation: AXIS_ORIENTATION_RIGHT,
				});
			}

			break;
		}
		case SET_XENOLITH_PROPERTIES: {
			chartModel.xenolithProperties = {
				...chartModel.xenolithProperties,
				...coeffects.action.payload,
			};
			break;
		}
		case ADDITIONAL_SETTINGS_CHANGE: {
			const { id, value } = coeffects.action.payload;

			chartModel.additionalSettings[id] = value;
			break;
		}
		case FILTERS_SETTINGS_CHANGE: {
			coeffects.action.payload.updates.forEach(({ id, value }) => {
				chartModel.filtersSettings[id] = value;
			});
			break;
		}
		case CHANGE_DATA_SOURCE_LABEL: {
			const {
				labelEditingValue,
				labelEditingOnIndex,
			} = coeffects.action.payload;

			if (chartModel.dataSources[labelEditingOnIndex]) {
				chartModel.dataSources[labelEditingOnIndex].label = labelEditingValue;
			}

			break;
		}
		case DATA_SOURCE_INFO_LOADED: {
			const {
				dataSource,
				addNewDataSource,
				dataSourceIndex,
			} = coeffects.action.payload;

			let dataSources = chartModel.dataSources || [];

			const clonedSource = { ...dataSource };

			if (!addNewDataSource) {
				const oldDataSourceId = dataSources[dataSourceIndex].id;

				if (oldDataSourceId !== dataSource.id) {
					chartModel.metrics.forEach(m => {
						if (m.dataSource === oldDataSourceId) {
							m.label = undefined;
							m.dataSource = clonedSource.id;
							m.aggregateField = undefined;
						}
					});
				}

				dataSources[dataSourceIndex] = clonedSource;
			} else {
				dataSources = [...dataSources, clonedSource];
			}

			chartModel.dataSources = dataSources;
			break;
		}
		case SET_MODEL_PROPS:
			chartModel = {
				...chartModel,
				...coeffects.action.payload,
			};
			break;
		case REMOVE_DATA_SOURCE: {
			const { dataSourceIndex } = coeffects.action.payload;

			chartModel.dataSources.splice(dataSourceIndex, 1);

			break;
		}

		case ADD_COLOR_RULE: {
			const { colorRule, metricIndex } = coeffects.action.payload;

			var colorRules = chartModel.metrics[metricIndex].colorRules;

			if (!colorRules) colorRules = [];

			var index;

			for (
				index = colorRules.length - 1;
				index >= 0 && colorRules[index].order > colorRule.order;
				index--
			);

			colorRules.splice(index + 1, 0, colorRule);
			break;
		}

		case EDIT_COLOR_RULE: {
			const {
				colorRuleIndex,
				colorRule,
				metricIndex,
			} = coeffects.action.payload;

			let colorRules = chartModel.metrics[metricIndex].colorRules;

			if (colorRules && colorRules.length >= colorRuleIndex) {
				let needsSort = colorRule.order !== colorRules[colorRuleIndex].order;

				colorRules[colorRuleIndex] = colorRule;
				if (needsSort)
					colorRules.sort((a: ColorRule, b: ColorRule) => {
						return a.order - b.order;
					});
			}

			break;
		}

		case DELETE_COLOR_RULE: {
			const { colorRuleIndex, metricIndex } = coeffects.action.payload;

			let colorRule = chartModel.metrics[metricIndex].colorRules;

			if (colorRule && colorRule.length >= colorRuleIndex)
				colorRule.splice(colorRuleIndex, 1);

			break;
		}
	}

	return chartModel;
};

export const createPatchState = (
	coeffects: Coeffects<State, any>,
	state: $Shape<State>
) => {
	const patchState: $Shape<State> = {};

	for (const property in state) {
		if (!isEqual(coeffects.state[property], state[property])) {
			patchState[property] = state[property];
		}
	}

	if (!isAutoUpdateEnabled(coeffects.state)) {
		patchState.hasChangesToUpdate =
			coeffects.state.hasChangesToUpdate || !isUndefined(patchState.chartModel);
	}

	return patchState;
};

const areDataSourcesInvalid = (
	chartModel: ChartModel,
	dataSourceInfos: DataSourceInfos
) => {
	return (
		chartModel.dataSources.length < 1 ||
		chartModel.dataSources.some(ds => !dataSourceInfos[ds.id || ''])
	);
};

export const getValidChartModelAndOptions = (
	chartModel: ChartModel,
	state: State
) => {
	const { dataSourceInfos, security } = state;

	if (areDataSourcesInvalid(chartModel, dataSourceInfos)) {
		return {};
	}

	chartModel.chartType = getValidChartType(
		chartModel,
		dataSourceInfos,
		state.dotWalkFieldDefinition
	);
	chartModel.dataSources = getValidDataSources(chartModel, state);
	chartModel.metrics = getValidMetrics(
		chartModel,
		dataSourceInfos,
		state.dotWalkFieldDefinition
	);
	chartModel.yAxisConfigs = getValidYAxisConfigs(
		chartModel,
		dataSourceInfos,
		state.dotWalkFieldDefinition
	);

	chartModel.groupByMax = getValidGroupByMax(chartModel, dataSourceInfos);
	chartModel.noOfGroupBasedOn = getValidNoOfGroupBasedOn(chartModel);
	chartModel.showOthers = getValidShowOthers(chartModel);

	chartModel.isStacked = getValidIsStacked(chartModel);

	chartModel.trendByFrequency = getValidTrendByFrequency(chartModel);

	chartModel.dataLabelSettings = {
		...chartModel.dataLabelSettings,
		decimalPrecision: getValidDecimalPrecision(
			chartModel,
			dataSourceInfos,
			state.dotWalkFieldDefinition
		),
	};

	chartModel.additionalSettings = getValidAdditionalSettings(
		chartModel,
		dataSourceInfos
	);

	chartModel.legendSettings = getValidLegendOptions(chartModel);

	chartModel.axisSettings = getValidXAxisSettings(
		chartModel,
		state.chartModel,
		dataSourceInfos
	);

	chartModel.realtime = getValidRealtime(chartModel, dataSourceInfos);
	chartModel.showRealtimeIndicator = getValidShowRealtimeIndicator(
		chartModel,
		dataSourceInfos
	);

	chartModel.chartSettings = getValidChartSettings(
		chartModel,
		state.singleColors,
		state.paletteColors
	);

	chartModel.displaySettings = getValidDisplaySettings(
		chartModel,
		dataSourceInfos
	);

	chartModel.xenolithProperties = getValidXenolithProperties(
		chartModel,
		dataSourceInfos,
		state.dotWalkFieldDefinition
	);

	chartModel.refreshFrequency = getValidRefreshFrequency(
		security && security.isAdmin,
		chartModel,
		dataSourceInfos
	);

	return {
		chartModel,
	};
};

export const updateAndValidateChartModel = (
	coeffects: Coeffects<State, any>
) => {
	const { dataSourceInfos } = coeffects.state;

	const chartModel = updateChartModel(coeffects);

	if (areDataSourcesInvalid(chartModel, dataSourceInfos)) {
		return;
	}

	const validChartModelAndOptions = getValidChartModelAndOptions(
		chartModel,
		coeffects.state
	);

	const patchState = createPatchState(coeffects, validChartModelAndOptions);

	if (isAutoUpdateEnabled(coeffects.state)) {
		dispatchPropsChange(coeffects, chartModel);
	}

	if (Object.keys(patchState).length) {
		coeffects.updateState(patchState);
	}
};

const handleAddSource = ({
	action,
	state,
	updateState,
	dispatch,
}: Coeffects<State, PayloadAddDataSource>) => {
	const { addNewDataSource, dataSource, dataSourceIndex } = action.payload;
	const { dataSources } = state.chartModel;

	if (
		!addNewDataSource &&
		dataSourceIndex !== undefined &&
		dataSource.sourceType === SOURCE_TYPE_TABLE &&
		dataSources[dataSourceIndex].tableName !== dataSource.tableName
	) {
		updateState({
			chartModel: {
				...state.chartModel,
				groupByField: undefined,
				stackByField: undefined,
			},
		});
	}

	dispatch(RETRIEVE_DATA_SOURCE_OPTIONS, action.payload);
};

const toggleAutoUpdatedEnabled = (coeffects: Coeffects<State, any>) => {
	coeffects.dispatch(UPDATE_USER_PREFERENCES, {
		preferences: [
			{
				name: AUTO_UPDATE_PREFERENCE,
				value: String(coeffects.action.payload.value),
			},
		],
	});

	if (coeffects.state.hasChangesToUpdate) {
		updateVisualizationChart(coeffects);
	}
};

const updateContainerStyle = (coeffects: Coeffects<State, any>) => {
	const {
		action: { payload },
		properties: { elementStyles, elementId },
	} = coeffects;

	const currentStyles = elementStyles[elementId];

	coeffects.dispatch(globalActionTypes.styleChange, {
		styles: {
			...currentStyles,
			...payload.styles,
		},
		elementId,
	});
};

const updateVisualizationChart = (coeffects: Coeffects<State, any>) => {
	if (!coeffects.state.hasChangesToUpdate) {
		return;
	}

	coeffects.updateState({
		hasChangesToUpdate: false,
	});

	dispatchPropsChange(coeffects, coeffects.state.chartModel);
};

export const modelActionHandlers = {
	[SET_CHART_TYPE]: updateAndValidateChartModel,
	[SET_GROUP_BY]: updateAndValidateChartModel,
	[SET_TREND_BY]: updateAndValidateChartModel,
	[SET_METRICS]: updateAndValidateChartModel,
	[ADD_NEW_AXIS_TO_METRIC]: updateAndValidateChartModel,
	[ADDITIONAL_SETTINGS_CHANGE]: updateAndValidateChartModel,
	[FILTERS_SETTINGS_CHANGE]: updateAndValidateChartModel,
	[DATA_SOURCE_INFO_LOADED]: updateAndValidateChartModel,
	[CHANGE_DATA_SOURCE_LABEL]: updateAndValidateChartModel,
	[actionTypesSourceConfiguration.ADD_SOURCE]: handleAddSource,
	[REMOVE_DATA_SOURCE]: updateAndValidateChartModel,
	[SET_SHOW_LEGEND]: updateAndValidateChartModel,
	[SET_SHOW_LEGEND_VALUE]: updateAndValidateChartModel,
	[SET_SHOW_LEGEND_PERCENTAGE]: updateAndValidateChartModel,
	[SET_LEGEND_POSITION]: updateAndValidateChartModel,
	[SET_DATA_LABEL_POSITION]: updateAndValidateChartModel,
	[SET_DATA_LABEL_STACKED_VALUE]: updateAndValidateChartModel,
	[SET_DATA_LABEL_SETTINGS]: updateAndValidateChartModel,
	[SET_OVERLAPPING_LABELS]: updateAndValidateChartModel,
	[SET_CHART_VARIATION]: updateAndValidateChartModel,
	[SET_SORT_BY]: updateAndValidateChartModel,
	[SET_ORDER_BY]: updateAndValidateChartModel,
	[SET_DISPLAY_ZERO]: updateAndValidateChartModel,
	[SET_SINGLE_SCORE_SIZE]: updateAndValidateChartModel,
	[SET_AXIS_SETTINGS]: updateAndValidateChartModel,
	[SET_Y_AXIS_CONFIG]: updateAndValidateChartModel,
	[SET_MODEL_PROPS]: updateAndValidateChartModel,
	[SET_AUTO_UPDATE]: toggleAutoUpdatedEnabled,
	[UPDATE_VISUALIZATION]: updateVisualizationChart,
	[SET_CONTAINER_STYLE]: updateContainerStyle,
	[SET_COLOR_REQUESTED]: updateAndValidateChartModel,
	[SET_XENOLITH_PROPERTIES]: updateAndValidateChartModel,
	[SET_CAN_CLICK]: updateAndValidateChartModel,
	[ADD_COLOR_RULE]: updateAndValidateChartModel,
	[EDIT_COLOR_RULE]: updateAndValidateChartModel,
	[DELETE_COLOR_RULE]: updateAndValidateChartModel,
};
