// @flow
// @jsx snabbdom.createElement
import snabbdom, { Fragment } from '@servicenow/ui-renderer-snabbdom'; // eslint-disable-line no-unused-vars
import { createCustomElement } from '@servicenow/ui-core';
import styles from './styles.scss';
import { type State } from './state';

import { t } from 'sn-translate';
import { isTableMetric } from '../helpers/metric';
import { getAggregateFields } from './aggregate-fields-view';

import {
	BTN_POPOVER_CLOSE,
	FIELD_DATA_SOURCE,
	FIELD_LABEL,
	BTN_POPOVER_APPLY,
} from './constants';

type SharedState = { properties: { sharedState: State } };

const view = ({ properties }: SharedState) => {
	const { sharedState } = properties;

	const {
		dataToApply,
		errorMessages,
		dataSourceOptions,
		editMetricIndex,
		properties: {
			chartModel: { dataSources },
		},
	} = sharedState;

	const dataSourceSelectMessages =
		errorMessages && errorMessages.dataSource
			? [{ status: 'critical', content: errorMessages.dataSource }]
			: undefined;

	return (
		<div
			class={{
				'viz-config-metric-popover': true,
				'viz-config-metric-popover--table': isTableMetric(
					dataToApply,
					dataSources
				),
			}}>
			<now-heading label={t('Metric')} level="3" variant="title-secondary" />
			<now-button-iconic
				bare
				icon="close-fill"
				size="md"
				variant="primary"
				append-to-payload={{
					id: BTN_POPOVER_CLOSE,
				}}
				config-aria={{ 'aria-label': t('Close') }}
			/>
			{dataSources.length > 1 ? (
				<now-select
					disabled={editMetricIndex !== undefined}
					className="viz-config-metric-data-source"
					label={t('Data source')}
					append-to-payload={{
						field: FIELD_DATA_SOURCE,
					}}
					items={dataSourceOptions}
					placeholder={t('Select the data source')}
					selected-item={dataToApply.dataSource}
					messages={dataSourceSelectMessages}
				/>
			) : null}
			<devx-input
				className="viz-config-metric-label"
				append-to-payload={{
					field: FIELD_LABEL,
				}}
				label={t('Label')}
				inputValue={dataToApply.label || ''}
				variant="secondary"
				placeholder={t('Default')}
				transparent={true}
				maxlength={100}
			/>
			{getAggregateFields(sharedState)}
			<now-button
				className="viz-config-metric-apply-button"
				label={t('Apply')}
				size="md"
				variant="primary"
				append-to-payload={{
					id: BTN_POPOVER_APPLY,
				}}
			/>
		</div>
	);
};

createCustomElement('viz-config-metric-popover-content', {
	renderer: { type: snabbdom },
	view,
	properties: {
		sharedState: {
			default: {},
		},
	},
	styles,
});
