// @flow
// @jsx snabbdom.createElement
import snabbdom, { Fragment } from '@servicenow/ui-renderer-snabbdom'; // eslint-disable-line no-unused-vars
import { CHART_TYPE_SELECTED } from '../actions/action-constants';
import {
	CHART_TYPES_CONFIG,
	CHART_MENU_GROUPS,
	type ChartTypeConfig,
} from '../../common/chart-settings';
import { type State } from '../state';
import { t } from 'sn-translate';
import {
	SOURCE_TYPE_INDICATOR,
	type DataSource,
	type DataSourceInfos,
} from '../../common/types/data-source';
import { type Metric } from '../../common/types/metric';
import { type FieldDefinitions } from '../../common/types/field';

import { MULTIPLE_DS_UNSUPPORTED } from '../../common/types/chart-type';
import { hasGroupByOptions } from '../actions/model-options';
import { nowPopoverDropdownPositions } from '../../common/popover-config';

const isItemDisabled = (
	chartConfig: ChartTypeConfig,
	dataSources: DataSource[],
	dataSourceInfos: DataSourceInfos,
	metrics: Metric[],
	dotWalkFieldDefinition: FieldDefinitions
) => {
	const doesntSupportMultipleMetrics =
		metrics.length > 1 && MULTIPLE_DS_UNSUPPORTED.includes(chartConfig.id);

	const hasIndicatorLimitations =
		dataSources.length === 1 &&
		dataSources[0].sourceType === SOURCE_TYPE_INDICATOR &&
		(chartConfig.menuGroup === 'bar' || chartConfig.menuGroup === 'pie') &&
		!hasGroupByOptions(dataSources, dataSourceInfos, dotWalkFieldDefinition);

	if (doesntSupportMultipleMetrics || hasIndicatorLimitations) {
		return true;
	}

	return false;
};

export const ChartTypeView = ({
	chartModel,
	dataSourceInfos,
	dotWalkFieldDefinition,
}: State) => {
	const { chartType, metrics, dataSources } = chartModel;

	if (!dataSources.length) {
		return undefined;
	}

	const groupOptionsDic = {};

	for (const groupConfig of CHART_MENU_GROUPS) {
		groupOptionsDic[groupConfig.id] = {
			label: groupConfig.label,
			children: [],
		};
	}

	for (const config of CHART_TYPES_CONFIG) {
		const groupOption = groupOptionsDic[config.menuGroup];

		if (groupOption) {
			groupOption.children.push({
				id: config.id,
				icon: config.chartIcon,
				label: '\xa0\xa0' + config.label,
				disabled: isItemDisabled(
					config,
					dataSources,
					dataSourceInfos,
					metrics,
					dotWalkFieldDefinition
				),
			});
		}
	}

	const groups = Object.values(groupOptionsDic);

	return (
		<div className="chart-type-view">
			<now-label-value-inline
				className="viz-config-title-label"
				label={t('Visualization type')}
			/>
			<now-dropdown
				append-to-payload={{
					payloadActionType: CHART_TYPE_SELECTED,
				}}
				data-testId="chart-type-selector"
				items={groups}
				select="single"
				selected-items={[chartType]}
				size="md"
				variant="tertiary"
				config-aria={{
					'aria-label': t('Visualization type'),
				}}
				panelFitProps={{
					constrain: {
						minHeight: 32,
						minWidth: 'target',
						maxWidth: 400,
						maxHeight: 500,
					},
					position: nowPopoverDropdownPositions,
				}}
			/>
		</div>
	);
};
