//@flow

import { type Metric } from '../../common/types/metric';
import {
	type DataSource,
	type DataSourceInfos,
} from '../../common/types/data-source';
import { type FieldDefinitions } from '../../common/types/field';
import {
	isTableMetric,
	isIndicatorMetric,
	getMetricDataSourceInfo,
	getMetricDataSource,
} from './metric-datasource';
import {
	AGGREGATE_FUNCTION_COUNT,
	AGGREGATE_FUNCTION_DISTINCT,
} from '../../metric-configuration/constants';
import { getFieldDefinition } from '../field';

export const TABLE_METRIC_UNIT = {
	COUNT: 'COUNT',
	DURATION: 'DURATION',
	FX_CURRENCY: 'FX_CURRENCY',
	CURRENCY: 'CURRENCY',
	DECIMAL: 'DECIMAL',
};

export const getMetricUnit = (
	metric: Metric,
	dataSources: DataSource[],
	dataSourceInfos: DataSourceInfos,
	fieldDefinitions: FieldDefinitions
) => {
	const dataSourceInfo = getMetricDataSourceInfo(metric, dataSourceInfos);

	if (isTableMetric(metric, dataSources)) {
		const { aggregateFunction, aggregateField } = metric;

		if (
			!aggregateFunction ||
			aggregateFunction === AGGREGATE_FUNCTION_COUNT ||
			aggregateFunction === AGGREGATE_FUNCTION_DISTINCT
		) {
			return TABLE_METRIC_UNIT.COUNT;
		}

		const fieldDefinition = getFieldDefinition(
			aggregateField || '',
			dataSourceInfo,
			getMetricDataSource(metric, dataSources),
			fieldDefinitions
		);

		return fieldDefinition && fieldDefinition.unit;
	} else if (isIndicatorMetric(metric, dataSources)) {
		return dataSourceInfo.unitId;
	}

	return undefined;
};
