// @flow
// @jsx snabbdom.createElement
import '@servicenow/now-heading';

import { type State } from './state';

import { createCustomElement } from '@servicenow/ui-core';
import snabbdom, { Fragment } from '@servicenow/ui-renderer-snabbdom'; // eslint-disable-line no-unused-vars
import { t } from 'sn-translate';

import styles from './styles.scss';

import {
	BTN_POPOVER_CLOSE,
	BTN_POPOVER_APPLY,
	SET_OPERATOR,
	FIELD_OPERATOR,
	OPERATOR_CONFIG,
	FIELD_VALUE1,
	FIELD_VALUE2,
	FIELD_COLOR,
	FIELD_ORDER,
	FIELD_OPERATOR_LESS_THAN,
	FIELD_OPERATOR_BETWEEN,
} from './constants';

type SharedState = {
	properties: {
		sharedState: State,
	},
};

createCustomElement('viz-config-color-rule-popover', {
	renderer: { type: snabbdom },
	view: (state: SharedState) => {
		const {
			properties: {
				sharedState: {
					dataToApply,
					properties: { singleColors },
					invalidInput,
				},
			},
		} = state;

		if (!dataToApply) return null;

		if (!dataToApply.operator) dataToApply.operator = '<';

		if (dataToApply.order === undefined) dataToApply.order = 100;

		if (!dataToApply.color && singleColors) {
			const firstColor = singleColors.find(color => !color.disabled);

			if (firstColor) dataToApply.color = firstColor.id;
		}

		const { operator, value1, value2, color, order } = dataToApply;

		const labelValue1 =
			operator === FIELD_OPERATOR_BETWEEN ? 'Value1' : 'Value';

		return (
			<div className="viz-config-color-rule-popover">
				<now-heading
					label={t('Color rule')}
					level="3"
					variant="title-secondary"
				/>
				<now-button-iconic
					bare
					icon="close-fill"
					size="md"
					variant="primary"
					append-to-payload={{
						id: BTN_POPOVER_CLOSE,
					}}
					config-aria={{ 'aria-label': t('Close') }}
				/>

				<div className="section-prop-size">
					<now-label-value-inline
						className="section-heading"
						label={t('Operator')}
					/>
					<now-dropdown
						append-to-payload={{
							payloadActionType: SET_OPERATOR,
							field: FIELD_OPERATOR,
						}}
						items={OPERATOR_CONFIG}
						select="single"
						selected-items={[operator ? operator : FIELD_OPERATOR_LESS_THAN]}
						size="md"
						variant="tertiary"
						config-aria={{
							'aria-label': t('Operator'),
						}}
					/>
				</div>
				<div className="section-prop-size section-flex-inputs">
					<now-input
						id="rule-value1"
						label={t(labelValue1)}
						name={FIELD_VALUE1}
						value={value1}
						type="number"
						required={true}
						invalid={invalidInput.value1}
						manageValue={true}
						placeholder={0}
					/>
					{operator === FIELD_OPERATOR_BETWEEN ? (
						<now-input
							id="rule-value2"
							label={t('Value2')}
							name={FIELD_VALUE2}
							value={value2}
							type="number"
							manageValue={true}
							required={true}
							invalid={invalidInput.value2}
							messages={
								value1 !== undefined && value2 !== undefined && value1 > value2
									? [
											{
												status: 'critical',
												content: t(
													'value2 must be greater than or equal to value1'
												),
											},
									  ]
									: []
							}
							placeholder={0}
						/>
					) : null}
				</div>
				<div className="section-prop-size">
					<now-select
						id="color-select"
						label={t('Color')}
						append-to-payload={{
							field: FIELD_COLOR,
						}}
						items={singleColors}
						placeholder={t('Select a color')}
						selected-item={color}
						search="contains"
						required
					/>
				</div>
				<now-input
					id="rule-order"
					label={t('Order')}
					name={FIELD_ORDER}
					value={order}
					type="number"
					placeholder={0}
					required={true}
					invalid={invalidInput.order}
					manageValue={true}
					manageInvalid={true}
				/>

				<now-button
					id="rule-apply-btn"
					label={t('Apply')}
					size="md"
					variant="primary"
					append-to-payload={{
						id: BTN_POPOVER_APPLY,
					}}
				/>
			</div>
		);
	},
	properties: {
		sharedState: {
			default: {},
		},
	},
	styles,
});
