// @flow
// @jsx snabbdom.createElement
import '../dot-walk';

import cloneDeep from 'lodash/cloneDeep';

import { createCustomElement } from '@servicenow/ui-core';
import snabbdom, { Fragment } from '@servicenow/ui-renderer-snabbdom'; // eslint-disable-line no-unused-vars
import { t } from 'sn-translate';

import styles from './styles.scss';

import { type DataSource } from '../common/types/data-source';
import { type FieldDefinition } from '../common/types/field';
import { isFieldReportRangeForDataSource } from '../helpers/data-source';
import {
	getGroupByOptionsFilter,
	getTableReferenceFields,
} from '../visualizations-config-panel/actions/model-options';
import { TIMESERIES_CHART_TYPES } from '../common/chart-settings';

import {
	APPLY_GROUP_BY_POPOVER,
	SHOW_OTHERS,
	GROUP_BY_MAX_NUMBER,
	GROUP_BY_MAX,
	NO_OF_GROUP_BASED_ON,
	SET_GROUP_BY_MAX,
	SET_NO_OF_GROUP_BASED_ON,
	GROUP_BY_FIELD,
	CLOSE_GROUP_BY_POPOVER,
	GROUP_BY_MAX_NUMBER_ALL,
	NO_OF_GROUP_BASED_ON_OPTIONS,
	NO_OF_GROUP_BASED_ON_FIRST_METRIC,
} from './constants';

import {
	SOURCE_TYPE_INDICATOR,
	SOURCE_TYPE_TABLE,
} from '../common/types/data-source';
import {
	createTransformMetadataFn,
	updateModelWithGroupByValues,
} from './helpers/transform-metadata';

createCustomElement('viz-config-group-by-popover-content', {
	renderer: { type: snabbdom },
	view: state => {
		const {
			properties: {
				sharedState: {
					chartModel,
					errorMessages,
					dataSourceInfos,
					dataToApply,
					localOptions,
					dotWalkFieldDefinition,
				},
			},
		} = state;

		const { chartType, dataSources } = chartModel;

		const patchedChartModel = updateModelWithGroupByValues(
			cloneDeep(chartModel),
			dataToApply
		);

		const transformMetadataFilter = (
			dataSource: DataSource,
			fieldDefinition: FieldDefinition
		) =>
			getGroupByOptionsFilter(
				dataSource,
				patchedChartModel.dataSources,
				dataSourceInfos,
				dotWalkFieldDefinition
			)(fieldDefinition);

		const transformMetadata = createTransformMetadataFn(
			transformMetadataFilter
		);

		const isIndicator = dataSources.some(
			dataSource => dataSource.sourceType === SOURCE_TYPE_INDICATOR
		);
		const isTimeseries = TIMESERIES_CHART_TYPES.includes(chartType);

		const showNumberOfGroups = !isTimeseries || (isTimeseries && isIndicator);

		const showNoOfGroupBasedOn = dataSources.length > 1;

		const isApplyDisabled = dataSources.some((ds, index) => {
			// Indicator displays only one group by field
			if (index > 0 && ds.sourceType === SOURCE_TYPE_INDICATOR) {
				return false;
			}

			const fieldName = `${GROUP_BY_FIELD}${index}`;

			return !dataToApply[fieldName];
		});

		const isFirstGroupByReportRange = isFieldReportRangeForDataSource(
			dataToApply[`${GROUP_BY_FIELD}${0}`],
			dataSources[0],
			dataSourceInfos
		);

		if (
			isFirstGroupByReportRange &&
			dataToApply.groupByMax !== GROUP_BY_MAX_NUMBER_ALL
		)
			dataToApply.groupByMax = GROUP_BY_MAX_NUMBER_ALL;

		if (isFirstGroupByReportRange) dataToApply.showOthers = false;
		else if (
			dataToApply.noOfGroupBasedOn === NO_OF_GROUP_BASED_ON_FIRST_METRIC &&
			dataSources.length > 1
		)
			dataToApply.showOthers = false;

		const showOthersToggle =
			dataToApply.groupByMax !== GROUP_BY_MAX_NUMBER_ALL &&
			(dataSources.length == 1 ||
				dataToApply.noOfGroupBasedOn !== NO_OF_GROUP_BASED_ON_FIRST_METRIC) &&
			!isIndicator &&
			!isTimeseries;

		return (
			<div className="viz-config-group-by-popover">
				<now-heading
					label={t('Group by')}
					level="3"
					variant="title-secondary"
				/>
				<now-button-iconic
					bare
					icon="close-fill"
					size="md"
					variant="primary"
					append-to-payload={{
						payloadActionType: CLOSE_GROUP_BY_POPOVER,
					}}
					config-aria={{ 'aria-label': t('Close') }}
				/>
				{dataSources.map((dataSource, index) => {
					if (index > 0 && dataSource.sourceType === SOURCE_TYPE_INDICATOR) {
						return undefined;
					}

					const tableName = dataSource.tableName;
					const fieldName = `${GROUP_BY_FIELD}${index}`;
					const options = localOptions[fieldName] || [];
					const noOptions = options.length === 0;

					const label =
						dataSource.sourceType === SOURCE_TYPE_TABLE
							? t('Field for {0}', dataSource.label)
							: t('Breakdown');

					const placeholder = noOptions
						? t('No matches')
						: dataSource.sourceType === SOURCE_TYPE_TABLE
						? t('Select a field')
						: t('Select a breakdown');

					const errorMessage = noOptions
						? t('There are no related matches to the first field')
						: errorMessages[fieldName];

					const messages = errorMessage
						? [{ status: 'critical', content: errorMessage }]
						: undefined;

					if (isIndicator) {
						return (
							<now-select
								disabled={noOptions}
								id={fieldName}
								label={label}
								append-to-payload={{
									fieldName,
								}}
								items={options}
								placeholder={placeholder}
								selected-item={
									dataToApply[fieldName] || dataSource.groupByField
								}
								messages={messages}
								search="contains"
							/>
						);
					}

					const hasReferenceFields =
						getTableReferenceFields(chartModel, dataSourceInfos[dataSource.id])
							.length > 0;

					const dotWalkError =
						!dataToApply[fieldName] && noOptions && hasReferenceFields
							? t('Please select field for group by')
							: null;

					const dotWalkErrorMessage = dotWalkError || errorMessages[fieldName];

					return (
						<viz-config-dot-walk
							key={fieldName}
							id={fieldName}
							label={label}
							placeholder={placeholder}
							tableName={tableName}
							dotWalkPath={dotWalkErrorMessage ? null : dataToApply[fieldName]}
							transform-metadata={transformMetadata.bind(null, dataSource)}
							disabled={
								!dataToApply[fieldName] && noOptions && !hasReferenceFields
							}
							error-message={dotWalkErrorMessage}
							append-to-payload={{
								fieldName,
								dataSource,
							}}
						/>
					);
				})}
				{showNumberOfGroups ? (
					<Fragment>
						<div className="section-prop-size">
							<now-label-value-inline
								className="section-heading"
								label={t('Max number of groups')}
							/>
							<now-dropdown
								id="groupByMax"
								append-to-payload={{
									payloadActionType: SET_GROUP_BY_MAX,
									fieldName: GROUP_BY_MAX,
								}}
								placeholder={t('Select a value')}
								items={GROUP_BY_MAX_NUMBER}
								selected-items={
									dataToApply.groupByMax ? [dataToApply.groupByMax] : []
								}
								disabled={isFirstGroupByReportRange}
								size="md"
								variant="tertiary"
								config-aria={{
									'aria-label': t('Max number of groups'),
								}}
							/>
							{showNoOfGroupBasedOn ? (
								<div>
									<now-label-value-inline
										className="section-heading"
										label={t('Number of groups based on')}
									/>
									<now-dropdown
										id="noOfGroupBasedOn"
										append-to-payload={{
											payloadActionType: SET_NO_OF_GROUP_BASED_ON,
											fieldName: NO_OF_GROUP_BASED_ON,
										}}
										items={NO_OF_GROUP_BASED_ON_OPTIONS}
										selected-items={[dataToApply.noOfGroupBasedOn]}
										disabled={isFirstGroupByReportRange}
										size="md"
										variant="tertiary"
										config-aria={{
											'aria-label': t('Number of groups based on'),
										}}
									/>
								</div>
							) : (
								undefined
							)}
						</div>
					</Fragment>
				) : (
					undefined
				)}
				{showOthersToggle ? (
					<viz-config-toggle
						id="showOthersToggle"
						append-to-payload={{ fieldName: SHOW_OTHERS }}
						checked={dataToApply.showOthers}
						label={t('Show others')}
					/>
				) : (
					undefined
				)}
				<now-button
					id="apply-group-by"
					label={t('Apply')}
					size="md"
					variant="primary"
					disabled={isApplyDisabled}
					append-to-payload={{
						payloadActionType: APPLY_GROUP_BY_POPOVER,
					}}
				/>
			</div>
		);
	},
	properties: {
		sharedState: {
			default: {},
		},
	},
	styles,
});
