// @flow

import { actionTypes, type Coeffects } from '@servicenow/ui-core';
import { type State } from './state';
import {
	SOURCE_TYPE_TABLE,
	SOURCE_TYPE_INDICATOR,
} from '../common/types/data-source';
import { createGraphQLEffect } from '@servicenow/ui-effect-graphql';

import {
	SELECT_DATASOURCE,
	type PayloadSelectDataSource,
	createSelectDatasourceAction,
	UPDATE_RECENT_INDICATOR,
	createUpdateRecentIndicatorAction,
	UPDATE_RECENT_TABLE,
	createUpdateRecentTableAction,
	createAddDatasourceAction,
} from './action-creators';

import { BREAKDOWN_CHANGED } from '../breakdown-selector/constants';
import { TABLE_CONFIG_CHANGED } from '../table-configuration/constants';
import { VIZ_CONFIG_SOURCE_SELECT_SELECTED } from '../source-selector/constants';

export const actionTypesSourceConfiguration = {
	ADD_SOURCE: 'DATA_SOURCE_SELECTOR#ADD_SOURCE',
	CANCEL: 'DATA_SOURCE_SELECTOR#CANCEL',
};

const handleRecenlyUsedTableUpdate = createGraphQLEffect(
	`mutation ($name: String!) {
		AppPARConfig_Mutation {
			recentTable (name: $name) {
			  result
			}
		  }
	}`,
	{
		variableList: ['name'],
	}
);

export const handleRecenlyUsedIndicatorUpdate = createGraphQLEffect(
	`mutation ($sysID: String!) {
		AppPARConfig_Mutation {
		  recentIndicator (sysID: $sysID) {
			result
		  }
		}
	  }`,
	{
		variableList: ['sysID'],
	}
);

function handleBootstrap(coeffects: Coeffects<State, void>) {
	const {
		properties: { dataSource, dataSourceInfo },
	} = coeffects.state;

	if (!dataSource || !dataSourceInfo) {
		return;
	}

	let displayLabel;

	if (dataSource.sourceType === SOURCE_TYPE_TABLE && dataSource.tableName)
		displayLabel =
			dataSourceInfo.displayLabel && dataSourceInfo.displayLabel !== ''
				? dataSourceInfo.displayLabel + ' [' + dataSource.tableName + ']'
				: dataSource.tableName;
	else displayLabel = dataSourceInfo.displayLabel || '';

	coeffects.dispatch(...createSelectDatasourceAction(displayLabel, dataSource));
}

function handleDatasourceSelected(
	coeffects: Coeffects<State, PayloadSelectDataSource>
) {
	const { displayLabel, dataSource } = coeffects.action.payload;

	switch (dataSource.sourceType) {
		case SOURCE_TYPE_TABLE: {
			let { tableName, filterQuery, predefinedFilterId } = dataSource;
			let { tableChanges } = coeffects.state;

			let tableFilter;

			if (tableName) {
				const tableChange = tableChanges[tableName];

				if (tableChange) {
					tableFilter = {
						...tableChange,
					};
				} else {
					tableFilter = {
						filterQuery,
						predefinedFilterId,
						previewFilterQuery: filterQuery,
						previewPredefinedFilterId: predefinedFilterId,
						isExpanded: true,
						isEditMode: !predefinedFilterId && !filterQuery,
						showCustomConditions: !!filterQuery,
					};
					tableChanges = {
						...tableChanges,
						[tableName]: {
							...tableFilter,
						},
					};
				}
			}

			const tableInfo = {
				tableName,
				...tableFilter,
			};

			coeffects.updateState({
				sourceType: dataSource.sourceType,
				displayLabel,
				tableInfo,
				indicatorInfo: undefined,
				tableChanges,
			});
			break;
		}

		case SOURCE_TYPE_INDICATOR: {
			const { uuid } = dataSource;

			if (!uuid) return;

			let indicatorUuids;
			let { indicatorChanges } = coeffects.state;

			const hasBreakdown = uuid.breakdowns && uuid.breakdowns.length;

			if (indicatorChanges[uuid.indicator]) {
				indicatorUuids = {
					...indicatorChanges[uuid.indicator],
				};
			} else {
				indicatorUuids = {
					uuid: {
						...uuid,
					},
					previewUuid: {
						...uuid,
					},
					isExpanded: true,
					isEditMode: !hasBreakdown,
				};
				indicatorChanges = {
					...indicatorChanges,
					[uuid.indicator]: {
						...indicatorUuids,
					},
				};
			}

			const indicatorInfo = {
				displaySourceDetails: false,
				...indicatorUuids,
			};

			coeffects.updateState({
				sourceType: dataSource.sourceType,
				displayLabel,
				indicatorInfo,
				tableInfo: undefined,
				indicatorChanges,
			});
			break;
		}
	}
}

export default {
	actions: {
		[actionTypesSourceConfiguration.ADD_SOURCE]: {
			private: false,
		},
		[actionTypesSourceConfiguration.CANCEL]: {
			private: false,
		},
	},
	actionHandlers: {
		[actionTypes.COMPONENT_BOOTSTRAPPED]: handleBootstrap,

		'NOW_MODAL#ADD_SOURCE': (coeffects: Coeffects<State, any>) => {
			coeffects.action.stopPropagation();
			const {
				sourceType,
				tableInfo,
				indicatorInfo,
				properties: {
					dataSource,
					operationMetadata: {
						addNewDataSource,
						editDataSourceIndex: dataSourceIndex,
					},
				},
			} = coeffects.state;

			if (sourceType === SOURCE_TYPE_TABLE && tableInfo) {
				const { tableName, filterQuery, predefinedFilterId } = tableInfo;

				coeffects.dispatch(
					...createUpdateRecentTableAction({
						name: tableName || '',
					})
				);

				coeffects.dispatch(
					...createAddDatasourceAction(
						{ addNewDataSource, dataSourceIndex, dataSource },
						{
							sourceType,
							tableName,
							filterQuery,
							predefinedFilterId,
						}
					)
				);
			}

			if (
				sourceType === SOURCE_TYPE_INDICATOR &&
				indicatorInfo &&
				indicatorInfo.uuid
			) {
				coeffects.dispatch(
					...createUpdateRecentIndicatorAction(indicatorInfo.uuid.indicator)
				);
				coeffects.dispatch(
					...createAddDatasourceAction(
						{ addNewDataSource, dataSourceIndex, dataSource },
						{
							sourceType,
							uuid: indicatorInfo.uuid,
						}
					)
				);
			}
		},
		'NOW_MODAL#OPENED_SET': (coeffects: Coeffects<State, any>) => {
			coeffects.action.stopPropagation();
			coeffects.dispatch(actionTypesSourceConfiguration.CANCEL);
		},
		[VIZ_CONFIG_SOURCE_SELECT_SELECTED]: (coeffects: Coeffects<State, any>) => {
			const { sourceType, id, displayLabel } = coeffects.action.payload;

			switch (sourceType) {
				case SOURCE_TYPE_TABLE: {
					coeffects.dispatch(
						...createSelectDatasourceAction(displayLabel, {
							sourceType,
							tableName: id,
						})
					);
					break;
				}

				case SOURCE_TYPE_INDICATOR: {
					coeffects.dispatch(
						...createSelectDatasourceAction(displayLabel, {
							sourceType,
							uuid: {
								indicator: id,
								breakdowns: [],
							},
						})
					);
					break;
				}
			}
		},
		[BREAKDOWN_CHANGED]: (coeffects: Coeffects<State, any>) => {
			const { indicatorInfo } = coeffects.state;

			if (indicatorInfo && indicatorInfo.uuid) {
				const indicatorInfoPatch = coeffects.action.payload;

				coeffects.updateState({
					indicatorInfo: {
						...indicatorInfo,
						...indicatorInfoPatch,
					},
					indicatorChanges: {
						...coeffects.state.indicatorChanges,
						[indicatorInfo.uuid.indicator]: {
							...coeffects.state.indicatorChanges[indicatorInfo.uuid.indicator],
							...indicatorInfoPatch,
						},
					},
				});
			}
		},
		[UPDATE_RECENT_TABLE]: handleRecenlyUsedTableUpdate,
		[UPDATE_RECENT_INDICATOR]: handleRecenlyUsedIndicatorUpdate,
		[TABLE_CONFIG_CHANGED]: (coeffects: Coeffects<State, any>) => {
			const { tableInfo } = coeffects.state;

			if (tableInfo && tableInfo.tableName) {
				const tableInfoPatch = coeffects.action.payload;

				const currentTableChanges = coeffects.state.tableChanges;

				coeffects.updateState({
					tableInfo: {
						...coeffects.state.tableInfo,
						...tableInfoPatch,
					},
					tableChanges: {
						...currentTableChanges,
						[tableInfo.tableName]: {
							...currentTableChanges[tableInfo.tableName],
							...tableInfoPatch,
						},
					},
				});
			}
		},
		[SELECT_DATASOURCE]: handleDatasourceSelected,
	},
};
