import {
	type DataSourceInfo,
	type DataSource,
} from '../../common/types/data-source';

import { type FieldDefinitions } from '../../common/types/field';
import { getFieldDefinitionKey } from './get-field-definition-key';

export const getFieldDefinition = (
	field: string,
	dataSourceInfo: DataSourceInfo,
	dataSource?: DataSource,
	dotWalkFieldDefinition: FieldDefinitions = {}
) => {
	if (/\./.test(field) && dataSource) {
		return dotWalkFieldDefinition[
			getFieldDefinitionKey({
				tableName: dataSource.tableName,
				fieldName: field,
			})
		];
	}

	const tableFields = dataSourceInfo.tableFields || [];

	return tableFields.find(f => f.name === field);
};
