// @flow
// @jsx snabbdom.createElement
import '@servicenow/now-toggle';
import '@servicenow/now-tooltip';
import '@servicenow/now-accordion';

import '../toggle';
import snabbdom from '@servicenow/ui-renderer-snabbdom'; // eslint-disable-line no-unused-vars
import { t } from 'sn-translate';
import { autoScrollToVisibility } from '../helpers/now-accordion/auto-scroll-to-visibility';
import { type State } from './state';
import {
	SET_REALTIME,
	SET_CAN_CLICK,
	SET_REALTIME_INDICATOR,
} from '../visualizations-config-panel/actions/action-constants';
import { isRealtimeRelevant } from '../visualizations-config-panel/actions/model-validators'; // eslint-disable-line no-unused-vars
import { jsxif } from '../helpers/seismic';
import { isRefreshFrequencyRelevant } from '../visualizations-config-panel/actions/model-validators'; // eslint-disable-line no-unused-vars
import { FIELD_REFRESH_FREQUENCY } from './constants';
import './../common/popover-content';

export default (state: State) => {
	const {
		properties: { chartModel, expanded, dataSourceInfos, security },
		errorMessages,
	} = state;
	const {
		filtersSettings: { followFilters, hideFiltersInfo },
	} = chartModel;
	// TODO: disabled should be true if there are no filters on the page
	const disabled = false;

	const showRealTime =
		dataSourceInfos && isRealtimeRelevant(chartModel, dataSourceInfos);

	const showRefreshFrequency =
		dataSourceInfos &&
		isRefreshFrequencyRelevant(
			security && security.isAdmin,
			chartModel,
			dataSourceInfos
		);

	const followFiltersPopOverContent = t(
		'Enable to filter the current data visualization'
	);

	return (
		<now-accordion-item
			expanded={expanded}
			append-to-payload={{ accordionItem: 'filterSettings' }}
			header={t('Data update settings')}
			on-click={autoScrollToVisibility}>
			<div className="now-accordion-content" slot="content">
				<div className="settings-item">
					<viz-config-toggle
						className="settings-toggle"
						append-to-payload={{ id: 'followFilters' }}
						checked={followFilters}
						disabled={disabled}
						label={t('Follow filters')}
						infoTooltip={followFiltersPopOverContent}
					/>
				</div>
				<viz-config-toggle
					className="settings-toggle"
					append-to-payload={{ id: 'hideFiltersInfo' }}
					checked={!hideFiltersInfo && followFilters}
					disabled={!followFilters}
					label={t('Show filter icon')}
				/>
				{jsxif(showRealTime) && (
					<div key="section-prop-realtime" className="section-prop-realtime">
						<viz-config-toggle
							append-to-payload={{ payloadActionType: SET_REALTIME }}
							checked={chartModel.realtime}
							label={t('Real time update')}
						/>
						{jsxif(chartModel.realtime) && (
							<viz-config-toggle
								append-to-payload={{
									payloadActionType: SET_REALTIME_INDICATOR,
								}}
								checked={chartModel.showRealtimeIndicator}
								label={t('Show score update time')}
							/>
						)}
					</div>
				)}

				<viz-config-toggle
					label={t('Enable drilldown')}
					checked={
						chartModel.canClick === undefined ? true : chartModel.canClick
					}
					append-to-payload={{
						payloadActionType: SET_CAN_CLICK,
					}}
				/>

				<div className="section-prop-refreshinterval">
					<now-input
						id="refresh-interval-input"
						label={t('Background refresh interval (minutes)')}
						name="refreshInterval"
						value={chartModel.refreshInterval}
						type="number"
						manageValue={true}
					/>
				</div>

				{jsxif(showRefreshFrequency) && (
					<div className="section-prop-refreshFrequency">
						<now-input
							id="refresh-rate-input"
							label={t('Live refresh rate (seconds)')}
							name={FIELD_REFRESH_FREQUENCY}
							value={chartModel.refreshFrequency}
							type="number"
							manageInvalid={true}
							invalid={errorMessages.refreshFrequency !== undefined}
							messages={
								errorMessages.refreshFrequency
									? [
											{
												status: 'critical',
												content: errorMessages.refreshFrequency,
											},
									  ]
									: undefined
							}
						/>
					</div>
				)}
			</div>
		</now-accordion-item>
	);
};
