// @flow
// @jsx snabbdom.createElement
import snabbdom, { Fragment } from '@servicenow/ui-renderer-snabbdom'; // eslint-disable-line no-unused-vars
import { type State } from './state';

import '@now-par-components/sn-par-pillar';
import '@servicenow/now-heading';
import '@servicenow/now-button';
import '@servicenow/now-popover';
import 'sn-devx-components';
import '@servicenow/now-accordion';
import './trendby-popover-content';

import {
	TREND_BY_NOT_SUPPORTED,
	getChartConfig,
} from '../common/chart-settings';

import { t } from 'sn-translate';

import { FREQUENCY_OPTIONS, TREND_BY_POPOVER } from './constants';

import { SOURCE_TYPE_TABLE } from '../common/types/data-source';
import { getOptionLabel } from '../common/options';
import { nowPopoverAllowedPositions } from '../common/popover-config';
import { autoScrollToVisibility } from '../helpers/now-accordion/auto-scroll-to-visibility';
import { getTrendByOptions } from '../visualizations-config-panel/actions/model-options';
import { formatMultipleLabels } from '../helpers/string-utils';
import { type Metric } from '../common/types/metric';
import { getFieldDefinitionKey } from '../helpers/field';
import { getMetricDataSource } from '../helpers/metric';

const getMissingTrendByIcon = (metrics: Metric[]) => {
	if (metrics.some(ds => !ds.trendByField)) {
		return (
			<now-icon
				className="pillar-error-icon"
				icon="circle-exclamation-fill"></now-icon>
		);
	}

	return undefined;
};

export default (state: State) => {
	const { chartModel, dataSourceInfos, expanded } = state.properties;

	const { chartType, trendByFrequency, dataSources, metrics } = chartModel;

	const { popoverOpened, popoverTarget } = state;

	const chartTypeConfig = getChartConfig(chartType);

	if (
		chartTypeConfig.trendBy === TREND_BY_NOT_SUPPORTED ||
		dataSources[0].sourceType !== SOURCE_TYPE_TABLE
	) {
		return;
	}

	const frequencyLabel = getOptionLabel(FREQUENCY_OPTIONS, trendByFrequency);

	const getOptionsForMetric = metric =>
		getTrendByOptions(chartModel, dataSourceInfos[metric.dataSource || '']);

	const getTrendByLabel = (trendByOptions, trendByField) =>
		getOptionLabel(trendByOptions, trendByField);

	const getFieldDefinitionLabel = metric => {
		const dataSource = getMetricDataSource(metric, dataSources);
		const key = getFieldDefinitionKey({
			tableName: dataSource.tableName,
			fieldName: metric.trendByField,
		});

		return state.properties.fieldDefinitions[key].label;
	};

	const pillarLabel = formatMultipleLabels(
		metrics
			.map(metric =>
				/\./g.test(metric.trendByField || '')
					? getFieldDefinitionLabel(metric)
					: getTrendByLabel(getOptionsForMetric(metric), metric.trendByField)
			)
			.filter(label => label)
	);

	return (
		<now-accordion-item
			dividers={{ top: false, bottom: true }}
			header={t('Trend by')}
			expanded={expanded}
			append-to-payload={{ accordionItem: 'trendBy' }}
			on-click={autoScrollToVisibility}>
			<div className="now-accordion-content" slot="content">
				<sn-par-pillar
					ellipsis
					clickable
					actions={[
						{ icon: 'pencil-outline', id: 'edit', label: t('Edit trend by') },
					]}>
					{getMissingTrendByIcon(metrics)}
					{t('{0} by', frequencyLabel)} {pillarLabel}
				</sn-par-pillar>

				<now-popover
					append-to-payload={{ type: TREND_BY_POPOVER }}
					opened={popoverOpened}
					positions={nowPopoverAllowedPositions}
					position-target={popoverTarget}
					manage-opened>
					<viz-config-trend-by-popover-content
						slot="content"
						sharedState={state}
						getOptionsForMetric={getOptionsForMetric}
					/>
				</now-popover>
			</div>
		</now-accordion-item>
	);
};
