//@flow
import uniq from 'lodash/uniq';
import { t } from 'sn-translate';

export function formatMultipleLabels(labels: string[]): string {
	const uniqueLabels = uniq(labels);
	const lastLabel = uniqueLabels.pop();

	return uniqueLabels.length === 0
		? lastLabel
		: uniqueLabels.join(', ') + t(' and ') + lastLabel;
}
