// @flow

import get from 'lodash/get';
import isEqual from 'lodash/isEqual';
import { actionTypes, type Coeffects } from '@servicenow/ui-core';
import { hasPropertyChanged } from '../helpers/props';
import { createGraphQLEffect } from '@servicenow/ui-effect-graphql';
import { effectProxy } from '../helpers/seismic';
import { type State } from './state';
import {
	INDICATOR_INFO_REQUESTED,
	INDICATOR_INFO_SUCCESS,
	INDICATOR_INFO_FAILURE,
	TOGGLE_INDICATOR_SOURCE_DETAILS,
} from './constants';
import {
	FETCH_ELEMENTS_FINISHED,
	BREAKDOWN_CHANGED,
} from '../breakdown-selector/constants';
import {
	type IndicatorInfo,
	type Condition,
	type Breakdown,
} from '../common/types/indicator';
import { INDICATOR_TYPE, AGGREGATE_TYPE } from '../common/types/data-source';
import { type Uuid } from '../common/types/uuid';

const handleBootstrap = (coeffects: Coeffects<State, void>) => {
	loadIndicatorInfo(coeffects);
};

const handlePropertyChange = (coeffects: Coeffects<State, any>) => {
	if (
		hasPropertyChanged(coeffects, 'selectedIndicatorInfo', [
			'previewUuid.indicator',
		])
	) {
		loadIndicatorInfo(coeffects);
	}
};

const loadIndicatorInfo = (coeffects: Coeffects<State, any>) => {
	coeffects.updateState({
		indicatorInfo: undefined,
		breakdownElementsLoading: {},
	});
	const { indicator } = coeffects.state.properties.selectedIndicatorInfo.uuid;

	coeffects.dispatch(INDICATOR_INFO_REQUESTED, {
		sysID: indicator,
		config: JSON.stringify({ indicator }),
	});
};

export const handleIndicatorInfoRequested = createGraphQLEffect(
	`query($sysID: String!, $config: String!) {
		query: AppPARConfig_Query {
			indicatorDetails (sysID: $sysID) {
				type {
					value
					label
				}
				direction {
					value
					label
				}
				formula {
					value
					displayValue
				}
				unit {
					sysID
					name
					format
				}
				precision
				frequency {
					value
					label
				}
				field
				aggregate {
					value
					name
					label
				}
				scripted
				script {
					sysID
					name
					script
				}
				conditions {
					value
					displayValue
				}
				indicatorSource {
					sysID
					name
					description
					table {
						name
						label
					}
					conditions {
						value
						displayValue
					}
				}
				result
			}
			breakdowns(uuid: $sysID) {
				values {
					sysId: sysID
					name
				}
				result
			}
		},
		elementFilter: AppDetailedScorecard_Query {
			result: uuidQuery(config:$config) {
			  breakdowns {
				  sysId
				  elementFilters {
					sysId
					title
				  }
			  },
			}
		}
	}`,
	{
		variableList: ['sysID', 'config'],
		successActionType: INDICATOR_INFO_SUCCESS,
		errorActionType: INDICATOR_INFO_FAILURE,
	}
);

const getReadableConditions = (conditions: ?Condition): ?Condition => {
	if (!conditions) return undefined;

	const { value, displayValue = '' } = conditions;

	const readableValue = displayValue
		.replace(/\.and\./gi, 'and')
		.replace(/\.or\./gi, 'or');

	return {
		value,
		displayValue: readableValue,
	};
};

/***
 * Updates breakdowns with element filters if exists
 * @param { Breakdown[] } breakdowns - Breakdowns fetched using AppPARConfig_Query
 * @param { Breakdown[] } breakdownsWithElementFilters - Breakdowns fetched using AppDetailedScorecard_Query
 * @param { IndicatorInfo } indicatorInfo - Indicator Information
 */
const updateBreakdownsWithElementFilters = (
	breakdowns: Breakdown[],
	breakdownsWithElementFilters: Breakdown[],
	indicatorInfo: IndicatorInfo
): Breakdown[] => {
	if (!shouldAddElementFilters(indicatorInfo)) return breakdowns;

	const elementFilterMap = breakdownsWithElementFilters.reduce((map, bd) => {
		map[bd.sysId] = bd.elementFilters;
		return map;
	}, {});

	return breakdowns.map(bd => {
		const elementFilters = elementFilterMap[bd.sysId];

		return elementFilters.length ? { ...bd, elementFilters } : bd;
	});
};

const shouldAddElementFilters = (indicatorInfo: IndicatorInfo): boolean => {
	const type = indicatorInfo.type.value;
	const aggregate = indicatorInfo.aggregate.value;

	if (
		type === INDICATOR_TYPE.AUTOMATED &&
		(aggregate === AGGREGATE_TYPE.COUNT ||
			aggregate === AGGREGATE_TYPE.SUM ||
			aggregate === AGGREGATE_TYPE.MIN ||
			aggregate === AGGREGATE_TYPE.MAX)
	) {
		return true;
	} else if (type === INDICATOR_TYPE.MANUAL || type === INDICATOR_TYPE.EXTERNAL)
		return true;

	return false;
};

const removeDisabledBreakdowns = (uuid: Uuid, indicatorInfo: IndicatorInfo) => {
	const activeBreakdowns = indicatorInfo.breakdowns;

	const breakdowns = (uuid.breakdowns || []).filter(breakdown => {
		const breakdownFound = activeBreakdowns.find(
			ab => ab.sysId === breakdown.sysId
		);

		if (!breakdownFound) {
			return false;
		}

		if (!breakdown.elementsFilterSysId) {
			return true;
		}

		const elementFilters = breakdownFound.elementFilters || [];

		return !!elementFilters.find(
			ef => ef.sysId === breakdown.elementsFilterSysId
		);
	});

	return {
		...uuid,
		breakdowns,
	};
};

const getBreakdownsLoading = (
	coeffects: Coeffects<State, any>,
	indicatorInfo: IndicatorInfo
) => {
	const {
		selectedIndicatorInfo: { uuid, previewUuid },
	} = coeffects.state.properties;

	const validUuid = removeDisabledBreakdowns(uuid, indicatorInfo);
	const validPreviewUuid = removeDisabledBreakdowns(previewUuid, indicatorInfo);

	if (!isEqual(uuid, validUuid) || !isEqual(previewUuid, validPreviewUuid)) {
		coeffects.dispatch(BREAKDOWN_CHANGED, {
			uuid: validUuid,
			previewUuid: validPreviewUuid,
		});
	}

	const breakdownElementsLoading = validUuid.breakdowns.reduce(
		(acc, { sysId }) => {
			acc[sysId] = true;
			return acc;
		},
		{}
	);

	return breakdownElementsLoading;
};

const handleIndicatorInfoSuccess = (coeffects: Coeffects<State, any>) => {
	const indicatorInfo: IndicatorInfo =
		get(coeffects.action, 'payload.data.query.indicatorDetails') || {};

	const breakdowns =
		get(coeffects.action, 'payload.data.query.breakdowns.values') || [];

	const breakdownsWithElementFilters =
		get(coeffects.action, 'payload.data.elementFilter.result.breakdowns') || [];

	indicatorInfo.breakdowns = updateBreakdownsWithElementFilters(
		breakdowns,
		breakdownsWithElementFilters,
		indicatorInfo
	);

	const { indicatorSource, conditions } = indicatorInfo;

	indicatorInfo.conditions = getReadableConditions(conditions);

	if (indicatorSource) {
		indicatorSource.conditions = getReadableConditions(
			indicatorSource.conditions
		);
	}

	const breakdownElementsLoading = getBreakdownsLoading(
		coeffects,
		indicatorInfo
	);

	coeffects.updateState({
		indicatorInfo,
		breakdownElementsLoading: breakdownElementsLoading,
	});
};

const handleIndicatorInfoFailure = () => {
	//TODO: create some good error-handling strategy
};

export default {
	actionHandlers: {
		[actionTypes.COMPONENT_BOOTSTRAPPED]: handleBootstrap,
		[actionTypes.COMPONENT_PROPERTY_CHANGED]: handlePropertyChange,
		[INDICATOR_INFO_REQUESTED]: effectProxy(handleIndicatorInfoRequested),
		[INDICATOR_INFO_SUCCESS]: handleIndicatorInfoSuccess,
		[INDICATOR_INFO_FAILURE]: handleIndicatorInfoFailure,
		'NOW_BUTTON_BARE#CLICKED': (coeffects: Coeffects<State, any>) => {
			switch (coeffects.action.payload.payloadActionType) {
				case TOGGLE_INDICATOR_SOURCE_DETAILS: {
					coeffects.action.stopPropagation();
					if (coeffects.state.indicatorInfo) {
						coeffects.updateState({
							displaySourceDetails: !coeffects.state.displaySourceDetails,
						});
					}

					break;
				}
			}
		},
		[FETCH_ELEMENTS_FINISHED]: (coeffects: Coeffects<State, any>) => {
			const { sysID } = coeffects.action.payload;

			coeffects.updateState({
				path: `breakdownElementsLoading[${sysID}]`,
				value: false,
				operation: 'set',
			});
		},
	},
};
