//@flow
import {
	type DataSource,
	type PayloadAddDataSource,
} from '../common/types/data-source';

import { actionTypesSourceConfiguration } from './actions';
import { shallowDataSourceCompare } from '../helpers/data-source';

export const SELECT_DATASOURCE = 'SELECT_DATASOURCE';

export type PayloadSelectDataSource = {
	displayLabel: string,
	dataSource: DataSource,
};

export function createSelectDatasourceAction(
	displayLabel: string,
	dataSource: $Shape<DataSource>
) {
	const payload: PayloadSelectDataSource = {
		displayLabel,
		dataSource,
	};

	return [SELECT_DATASOURCE, payload];
}

export function createAddDatasourceAction(
	metadata: $Shape<PayloadAddDataSource>,
	dataSource: $Shape<DataSource>
) {
	if (
		metadata.dataSource &&
		shallowDataSourceCompare(dataSource, metadata.dataSource)
	) {
		dataSource.id = metadata.dataSource.id;
		dataSource.label = metadata.dataSource.label;
		dataSource.groupByField = metadata.dataSource.groupByField;
		dataSource.stackByField = metadata.dataSource.stackByField;
	}

	const payload: PayloadAddDataSource = {
		...metadata,
		dataSource,
	};

	return [actionTypesSourceConfiguration.ADD_SOURCE, payload];
}

export const UPDATE_RECENT_INDICATOR = 'UPDATE_RECENT_INDICATOR';
export type PayloadUpdateRecentIndicator = {
	sysID: string,
};
export function createUpdateRecentIndicatorAction(sysID: string) {
	const payload: PayloadUpdateRecentIndicator = { sysID };

	return [UPDATE_RECENT_INDICATOR, payload];
}

export const UPDATE_RECENT_TABLE = 'UPDATE_RECENT_TABLE';
export type PayloadUpdateRecentTable = {
	name: string,
};
export function createUpdateRecentTableAction(
	payload: PayloadUpdateRecentTable
) {
	return [UPDATE_RECENT_TABLE, payload];
}
