import snabbdom, { Fragment } from '@servicenow/ui-renderer-snabbdom'; // eslint-disable-line no-unused-vars
import '@servicenow/now-condition-builder';
import { type State } from './state';
import { t } from 'sn-translate';

export function ConditionBuilderView(state: State) {
	const { initialFilterQuery } = state;
	const { tableName } = state.properties.tableInfo;

	return (
		<div>
			{!global.TEST_SKIP_RENDER_CB ? (
				<now-condition-builder
					tableName={tableName}
					tableLabel={t('Custom')}
					encodedQuery={initialFilterQuery}
				/>
			) : null}
		</div>
	);
}
