// @flow
// @jsx snabbdom.createElement
import '@servicenow/now-button';
import '@servicenow/now-dot-walk';
import '@servicenow/now-tooltip';
import '@servicenow/now-popover';

import snabbdom from '@servicenow/ui-renderer-snabbdom'; // eslint-disable-line no-unused-vars
import { type State } from './state';
import { type Helpers } from '@servicenow/ui-core';
import { nowPopoverDropdownPositions } from '../common/popover-config';

export default (state: State, { updateState }: Helpers<State>) => {
	const {
		properties: {
			dotWalkPath,
			disabled,
			placeholder,
			tableName,
			label,
			transformMetadata,
			errorMessage,
		},
		opened,
		dotWalkLabel,
	} = state;

	const triggerLabel = dotWalkPath ? dotWalkLabel : placeholder;

	return (
		<div class={{ 'dot-walk-container': true, error: Boolean(errorMessage) }}>
			<label id="dot-walk-label" className="dot-walk-label">
				{label}
			</label>

			<now-popover
				opened={opened}
				managed-opened
				interaction-type="listbox"
				positions={nowPopoverDropdownPositions}
				hide-tail>
				<button
					slot="trigger"
					class={{ 'btn-trigger': true, 'is-open': opened, disabled: disabled }}
					aria-haspopup="listbox"
					aria-labelledby="dot-walk-label trigger-label"
					aria-expanded={String(opened)}
					aria-describedby="dot-walk-tooltip"
					on-click={e => {
						e.stopPropagation(); // avoid now-popover to open/close (even with manage-opened)

						if (disabled || e.detail > 1) return;

						updateState({ opened: !opened });
					}}
					disabled={disabled}>
					<span
						id="trigger-label"
						class={{ 'trigger-label': true, placeholder: !dotWalkPath }}>
						{triggerLabel}
					</span>
					<now-icon
						class={{ disabled: disabled }}
						icon="caret-down-outline"></now-icon>
				</button>
				<now-dot-walk-connected
					style={{ minHeight: '300px', minWidth: '250px', display: 'block' }}
					className="dot-walk-panel"
					slot="content"
					dot-walk-path={dotWalkPath}
					table-name={tableName}
					dotWalkEnabledFieldTypes={['reference']}
					transformMetadata={transformMetadata}
				/>
			</now-popover>

			{errorMessage ? (
				<span className="dot-walk-error">{errorMessage}</span>
			) : null}
		</div>
	);
};
