// @flow

import { JOURNAL, JOURNAL_INPUT } from './constants';
import { type FieldDefinition } from '../../common/types/field';

/**
 * Verify if the field can be sorted
 * Logic extracted from service.dotWalkingFilters.js: canSort
 * @param {FieldDefinition} item
 * @return {boolean}
 */
export const canSort = (item: FieldDefinition): boolean => {
	if (
		!item.canSort ||
		item.name.indexOf('password') > -1 ||
		item.name === 'sys_id' ||
		item.internalType === JOURNAL ||
		item.internalType === JOURNAL_INPUT ||
		item.isArray
	) {
		return false;
	}

	return true;
};
