// @flow
import { t } from 'sn-translate';
import {
	SOURCE_TYPE_TABLE,
	type DataSource,
} from '../../common/types/data-source';

import { CHART_TYPES } from '../../common/types/chart-type';
import { BAR_CHART_TYPES } from '../../common/chart-settings';
import { type ChartModel } from '../../visualizations-config-panel/model/chart-model';

export const DEFAULT_SCORE_SIZE = 'md';

export const SINGLE_SCORE_SIZES = [
	{ label: t('XS').toUpperCase(), id: 'xs' },
	{ label: t('S').toUpperCase(), id: 'sm' },
	{ label: t('M').toUpperCase(), id: DEFAULT_SCORE_SIZE },
	{ label: t('L').toUpperCase(), id: 'lg' },
	{ label: t('XL').toUpperCase(), id: 'xl' },
	{ label: t('XXL').toUpperCase(), id: 'xxl' },
];

export const doesShowSortBy = (chartType: string): boolean => {
	return (
		chartType === CHART_TYPES.BAR ||
		chartType === CHART_TYPES.HORIZONTAL_BAR ||
		chartType === CHART_TYPES.PIE ||
		chartType === CHART_TYPES.DONUT ||
		chartType === CHART_TYPES.SEMI_DONUT
	);
};

export const doesShowDisplayZero = (
	chartType: string,
	dataSources: DataSource[]
): boolean => {
	return (
		chartType === CHART_TYPES.SINGLE_SCORE &&
		dataSources[0].sourceType === SOURCE_TYPE_TABLE
	);
};

export const doesShowChartVariation = (chartModel: ChartModel): boolean => {
	const { metrics, chartType, dataSources } = chartModel;

	const hasStackBy = dataSources.some(ds => ds.stackByField);

	return (
		hasStackBy || (metrics.length > 1 && BAR_CHART_TYPES.includes(chartType))
	);
};
