// @flow

import {
	INTEGER,
	DECIMAL,
	NUMERIC,
	FLOAT,
	DOMAIN_NUMBER,
	AUTO_INCREMENT,
	PERCENT_COMPLETE,
} from './constants';

/**
 * Check if the field type is numeric
 * @param {string} type
 * @return {boolean}
 */
export const isNumericField = (type: string): boolean => {
	return (
		type === INTEGER ||
		type === DECIMAL ||
		type === NUMERIC ||
		type === FLOAT ||
		type === DOMAIN_NUMBER ||
		type === AUTO_INCREMENT ||
		type === PERCENT_COMPLETE
	);
};
