// @flow
import { type Coeffects } from '@servicenow/ui-core';
import { type State } from './state';
import { FILTERS_SETTINGS_CHANGE, FIELD_REFRESH_FREQUENCY } from './constants';
import { t } from 'sn-translate';
import { SET_MODEL_PROPS } from '../visualizations-config-panel/actions/action-constants';

const refreshFrequencyErrorMessage = (
	refreshFrequency: string,
	minRefreshFrequency: string
) => {
	if (!refreshFrequency || refreshFrequency === '0') return undefined;

	if (Number(refreshFrequency) < Number(minRefreshFrequency))
		return minRefreshFrequency === '1'
			? t('Minimum allowed value is 1 second')
			: t('Minimum allowed value is {0} seconds', minRefreshFrequency);

	return undefined;
};

const handleNowInputValueSet = ({
	action,
	state,
	updateState,
	dispatch,
}: Coeffects<State, any>) => {
	const { name } = action.payload;

	const value =
		action.type === 'NOW_INPUT#INVALID_SET'
			? action.payload.fieldValue
			: action.payload.value;

	action.stopPropagation();

	switch (name) {
		case FIELD_REFRESH_FREQUENCY: {
			const {
				errorMessages,
				properties: { minRefreshFrequency },
			} = state;

			const errorMessage = refreshFrequencyErrorMessage(
				value,
				minRefreshFrequency
			);

			if (!errorMessage) {
				dispatch(SET_MODEL_PROPS, {
					[FIELD_REFRESH_FREQUENCY]: value,
				});
			}

			updateState({
				errorMessages: {
					...errorMessages,
					refreshFrequency: errorMessage,
				},
			});

			break;
		}
		default:
			break;
	}
};

export default {
	actions: {
		[FILTERS_SETTINGS_CHANGE]: {},
	},
	actionHandlers: {
		'VIZ_CONFIG_TOGGLE#CHECKED_SET': ({
			dispatch,
			action,
		}: Coeffects<any, any>) => {
			let { id, value } = action.payload;

			if (id === 'hideFiltersInfo') {
				value = !value;
			}

			const updates = [{ id, value }];

			if (id === 'followFilters') {
				updates.push({
					id: 'hideFiltersInfo',
					value: !value,
				});
			}

			dispatch(FILTERS_SETTINGS_CHANGE, { updates });
		},

		'NOW_INPUT#VALUE_SET': handleNowInputValueSet,
		'NOW_INPUT#INVALID_SET': handleNowInputValueSet,
	},
};
