// @flow
// @jsx snabbdom.createElement
import snabbdom, { Fragment } from '@servicenow/ui-renderer-snabbdom'; // eslint-disable-line no-unused-vars

import '@servicenow/now-heading';
import '@servicenow/now-button';
import 'sn-devx-components';
import '@now-par-components/sn-par-pillar';
import '@servicenow/now-accordion';
import '@servicenow/now-select';
import '@servicenow/now-popover';
import './metric-popover-content';

import { type State } from './state';

import {
	AGGREGATE_CONFIG,
	BTN_ADD_METRIC,
	METRIC_POPOVER,
	PILLAR_ACTION_EDIT,
	PILLAR_ACTION_DELETE,
	PILLAR_ACTION_ADD_NEW_AXIS,
} from './constants';

import { t } from 'sn-translate';
import { type Metric } from '../common/types/metric';
import { type ChartModel } from '../visualizations-config-panel/model/chart-model';
import { type DataSourceInfos } from '../common/types/data-source';

import { nowPopoverAllowedPositions } from '../common/popover-config';
import { getAggregateOptionsIndicator } from '../visualizations-config-panel/actions/model-options';

import { getOptionLabel } from '../common/options';

import {
	isTableMetric,
	isIndicatorMetric,
	getMetricUnit,
} from '../helpers/metric';

import {
	getAxisLabel,
	getAxisUnit,
	supportMultipleAxis,
} from '../helpers/axis';

import { autoScrollToVisibility } from '../helpers/now-accordion/auto-scroll-to-visibility';
import { MULTIPLE_DS_UNSUPPORTED } from '../common/types/chart-type';
import {
	CHART_TYPES_CONFIG,
	TIMESERIES_CHART_TYPES,
	getChartConfig,
} from '../common/chart-settings';
import { getMetricFrequency } from '../helpers/metric';
import { getMetricLabel } from '../helpers/metric/metric-label';

const getPillarModifier = (state: State, metric: Metric) => {
	const { chartModel, dataSourceInfos } = state.properties;
	const { dataSources } = chartModel;

	if (isTableMetric(metric, dataSources)) {
		const aggregateFunction = AGGREGATE_CONFIG.find(
			a => a.id === metric.aggregateFunction
		);

		return aggregateFunction && aggregateFunction.label;
	} else if (isIndicatorMetric(metric, dataSources) && metric.dataSource) {
		const aggregateOptions = getAggregateOptionsIndicator(
			metric.dataSource,
			chartModel,
			dataSourceInfos
		);
		const { aggregateIndicator = '' } = metric;

		return getOptionLabel(aggregateOptions, aggregateIndicator);
	}

	return '';
};

export const getInvalidAggregationIcon = (
	chartModel: ChartModel,
	dataSourceInfos: DataSourceInfos,
	metricIndex: number
) => {
	const { metrics, dataSources, chartType } = chartModel;

	if (
		metricIndex > 0 &&
		isIndicatorMetric(metrics[metricIndex], dataSources) &&
		TIMESERIES_CHART_TYPES.includes(chartType)
	) {
		const metricFrequency1 = getMetricFrequency(metrics[0], dataSourceInfos);
		const metricFrequency2 = getMetricFrequency(
			metrics[metricIndex],
			dataSourceInfos
		);

		if (metricFrequency1 !== metricFrequency2) {
			return (
				<now-icon
					className="invalid-aggregation-icon"
					icon="circle-exclamation-fill"></now-icon>
			);
		}
	}

	return undefined;
};

const getPillars = (state: State) => {
	const {
		chartModel,
		dataSourceInfos,
		dotWalkFieldDefinition,
	} = state.properties;

	const { dataSources, metrics, chartType, yAxisConfigs } = chartModel;

	return metrics.map((metric, index) => {
		const metricsPerDataSource = metrics.filter(
			m => m.dataSource === metric.dataSource
		).length;

		const contextMenuActions = [
			{
				id: 'editSection',
				children: [
					{
						icon: 'sliders-outline',
						id: PILLAR_ACTION_EDIT,
						label: t('Edit'),
					},
				],
			},
		];

		const actions = [];

		if (supportMultipleAxis(chartModel) && metrics.length > 1) {
			const axisOptions = yAxisConfigs.map((axisConfig, axisIndex) => {
				const axisUnit = getAxisUnit(
					axisConfig.id,
					chartModel,
					dataSourceInfos,
					dotWalkFieldDefinition
				);

				const metricUnic = getMetricUnit(
					metric,
					dataSources,
					dataSourceInfos,
					dotWalkFieldDefinition
				);

				return {
					icon: axisConfig.id === metric.axisId ? 'check-outline' : '',
					id: axisConfig.id,
					label: getAxisLabel(axisIndex),
					disabled: axisUnit !== metricUnic,
				};
			});

			const metricsWithSameAxis = metrics.filter(
				m => m.axisId === metric.axisId
			);

			if (metricsWithSameAxis.length > 1) {
				axisOptions.push({
					icon: 'plus-outline',
					id: PILLAR_ACTION_ADD_NEW_AXIS,
					label: t('Add new axis'),
				});
			}

			contextMenuActions.push({
				id: 'axisSection',
				label: 'DEFINE Y AXIS',
				children: axisOptions,
			});
		}

		if (TIMESERIES_CHART_TYPES.includes(chartType) && metrics.length > 1) {
			const metricChartTypes = CHART_TYPES_CONFIG.filter(
				c => c.menuGroup === 'time'
			).map(c => ({
				id: c.id,
				label: c.label,
				icon: c.chartIcon,
			}));

			const metricChartType = metric.chartType || chartType;
			const chartConfig = getChartConfig(metricChartType);

			actions.push({
				id: 'changeChartType',
				icon: chartConfig.chartIcon,
				label: t('Change chart type'),
				type: 'dropdown',
				items: metricChartTypes,
			});
		}

		contextMenuActions.push({
			id: 'deleteSection',
			children: [
				{
					icon: 'trash-outline',
					id: PILLAR_ACTION_DELETE,
					label: t('Delete'),
					disabled: metricsPerDataSource === 1,
				},
			],
		});

		return (
			<sn-par-pillar
				id={`metric-pillar-${index}`}
				clickable
				modifier-label={getPillarModifier(state, metric)}
				append-to-payload={{
					metricIndex: index,
				}}
				actions={actions}
				context-menu-actions={contextMenuActions}>
				{getInvalidAggregationIcon(chartModel, dataSourceInfos, index)}
				{getMetricLabel(
					dataSources,
					dataSourceInfos,
					metric,
					dotWalkFieldDefinition
				)}
			</sn-par-pillar>
		);
	});
};

export const metricView = (state: State) => {
	const { popoverOpened, popoverTarget } = state;

	const {
		chartModel: { chartType },
		expanded,
	} = state.properties;

	const supportMultipleMetrics = !MULTIPLE_DS_UNSUPPORTED.includes(chartType);

	return (
		<now-accordion-item
			dividers={{ top: false, bottom: true }}
			expanded={expanded}
			append-to-payload={{ accordionItem: 'metric' }}
			header={t('Metric')}
			on-click={autoScrollToVisibility}>
			<div
				slot="content"
				className="metric-configuration now-accordion-content">
				<div class={{ 'pillar-box': supportMultipleMetrics }}>
					{getPillars(state)}
					{supportMultipleMetrics ? (
						<now-button
							id="btn-add-metric"
							append-to-payload={{
								id: BTN_ADD_METRIC,
							}}
							icon="plus-fill"
							label={t('Add')}
							size="sm"
							variant="secondary"></now-button>
					) : (
						undefined
					)}
				</div>

				<now-popover
					append-to-payload={{ type: METRIC_POPOVER }}
					opened={popoverOpened}
					positions={nowPopoverAllowedPositions}
					position-target={popoverTarget}
					manage-opened>
					<viz-config-metric-popover-content
						slot="content"
						shared-state={state}
					/>
				</now-popover>
			</div>
		</now-accordion-item>
	);
};
