// @flow
// @jsx snabbdom.createElement

import snabbdom, { // eslint-disable-line no-unused-vars
	Fragment, // eslint-disable-line no-unused-vars
} from '@servicenow/ui-renderer-snabbdom';
import { t } from 'sn-translate';
import '@servicenow/now-label-value';
import '@servicenow/now-heading';
import '@servicenow/now-loader';
import 'sn-component-visualization';
import '@now-par-components/sn-par-popover';
import {
	INDICATOR_TYPE_LABEL,
	INDICATOR_SOURCE,
	AVAILABLE_BREAKDOWNS,
	ADITIONAL_CONDITIONS,
	CALCULATION,
	GOAL,
	UNIT,
	FREQUENCY,
	PRECISION,
} from './constants';
import { type LabelItem, type State } from './state';
import { jsxif } from '../helpers/seismic';

export default function(state: State) {
	const {
		properties: {
			indicatorInfo,
			indicatorInfo: { indicatorSource },
			indicatorProps: {
				type,
				breakdowns,
				calculation,
				goal,
				unit,
				precision,
				frequency,
			},
		},
	} = state;
	const indicatorInfoContiditions = indicatorInfo && indicatorInfo.conditions;

	const indicatorSourcePopover = indicatorSource => {
		const tableText = indicatorSource.table
			? indicatorSource.table.label
			: null;
		const conditionText = indicatorSource.conditions
			? indicatorSource.conditions.displayValue
			: null;
		const indicatorPropertiesConditionPopover = (
			<div className="popover-container" id="popover-indicator-source">
				{jsxif(tableText) && (
					<div>
						<label className="popover-title"> {t('Table')} </label>
						<label>{tableText}</label>
					</div>
				)}
				{jsxif(conditionText) && (
					<div>
						<label className="popover-title popover-conditions">
							{' '}
							{t('Conditions')}{' '}
						</label>
						<label>{conditionText}</label>
					</div>
				)}
			</div>
		);

		return (
			<now-popover interactionType="none" id="popover-datasources">
				<now-icon slot="trigger" icon="info-circle-outline" />
				<viz-config-popover-content
					slot="content"
					popOverContent={indicatorPropertiesConditionPopover}
				/>
			</now-popover>
		);
	};

	const source = indicatorSource ? (
		<Fragment>
			<div className="indicator-source-popover">
				<div className="indicator-source-value">{indicatorSource.name}</div>
				<div>{indicatorSourcePopover(indicatorSource)}</div>
			</div>
		</Fragment>
	) : (
		''
	);

	const getItem = (
		label: string,
		value: any,
		type: string = 'jsx'
	): Array<LabelItem> => {
		return [{ label, value: { type, value } }];
	};

	const getBreakdownItem = () => {
		if (breakdowns) {
			return getItem(AVAILABLE_BREAKDOWNS, breakdowns);
		}

		const noBreakdownsLabel = (
			<Fragment>
				<span style={{ color: `rgb(var(--now-color--neutral-9))` }}>
					{t('No breakdowns defined')}
				</span>
			</Fragment>
		);

		return getItem(AVAILABLE_BREAKDOWNS, noBreakdownsLabel, 'jsx');
	};

	const IndicatorProperties = (
		<Fragment>
			<label>{t('Properties')}</label>
			<div className="label-stacked">
				<label className="label-stacked-name">{INDICATOR_TYPE_LABEL}</label>
				<label className="label-stacked-value">
					<now-icon
						className="indicator-type-icon"
						icon="analytics-center-outline"
						size="md"
					/>
					<span data-testId="indicator-type">{type}</span>
				</label>
			</div>

			{indicatorSource ? (
				<Fragment>
					<div className="label-stacked">
						<label className="label-stacked-name">{INDICATOR_SOURCE}</label>
						<label className="label-stacked-value">{source}</label>
					</div>
				</Fragment>
			) : null}

			{indicatorInfoContiditions ? (
				<div className="label-stacked">
					<label className="label-stacked-name">{ADITIONAL_CONDITIONS}</label>
					<label className="label-stacked-value">
						{indicatorInfoContiditions.displayValue}
					</label>
				</div>
			) : null}
			<now-label-value-stacked
				data-testId="calculation"
				align="vertical"
				items={getItem(CALCULATION, calculation)}
				size="md"
			/>
			<now-label-value-stacked
				data-testId="bd-item"
				align="vertical"
				items={getBreakdownItem()}
				size="md"
			/>
			<div className="properties-one-row">
				<now-label-value-stacked
					data-testId="goal"
					align="vertical"
					items={getItem(GOAL, goal)}
					size="md"
				/>
				<now-label-value-stacked
					data-testId="frequency"
					align="vertical"
					items={getItem(FREQUENCY, frequency)}
					size="md"
				/>
				<now-label-value-stacked
					data-testId="unit"
					align="vertical"
					items={getItem(UNIT, unit)}
					size="md"
				/>
				<now-label-value-stacked
					data-testId="precision"
					align="vertical"
					items={getItem(PRECISION, precision)}
					size="md"
				/>
			</div>
		</Fragment>
	);

	return <div className="indicator-properties">{IndicatorProperties}</div>;
}
