// @flow

import { type Uuid } from '../common/types/uuid';
import {
	type DataSource,
	type DataSourceInfo,
	type DataSourceType,
} from '../common/types/data-source';

export type State = {|
	sourceType: DataSourceType,
	displayLabel?: string,

	// Table-specific
	tableInfo?: {
		tableName?: string,
		filterQuery?: string,
		predefinedFilterId?: string,
		previewPredefinedFilterId?: string,
		previewFilterQuery?: string,
		isExpanded?: boolean,
		isEditMode?: boolean,
		showCustomConditions?: boolean,
	},

	// Indicator-specific
	indicatorInfo?: {
		uuid?: Uuid,
		previewUuid?: Uuid,
		isExpanded?: boolean,
		isEditMode?: boolean,
	},

	tableChanges: {
		[string]: {|
			predefinedFilterId?: string,
			filterQuery?: string,
			previewPredefinedFilterId?: string,
			previewFilterQuery?: string,
			isExpanded?: boolean,
			isEditMode?: boolean,
			showCustomConditions?: boolean,
		|},
	},
	indicatorChanges: {
		[string]: {|
			uuid?: Uuid,
			previewUuid?: Uuid,
			isExpanded?: boolean,
			isEditMode?: boolean,
		|},
	},

	properties: {
		operationMetadata: {
			addNewDataSource: boolean,
			editDataSourceIndex: number,
			allowedSourceType: string,
		},
		dataSource: DataSource,
		dataSourceInfo: DataSourceInfo,
	},
|};

export const initialState: $Shape<State> = {
	tableChanges: {},
	indicatorChanges: {},
};
