// @flow
// @jsx snabbdom.createElement

import snabbdom, { Fragment } from '@servicenow/ui-renderer-snabbdom'; // eslint-disable-line no-unused-vars
import { type Helpers } from '@servicenow/ui-core';
import { t } from 'sn-translate';
import '@servicenow/now-label-value';
import '@servicenow/now-collapse';
import 'sn-devx-components';
import { type State } from './state';
import {
	REMOVE_PREDEFINED_CONDITION,
	EDIT_FILTER,
	UPDATE_TABLE_PREVIEW,
	TABLE_CONFIG_CHANGED,
	ADD_CUSTOM_CONDITIONS,
} from './constants';

import { ConditionBuilderView } from './condition-builder-view';

const getSelectedPredefinedFilter = (state: State) => {
	const {
		predefinedFilters,
		properties: {
			tableInfo: { predefinedFilterId },
		},
	} = state;

	if (!predefinedFilterId) {
		return undefined;
	}

	return predefinedFilters.find(({ id }) => id === predefinedFilterId);
};

const EditFilterView = (state: State) => {
	const {
		predefinedFilters,
		properties: {
			tableInfo: {
				filterQuery,
				predefinedFilterId,
				previewFilterQuery,
				previewPredefinedFilterId,
				showCustomConditions,
			},
		},
	} = state;

	const choices = predefinedFilters.map(item => ({
		id: item.id,
		label: item.name,
		query: item.query,
	}));

	const selectedFilter = getSelectedPredefinedFilter(state);

	return (
		<div className="edit-filter">
			<div className="predefined-condition-container">
				{choices.length ? (
					<div className="predefined-condition-selector">
						<devx-typeahead
							label={t('Predefined conditions')}
							append-to-payload={{
								payloadActionType: 'predefinedConditionTypeahead',
							}}
							compact
							items={choices}
							placeholder={t('Select a predefined condition')}
							name="predefined_conditions"
							selected-item={predefinedFilterId}
							key={predefinedFilterId}
						/>
						{predefinedFilterId ? (
							<now-button-bare
								append-to-payload={{
									payloadActionType: REMOVE_PREDEFINED_CONDITION,
								}}
								label={t('Remove')}
								size="md"
								variant="primary"
								icon-start="trash-outline"
							/>
						) : (
							undefined
						)}
					</div>
				) : (
					<Fragment>
						<div className="condition-label">{t('Predefined conditions')}</div>
						<div className="no-conditions-label">
							{t('We couldnt find any predefined conditions for this table.')}
						</div>
					</Fragment>
				)}
				{selectedFilter ? (
					<div className="selected-predefined-condition">
						<span>{t('Conditions:')}</span>
						<span>{selectedFilter.readableQuery}</span>
					</div>
				) : null}
			</div>
			<div className="custom-conditions-container">
				{!showCustomConditions ? (
					<now-button-bare
						append-to-payload={{
							payloadActionType: ADD_CUSTOM_CONDITIONS,
						}}
						icon-start="plus-fill"
						label={t('Add custom conditions')}
						size="md"
						variant="secondary"></now-button-bare>
				) : null}
				<div
					class={{
						'condition-builder': true,
						'is-visible': showCustomConditions,
					}}>
					{ConditionBuilderView(state)}
				</div>
			</div>
			<div className="edit-filter-actions">
				<now-button
					className="btn-update-preview"
					append-to-payload={{
						payloadActionType: UPDATE_TABLE_PREVIEW,
					}}
					label={t('Run')}
					size="md"
					disabled={
						filterQuery === previewFilterQuery &&
						predefinedFilterId === previewPredefinedFilterId
					}
					variant="secondary"></now-button>
				{/* <now-button
					append-to-payload={{
						payloadActionType: DISCARD_FILTER,
					}}
					label={t('Discard')}
					size="md"
					disabled={!predefinedFilterId && !filterQuery}
					variant="tertiary"></now-button> */}
			</div>
		</div>
	);
};

const DisplayFilterView = (state: State) => {
	const selectedFilter = getSelectedPredefinedFilter(state);

	const { readableFilterQuery } = state;

	return (
		<div className="display-filter">
			{selectedFilter ? (
				<Fragment>
					<label className="condition-label">
						{t('Predefined conditions')}
					</label>
					<div className="selected-predefined-condition">
						<span>{selectedFilter.name}:</span>
						<span>{selectedFilter.readableQuery}</span>
					</div>
				</Fragment>
			) : null}
			{readableFilterQuery ? (
				<Fragment>
					<label className="condition-label">{t('Custom conditions')}</label>
					<div className="selected-custom-condition">
						<span>{readableFilterQuery}</span>
					</div>
				</Fragment>
			) : null}
		</div>
	);
};

const getFilterTag = (state: State) => {
	const {
		properties: {
			tableInfo: { predefinedFilterId, filterQuery, isExpanded },
		},
	} = state;

	if (isExpanded) {
		return null;
	}

	let filterTagLabel = '';

	if (predefinedFilterId) {
		if (filterQuery) {
			filterTagLabel = t('Predefined and custom conditions applied');
		} else {
			filterTagLabel = t('Predefined conditions applied');
		}
	} else if (filterQuery) {
		filterTagLabel = t('Custom conditions applied');
	}

	if (!filterTagLabel) {
		return null;
	}

	return <span className="filter-tag">{filterTagLabel}</span>;
};

export function FilterView(state: State, { dispatch }: Helpers<State>) {
	const {
		properties: {
			tableInfo: { isExpanded, isEditMode },
		},
	} = state;

	return (
		<div className={'filter-accordion' + (isExpanded ? ' expanded' : '')}>
			<div className="filter-header">
				<button
					type="button"
					className="filter-header-label"
					on-click={() => {
						dispatch(TABLE_CONFIG_CHANGED, { isExpanded: !isExpanded });
					}}>
					<now-icon
						className="icon-filter"
						icon="filter-fill"
						size="md"></now-icon>
					<span className="filter-title">{t('Filters')}</span>
					{getFilterTag(state)}
				</button>
				<div className="filter-header-action">
					<now-button-bare
						append-to-payload={{
							payloadActionType: EDIT_FILTER,
						}}
						hide-padding
						icon-start="pencil-fill"
						label={t('Edit')}
						size="md"
						disabled={isEditMode}
						variant="secondary"></now-button-bare>
				</div>
			</div>
			<div
				class={{
					'filter-content': true,
					expanded: isExpanded,
				}}>
				{isEditMode ? EditFilterView(state) : DisplayFilterView(state)}
			</div>
		</div>
	);
}
