import isEqual from 'lodash/isEqual';

/**
 * Using stringify -> parse means all undefined are removed
 * But we can't just compare strings since stringify messes up the order of properties
 * So we parse it again and compare via lodash/isEqual which compares deeply
 */
export function compareValidProperties(obj1, obj2) {
	if (!obj1 || !obj2) {
		return false;
	}

	try {
		return isEqual(
			JSON.parse(JSON.stringify(obj1)),
			JSON.parse(JSON.stringify(obj2))
		);
	} catch (_) {
		return false;
	}
}
