// @flow

export type PredefinedFilter = {|
	id: string,
	name: string,
	query: string,
	readableQuery: string,
|};

export type State = {|
	isFilterLoading: boolean,
	isTableLoading: boolean,
	predefinedFilters: PredefinedFilter[],
	tableName: string,
	readableFilterQuery: string,
	initialFilterQuery: string,
	properties: {
		tableInfo: {
			tableName: string,
			filterQuery: string,
			predefinedFilterId: string,
			previewFilterQuery: string,
			previewPredefinedFilterId: string,
			isExpanded: boolean,
			isEditMode: boolean,
			showCustomConditions: boolean,
		},
	},
|};

export const initialState: $Shape<State> = {
	isFilterLoading: true,
	isTableLoading: true,
	predefinedFilters: [],
};
