// @flow
// @jsx snabbdom.createElement
import find from 'lodash/find';

import { type State } from '../state';

import snabbdom from '@servicenow/ui-renderer-snabbdom'; // eslint-disable-line no-unused-vars
import { t } from 'sn-translate';

import {
	SET_AUTO_UPDATE,
	UPDATE_VISUALIZATION,
} from '../actions/action-constants';
import { SYS_PROP_ALLOW_MANUAL_UPDATE } from '../actions/preferences-security-action-handlers';

import { AUTO_UPDATE_PREFERENCE } from './view-constants';

import '@servicenow/now-button';
import '../../toggle';

export const isManualUpdateAllowed = (state: State): boolean => {
	const preference = state.sysProperties.find(
		x => x && x.name === SYS_PROP_ALLOW_MANUAL_UPDATE
	);

	return preference && preference.isDefined
		? preference.booleanValue === true
		: true;
};

export const isAutoUpdateEnabled = (state: State): boolean => {
	if (!isManualUpdateAllowed(state)) {
		return true;
	}

	const preference = find(state.userPreferences, [
		'name',
		AUTO_UPDATE_PREFERENCE,
	]);

	return preference ? preference.value !== 'false' : false;
};

export default (state: State) => {
	if (!isManualUpdateAllowed(state)) {
		return null;
	}

	const autoUpdateEnabled = isAutoUpdateEnabled(state);

	return (
		<div className="viz-config-auto-update">
			<div className="viz-config-auto-update__container">
				<viz-config-toggle
					disable-margin={true}
					append-to-payload={{ payloadActionType: SET_AUTO_UPDATE }}
					checked={autoUpdateEnabled}
					label={t('Auto-update')}
				/>

				<now-button
					label={t('update')}
					disabled={autoUpdateEnabled || !state.hasChangesToUpdate}
					size="sm"
					variant="primary"
					append-to-payload={{
						payloadActionType: UPDATE_VISUALIZATION,
					}}
				/>
			</div>
		</div>
	);
};
