//@flow

import { type ChartType } from '../../common/types/chart-type';
import { type DataSource } from '../../common/types/data-source';
import { type Metric } from '../../common/types/metric';
import { type AxisConfig } from '../../common/types/axis';
import { type ColorCondition } from './chart-props';

export type DateRangeType = 'Y' | 'M' | 'D';

export type AdditionalSettings = {|
	include_scores?: boolean,
	include_benchmark?: boolean,
	include_forecast?: boolean,
	include_forecast_range?: boolean,
	include_targets?: boolean,
	include_thresholds?: boolean,
	include_change?: boolean,
	include_change_percentage?: boolean,
	include_comments?: boolean,
	include_initiatives?: boolean,
	include_sparkline?: boolean,
	include_gap_percentage?: boolean,
	include_trend?: boolean,
	include_band?: boolean,
	showTotal?: boolean,
	showMetricLabel?: boolean,
|};

export type FiltersSettings = {|
	followFilters?: boolean,
	hideFiltersInfo?: boolean,
|};

export type AdditionalSettingName = $Keys<AdditionalSettings>;

export type XenolithProperties = {
	totalLabel?: string,
	heading?: string,
	headingPosition?: 'top' | 'bottom',
};

export type ChartModel = {
	chartType: ChartType,
	dataSources: DataSource[],
	groupByMax?: any,
	noOfGroupBasedOn?: string,
	showOthers?: boolean,
	trendByFrequency?: string,
	isStacked?: string,
	additionalSettings: AdditionalSettings,
	filtersSettings: FiltersSettings,
	chartWidth?: string,
	chartHeight?: string,
	legendSettings?: LegendSettings,
	dataLabelSettings: DataLabelSettings,
	axisSettings?: AxisSettings,
	variant?: string,
	border?: boolean,
	label?: string,
	realtime?: boolean,
	showRealtimeIndicator?: boolean,
	refreshInterval?: string,
	refreshFrequency?: string,
	hideHeader?: boolean,
	chartSettings?: ModelChartSettings | void,
	displaySettings: DisplaySettings,
	metrics: Metric[],
	yAxisConfigs: AxisConfig[],
	xenolithProperties: XenolithProperties,
	canClick?: boolean,
};

export type ModelChartSettings = {
	color?: ColorConfiguration,
};

export type ColorConfiguration = {
	type?: string,
	sys_id?: string,
	conditions?: ColorCondition[],
};

export type LegendSettings = {
	showLegend: boolean,
	showLegendValue: boolean,
	showLegendPercentage?: boolean,
	legendPosition: string,
};

export type DataLabelSettings = {
	showDataLabels: boolean,
	decimalPrecision?: number,
	dataLabelPosition: ?string,
	align?: string,
	showOverlappingLabels?: boolean,
	stackedValue?: string,
	showDataLabelAs?: string,
};

export type AxisSettings = {
	xAxisName?: string,
	dateRangeShowMax?: boolean,
	dateRangeAmount?: number,
	dateRangeType?: DateRangeType,
};

export type DisplaySettings = {
	sortByField?: string,
	orderByField?: string,
	scoreSize?: string,
	displayZero?: boolean,
};
