// @flow

import { type Coeffects } from '@servicenow/ui-core';
import get from 'lodash/get';
import isEqual from 'lodash/isEqual';

type State = any;

export const hasPropertyChanged = (
	coeffects: Coeffects<State, any>,
	propertyName: string,
	paths: ?(string[])
): boolean => {
	const { name, previousValue, value } = coeffects.action.payload;

	if (name !== propertyName) {
		return false;
	}

	if (!paths) {
		return true;
	}

	const hasChanged = paths.find(path => {
		return !isEqual(get(previousValue, path), get(value, path));
	});

	return !!hasChanged;
};
