import '@servicenow/now-content-tree';
import { Fragment, createRef } from '@servicenow/ui-renderer-snabbdom'; // eslint-disable-line no-unused-vars
import { t } from 'sn-translate';
import '@now-par-components/sn-par-popover';
import '@servicenow/now-icon';
import { jsxif } from '../helpers/seismic';

export default (state, { dispatch }) => {
	const {
		items,
		selectedItems,
		expandedItems,
		loadingPath,
		searchQuery,
		isLoading,
		hasAccessToIndicators,
	} = state;

	const handleKeyPress = e => {
		if (e.keyCode === 13 && e.target.value !== searchQuery) {
			dispatch('SEARCH_SOURCE', e.target.value);
		}
	};

	const onlyFilledSections = items.filter(item => item.childrenAvailable > 0);

	const hasAnyResults = items.reduce(
		(final, item) => item.childrenAvailable > 0 || final,
		false
	);

	const loadingElement = (
		<div className="loading-container">
			<now-loader label={t('Loading...')} size="lg" />
		</div>
	);

	const noResultText = t(
		'Your search for "{0}" didn\'t return any results.',
		searchQuery
	);

	const noResultsFound = (
		<div className="source-selector-empty-state">
			<div className="source-selector-empty-state-row">
				<h1 className="source-selector-empty-state-header">
					{t('No results found.')}
				</h1>
			</div>
			<div className="source-selector-empty-state-row">
				<span className="source-selector-empty-state-body">{noResultText}</span>
			</div>
		</div>
	);

	const treeComponent = (
		<div className="content-tree-container">
			<now-content-tree
				select="single"
				items={onlyFilledSections}
				search-term={searchQuery}
				selected-items={selectedItems}
				manage-selected-items
				expanded-items={expandedItems}
				loading-items={loadingPath}
				overflow="truncate"
			/>
		</div>
	);

	const dynamicContent = hasAnyResults ? treeComponent : noResultsFound;

	const addDataSourcePopOverContent = (
		<div className="popover-container" id="popover-datasources">
			{jsxif(hasAccessToIndicators) && (
				<div>
					<div className="popover-title">
						<now-icon icon="analytics-center-outline" size="md" />
						<label className="popover-title-label">{t('Indicators')}</label>
					</div>

					<label>
						{t(
							'Predefined KPIs that show trends and help forecast future events.'
						)}
					</label>
				</div>
			)}
			<div>
				<div className="popover-title popover-tables">
					<now-icon icon="list-outline" size="md" />
					<label className="popover-title-label">{t('Tables')}</label>
				</div>

				<label>
					{t(
						'Show the current state of your data. You can also add conditions to tables.'
					)}
				</label>
			</div>
		</div>
	);

	return (
		<div className="source-selector">
			<div className="source-selector-select-source">
				<label>{t('Select a source')}</label>
				<now-popover interactionType="none" id="popover-datasources">
					<now-icon slot="trigger" icon="info-circle-outline" />
					<viz-config-popover-content
						slot="content"
						popOverContent={addDataSourcePopOverContent}
					/>
				</now-popover>
			</div>

			<div className="search-container left-align">
				<span className="icon">
					<now-icon icon="magnifying-glass-outline" size="md" />
				</span>
				<input
					aria-label={t('Search data sources')}
					className="search-box"
					type="text"
					on-keydown={handleKeyPress}
					value={searchQuery}
					aria-label={t('Search sources')}
					placeholder={t('Search sources')}
				/>
			</div>
			{isLoading ? loadingElement : dynamicContent}
		</div>
	);
};
