// @flow

import { CHART_TYPES } from '../common/types/chart-type';
import { SOURCE_TYPE_TABLE } from '../common/types/data-source';
import {
	TIMESERIES_CHART_TYPES,
	PIES_CHART_TYPES,
	BAR_CHART_TYPES,
} from '../common/chart-settings';

import { t } from 'sn-translate';

import { type ChartModel } from '../visualizations-config-panel/model/chart-model';

export const PREFIX = 'COLOR_PALETTE';
export const LOAD_COLOR_REQUESTED = PREFIX + 'LOAD_COLOR_REQUESTED';
export const COLOR_TYPE_REQUESTED = PREFIX + 'COLOR_TYPE_REQUESTED';
export const LOAD_ALL_COLOR_OPTIONS = PREFIX + 'LOAD_ALL_COLOR_OPTIONS';
export const LOAD_ALL_COLORS_SUCCESS = PREFIX + 'LOAD_ALL_COLORS_SUCCESS';
export const SET_COLOR_REQUESTED = PREFIX + 'SET_COLOR_REQUESTED';
export const COLOR_TYPE_PALETTE = 'palette';
export const COLOR_TYPE_SINGLE = 'single_color';
export const COLOR_TYPE_FIXED = 'chart_color';
export const COLOR_TYPE_DEFAULT = 'default_color';
export const COLOR_RULE_EDIT = 'COLOR_RULE_EDIT';
export const COLOR_RULE_DELETE = 'COLOR_RULE_DELETE';
export const ADD_COLOR_RULE = 'ADD_COLOR_RULE';

export const COLOR_CHOICES = [
	{
		id: COLOR_TYPE_DEFAULT,
		label: t('Default'),
	},
	{
		id: COLOR_TYPE_PALETTE,
		label: t('Color palette'),
		extraVerification: (chartModel: ChartModel) => {
			const { chartType } = chartModel;

			return chartType !== CHART_TYPES.SINGLE_SCORE;
		},
	},
	{
		id: COLOR_TYPE_FIXED,
		label: t('Fixed element color'),
		extraVerification: (chartModel: ChartModel) => {
			const { chartType, dataSources, metrics } = chartModel;

			const hasTable = dataSources.some(
				ds => ds.sourceType === SOURCE_TYPE_TABLE
			);

			const piesAndTable = hasTable && PIES_CHART_TYPES.includes(chartType);

			const groupByTsAndTable =
				TIMESERIES_CHART_TYPES.includes(chartType) &&
				dataSources.some(ds => ds.groupByField) &&
				hasTable;

			const barAndTable = BAR_CHART_TYPES.includes(chartType) && hasTable;

			const hasMultipleMetrics = metrics.length > 1;

			return (
				!hasMultipleMetrics &&
				(piesAndTable || groupByTsAndTable || barAndTable)
			);
		},
	},
	{
		id: COLOR_TYPE_SINGLE,
		label: t('Single color'),
		extraVerification: (chartModel: ChartModel) => {
			const { chartType, dataSources } = chartModel;

			const hasTable = dataSources.some(
				ds => ds.sourceType === SOURCE_TYPE_TABLE
			);

			const singleScoreAndTable =
				hasTable && chartType === CHART_TYPES.SINGLE_SCORE;

			const hasNoStackBy = !dataSources.some(ds => ds.stackByField);
			const barAndNoStackBy =
				BAR_CHART_TYPES.includes(chartType) && hasNoStackBy;

			const hasNoGroupBy = !dataSources.some(ds => ds.groupByField);
			const timeseriesWithoutGroupBy =
				TIMESERIES_CHART_TYPES.includes(chartType) && hasNoGroupBy;

			return singleScoreAndTable || barAndNoStackBy || timeseriesWithoutGroupBy;
		},
	},
];
