// @flow
// @jsx snabbdom.createElement

import snabbdom, { Fragment } from '@servicenow/ui-renderer-snabbdom'; // eslint-disable-line no-unused-vars
import 'sn-devx-components';
import '@servicenow/now-typeahead';
import { t } from 'sn-translate';
import { jsxif } from '../helpers/seismic';
import { type State } from './state';
import { type Helpers } from '@servicenow/ui-core';
import { BREAKDOWN_CHANGED, BD_OPERATORS } from './constants';
import isEqual from 'lodash/isEqual';

const MAX_BREAKDOWNS = 2;

export default function view(state: State, helpers: Helpers<State>) {
	return (
		<div className="breakdown-selector">{renderEverything(state, helpers)}</div>
	);
}

const EditFilterView = (state: State) => {
	const {
		properties: {
			selectedIndicatorInfo: { uuid, previewUuid },
		},
	} = state;

	return (
		<div className="edit-filter">
			<div className="msg-filter-header">
				{t('All conditions must be met:')}
			</div>
			{state.breakdowns.map((br, index) => {
				const otherBreakdowns = state.breakdowns
					.filter((x, i) => i !== index)
					.map(x => x.sysId);

				const availableBreakdownOptions = br.breakdownOptions
					? br.breakdownOptions.filter(x => !otherBreakdowns.includes(x.id))
					: [];
				const isAddBreakdownButtonVisible =
					br.sysId &&
					index < MAX_BREAKDOWNS - 1 &&
					br.breakdownOptions &&
					br.breakdownOptions.length > 1;

				return (
					<div className="conditions-container">
						<div className="breakdown-row" key={`br-row${br.sysId || ''}`}>
							<div className="breakdown-typeahead">
								<devx-typeahead
									append-to-payload={{
										payloadActionType: 'breakdownSelectTypeahead',
										breakdownIndex: index,
									}}
									items={availableBreakdownOptions}
									placeholder={
										br.sysId && !br.breakdownOptions
											? t('Loading...')
											: t('Select a breakdown')
									}
									label={t('Breakdown')}
									compact
									loading={!br.breakdownOptions}
									selected-item={br.breakdownOptions && br.sysId}
								/>
							</div>
							<div className="operator-typeahead">
								{jsxif(br.sysId) && (
									<devx-typeahead
										append-to-payload={{
											payloadActionType: 'breakdownOperator',
											breakdownIndex: index,
										}}
										items={br.operatorOptions}
										placeholder={
											br.sysId && !br.breakdownOptions
												? t('Loading...')
												: t('Select an operator')
										}
										label={t('Operator')}
										compact
										loading={!br.breakdownOptions}
										selected-item={br.sysId && br.operator}
									/>
								)}
							</div>
							<div className="element-typeahead">
								{jsxif(br.sysId) &&
									(br.operator === BD_OPERATORS.MULTI ? (
										<now-typeahead-multi
											append-to-payload={{
												payloadActionType: 'elementSelectTypeahead',
												breakdownIndex: index,
											}}
											items={br.elementOptions}
											label={t('Elements')}
											selected-items={
												br.elementOptions &&
												br.elementSysIds &&
												br.elementSysIds.length > 0
													? br.elementSysIds.map(sysId =>
															// $FlowFixMe
															br.elementOptions.find(
																option => option.id === sysId
															)
													  )
													: []
											}
											manageSelectedItems={true}
											invalid={
												!br.elementOptions ||
												!br.elementSysIds ||
												br.elementSysIds.length === 0
											}
											search="contains"
										/>
									) : (
										<devx-typeahead
											append-to-payload={{
												payloadActionType: 'elementSelectTypeahead',
												breakdownIndex: index,
											}}
											items={br.elementOptions}
											placeholder={
												br.elementSysIds &&
												br.elementSysIds.length > 0 &&
												!br.elementOptions
													? t('Loading...')
													: t('Select an element')
											}
											label={t('Element')}
											selected-item={
												br.elementOptions &&
												br.elementSysIds &&
												br.elementSysIds.length > 0
													? br.elementSysIds[0]
													: br.elementsFilterSysId || undefined
											}
											loading={!br.elementOptions}
											compact={!br.isElementRequired}
											errorMsg={
												br.isElementRequired
													? t('Please select an element')
													: undefined
											}
										/>
									))}
							</div>
							<div className="row-buttons">
								{jsxif(br.sysId || index > 0) && (
									<now-button-bare
										append-to-payload={{
											payloadActionType: 'removeBreakdown',
											breakdownIndex: index,
										}}
										icon-start="close-outline"
										size="lg"
										hidePadding
										tooltip-content={t('Remove a breakdown')}
										configAria={{
											'aria-label': t('Remove a breakdown'),
										}}
										variant="tertiary"
									/>
								)}
								<div
									className="btn-add-container"
									style={{
										visibility: isAddBreakdownButtonVisible
											? 'visible'
											: 'hidden',
									}}>
									<now-button
										append-to-payload={{
											payloadActionType: 'btnAddNewBreakdown',
										}}
										size="md"
										label={t('AND')}
										variant="tertiary"
										hidePadding
										configAria={{
											'aria-label': t('Add new breakdown'),
										}}
										disabled={state.breakdowns.length === MAX_BREAKDOWNS}
									/>
								</div>
							</div>
						</div>
					</div>
				);
			})}
			<div className="edit-filter-actions">
				<now-button
					className="btn-update-preview"
					append-to-payload={{
						payloadActionType: 'applyFilter',
					}}
					size="md"
					variant="secondary"
					label={t('Run')}
					disabled={isEqual(uuid, previewUuid)}
				/>
				{/* <now-button
					append-to-payload={{
						payloadActionType: 'cancelFilter',
					}}
					size="md"
					variant="tertiary"
					label={t('Discard')}
					disabled={getNumberOfConditions(state) === 0}
				/> */}
			</div>
		</div>
	);
};

const DisplayFilterView = (state: State) => {
	const filterParts = [];

	let canRender = true;

	for (const br of state.breakdowns) {
		const selectedElement =
			br.elementSysIds && br.elementSysIds.length > 0
				? br.elementSysIds
				: [br.elementsFilterSysId];

		if (
			br.sysId &&
			br.breakdownOptions &&
			selectedElement &&
			selectedElement[0]
		) {
			const breakdown = br.breakdownOptions.find(x => x.id === br.sysId);

			if (breakdown) {
				if (br.elementOptions) {
					const elementLabels = [];

					for (const elementId of selectedElement) {
						const element = br.elementOptions.find(x => x.id === elementId);

						if (element) {
							elementLabels.push(element.label);
						}
					}

					if (elementLabels.length > 0) {
						filterParts.push({
							brLabel: breakdown.label,
							elementLabels: elementLabels,
						});
					}
				} else {
					canRender = false;
				}
			}
		}
	}

	return (
		<div className="display-filter">
			<div className="condition-title">{t('Conditions')}</div>
			{jsxif(canRender && filterParts.length > 0) &&
				filterParts.map((filterPart, fIndex) => {
					const elements = filterPart.elementLabels.join(', ');

					let text;

					if (fIndex === 0)
						text =
							filterPart.elementLabels.length > 1
								? t('{0} is one of ({1})', filterPart.brLabel, elements)
								: t('{0} is {1}', filterPart.brLabel, elements);
					else
						text =
							filterPart.elementLabels.length > 1
								? t('AND {0} is one of ({1})', filterPart.brLabel, elements)
								: t('AND {0} is {1}', filterPart.brLabel, elements);

					return <div className="condition-value">{text}</div>;
				})}
			{jsxif(canRender && filterParts.length === 0) && (
				<div className="condition-value">{t('No conditions selected')}</div>
			)}
		</div>
	);
};

function getCount(state: State) {
	const {
		properties: {
			selectedIndicatorInfo: { isExpanded },
		},
	} = state;

	if (isExpanded) {
		return null;
	}

	const numberOfConditions = getNumberOfConditions(state);

	if (numberOfConditions === 0) {
		return <span className="filter-subtitle">{t('[No filters applied]')}</span>;
	} else {
		return (
			<span className="filter-tag">
				{numberOfConditions}
				{t(' condition(s) applied')}
			</span>
		);
	}
}

function getNumberOfConditions(state: State) {
	let numberOfConditions = 0;

	state.breakdowns.forEach(br => {
		if (br.sysId && br.elementSysIds && br.elementSysIds.length)
			numberOfConditions++;
	});

	return numberOfConditions;
}

function renderEverything(state: State, { dispatch }: Helpers<State>) {
	const {
		indicator,
		breakdowns,
		properties: {
			loadingBreakdownSelector,
			selectedIndicatorInfo: { uuid, isExpanded, isEditMode },
		},
	} = state;

	if (
		indicator !== uuid.indicator ||
		breakdowns.length === 0 ||
		loadingBreakdownSelector
	) {
		return null;
	}

	const noBreakdownsAvailable =
		breakdowns[0].breakdownOptions &&
		breakdowns[0].breakdownOptions.length === 0;

	if (noBreakdownsAvailable) {
		return (
			<div className="filter-accordion">
				<div className="filter-header">
					<div className="filter-header-label disabled">
						<now-icon
							className="icon-filter"
							icon="filter-fill"
							size="md"></now-icon>
						<span className="filter-title">{t('Filters')}</span>
						<span className="filter-subtitle">
							{t(
								'No breakdowns defined. So you cant filter, and some visualization types wont be available.'
							)}
						</span>
					</div>
				</div>
			</div>
		);
	}

	return (
		<div className="filter-accordion">
			<div className={'filter-header' + (isExpanded ? ' expanded' : '')}>
				<button
					className="filter-header-label"
					on-click={() => {
						dispatch(BREAKDOWN_CHANGED, { isExpanded: !isExpanded });
					}}>
					<now-icon
						className="icon-filter"
						icon="filter-fill"
						size="md"></now-icon>
					<span className="filter-title">{t('Filters')}</span>
					{getCount(state)}
				</button>
				<div className="filter-header-action">
					<now-button-bare
						variant="primary"
						append-to-payload={{
							payloadActionType: 'editFilter',
						}}
						hide-padding
						icon-start="pencil-fill"
						size="md"
						disabled={isEditMode}
						label={t('Edit')}
					/>
				</div>
			</div>
			<now-collapse expanded={isExpanded}>
				<div className="filter-content">
					{isEditMode ? EditFilterView(state) : DisplayFilterView(state)}
				</div>
			</now-collapse>
		</div>
	);
}
