// @flow
import get from 'lodash/get';
import { createGraphQLEffect } from '@servicenow/ui-effect-graphql';
import {
	actionTypes,
	type Coeffects,
	type DeclarativeStateUpdate,
} from '@servicenow/ui-core';
import {
	TABLES_REQUESTED,
	SEARCH_SOURCE,
	COMPONENT_INIT,
	TABLES_LOADED,
	TABLES_FAILED,
	INDICATORS_REQUESTED,
	INDICATORS_LOADED,
	INDICATORS_FAILED,
	SUGGESTED_REQUESTED,
	SUGGESTED_LOADED,
	SUGGESTED_FAILED,
	TABLES,
	INDICATORS,
	SUGGESTED,
	TABLES_ID,
	INDICATORS_ID,
	SUGGESTED_ID,
	LIMIT,
	NAME_LIKE,
	SOURCE_TYPE_ICONS,
	INDICATOR_SUGGESTED_LOADED,
	INDICATOR_SUGGESTED_REQUESTED,
	INDICATOR_SUGGESTED_FAILED,
	MAP_SOURCE_TYPE_TO_ID,
	INSUFFICIENT_ROLES,
} from './constants';
import {
	SOURCE_TYPE_TABLE,
	SOURCE_TYPE_INDICATOR,
} from '../common/types/data-source';
import { VIZ_CONFIG_SOURCE_SELECT_SELECTED } from './constants';
import { type State, type ContentTreeItem } from './state';
import {
	setItemChildren,
	removePath,
	hasPath,
	clearItemChildren,
} from './utils';
import { effectProxy } from '../helpers/seismic';

const pathToIndexMap: Object = {
	[SUGGESTED_ID]: 0,
	[TABLES_ID]: 1,
	[INDICATORS_ID]: 2,
};

const handleLoadingRequested = ({
	state,
	action,
	dispatch,
	updateState,
}: Coeffects<State, any>) => {
	action.stopPropagation();
	const { loadingPath, isLoadingTables } = state;
	const { item, path } = action.payload;
	const start = (item.children || []).length;

	if (path[0] === TABLES_ID && !isLoadingTables) {
		dispatch(TABLES_REQUESTED, {
			start,
			count: LIMIT + 1,
			nameContains: state.searchQuery,
		});
	} else if (path[0] === INDICATORS_ID) {
		dispatch(INDICATORS_REQUESTED, {
			start,
			count: LIMIT + 1,
			queryConditions: `${NAME_LIKE}${state.searchQuery}`,
		});
	} else if (path[0] === SUGGESTED_ID) {
		dispatch(SUGGESTED_REQUESTED, {
			nameContains: state.searchQuery,
		});
	}

	const loadingArray: any = [...removePath(loadingPath, path), { path }];

	updateState({
		...(path[0] === TABLES_ID && { isLoadingTables: true }),
		loadingPath: loadingArray,
	});
};

const handleLoadingCancelled = ({
	state,
	action,
	updateState,
}: Coeffects<State, any>) => {
	action.stopPropagation();
	const { loadingPath } = state;
	const { path } = action.payload;

	updateState({
		loadingPath: [...removePath(loadingPath, path)],
	});
};

const handleExpandedItemsSet = ({
	action,
	updateState,
}: Coeffects<State, any>) => {
	action.stopPropagation();
	updateState({
		expandedItems: action.payload.value,
	});
};

const handleItemClicked = ({ action, dispatch }: Coeffects<State, any>) => {
	action.stopPropagation();
	const item = action.payload.item;

	if (item.hasOwnProperty('children')) return;

	const id = item.id.split(':')[1];
	const displayLabel = item.label;

	dispatch(VIZ_CONFIG_SOURCE_SELECT_SELECTED, {
		id,
		sourceType: item.type,
		displayLabel,
	});
};

const getSourceItems = (
	results: any,
	path: string,
	allowedSourceType?: string
): ContentTreeItem[] => {
	if (results && results.length === LIMIT + 1) {
		results.pop();
	}

	const mappedResults = results.map(result => {
		const item: ContentTreeItem = {};

		item.id = `${path}:${result.id}`;
		item.label = result.label;

		if (path === SUGGESTED_ID) {
			const sourceType =
				result.type === 'table' ? SOURCE_TYPE_TABLE : SOURCE_TYPE_INDICATOR;

			item.icon = SOURCE_TYPE_ICONS[sourceType];
			item.type = sourceType;
		} else if (path === TABLES_ID) {
			item.type = SOURCE_TYPE_TABLE;
		} else if (path === INDICATORS_ID) {
			item.type = SOURCE_TYPE_INDICATOR;
		}

		return item;
	});

	/**
	 * FE Filter for suggested when user is editing or adding one
	 * Data source and we have to limit the type
	 */
	if (path === SUGGESTED_ID && allowedSourceType !== undefined) {
		return mappedResults.filter(item => item.type === allowedSourceType);
	}

	return mappedResults;
};

const handleIndicatorSuggestedLoaded = ({
	state,
	action,
	updateState,
	dispatch,
}: Coeffects<State, any>) => {
	const { loadingPath } = state;

	let { items } = state;

	const indicators =
		get(action, 'payload.data.indicators.results.values') || [];

	const hasAccessToIndicators =
		get(action, 'payload.data.indicators.results.result') !==
		INSUFFICIENT_ROLES;

	const indicatorCount = indicators.length;

	items = setItemChildren(
		state.items,
		[INDICATORS_ID],
		getSourceItems(indicators, INDICATORS_ID),
		indicatorCount
	);

	const suggested = get(action, 'payload.data.suggested.results') || [];
	const suggestedCount = suggested.length;

	items = setItemChildren(
		items,
		[SUGGESTED_ID],
		getSourceItems(suggested, SUGGESTED_ID),
		suggestedCount
	);

	updateState({
		...state,
		isLoading: false,
		loadingPath: removePath(loadingPath, [INDICATORS_ID, SUGGESTED_ID]),
		items,
		hasAccessToIndicators,
	});

	if (suggestedCount > 0) {
		dispatch('NOW_CONTENT_TREE#EXPANDED_ITEMS_SET', {
			value: [[SUGGESTED_ID]],
		});

		const id = state.properties.id;

		dispatch('NOW_CONTENT_TREE#SELECTED_ITEMS_SET', {
			value: [[SUGGESTED_ID, `${SUGGESTED_ID}:${id}`]],
		});
	}
};

const handleDataLoaded = ({
	state,
	action,
	updateState,
	dispatch,
}: Coeffects<State, any>) => {
	let path = '';

	switch (action.type) {
		case TABLES_LOADED:
			path = TABLES_ID;
			break;
		case INDICATORS_LOADED:
			path = INDICATORS_ID;
			break;
		case SUGGESTED_LOADED:
			path = SUGGESTED_ID;
			break;
	}

	const { allowedSourceType } = state.properties;
	const mappedSourceTypeLimit = MAP_SOURCE_TYPE_TO_ID[allowedSourceType];

	const results =
		get(
			action,
			action.type === SUGGESTED_LOADED
				? 'payload.data.AppPARConfig_Query.results'
				: 'payload.data.AppPARConfig_Query.results.values'
		) || [];

	let count = results && results.length;

	if (count === LIMIT + 1) {
		results.pop();
	}

	const children: ContentTreeItem[] = getSourceItems(
		results,
		path,
		allowedSourceType
	);

	if (allowedSourceType !== undefined) {
		if (path === SUGGESTED_ID) {
			count = children.length;
		}

		if (path === mappedSourceTypeLimit) {
			const id = state.properties.id;

			dispatch('NOW_CONTENT_TREE#SELECTED_ITEMS_SET', {
				value: [
					[SUGGESTED_ID, `${SUGGESTED_ID}:${id}`],
					[mappedSourceTypeLimit, `${mappedSourceTypeLimit}:${id}`],
				],
			});
		}
	}

	if (state.searchQuery || action.type == SUGGESTED_LOADED) {
		const pathToExpand =
			action.type == SUGGESTED_LOADED &&
			children.length <= 0 &&
			allowedSourceType !== undefined
				? mappedSourceTypeLimit
				: path;

		const pushToExpandedItems: DeclarativeStateUpdate = {
			path: 'expandedItems',
			operation: 'push',
			value: [pathToExpand],
		};

		updateState(pushToExpandedItems);
	}

	const { items, loadingPath } = state;
	const index = pathToIndexMap[path];

	/**
	 * Update items separately via declarative update
	 * To avoid cloning & racing condition which would
	 * Erase other or not assign other values if 2 or more sources
	 * are being loaded at the same time (i.e.: first time render)
	 */
	updateState({
		operation: 'merge',
		path: `items[${index}]`,
		value: {
			children: [...(items[index].children || []), ...children],
			childrenAvailable: (items[index].children || []).length + count,
		},
	});

	updateState({
		isLoading: false,
		loadingPath: removePath(loadingPath, [path]),
		...(path === TABLES_ID && { isLoadingTables: false }),
	});
};

const handleChildrenFailed = ({
	state,
	action,
	updateState,
}: Coeffects<State, any>) => {
	let path;

	switch (action.type) {
		case TABLES_FAILED: {
			path = [TABLES_ID];
			break;
		}
		case INDICATORS_FAILED: {
			path = [INDICATORS_ID];
			break;
		}
		default:
			return;
	}

	const { loadingPath } = state;

	// Only mark the path with a loading error if it in the loadingPath list
	// If it has been removed from the loadingPath list, loading was cancelled
	if (hasPath(loadingPath, path))
		updateState({
			loadingPath: [...removePath(loadingPath, path), { path, error: true }],
		});
};

const handleTablesRequest = createGraphQLEffect(
	`query($start: Int, $count: Int, $nameContains: String) {
		AppPARConfig_Query {
			results: tables(start: $start, count: $count, nameContains: $nameContains) {
				values {
					id: name
					label
				}
				result
			}
		}
	}`,
	{
		variableList: ['start', 'count', 'nameContains'],
		successActionType: TABLES_LOADED,
		errorActionType: TABLES_FAILED,
	}
);

const handleIndicatorsRequest = createGraphQLEffect(
	`query($start: Int, $count: Int, $queryConditions: String){
		AppPARConfig_Query {
			results: indicators(start: $start, count: $count, queryConditions: $queryConditions) {
				values {
					id: sysID
					label: name
				}
				result
			}
		}
	}`,
	{
		templateVarList: ['tableName', 'id', 'label'],
		variableList: ['start', 'count', 'queryConditions'],
		successActionType: INDICATORS_LOADED,
		errorActionType: INDICATORS_FAILED,
	}
);

const handleSuggestedRequest = createGraphQLEffect(
	`query($nameContains: String) {
		AppPARConfig_Query {
			results: suggestions(nameContains: $nameContains) {
				type
				id
				label
			}
		}
	}`,
	{
		variableList: ['nameContains'],
		successActionType: SUGGESTED_LOADED,
		errorActionType: SUGGESTED_FAILED,
	}
);

export const handleIndicatorSuggestedRequested = createGraphQLEffect(
	`query($count: Int) {
		indicators: AppPARConfig_Query {
			results: indicators(start: 0, count: $count) {
				values {
					id: sysID
					label: name
				}
				result
			}
		}
		suggested: AppPARConfig_Query {
			results: suggestions {
				type
				id
				label
			}
		}
	}`,
	{
		variableList: ['count'],
		successActionType: INDICATOR_SUGGESTED_LOADED,
		errorActionType: INDICATOR_SUGGESTED_FAILED,
	}
);

const handleSearch = ({
	dispatch,
	action,
	updateState,
	state,
}: Coeffects<State, any>) => {
	let items: ContentTreeItem[] = [...state.items];

	items = clearItemChildren(items, [TABLES_ID]);
	items = clearItemChildren(items, [INDICATORS_ID]);
	items = clearItemChildren(items, [SUGGESTED_ID]);
	updateState({
		searchQuery: action.payload && action.payload.trim(),
		items,
		expandedItems: [],
		loadingPath: [],
	});
	dispatch(COMPONENT_INIT, { isLoadSearchResult: true });
};

const handleComponentInit = ({
	state,
	dispatch,
	updateState,
	action,
}: Coeffects<State, any>) => {
	const { isLoadSearchResult } = action.payload;
	const { allowedSourceType } = state.properties;
	const mappedSourceTypeId = MAP_SOURCE_TYPE_TO_ID[allowedSourceType];
	const suggestedObject: ContentTreeItem = {
		id: SUGGESTED_ID,
		label: SUGGESTED,
		icon: 'lightbulb-outline',
		childrenAvailable: 0,
		actions: [
			{
				label: SUGGESTED,
			},
		],
	};
	const tableObject: ContentTreeItem = {
		id: TABLES_ID,
		label: TABLES,
		icon: 'list-fill',
		childrenAvailable:
			isLoadSearchResult ||
			(allowedSourceType && mappedSourceTypeId !== TABLES_ID)
				? 0
				: 1,
		...(!isLoadSearchResult && { children: [] }),
		actions: [
			{
				label: TABLES,
			},
		],
	};
	const indicatorObject: ContentTreeItem = {
		id: INDICATORS_ID,
		label: INDICATORS,
		icon: 'analytics-center-outline',
		childrenAvailable: 0,
		actions: [
			{
				label: INDICATORS,
			},
		],
	};

	const items = [suggestedObject, tableObject, indicatorObject];

	updateState({
		isLoading: true,
		items,
	});

	if (isLoadSearchResult && !allowedSourceType) {
		dispatch('NOW_CONTENT_TREE#LOADING_REQUESTED', {
			path: [TABLES_ID],
			item: [],
		});

		dispatch('NOW_CONTENT_TREE#LOADING_REQUESTED', {
			path: [INDICATORS_ID],
			item: [],
		});

		dispatch('NOW_CONTENT_TREE#LOADING_REQUESTED', {
			path: [SUGGESTED_ID],
			item: [],
		});
	} else if (allowedSourceType || isLoadSearchResult) {
		dispatch('NOW_CONTENT_TREE#LOADING_REQUESTED', {
			path: [mappedSourceTypeId],
			item: [],
		});

		dispatch('NOW_CONTENT_TREE#LOADING_REQUESTED', {
			path: [SUGGESTED_ID],
			item: [],
		});
	} else
		dispatch(INDICATOR_SUGGESTED_REQUESTED, {
			count: LIMIT + 1,
		});
};

const handleItemSet = ({ action, updateState }: Coeffects<State, any>) => {
	updateState({
		operation: 'set',
		value: action.payload.value,
		path: 'selectedItems',
	});
};

export default {
	actionHandlers: {
		'NOW_CONTENT_TREE#EXPANDED_ITEMS_SET': handleExpandedItemsSet,
		'NOW_CONTENT_TREE#LOADING_REQUESTED': handleLoadingRequested,
		'NOW_CONTENT_TREE#LOADING_CANCELLED': handleLoadingCancelled,
		'NOW_CONTENT_TREE#ITEM_CLICKED': handleItemClicked,
		'NOW_CONTENT_TREE#SELECTED_ITEMS_SET': handleItemSet,
		[INDICATOR_SUGGESTED_REQUESTED]: effectProxy(
			handleIndicatorSuggestedRequested
		),
		[INDICATOR_SUGGESTED_LOADED]: handleIndicatorSuggestedLoaded,
		[INDICATOR_SUGGESTED_FAILED]: handleChildrenFailed,
		[TABLES_REQUESTED]: handleTablesRequest,
		[TABLES_LOADED]: handleDataLoaded,
		[TABLES_FAILED]: handleChildrenFailed,
		[INDICATORS_REQUESTED]: handleIndicatorsRequest,
		[INDICATORS_LOADED]: handleDataLoaded,
		[INDICATORS_FAILED]: handleChildrenFailed,
		[SUGGESTED_REQUESTED]: handleSuggestedRequest,
		[SUGGESTED_LOADED]: handleDataLoaded,
		[SUGGESTED_FAILED]: handleChildrenFailed,
		[actionTypes.COMPONENT_BOOTSTRAPPED]: handleComponentInit,
		[COMPONENT_INIT]: handleComponentInit,
		[SEARCH_SOURCE]: handleSearch,
	},
};
