//@flow
import { type OptionItem } from './state';

let cache: { [key: string]: OptionItem[] } = {};

export function invalidateCache() {
	cache = {};
}

export function getBreakdownOptionsFromCache(uuid: string) {
	return cache[uuid];
}

export function setBreakdownOptionsToCache(
	uuid: string,
	options: OptionItem[]
) {
	cache[uuid] = options;
}

export function getElementOptionsFromCache(uuid: string, sysId: string) {
	return cache[uuid + '->' + sysId];
}

export function setElementOptionsToCache(
	uuid: string,
	sysId: string,
	options: OptionItem[]
) {
	cache[uuid + '->' + sysId] = options;
}
