// @flow

// @jsx snabbdom.createElement
import snabbdom, { Fragment } from '@servicenow/ui-renderer-snabbdom'; // eslint-disable-line no-unused-vars
import { t } from 'sn-translate';
import { type State } from './state';
import '@servicenow/now-accordion';
import '@servicenow/now-dropdown';
import { autoScrollToVisibility } from '../helpers/now-accordion/auto-scroll-to-visibility';
import 'sn-devx-components';
import {
	COLOR_TYPE_SINGLE,
	COLOR_TYPE_PALETTE,
	COLOR_CHOICES,
	COLOR_TYPE_REQUESTED,
	LOAD_COLOR_REQUESTED,
} from './constants';
import { CHART_TYPES } from '../common/types/chart-type';
import { SOURCE_TYPE_INDICATOR } from '../common/types/data-source';
import { getMetricLabel } from '../helpers/metric/metric-label';

import '../color-rules';

import { type Metric } from '../common/types/metric';

export const colorPaletteView = (state: State) => {
	const {
		properties: {
			chartModel: { chartType, chartSettings, dataSources, metrics },
			accordionState,
			dataSourceInfos,
			singleColors,
			paletteColors,
			fieldDefinitions,
		},
	} = state;

	const colorType =
		chartSettings && chartSettings.color && chartSettings.color.type;
	const colorId =
		chartSettings && chartSettings.color && chartSettings.color.sys_id;
	const singleScoreAndIndicator =
		dataSources[0].sourceType === SOURCE_TYPE_INDICATOR &&
		chartType === CHART_TYPES.SINGLE_SCORE;
	const singleScoreAndReport =
		dataSources[0].sourceType !== SOURCE_TYPE_INDICATOR &&
		chartType === CHART_TYPES.SINGLE_SCORE;

	const colorItems = COLOR_CHOICES.filter(o => {
		return (
			!o.extraVerification || o.extraVerification(state.properties.chartModel)
		);
	});

	let numberOfColorSeries: Metric[] =
		colorType === COLOR_TYPE_PALETTE
			? [{ colorSysID: colorId, dataSource: dataSources[0].id }]
			: metrics;

	if (!singleScoreAndIndicator) {
		return (
			<now-accordion-item
				id="color-accordion"
				dividers={{ top: false, bottom: true }}
				header={t('Colors')}
				expanded={accordionState.color}
				append-to-payload={{ accordionItem: 'color' }}
				on-click={autoScrollToVisibility}>
				<div className="now-accordion-content" slot="content">
					<now-label-value-inline
						className="section-heading"
						label={t('Set color type')}
					/>
					<now-dropdown
						className="set-color-type-dropdown"
						append-to-payload={{
							payloadActionType: COLOR_TYPE_REQUESTED,
						}}
						items={colorItems}
						select="single"
						selected-items={[colorType]}
						size="md"
						variant="tertiary"
						config-aria={{
							'aria-label': t('Select color type'),
						}}
					/>
					{colorType === COLOR_TYPE_SINGLE || colorType === COLOR_TYPE_PALETTE
						? numberOfColorSeries.map((metric, index) => {
								const metricLabel = getMetricLabel(
									dataSources,
									dataSourceInfos,
									metric,
									fieldDefinitions
								);

								return (
									<div className="color-and-rules">
										<now-select
											id={`color-select-${index}`}
											label={
												colorType === COLOR_TYPE_SINGLE
													? metrics.length > 1
														? t('Color for ') + metricLabel
														: t('Color')
													: t('Palette')
											}
											append-to-payload={{
												payloadActionType: LOAD_COLOR_REQUESTED,
												type: colorType,
												metricIndex: index,
											}}
											items={
												colorType === COLOR_TYPE_SINGLE
													? singleColors
													: paletteColors
											}
											placeholder={t('Set color')}
											selected-item={metric.colorSysID}
											search="contains"
										/>
										{singleScoreAndReport ? (
											<viz-config-color-rules
												singleColors={singleColors}
												chartModel={state.properties.chartModel}
												metric={metric}
												metricIndex={index}
											/>
										) : null}
									</div>
								);
						  })
						: ''}
				</div>
			</now-accordion-item>
		);
	}
};
