// @flow
// @jsx snabbdom.createElement
import '@servicenow/now-select';
import '../dot-walk';

import { type FieldDefinition } from '../common/types/field';

import { createCustomElement } from '@servicenow/ui-core';
import snabbdom, { Fragment } from '@servicenow/ui-renderer-snabbdom'; // eslint-disable-line no-unused-vars
import { t } from 'sn-translate';

import styles from './styles.scss';

import {
	APPLY_GROUP_BY_POPOVER,
	CLOSE_GROUP_BY_POPOVER,
	PILLAR_ADD_ACTION,
	STACK_BY_FIELD,
} from './constants';

import { SOURCE_TYPE_INDICATOR } from '../common/types/data-source';

import { createTransformMetadataFn } from './helpers/transform-metadata';

createCustomElement('viz-config-stack-by-popover-content', {
	renderer: { type: snabbdom },
	view: state => {
		const {
			properties: {
				sharedState: {
					properties: { chartModel },
					popoverAction,
					dataToApply,
					errorMessages,
					localOptions,
				},
			},
		} = state;

		const { dataSources } = chartModel;

		const transformMetadataFilter = (_, fieldDefinition: FieldDefinition) =>
			fieldDefinition.canGroupBy;

		const transformMetadata = createTransformMetadataFn(
			transformMetadataFilter
		);

		const isIndicator = dataSources.some(
			dataSource => dataSource.sourceType === SOURCE_TYPE_INDICATOR
		);

		const isApplyDisabled = dataSources.some((ds, index) => {
			const fieldName = `${STACK_BY_FIELD}${index}`;

			return !dataToApply[fieldName];
		});

		return (
			<div className="viz-config-group-by-popover">
				<now-heading
					label={
						popoverAction === PILLAR_ADD_ACTION
							? t('Add group by')
							: t('Edit group by')
					}
					level="3"
					variant="title-secondary"
				/>
				<now-button-iconic
					bare
					icon="close-fill"
					size="md"
					variant="primary"
					append-to-payload={{
						payloadActionType: CLOSE_GROUP_BY_POPOVER,
					}}
					config-aria={{ 'aria-label': t('Close') }}
				/>
				{dataSources.map((dataSource, index) => {
					const fieldName = `${STACK_BY_FIELD}${index}`;
					const options = localOptions[fieldName] || [];
					const message = errorMessages[fieldName];
					const messages = message
						? [{ status: 'critical', content: message }]
						: undefined;

					if (isIndicator) {
						return (
							<now-select
								id={fieldName}
								label={t('Field for {0}', dataSource.label)}
								append-to-payload={{
									fieldName,
								}}
								items={options}
								placeholder={t('Select a field')}
								selected-item={
									dataToApply[fieldName] || dataSource.stackByField
								}
								messages={messages}
								search="contains"
								required
							/>
						);
					}

					return (
						<viz-config-dot-walk
							key={fieldName}
							id={fieldName}
							label={t('Field for {0}', dataSource.label)}
							placeholder={t('Select a field')}
							tableName={dataSource.tableName}
							dotWalkPath={dataToApply[fieldName]}
							transform-metadata={transformMetadata.bind(null, dataSource)}
							append-to-payload={{
								fieldName,
								dataSource,
							}}
						/>
					);
				})}
				<now-button
					id="apply-stack-by"
					label={t('Apply')}
					size="md"
					variant="primary"
					disabled={isApplyDisabled}
					append-to-payload={{
						payloadActionType: APPLY_GROUP_BY_POPOVER,
					}}
				/>
			</div>
		);
	},
	properties: {
		sharedState: {
			default: {},
		},
	},
	styles,
});
