export const autoScrollToVisibility = e => {
	if (e.detail > 1) return; // Suppress double and tripple clicks

	const ref = e.target;
	const path = e.composedPath();

	if (!e.target.expanded) {
		setTimeout(() => {
			scrollToVisitibility(ref, path);
		}, 400);
	}
};

const scrollToVisitibility = (accordionEl, path) => {
	// Just scroll if user clicked on the accordion trigger
	const accordionTrigger = path.find(({ id }) => id === 'accordion-trigger');

	if (!accordionTrigger) {
		return;
	}

	const accordionItem = path.find(
		({ tagName }) => tagName === 'NOW-ACCORDION-ITEM'
	);

	if (!accordionItem) {
		return;
	}

	accordionItem.scrollIntoView({
		behavior: 'smooth',
		block: 'nearest',
		inline: 'start',
	});
};
