//@flow

import { actionTypes, type Coeffects } from '@servicenow/ui-core';
import { type State } from './state';
import { type ColorRule } from '../common/types/color-rules';
import {
	ADD_COLOR_RULE,
	SET_OPERATOR,
	BTN_ADD_COLOR_RULE,
	BTN_POPOVER_APPLY,
	BTN_POPOVER_CLOSE,
	COLOR_RULE_EDIT,
	COLOR_RULE_DELETE,
	DELETE_COLOR_RULE,
	EDIT_COLOR_RULE,
	FIELD_OPERATOR_BETWEEN,
	FIELD_VALUE2,
	RULE_POPOVER,
} from './constants';

import {
	SN_PAR_PILLAR_CLICKED,
	SN_PAR_PILLAR_ACTION_CLICKED,
} from '@now-par-components/sn-par-pillar';

const isValue1GreaterThanValue2 = ({
	operator,
	value1,
	value2,
}: $Shape<ColorRule>) => {
	return (
		operator === FIELD_OPERATOR_BETWEEN &&
		value1 !== undefined &&
		value2 !== undefined &&
		value1 > value2
	);
};

const handleApply = ({
	state,
	updateState,
	dispatch,
}: Coeffects<State, any>) => {
	const { dataToApply, colorRuleIndex } = state;

	const { order, value1, value2, operator } = dataToApply;

	const newInvalidInput = {
		order: order === undefined,
		value1: value1 === undefined,
		value2:
			operator === FIELD_OPERATOR_BETWEEN &&
			(value2 === undefined || isValue1GreaterThanValue2(dataToApply)),
	};

	if (
		newInvalidInput.order ||
		newInvalidInput.value1 ||
		newInvalidInput.value2
	) {
		updateState({
			invalidInput: newInvalidInput,
		});
		return;
	}

	if (colorRuleIndex === undefined)
		dispatch(ADD_COLOR_RULE, {
			colorRule: dataToApply,
			metricIndex: state.properties.metricIndex,
		});
	else
		dispatch(EDIT_COLOR_RULE, {
			colorRule: dataToApply,
			metricIndex: state.properties.metricIndex,
			colorRuleIndex: colorRuleIndex,
		});

	updateState(getDefaultClosedPopoverState(state));
};

const deleteColorRule = ({
	state,
	updateState,
	dispatch,
	action,
}: Coeffects<State, any>) => {
	const colorRuleIndex = action.payload.colorRuleIndex;

	dispatch(DELETE_COLOR_RULE, {
		colorRuleIndex,
		metricIndex: state.properties.metricIndex,
	});

	updateState(getDefaultClosedPopoverState(state));
};

const getDefaultClosedPopoverState = (state: State) => {
	return {
		...state,
		popoverOpened: false,
		popoverClosedAt: new Date().getTime(),
		colorRuleIndex: undefined,
		dataToApply: {},
		invalidInput: {
			value1: false,
			value2: false,
			order: false,
		},
	};
};

const toggleColorRulePopover = ({
	state,
	updateState,
	action,
}: Coeffects<State, any>) => {
	const { colorRuleIndex } = action.payload;
	const {
		properties: { metric },
		componentHost,
		popoverOpened,
	} = state;
	const selectorPopoverTarget =
		colorRuleIndex === undefined
			? '#btn-add-color-rule'
			: `#color-rule-pillar-${colorRuleIndex}`;

	let dataToApply;

	if (
		colorRuleIndex === undefined ||
		!metric.colorRules ||
		metric.colorRules.length <= colorRuleIndex
	)
		dataToApply = {};
	else dataToApply = metric.colorRules[colorRuleIndex];

	updateState({
		dataToApply,
		colorRuleIndex,
		popoverTarget:
			componentHost &&
			componentHost.shadowRoot &&
			componentHost.shadowRoot.querySelector(selectorPopoverTarget),
		popoverOpened: !popoverOpened,
	});
};

const handleButtonClicked = (coeffects: Coeffects<State, any>) => {
	switch (coeffects.action.payload.id) {
		case BTN_POPOVER_APPLY:
			handleApply(coeffects);
			break;

		case BTN_ADD_COLOR_RULE:
			toggleColorRulePopover(coeffects);
			break;
	}
};

const handleButtonIconicClicked = (coeffects: Coeffects<State, any>) => {
	const { updateState, state } = coeffects;

	switch (coeffects.action.payload.id) {
		case BTN_POPOVER_CLOSE:
			updateState(getDefaultClosedPopoverState(state));
			break;
	}
};

const handleInputValueSet = ({
	action,
	state,
	updateState,
}: Coeffects<State, any>) => {
	if (!action.payload.name) return;

	const { dataToApply, invalidInput } = state;

	action.stopPropagation();

	const { name, value } = action.payload;

	if (!name || value === undefined) return;

	const newDataToApply = {
		...dataToApply,
		[name]: Number(value),
	};

	updateState({
		dataToApply: newDataToApply,
		invalidInput: {
			...invalidInput,
			[name]:
				name === FIELD_VALUE2 && isValue1GreaterThanValue2(newDataToApply),
		},
	});
};

const handleInvalidInputValueSet = ({
	action,
	state,
	updateState,
}: Coeffects<State, any>) => {
	if (!action.payload.name) return;

	const { dataToApply, invalidInput } = state;

	action.stopPropagation();

	const { name, value, fieldValue } = action.payload;

	if (!name || !value || fieldValue === undefined) return;

	updateState({
		dataToApply: {
			...dataToApply,
			[name]: undefined,
		},
		invalidInput: {
			...invalidInput,
			[name]: true,
		},
	});
};

const handleSetOperator = ({
	action,
	state,
	updateState,
}: Coeffects<State, any>) => {
	const { dataToApply } = state;
	const field = action.payload.field;

	if (!field) return;

	updateState({
		dataToApply: {
			...dataToApply,
			[field]: action.payload.value[0],
		},
	});
};

const handleNowDropdownItemSelected = (coeffects: Coeffects<State, any>) => {
	switch (coeffects.action.payload.payloadActionType) {
		case SET_OPERATOR:
			handleSetOperator(coeffects);
			break;
	}
};

const handleDropdownSelection = ({
	action,
	state,
	updateState,
}: Coeffects<State, any>) => {
	const { dataToApply } = state;

	action.stopPropagation();

	const field = action.payload.field;

	if (!field) return;

	updateState({
		dataToApply: {
			...dataToApply,
			[field]: action.payload.value,
		},
	});
};

const handlePillarActionClicked = (coeffects: Coeffects<State, any>) => {
	switch (coeffects.action.payload.id) {
		case COLOR_RULE_EDIT:
			toggleColorRulePopover(coeffects);
			break;

		case COLOR_RULE_DELETE:
			deleteColorRule(coeffects);
			break;
	}
};

const handlePopoverOpenedSet = ({
	updateState,
	action,
}: Coeffects<State, any>) => {
	if (action.payload.type === RULE_POPOVER && !action.payload.value) {
		updateState({
			popoverOpened: false,
			dataToApply: {},
		});
	}
};

export default {
	actionHandlers: {
		[actionTypes.COMPONENT_BOOTSTRAPPED]: ({
			action,
			updateState,
		}: Coeffects<State, any>) => {
			updateState({ componentHost: action.payload.host });
		},
		'NOW_POPOVER#OPENED_SET': handlePopoverOpenedSet,
		'NOW_BUTTON#CLICKED': handleButtonClicked,
		'NOW_BUTTON_ICONIC#CLICKED': handleButtonIconicClicked,
		'NOW_INPUT#VALUE_SET': handleInputValueSet,
		'NOW_INPUT#INVALID_SET': handleInvalidInputValueSet,
		'NOW_DROPDOWN#SELECTED_ITEMS_SET': handleNowDropdownItemSelected,
		'NOW_SELECT#SELECTED_ITEM_SET': handleDropdownSelection,
		[SN_PAR_PILLAR_ACTION_CLICKED]: handlePillarActionClicked,
		[SN_PAR_PILLAR_CLICKED]: toggleColorRulePopover,
	},
};
