/**
 This is INTERNAL chart types which we use in our specific logic
 Now they are the same as chart types consumed
 by visualization component and it is super confusing

 TODO: prefix all OUR chart types with something like "viz-config-" or "$"
 TODO: use proper constants in all the code
 */
export type ChartType =
	| 'bar'
	| 'horizontal_bar'
	| 'pie'
	| 'donut'
	| 'semi_donut'
	| 'single_score'
	| 'line'
	| 'area'
	| 'spline'
	| 'step_line'
	| 'column';

export const CHART_TYPES = {
	BAR: 'bar',
	HORIZONTAL_BAR: 'horizontal_bar',
	PIE: 'pie',
	DONUT: 'donut',
	SEMI_DONUT: 'semi_donut',
	SINGLE_SCORE: 'single_score',
	LINE: 'line',
	AREA: 'area',
	SPLINE: 'spline',
	STEP_LINE: 'step_line',
	COLUMN: 'column',
};

export const MULTIPLE_DS_UNSUPPORTED = [
	CHART_TYPES.SINGLE_SCORE,
	CHART_TYPES.PIE,
	CHART_TYPES.DONUT,
	CHART_TYPES.SEMI_DONUT,
];
