//@flow

/**
 Helper function to help perform conditional rendering with snabbdom implementation of JSX
 The problem with snabbdom that is that it gives an error while rendering this:
 ```
	<div>
		{false}
	</div>
 ```

 This is a problem because in the React-world we have popular conditional-rendering pattern:
 ```
	<div>
		{!state.isFilterLoading && (
			<AwesomeFilterComponent filter={state.filter} />
		)}
	</div>
 ```

 In harsh snabbdom reality using this pattern will lead to a nasty error during rendering.
 We can work-around and use ternary operator:
 ```
 	<div>
		{!state.isFilterLoading ? (
			<AwesomeFilterComponent filter={state.filter} />
		) : (
			undefined
		)}
	</div>
 ```

 Or we can use magic jsxif and write it like this:
 ```
 	<div>
		{jsxif(!state.isFilterLoading) && (
			<AwesomeFilterComponent filter={state.filter} />
		)}
	</div>
```
 */
export function jsxif<T>(x: T) {
	return x || undefined;
}
