// @flow
// @jsx snabbdom.createElement
import '@servicenow/now-toggle';
import '../toggle';

import snabbdom, { Fragment } from '@servicenow/ui-renderer-snabbdom'; // eslint-disable-line no-unused-vars
import { t } from 'sn-translate';
import { getBooleanSettings } from './settings';
import { type State } from './state';
import { type Helpers } from '@servicenow/ui-core';
import '@servicenow/now-accordion';
import { autoScrollToVisibility } from '../helpers/now-accordion/auto-scroll-to-visibility';
import { isChecked, isDisabled } from './settings-helpers';
import { MetricLabelPosition } from './sub-settings/metric-label-position';
import { CHART_TYPES } from '../common/types/chart-type';

const renderSubSettings = (item, state, helpers) => {
	const {
		properties: { chartModel },
	} = state;

	switch (item.id) {
		case 'showMetricLabel':
			return chartModel.chartType === CHART_TYPES.SINGLE_SCORE
				? MetricLabelPosition(item, state, helpers)
				: undefined;

		default:
			return null;
	}
};

export default (state: State, helpers: Helpers<State>) => {
	const {
		properties: { chartModel, dataSourceInfos, expanded },
	} = state;

	const settings = getBooleanSettings(chartModel, dataSourceInfos);

	if (!settings.length) {
		return null;
	}

	return (
		<now-accordion-item
			dividers={{ top: false, bottom: true }}
			header={t('Additional settings')}
			expanded={expanded}
			append-to-payload={{ accordionItem: 'additionalSettings' }}
			on-click={autoScrollToVisibility}>
			<div className="now-accordion-content" slot="content">
				{settings.map(item => (
					<Fragment>
						<viz-config-toggle
							id={item.id}
							key={item.id}
							append-to-payload={{ id: item.id }}
							checked={isChecked(item.id, chartModel)}
							disabled={isDisabled(item.id, chartModel)}
							label={item.label}
						/>
						<div className="add-settings__sub-settings">
							{renderSubSettings(item, state, helpers)}
						</div>
					</Fragment>
				))}
			</div>
		</now-accordion-item>
	);
};
