// @flow
// @jsx snabbdom.createElement

import snabbdom, { Fragment } from '@servicenow/ui-renderer-snabbdom'; // eslint-disable-line no-unused-vars
import { type State } from './state';
import { type Helpers } from '@servicenow/ui-core';
import { t } from 'sn-translate';

import 'sn-devx-components';
import '@servicenow/now-accordion';
import '@servicenow/now-loader';
import '@servicenow/now-label-value';
import '@now-par-components/sn-par-pillar';

import './../source-configuration';
import { ConfigPanelEmptyStateView } from './config-panel-empty-view';
import { SOURCE_TYPE_ICONS } from '../source-selector/constants';
import { ADD_NEW_DATA_SOURCE, SAVE_LABEL } from './constants';
import { MULTIPLE_DS_UNSUPPORTED } from '../common/types/chart-type';
import { type DataSource } from '../common/types/data-source';
import { createSetFocusHandler } from '../helpers/element/focus';

export default function SourceListView(
	state: State,
	{ dispatch }: Helpers<State>
) {
	const {
		addNewDataSource,
		editDataSource,
		editDataSourceIndex,
		properties: { chartType, dataSources, dataSourceInfos, expanded },
		labelEditingOnIndex,
	} = state;

	if (!dataSources) {
		return undefined;
	}

	const addNewDSDisabled = MULTIPLE_DS_UNSUPPORTED.includes(chartType);

	const multiDataSourceNotSupportedMsg = t(
		"Single score, Pie, Donut and Semi donut chart types don't support multiple data sources"
	);

	const isEmptyDatasource = dataSources.length <= 0;

	const getPillarOptions = (dataSource: DataSource, index: number) => {
		if (labelEditingOnIndex === index) {
			return [
				{
					icon: 'check-fill',
					id: 'save-label',
					label: t('Save label change'),
				},
			];
		}

		return [];
	};

	const getPillarContextOptions = (dataSource: DataSource, index: number) => {
		if (labelEditingOnIndex === index) {
			return [];
		}

		return [
			{
				icon: 'sliders-outline',
				id: 'edit',
				label: t('Edit'),
			},
			{
				icon: 'pencil-page-outline',
				id: 'rename',
				label: t('Rename'),
			},
			{
				icon: 'trash-outline',
				id: 'remove',
				disabled: dataSources.length <= 1,
				label: t('Delete'),
			},
		];
	};

	const getPillarContent = (dataSource: DataSource, index: number) => {
		if (labelEditingOnIndex !== index) {
			return <div className="source-list--ds-name">{dataSource.label}</div>;
		}

		return (
			<div className="source-list__label-edit">
				<devx-input
					hook-insert={createSetFocusHandler(elm => elm.querySelector('input'))}
					className="viz-config-ds-label"
					inputValue={dataSource.label}
					placeholder={t('Data source label')}
					transparent={true}
					compact
					on-keydown={e => e.keyCode === 13 && dispatch(SAVE_LABEL, {})}
				/>
			</div>
		);
	};

	return (
		<Fragment>
			{isEmptyDatasource ? (
				ConfigPanelEmptyStateView()
			) : (
				<now-accordion
					hide-dividers
					heading-level="2"
					trigger-icon={{ type: 'chevron', position: 'end' }}>
					<now-accordion-item
						header={t('Data sources')}
						expanded={expanded}
						append-to-payload={{ accordionItem: 'dataSources' }}>
						<div className="source-list-container" slot="content">
							{dataSources.map((dataSource, dataSourceIndex) => (
								<sn-par-pillar
									key={dataSource.id}
									append-to-payload={{ dataSource, dataSourceIndex }}
									hook-insert={
										dataSourceIndex === 0 &&
										createSetFocusHandler(elm =>
											elm.querySelector('.sn-par-pillar')
										)
									}
									icon={SOURCE_TYPE_ICONS[dataSource.sourceType]}
									actions={getPillarOptions(dataSource, dataSourceIndex)}
									context-menu-actions={getPillarContextOptions(
										dataSource,
										dataSourceIndex
									)}
									compact={labelEditingOnIndex === dataSourceIndex}
									clickable={labelEditingOnIndex !== dataSourceIndex}>
									{getPillarContent(dataSource, dataSourceIndex)}
								</sn-par-pillar>
							))}
							<now-button
								className="btn-add-data-source"
								append-to-payload={{
									payloadActionType: ADD_NEW_DATA_SOURCE,
								}}
								icon="plus-fill"
								label={t('Add data source')}
								disabled={addNewDSDisabled}
								tooltipContent={
									addNewDSDisabled ? multiDataSourceNotSupportedMsg : undefined
								}
								size="sm"
								variant="secondary"></now-button>
						</div>
					</now-accordion-item>
				</now-accordion>
			)}
			{state.sourceConfigurationOpened ? (
				<viz-config-source-configuration
					operationMetadata={{
						addNewDataSource,
						editDataSourceIndex,
						allowedSourceType:
							dataSources.length > 1 ||
							(dataSources.length >= 1 && addNewDataSource)
								? dataSources[0].sourceType
								: undefined,
					}}
					dataSource={addNewDataSource ? undefined : editDataSource}
					dataSourceInfo={
						addNewDataSource
							? undefined
							: dataSourceInfos[editDataSource.id || '']
					}
				/>
			) : (
				undefined
			)}
		</Fragment>
	);
}
