// @flow

export type ContentTreeIdentifier = {|
	type: 'icon' | 'avatar',
	icon: string,
	userName: string,
	imageSrc: string,
	ariaHidden: string,
|};

export type ContentTreeAction = {|
	icon?: string,
	label?: string,
	clickActionType?: string,
	mouseenterActionType?: string,
	mouseleaveActionType?: string,
|};

export type ContentTreeHighlightedValue = {|
	label: string,
	width: string,
	status: string,
|};

export type ContentTreeItem = {
	id: string,
	label?: string,
	icon?: string,
	children?: ContentTreeItem[],
	childrenAvailable?: number,
	identifierProps?: ContentTreeIdentifier,
	actionable?: boolean,
	actions?: ContentTreeAction[],
	highlightedValueProps?: [ContentTreeHighlightedValue],
	disabled?: boolean,
	type?: string,
};

export type Properties = {|
	id: string,
	sourceType: string,
	allowedSourceType: string,
|};

export type LoadingItem = {
	path?: Array<string>,
	error?: boolean,
	errorMessage?: string | null,
};

export type State = {|
	items: ContentTreeItem[],
	searchQuery: string,
	selectedItems: Array<Array<string>>,
	expandedItems: Array<Array<string>>,
	loadingPath: LoadingItem[],
	properties: Properties,
	isLoading: boolean,
	isLoadingTables: boolean,
	hasAccessToIndicators: boolean,
|};

export const initialState: $Shape<State> = {
	searchQuery: '',
	items: [],
	selectedItems: [],
	expandedItems: [],
	loadingPath: [],
	isLoading: true,
	isLoadingTables: false,
	hasAccessToIndicators: true,
};
