// @flow

/**
 * Extract the attributes from FieldDefinition.attributes
 * @param {string} attributes
 * @return {object}
 */
export const getNamedAttributes = (
	attributes: string | void
): { [key: string]: string } => {
	const namedAttributes = {};

	if (attributes && attributes.length) {
		const pairs = attributes.split(',');

		for (var i = 0; i < pairs.length; i++) {
			const parts = pairs[i].split('=');

			if (parts.length === 2) {
				namedAttributes[parts[0]] = parts[1];
			}
		}
	}

	return namedAttributes;
};
