// @flow
import { createCustomElement } from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';

import { initialState } from './state';
import { lifeCycleActionHandlers } from './actions/lifecycle-action-handlers';
import { uiActionHandlers } from './actions/ui-action-handlers';
import { modelActionHandlers } from './actions/model-action-handlers';
import { dataSourceInfoActionHandlers } from './actions/data-source-info-handlers';
import { userDataSysPropertiesActionHandlers } from './actions/preferences-security-action-handlers';
import { colorPaletteActionHandlers } from './actions/color-palette-action-handlers';
import { fieldsActionHandlers } from './actions/fields-action-handlers';
import { MainView } from './views/main-view';
import styles from './styles.scss';

createCustomElement('now-uxf-visualization-config-panel', {
	renderer: { type: snabbdom },
	view: MainView,
	styles,
	initialState,
	transformState(state) {
		const loadingValues = Object.values(state.isLoadingDataSource);
		const isLoadingDataSource = loadingValues.some(Boolean);

		return {
			...state,
			isLoadingDataSource,
		};
	},
	properties: {
		label: {
			schema: {
				type: 'string',
			},
		},
		chartType: {
			schema: {
				type: 'string',
			},
		},
		dataConfigurations: {
			schema: {
				type: 'array',
			},
		},
		styleConfigurations: {
			schema: {
				type: 'object',
			},
		},
		isRealtime: {
			schema: {
				type: 'boolean',
			},
		},
		showRealtimeIndicator: {
			schema: {
				type: 'boolean',
			},
		},
		variant: {
			schema: {
				type: 'string',
			},
		},
		border: {
			schema: {
				type: 'boolean',
			},
		},
		refreshInterval: {
			schema: {
				type: 'string',
			},
		},
		refreshFrequency: {
			schema: {
				type: 'string',
			},
		},
		followFilters: {
			schema: {
				type: 'boolean',
			},
		},
		hideFiltersInfo: {
			schema: {
				type: 'boolean',
			},
		},
		filterConfigurations: {
			schema: {
				type: 'string',
			},
		},
		hideHeader: {
			schema: {
				type: 'boolean',
			},
		},
		pageMetadata: {
			default: '@uxf/pageMetadata',
		},
		elementId: {
			default: '@ui-state/selectedElementId',
		},
		elementStyles: {
			default: '@uxf/elementStyles',
		},
	},
	actions: {
		'DEVX_INPUT#INPUT_CHANGED': {
			private: true,
		},
		'DEVX_INPUT#INPUT_BLUR': {
			private: true,
		},
		'NOW_TABS#SELECTED_ITEM_SET': {
			private: true,
		},
	},
	actionHandlers: {
		...lifeCycleActionHandlers,
		...uiActionHandlers,
		...modelActionHandlers,
		...dataSourceInfoActionHandlers,
		...userDataSysPropertiesActionHandlers,
		...colorPaletteActionHandlers,
		...fieldsActionHandlers,
	},
});
