// @flow
import { type ChartModel } from '../visualizations-config-panel/model/chart-model';
import { type Option } from '../common/types/option';
import { type FieldDefinitions } from '../common/types/field';
import {
	type DataSourceInfos,
	type DataSource,
} from '../common/types/data-source';

export type TrendbyData = {
	trendByField?: string[],
	trendByLabel?: string[],
	trendByFrequency?: string,
};

export type TrendbyErrors = {
	fieldError?: string[],
	frequencyError?: string,
};

export type TrendbyTypeTrack = {
	fieldTyped?: boolean[],
	frequencyTyped?: boolean,
};

export type State = {
	componentHost?: HTMLElement,
	popoverOpened?: boolean,
	popoverClosedAt: number,
	popoverTarget?: HTMLElement,
	localTrendByList: {
		dataSource: DataSource,
		hasReferenceFields: boolean,
		label: string,
		options: Option[],
	}[],
	dataToApply?: TrendbyData,
	typingTrack: TrendbyTypeTrack,
	errorMessages: TrendbyErrors,
	properties: {
		dataSourceInfos: DataSourceInfos,
		chartModel: ChartModel,
		fieldDefinitions: FieldDefinitions,
		expanded: boolean,
	},
};

export const initialState: State = {
	properties: {},
	typingTrack: {},
	errorMessages: {},
	popoverClosedAt: 0,
	localTrendByList: [],
};
