//@flow

export const setWebcomponentFocus = (
	elm: WebComponentElement,
	elemGetter: (elm: ShadowRoot) => ?HTMLElement
): void => {
	requestAnimationFrame(() => {
		elm.shadowRoot &&
			setTimeout(
				shadowRoot => {
					const element = elemGetter(shadowRoot);

					element && element.focus();
				},
				0,
				elm.shadowRoot
			);
	});
};

export const createSetFocusHandler = (
	elemGetter: (elm: ShadowRoot) => ?HTMLElement
) => ({ elm }: any) => setWebcomponentFocus(elm, elemGetter);
