// @flow

import {
	DATE,
	DATETIME,
	DUE_DATE,
	GLIDE_DATE,
	GLIDE_DATE_TIME,
} from './constants';

/**
 * Check if the field type is date
 * @param {string} type
 * @return {boolean}
 */
export const isDateField = (type: string): boolean => {
	return (
		type === DATE ||
		type === DATETIME ||
		type === DUE_DATE ||
		type === GLIDE_DATE ||
		type === GLIDE_DATE_TIME
	);
};
