// @flow
import { actionTypes, type Coeffects } from '@servicenow/ui-core';
import { type State } from './state';
import { t } from 'sn-translate';
import get from 'lodash/get';

import { type FieldDefinition } from '../common/types/field';

import {
	DOT_WALK_PATH_UPDATED,
	RETRIEVE_FIELD_LABEL,
	RETRIEVE_FIELD_LABEL_SUCCESS,
	DOT_WALK_SEPARATOR,
	RETRIEVE_FIELD_DATA_SUCCESS,
	RETRIEVE_FIELD_DATA_FAILURE,
	RETRIEVE_FIELD_DATA,
} from './constants';

import { getFieldLabelFromCache, setFieldLabelToCache } from './cache';

import rtlBehavior from '@servicenow/behavior-rtl';
import { createGraphQLEffect } from '@servicenow/ui-effect-graphql';
import { effectProxy } from '../helpers/seismic';
import { hasPropertyChanged } from '../helpers/props';
import { RETRIEVE_FIELD_METADATA_SUCCESS } from '../visualizations-config-panel/actions/action-constants';

const handlePropertyChanged = (coeffects: Coeffects<State, any>) => {
	if (
		hasPropertyChanged(coeffects, 'tableName') ||
		hasPropertyChanged(coeffects, 'dotWalkPath')
	) {
		loadDotWalkLabel(coeffects);
	}
};

const loadDotWalkLabel = ({
	state,
	updateState,
	dispatch,
}: Coeffects<State, any>) => {
	const { tableName, dotWalkPath } = state.properties;

	if (tableName && dotWalkPath) {
		const fieldLabel = getFieldLabelFromCache(tableName, dotWalkPath);

		if (fieldLabel) {
			updateState({
				dotWalkLabel: fieldLabel,
			});
			return;
		}

		updateState({
			dotWalkLabel: t('Loading...'),
		});

		dispatch(RETRIEVE_FIELD_LABEL, {
			table: tableName,
			field: dotWalkPath,
			separator: DOT_WALK_SEPARATOR,
		});
	}
};

const dotWalkPathUpdated = ({
	state,
	action,
	dispatch,
	updateState,
}: Coeffects<State, any>) => {
	action.stopPropagation();

	const { path } = action.payload;

	if (path && path.length) {
		const dotWalkPath = path.map(p => p.field).join('.');
		const dotWalkLabel = path.map(p => p.displayValue).join(DOT_WALK_SEPARATOR);
		const fields = [
			{ tableName: state.properties.tableName, fieldName: dotWalkPath },
		];

		dispatch(
			RETRIEVE_FIELD_DATA,
			{ fields: JSON.stringify(fields) },
			{
				fields,
				dotWalkPath,
				dotWalkLabel,
				path,
			}
		);

		setFieldLabelToCache(state.properties.tableName, dotWalkPath, dotWalkLabel);

		updateState({
			dotWalkLabel: t('Loading...'),
			opened: false,
		});
	}
};

const handleClickOutside = ({
	state,
	action,
	updateState,
}: Coeffects<State, any>) => {
	const { opened } = state;

	if (!opened) {
		return;
	}

	const { event } = action.payload;
	const path = event.composedPath ? event.composedPath() : event.path;

	const panelInPath = path.some(
		el => el.classList && el.classList.contains('dot-walk-panel')
	);

	if (!panelInPath) {
		updateState({ opened: false });
	}
};

export const handleRequestFieldLabel = createGraphQLEffect(
	`query($table: String!, $field: String!, $separator: String){
		AppPARConfig_Query {
			fieldLabel(table: $table, field: $field, separator: $separator) {
				label
			}
		}
	}`,
	{
		variableList: ['table', 'field', 'separator'],
		successActionType: RETRIEVE_FIELD_LABEL_SUCCESS,
		errorActionType: RETRIEVE_FIELD_LABEL_SUCCESS,
	}
);

const handleFieldDataRetrieval = createGraphQLEffect(
	`query($fields: String!){
		AppPARConfig_Query {
			fieldDefinitions(fields: $fields) {
				value {
					field {
						label
						name
						type
						internalType
						reference
						canCountDistinct
						canGroupBy
						canSum
						canTrendBy
						isRange
						unit
					}
					result
				}
				result
			}
		}
	}`,
	{
		variableList: ['fields'],
		successActionType: RETRIEVE_FIELD_DATA_SUCCESS,
		errorActionType: RETRIEVE_FIELD_DATA_FAILURE,
	}
);

export const handleRequestFieldLabelSuccess = ({
	action,
	updateState,
	state,
}: Coeffects<State, any>) => {
	const { tableName, dotWalkPath } = state.properties;
	const dotWalkLabel = get(
		action.payload,
		'data.AppPARConfig_Query.fieldLabel.label'
	);

	setFieldLabelToCache(tableName, dotWalkPath, dotWalkLabel);

	updateState({
		dotWalkLabel,
	});
};

const handleFieldDataRetrievalSuccess = (coeffects: Coeffects<State, any>) => {
	const definition: { field: FieldDefinition, result: string } = get(
		coeffects.action.payload,
		'data.AppPARConfig_Query.fieldDefinitions.value[0]'
	);

	const { dotWalkPath, dotWalkLabel, path } = coeffects.action.meta;

	if (definition.result !== 'SUCCESS') {
		return;
	}

	coeffects.dispatch(
		RETRIEVE_FIELD_METADATA_SUCCESS,
		coeffects.action.payload,
		coeffects.action.meta
	);

	coeffects.dispatch(DOT_WALK_PATH_UPDATED, {
		definition,
		dotWalkPath,
		dotWalkLabel,
		path,
	});
};

export default {
	actionHandlers: {
		[actionTypes.COMPONENT_BOOTSTRAPPED]: loadDotWalkLabel,
		[actionTypes.COMPONENT_PROPERTY_CHANGED]: handlePropertyChanged,
		'NOW_DOT_WALK_CONNECTED#PATH_UPDATED': dotWalkPathUpdated,
		[RETRIEVE_FIELD_LABEL]: effectProxy(handleRequestFieldLabel),
		[RETRIEVE_FIELD_LABEL_SUCCESS]: handleRequestFieldLabelSuccess,
		[RETRIEVE_FIELD_DATA]: handleFieldDataRetrieval,
		[RETRIEVE_FIELD_DATA_SUCCESS]: handleFieldDataRetrievalSuccess,
	},
	eventHandlers: [
		{
			events: ['click'],
			target: document,
			effect: handleClickOutside,
		},
	],
	behaviors: [{ behavior: rtlBehavior }],
};
