// @flow

import { type Uuid } from '../../common/types/uuid';
import * as constants from './constants';

export const stringifyUuid = (uuidModel?: Uuid): string => {
	if (!uuidModel) {
		return '';
	}

	const uuid = [];

	uuid.push(uuidModel.indicator);
	if (uuidModel.breakdowns)
		for (const br of uuidModel.breakdowns) {
			if (br.sysId) {
				uuid.push(br.sysId);
				if (br.elementSysIds && br.elementSysIds.length > 0)
					uuid.push(br.elementSysIds[0]);
			}
		}

	if (uuidModel.aggregate) {
		uuid.push(uuidModel.aggregate);
	}

	const strUuid = uuid.join(constants.UUID_SEPARATOR);

	if (uuidModel.domain) {
		return strUuid + constants.UUID_DOMAIN_SEPARATOR + uuidModel.domain;
	}

	return strUuid;
};
