// @flow

// @jsx snabbdom.createElement
import '@servicenow/now-toggle';
import '@servicenow/now-dropdown';
import '@servicenow/now-accordion';
import 'sn-devx-components';

import snabbdom, { Fragment } from '@servicenow/ui-renderer-snabbdom'; // eslint-disable-line no-unused-vars
import { t } from 'sn-translate';
import { type State } from '../state';
import { type Helpers } from '@servicenow/ui-core';
import { CHART_TYPES } from '../../common/types/chart-type';
import { BAR_CHART_TYPES, STACKED } from '../../common/chart-settings';
import {
	AGGREGATE_FUNCTION_SUM,
	AGGREGATE_FUNCTION_COUNT,
} from '../../metric-configuration/constants';
import {
	SET_DATA_LABEL_SETTINGS,
	SET_OVERLAPPING_LABELS,
	SET_DECIMAL_PRECISION,
	SET_DATA_LABEL_POSITION,
	SET_DATA_LABEL_STACKED_VALUE,
	SET_SHOW_DATA_LABEL_AS,
} from '../actions/action-constants';
import {
	INPUT_BLUR,
	DATA_LABEL_POSITION_OPTIONS,
	DATA_LABEL_STACKED_VALUES,
	SHOW_DATA_LABEL_AS_OPTIONS,
} from './view-constants';
import { INDICATOR_TYPE } from '../../common/types/data-source';
import { SOURCE_TYPE_TABLE } from '../../common/types/data-source';
import { shouldEnableDecimalPrecision } from '../actions/model-validators';
import { autoScrollToVisibility } from '../../helpers/now-accordion/auto-scroll-to-visibility';

export default (state: State, { dispatch }: Helpers<State>) => {
	const { chartModel, dataSourceInfos, accordionState } = state;
	const {
		chartType,
		dataLabelSettings,
		isStacked,
		metrics,
		dataSources,
	} = chartModel;

	if (
		!dataLabelSettings ||
		(chartType === CHART_TYPES.SINGLE_SCORE &&
			!shouldEnableDecimalPrecision(
				chartModel,
				dataSourceInfos,
				state.dotWalkFieldDefinition
			))
	) {
		return null;
	}

	const dataToggleView = () => {
		if (chartType === CHART_TYPES.SINGLE_SCORE) {
			return null;
		}

		return (
			<viz-config-toggle
				id="show-data-label"
				append-to-payload={{ payloadActionType: SET_DATA_LABEL_SETTINGS }}
				checked={dataLabelSettings.showDataLabels}
				label={t('Show data labels')}
			/>
		);
	};

	const showDataLabelAsView = () => {
		if (
			dataLabelSettings.showDataLabels &&
			(chartType === CHART_TYPES.PIE ||
				chartType === CHART_TYPES.DONUT ||
				chartType === CHART_TYPES.SEMI_DONUT)
		) {
			return (
				<div className="show-data-label-as-container">
					<now-label-value-inline
						className="section-heading"
						label={t('Show label as')}
					/>
					<now-dropdown
						id="data-label-as"
						append-to-payload={{
							payloadActionType: SET_SHOW_DATA_LABEL_AS,
						}}
						items={SHOW_DATA_LABEL_AS_OPTIONS}
						select="single"
						selected-items={[dataLabelSettings.showDataLabelAs]}
						size="md"
						variant="tertiary"
						config-aria={{
							'aria-label': t('Show label as'),
						}}
					/>
				</div>
			);
		}
	};

	/**
	 * Returns true when the entered value is a number between 0-9 and
	 * It also allows other non character keys like tab, backspace,
	 * enter, escape, delete, navigation etc
	 * @param {Event} e
	 * @returns {boolean}
	 */
	const isValidDecimalRange = e => {
		const key = e.keyCode ? e.keyCode : e.which;

		if (
			!(
				[8, 9, 13, 27, 46].indexOf(key) !== -1 ||
				(key == 65 && (e.ctrlKey || e.metaKey)) ||
				(key >= 35 && key <= 40) ||
				(key >= 48 && key <= 57 && !(e.shiftKey || e.altKey)) ||
				(key >= 96 && key <= 105)
			)
		)
			return true;
	};

	const decimalPrecisionView = () => {
		if (
			shouldEnableDecimalPrecision(
				chartModel,
				dataSourceInfos,
				state.dotWalkFieldDefinition
			)
		)
			return (
				<span className="viz-config-input-container">
					<now-label-value-inline label={t('Decimal Precision')} />
					<input
						id="decimal-precision-input"
						className="viz-config-input-text"
						value={dataLabelSettings.decimalPrecision}
						maxlength="1"
						onkeydown={e => isValidDecimalRange(e) && e.preventDefault()}
						onblur={e =>
							dispatch(INPUT_BLUR, {
								type: SET_DECIMAL_PRECISION,
								ref: e.target,
							})
						}
						min="0"
						max="9"
					/>
				</span>
			);

		return null;
	};

	const dataLabelPositionView = () => {
		if (
			dataLabelSettings.showDataLabels &&
			(chartType === CHART_TYPES.BAR ||
				chartType === CHART_TYPES.HORIZONTAL_BAR)
		) {
			return (
				<div className="data-label-position-container">
					<label>{t('Data label position')}</label>
					<now-dropdown
						id="data-label-position"
						append-to-payload={{
							payloadActionType: SET_DATA_LABEL_POSITION,
						}}
						items={DATA_LABEL_POSITION_OPTIONS}
						select="single"
						selected-items={[dataLabelSettings.dataLabelPosition]}
						size="md"
						variant="tertiary"
						config-aria={{
							'aria-label': t('Data Label position'),
						}}
					/>
				</div>
			);
		}
	};

	const showOverlappingLabelsView = () => {
		if (
			dataLabelSettings.showDataLabels &&
			BAR_CHART_TYPES.includes(chartType)
		) {
			return (
				<div>
					<viz-config-toggle
						id="show-overlapping-label"
						append-to-payload={{ payloadActionType: SET_OVERLAPPING_LABELS }}
						checked={dataLabelSettings.showOverlappingLabels}
						label={t('Show overlapping labels')}
					/>
				</div>
			);
		}
	};

	const checkTableAggregationFunction = (metrics, dataSources) => {
		return metrics.every(metric => {
			return (
				(dataSources[0].sourceType === SOURCE_TYPE_TABLE &&
					metric.aggregateFunction === AGGREGATE_FUNCTION_SUM) ||
				(dataSources[0].sourceType === SOURCE_TYPE_TABLE &&
					metric.aggregateFunction === AGGREGATE_FUNCTION_COUNT)
			);
		});
	};

	const checkIndicatorTypes = (dataSourceInfos, dataSources) => {
		return dataSources.every(dataSource => {
			return (
				(dataSourceInfos[dataSource.id].type === INDICATOR_TYPE.AUTOMATED &&
					(dataSourceInfos[dataSource.id].aggregate === 2 ||
						dataSourceInfos[dataSource.id].aggregate === 1)) ||
				dataSourceInfos[dataSource.id].type === INDICATOR_TYPE.MANUAL ||
				dataSourceInfos[dataSource.id].type === INDICATOR_TYPE.EXTERNAL
			);
		});
	};

	const showStackedValuesView = () => {
		if (
			dataLabelSettings.showDataLabels &&
			isStacked === STACKED &&
			BAR_CHART_TYPES.includes(chartType) &&
			(checkTableAggregationFunction(metrics, dataSources) ||
				checkIndicatorTypes(dataSourceInfos, dataSources))
		) {
			return (
				<div className="stacked-label-value-container">
					<label>{t('Show stacked values')}</label>
					<now-dropdown
						id="stacked-values"
						append-to-payload={{
							payloadActionType: SET_DATA_LABEL_STACKED_VALUE,
						}}
						items={DATA_LABEL_STACKED_VALUES}
						select="single"
						selected-items={[dataLabelSettings.stackedValue]}
						size="md"
						variant="tertiary"
						config-aria={{
							'aria-label': t('Show stacked values'),
						}}
					/>
				</div>
			);
		}
	};

	return (
		<now-accordion-item
			id="data-label-accordion"
			header={t('Data label')}
			expanded={accordionState.dataLabel}
			append-to-payload={{ accordionItem: 'dataLabel' }}
			on-click={autoScrollToVisibility}>
			<div className="now-accordion-content" slot="content">
				{dataToggleView()}
				{dataLabelSettings.showDataLabels ? showDataLabelAsView() : ''}
				{dataLabelPositionView()}
				{showStackedValuesView()}
				{showOverlappingLabelsView()}
				{decimalPrecisionView()}
			</div>
		</now-accordion-item>
	);
};
