// @flow
// @jsx snabbdom.createElement

import snabbdom, { Fragment } from '@servicenow/ui-renderer-snabbdom'; // eslint-disable-line no-unused-vars
import { t } from 'sn-translate';
import '@servicenow/now-record-list-connected';
import { type State } from './state';

const getPreviewPredefinedFilter = (state: State) => {
	const {
		predefinedFilters,
		properties: {
			tableInfo: { previewPredefinedFilterId },
		},
	} = state;

	if (!previewPredefinedFilterId) {
		return undefined;
	}

	return predefinedFilters.find(({ id }) => id === previewPredefinedFilterId);
};

const getFilterQuery = (state: State) => {
	const {
		properties: {
			tableInfo: { previewFilterQuery },
		},
	} = state;

	const filterQueryItems = [];

	if (previewFilterQuery) filterQueryItems.push(previewFilterQuery);

	const predefinedFilter = getPreviewPredefinedFilter(state);

	if (predefinedFilter) filterQueryItems.push(predefinedFilter.query);

	return filterQueryItems.join('^');
};

export function TableRecordsView(state: State) {
	const tableName = state.properties.tableInfo.tableName;
	const filterQuery = getFilterQuery(state);

	return (
		<now-record-list-connected-snapshot
			table={tableName}
			listTitle={t('Preview record list')}
			hideFilterPanel={true}
			hideColumnResizing={false}
			hideRefreshButton={true}
			hideCellFilter={true}
			hidePanelAdvanced={true}
			hideRowSelector={true}
			hideUnnecessaryRowSelectors={true}
			limit={10}
			hideViewAll={true}
			hidePagination={false}
			hideColumnFiltering={true}
			hideColumnSorting={false}
			query={filterQuery}
		/>
	);
}
