// @flow
// @jsx snabbdom.createElement

import snabbdom, { Fragment } from '@servicenow/ui-renderer-snabbdom'; // eslint-disable-line no-unused-vars
import { type Helpers } from '@servicenow/ui-core';
import { type State } from '../state';
import { ChartTypeView } from './chart-type-view';
import AutoUpdateView from './auto-update-view';
import LegendsView from './legends-view';
import ComponentHeaderView from './component-header-view';
import AxisView from './axis-settings-view';
import DataLabelView from './data-label-view';
import DisplaySettingsView from './display-settings-view';
import SortingView from './sorting-view';
import './../../source-list';
import './../../group-by-selector';
import './../../metric-configuration';
import './../../additional-settings';
import './../../data-update-settings';
import './../../trendby-selector';
import './../../empty-state';
import '../../color-palette';
import '@servicenow/now-tabs';
import '@servicenow/now-accordion';
import '@servicenow/now-illustration';
import '@servicenow/now-message';
import { CONFIG_TAB, STYLE_TAB } from './view-constants';
import { t } from 'sn-translate';
import {
	UNREADABLE_INDICATOR,
	UNREPORTABLE_TABLE,
} from '../actions/action-constants';

import { SYS_PROP_MIN_REFRESH_FREQUENCY } from '../actions/preferences-security-action-handlers';

export const ConfigTabView = (state: State) => {
	const {
		chartModel,
		dataSourceInfos,
		accordionState,
		dotWalkFieldDefinition,
		security,
		sysProperties,
	} = state;

	const minRefreshFrequencyProp = sysProperties.find(
		property => property.name === SYS_PROP_MIN_REFRESH_FREQUENCY
	);

	let minRefreshFrequency =
		minRefreshFrequencyProp && minRefreshFrequencyProp.value
			? minRefreshFrequencyProp.value
			: '30';

	if (Number(minRefreshFrequency) < 1) minRefreshFrequency = '1';

	return (
		<div className="main-section">
			<now-accordion
				heading-level="2"
				trigger-icon={{ type: 'chevron', position: 'end' }}>
				<viz-config-metric-configuration
					chartModel={chartModel}
					dataSourceInfos={dataSourceInfos}
					dotWalkFieldDefinition={dotWalkFieldDefinition}
					expanded={accordionState.metric}
				/>
				<viz-config-trend-by-selector
					dataSourceInfos={dataSourceInfos}
					chartModel={chartModel}
					expanded={accordionState.trendBy}
					fieldDefinitions={dotWalkFieldDefinition}
				/>
				<viz-config-group-by-selector
					chartModel={chartModel}
					dataSourceInfos={dataSourceInfos}
					dotWalkFieldDefinition={dotWalkFieldDefinition}
					expanded={accordionState.groupBy}
				/>
				<viz-config-additional-settings
					chartModel={chartModel}
					dataSourceInfos={dataSourceInfos}
					expanded={accordionState.additionalSettings}
				/>
				{ComponentHeaderView(state)}
				<viz-config-data-update-settings
					chartModel={chartModel}
					expanded={accordionState.filterSettings}
					dataSourceInfos={dataSourceInfos}
					security={security}
					minRefreshFrequency={minRefreshFrequency}
				/>
			</now-accordion>
		</div>
	);
};

export const StyleTabView = (state: State, helpers: Helpers<State>) => {
	const {
		chartModel,
		dataSourceInfos,
		accordionState,
		singleColors,
		paletteColors,
		dotWalkFieldDefinition,
	} = state;

	return (
		<div className="main-section">
			<now-accordion
				heading-level="2"
				trigger-icon={{ type: 'chevron', position: 'end' }}>
				{DisplaySettingsView(state)}
				{SortingView(state)}
				{AxisView(state, helpers)}
				<viz-config-color-palette
					chartModel={chartModel}
					dataSourceInfos={dataSourceInfos}
					accordionState={accordionState}
					singleColors={singleColors}
					paletteColors={paletteColors}
					fieldDefinitions={dotWalkFieldDefinition}
				/>
				{LegendsView(state)}
				{DataLabelView(state, helpers)}
			</now-accordion>
		</div>
	);
};

export const TabView = (state: State, helpers: Helpers<State>) => {
	const {
		chartModel: { dataSources },
		selectedTab,
	} = state;

	const tabItems = [
		{ id: CONFIG_TAB, label: t('Data') },
		{ id: STYLE_TAB, label: t('Presentation') },
	];

	if (!dataSources.length) {
		return null;
	}

	return (
		<Fragment>
			<div className="tabs-container">
				<now-tabs
					fixed-width
					items={tabItems}
					selected-item={selectedTab}></now-tabs>
			</div>
			{selectedTab === CONFIG_TAB
				? ConfigTabView(state)
				: StyleTabView(state, helpers)}
		</Fragment>
	);
};

export const ErrorView = (state: State) => {
	const isPermissionError =
		state.dataSourceError === UNREADABLE_INDICATOR ||
		state.dataSourceError === UNREPORTABLE_TABLE;

	const message = isPermissionError
		? t(`You don't have access to the data source used in this visualization.`)
		: t(`The data source used in this visualization is not valid anymore.`);

	const description = t(`Contact your admin for more information.`);

	return (
		<viz-config-empty-state illustration="unconfigured" force-size="sm">
			<span slot="primary">{message}</span>
			<span slot="secondary">{description}</span>
		</viz-config-empty-state>
	);
};

export const MainView = (state: State, helpers: Helpers<State>) => {
	const {
		chartModel,
		dataSourceInfos,
		dataSourceError,
		isLoadingDataSource,
		isInitializingDotWalkFields,
		accordionState,
	} = state;

	const preferencesLoaded =
		state.userPreferences.length > 0 && state.sysProperties.length > 0;

	if (
		isLoadingDataSource ||
		!preferencesLoaded ||
		isInitializingDotWalkFields
	) {
		return (
			<div className="main-loading-container">
				<now-loader size="lg" />
			</div>
		);
	}

	if (dataSourceError) {
		return ErrorView(state);
	}

	return (
		<Fragment>
			<viz-config-source-list
				chartType={chartModel.chartType}
				dataSources={chartModel.dataSources}
				dataSourceInfos={dataSourceInfos}
				expanded={accordionState.dataSources}
			/>
			<div className="main-section">{ChartTypeView(state)}</div>
			{TabView(state, helpers)}
			{AutoUpdateView(state)}
		</Fragment>
	);
};
