// @flow
// @jsx snabbdom.createElement
import '@servicenow/now-toggle';
import '@servicenow/now-dropdown';
import '@servicenow/now-accordion';

import snabbdom, { Fragment } from '@servicenow/ui-renderer-snabbdom'; // eslint-disable-line no-unused-vars
import { t } from 'sn-translate';
import { type State } from '../state';
import { CHART_TYPES } from '../../common/types/chart-type';
import {
	SET_SHOW_LEGEND,
	SET_SHOW_LEGEND_VALUE,
	SET_SHOW_LEGEND_PERCENTAGE,
	SET_LEGEND_POSITION,
} from '../actions/action-constants';
import { LEGEND_POSITION_OPTIONS } from './view-constants';
import { autoScrollToVisibility } from '../../helpers/now-accordion/auto-scroll-to-visibility';

export default (state: State) => {
	const {
		chartModel: { chartType, legendSettings },
		accordionState,
	} = state;

	if (chartType === CHART_TYPES.SINGLE_SCORE || !legendSettings) {
		return null;
	}

	const {
		showLegend,
		showLegendValue,
		legendPosition,
		showLegendPercentage,
	} = legendSettings;

	const checkLegendValueAndChartType =
		showLegendValue &&
		(chartType === CHART_TYPES.PIE ||
			chartType === CHART_TYPES.DONUT ||
			chartType === CHART_TYPES.SEMI_DONUT);

	return (
		<now-accordion-item
			id="legend-accordion"
			dividers={{ top: false, bottom: true }}
			header={t('Legend')}
			expanded={accordionState.legend}
			append-to-payload={{ accordionItem: 'legend' }}
			on-click={autoScrollToVisibility}>
			<div className="now-accordion-content" slot="content">
				{' '}
				<viz-config-toggle
					id="show-legend-toggle"
					append-to-payload={{ payloadActionType: SET_SHOW_LEGEND }}
					checked={showLegend}
					label={t('Show legend')}
				/>
				<viz-config-toggle
					id="show-legend-value-toggle"
					append-to-payload={{ payloadActionType: SET_SHOW_LEGEND_VALUE }}
					disabled={!showLegend}
					checked={showLegendValue}
					label={t('Show legend value')}
				/>
				{checkLegendValueAndChartType ? (
					<viz-config-toggle
						id="show-legend-percentage-toggle"
						append-to-payload={{
							payloadActionType: SET_SHOW_LEGEND_PERCENTAGE,
						}}
						checked={showLegendPercentage}
						label={t('Show percentage')}
					/>
				) : null}
				<div className="legend-position">
					<now-label-value-inline
						className="section-heading"
						label={t('Legend position')}
					/>
					<now-dropdown
						id="legend-position-dropdown"
						append-to-payload={{
							payloadActionType: SET_LEGEND_POSITION,
						}}
						items={LEGEND_POSITION_OPTIONS}
						select="single"
						selected-items={[legendPosition]}
						disabled={!showLegend}
						size="md"
						variant="tertiary"
						config-aria={{
							'aria-label': t('Legend position'),
						}}
					/>
				</div>
			</div>
		</now-accordion-item>
	);
};
