// @flow
// @jsx snabbdom.createElement

import snabbdom, { Fragment } from '@servicenow/ui-renderer-snabbdom'; // eslint-disable-line no-unused-vars
import { type State } from './state';
import '../indicator-preview';
import '../breakdown-selector';
import { t } from 'sn-translate';
import isEmpty from 'lodash/isEmpty';

export default function(state: State) {
	const {
		properties: { selectedIndicatorInfo },
		indicatorInfo,
		breakdownElementsLoading,
	} = state;

	let loadingBreakdownSelector = false;

	for (const sysId in breakdownElementsLoading) {
		if (breakdownElementsLoading[sysId]) {
			loadingBreakdownSelector = true;
			break;
		}
	}

	return (
		<Fragment>
			{isEmpty(indicatorInfo) || loadingBreakdownSelector ? (
				<div className="loading-container" key="loading-container">
					<now-loader label={t('Loading...')} size="lg" />
				</div>
			) : null}
			{!isEmpty(indicatorInfo) ? (
				<div
					key="indicator-config"
					class={{
						'indicator-config': true,
						'is-loading': loadingBreakdownSelector,
					}}>
					<viz-config-breakdown-selector
						selectedIndicatorInfo={selectedIndicatorInfo}
						indicatorInfo={indicatorInfo}
						loadingBreakdownSelector={loadingBreakdownSelector}
					/>

					<viz-config-indicator-preview
						uuid={selectedIndicatorInfo.previewUuid}
						indicatorInfo={indicatorInfo}
					/>
				</div>
			) : null}
		</Fragment>
	);
}
