// @flow

import {
	type DataSource,
	type DataSourceInfo,
	type DataSourceInfos,
	SOURCE_TYPE_TABLE,
	SOURCE_TYPE_INDICATOR,
} from '../../common/types/data-source';
import { type Metric } from '../../common/types/metric';
import { type FieldDefinitions } from '../../common/types/field';
import { FIELD_TYPES } from '../../common/types/field';
import { getFieldDefinition } from '../field';

export const getMetricDataSource = (
	metric: Metric,
	dataSources: DataSource[]
): DataSource => {
	const dataSourceFound = dataSources.find(ds => ds.id === metric.dataSource);

	if (dataSourceFound) {
		return dataSourceFound;
	}

	throw Error('Metric should have a valid data source');
};

export const isTableMetric = (
	metric: Metric,
	dataSources: DataSource[]
): boolean => {
	if (!metric.dataSource) {
		return false;
	}

	const dataSource = getMetricDataSource(metric, dataSources);

	return dataSource.sourceType === SOURCE_TYPE_TABLE;
};

export const isFXCurrencyMetric = (
	metric: Metric,
	dataSources: DataSource[],
	dataSourceInfos: DataSourceInfos,
	fieldDefinitions: FieldDefinitions
): boolean => {
	if (!metric.dataSource) return false;

	const dataSource = getMetricDataSource(metric, dataSources);
	const aggregateField = metric.aggregateField;

	if (!dataSource || !dataSource.id) return false;

	return isAggregateFieldFxCurrency(
		aggregateField,
		dataSource,
		dataSourceInfos,
		fieldDefinitions
	);
};

export const isIndicatorMetric = (
	metric: Metric,
	dataSources: DataSource[]
): boolean => {
	if (!metric.dataSource) {
		return false;
	}

	const dataSource = getMetricDataSource(metric, dataSources);

	return dataSource.sourceType === SOURCE_TYPE_INDICATOR;
};

export const getMetricFrequency = (
	{ dataSource, aggregateIndicator }: Metric,
	dataSourceInfos: DataSourceInfos
): ?number => {
	if (!dataSource || !dataSourceInfos[dataSource]) {
		return undefined;
	}

	const dataSourceInfo = dataSourceInfos[dataSource];

	if (!aggregateIndicator) {
		return dataSourceInfo.frequencyValue;
	}

	const aggregate = (dataSourceInfo.aggregates || []).find(
		a => a.sysId === aggregateIndicator
	);

	return aggregate && aggregate.frequencyValue;
};

export const getMetricDataSourceInfo = (
	metric: Metric,
	dataSourceInfos: DataSourceInfos
): DataSourceInfo => {
	if (metric.dataSource && dataSourceInfos[metric.dataSource]) {
		return dataSourceInfos[metric.dataSource];
	}

	throw Error('Metric should have a valid data source info');
};

export const getAggregateWithSameFrequency = (
	metric1: Metric,
	metric2: Metric,
	dataSourceInfos: DataSourceInfos
) => {
	const metricFrequency1 = getMetricFrequency(metric1, dataSourceInfos);
	const metricFrequency2 = getMetricFrequency(metric2, dataSourceInfos);

	// If both aggregate frequency are the same, use aggregate from metric 2
	if (metricFrequency1 === metricFrequency2) {
		return metric2.aggregateIndicator;
	}

	const dataSourceInfo = getMetricDataSourceInfo(metric2, dataSourceInfos);

	// If same aggregate from metric 1, exists in metric 2, use it
	const sameAggregateFound = (dataSourceInfo.aggregates || []).find(
		a => a.sysId === metric1.aggregateIndicator
	);

	if (sameAggregateFound) {
		return sameAggregateFound.sysId;
	}

	// If indicator frequency is same of metric 1, use empty frequency
	if (dataSourceInfo.frequencyValue === metricFrequency1) {
		return undefined;
	}

	// Use any aggregate with same frequency available
	const sameAggregateFrequencyFound = (dataSourceInfo.aggregates || []).find(
		a => a.frequencyValue === metricFrequency1
	);

	return sameAggregateFrequencyFound && sameAggregateFrequencyFound.sysId;
};

export const isAggregateFieldFxCurrency = (
	aggregateField?: string,
	dataSource: DataSource,
	dataSourceInfos: DataSourceInfos,
	fieldDefinitions: FieldDefinitions
): boolean => {
	const dataSourceInfo = dataSourceInfos[dataSource.id];

	if (!dataSourceInfo) return false;

	const field = getFieldDefinition(
		aggregateField || '',
		dataSourceInfo,
		dataSource,
		fieldDefinitions
	);

	return Boolean(field && field.type === FIELD_TYPES.FX_CURRENCY);
};
