// @flow
import get from 'lodash/get';

import { type Coeffects } from '@servicenow/ui-core';
import { type State } from '../state';
import {
	type DataSource,
	type PayloadAddDataSource,
} from '../../common/types/data-source';

import {
	type DataSourceInfo,
	SOURCE_TYPE_INDICATOR,
	SOURCE_TYPE_TABLE,
} from '../../common/types/data-source';
import { type Aggregate, type Breakdown } from '../../common/types/indicator';
import {
	RETRIEVE_DATA_SOURCE_OPTIONS,
	RETRIEVE_MULTI_DATA_SOURCE_OPTIONS,
	RETRIEVE_TABLE_INFO_REQUESTED,
	RETRIEVE_TABLE_INFO_SUCCESS,
	RETRIEVE_TABLE_INFO_FAILURE,
	RETRIEVE_INDICATOR_INFO_REQUEST,
	RETRIEVE_INDICATOR_INFO_SUCCESS,
	RETRIEVE_INDICATOR_INFO_FAILURE,
	DATA_SOURCE_INFO_LOADED,
	RETRIEVE_SOURCE_TABLE_INFO_REQUESTED,
	VERIFY_DATA_SOURCE_INFO_IS_LOADED,
	RETRIEVE_FIELD_METADATA,
	UNREADABLE_INDICATOR,
	SET_DATA_SOURCE_ERROR,
	INVALID_INDICATOR,
	UNREPORTABLE_TABLE,
	INVALID_TABLE,
} from './action-constants';
import { FREQUENCY_OPTIONS } from '../views/view-constants';
import { createGraphQLEffect } from '@servicenow/ui-effect-graphql';
import { stringifyUuid } from '../../helpers/uuid/stringify-uuid';
import { effectProxy } from '../../helpers/seismic';
import {
	getUniqueDataSourceLabel,
	getDataSourceUniqueId,
} from '../../helpers/data-source';
import { getFieldMetadataRequest } from '../../helpers/field';

type PayloadMultiDataSourceLoad = {
	dataSources: DataSource[],
	dataSourcesNeedsLoad: boolean[],
};

const retrieveMultipleDataSourceOptions = (
	coeffects: Coeffects<State, PayloadMultiDataSourceLoad>
) => {
	const { dataSources, dataSourcesNeedsLoad } = coeffects.action.payload;

	/**
	 * dataSourcesNeedsLoad Is a map saying which index can be loaded (true) or canT (false)
	 * If there is no map, means all the data source should load info
	 */
	const needsLoad = dataSourcesNeedsLoad || dataSources.map(() => true);

	if (!needsLoad.length) {
		/**
		 * If it's the first load  and there && empty state
		 * Just go directly to handle the dot walk initialization
		 */
		return coeffects.dispatch(RETRIEVE_FIELD_METADATA, {});
	}

	dataSources.forEach(
		(dataSource, dataSourceIndex) =>
			needsLoad[dataSourceIndex] &&
			coeffects.dispatch(RETRIEVE_DATA_SOURCE_OPTIONS, {
				dataSource,
				dataSourceIndex,
			})
	);
};

const retrieveDataSourceOptions = (
	coeffects: Coeffects<State, PayloadAddDataSource>
) => {
	const { dataSource, dataSourceIndex } = coeffects.action.payload;

	const { sourceType, tableName, uuid, predefinedFilterId } = dataSource;

	const loadingIndex: number =
		dataSourceIndex !== undefined
			? dataSourceIndex
			: coeffects.state.chartModel.dataSources.length;

	/**
	 * Single source for setting the isLoading data source = true
	 * Otherwise it can be messed up because of the cache and call ordering
	 * AVOID SETTING isLoading in another place. Firing this action should be enough
	 */
	const setDataSourceIsLoading = () =>
		coeffects.updateState({
			operation: 'merge',
			value: {
				isLoadingDataSource: { [loadingIndex]: true },
				dataSourceInfo: undefined,
			},
		});

	switch (sourceType) {
		case SOURCE_TYPE_TABLE: {
			if (predefinedFilterId) {
				setDataSourceIsLoading();
				coeffects.dispatch(
					RETRIEVE_SOURCE_TABLE_INFO_REQUESTED,
					{ sourceId: predefinedFilterId },
					coeffects.action.payload
				);
			} else if (tableName) {
				setDataSourceIsLoading();
				coeffects.dispatch(
					RETRIEVE_TABLE_INFO_REQUESTED,
					{ tableName },
					coeffects.action.payload
				);
			}

			break;
		}
		case SOURCE_TYPE_INDICATOR: {
			if (uuid && uuid.indicator) {
				setDataSourceIsLoading();
				coeffects.dispatch(
					RETRIEVE_INDICATOR_INFO_REQUEST,
					{
						indicator: uuid.indicator,
						uuid: stringifyUuid(uuid),
					},
					coeffects.action.payload
				);
			}

			break;
		}
		default: {
			coeffects.updateState({
				operation: 'merge',
				value: {
					isLoadingDataSource: { [loadingIndex]: true },
					dataSourceInfo: undefined,
				},
			});
		}
	}
};

const setDataSource = (
	coeffects: Coeffects<State, any>,
	{ dataSource, addNewDataSource, dataSourceIndex }: PayloadAddDataSource,
	dataSourceInfo: DataSourceInfo
) => {
	const dataSourceDisplayLabel =
		dataSource.sourceType === SOURCE_TYPE_TABLE &&
		dataSourceInfo.displayLabel === ''
			? dataSource.tableName
			: dataSourceInfo.displayLabel;

	const label = getUniqueDataSourceLabel(
		dataSourceDisplayLabel,
		coeffects.state.chartModel.dataSources
	);

	const newId = getDataSourceUniqueId(
		dataSource,
		coeffects.state.chartModel.dataSources
	);

	let index;

	if (dataSourceIndex !== undefined) {
		index = dataSourceIndex;
	} else if (addNewDataSource && coeffects.state.chartModel.dataSources) {
		index = coeffects.state.chartModel.dataSources.length;
	} else {
		index = 0;
	}

	// dataConfiguration didn't have label in Paris, so metric.dataSource is empty and needs to be fixed
	const metricWithoutDataSource = coeffects.state.chartModel.metrics.findIndex(
		m => !m.dataSource
	);

	if (!dataSource.id && metricWithoutDataSource >= 0) {
		coeffects.updateState({
			operation: 'set',
			path: `chartModel.metrics[${metricWithoutDataSource}].dataSource`,
			value: newId,
			shouldRender: false,
		});
	}

	if (!dataSource.label) {
		dataSource.label = label;
	}

	// If "!dataSource.id", means it's an old data that didn't have the label/id
	// Or the user is editing an existent DS but we can't reach the ID
	if (addNewDataSource || !dataSource.id) {
		dataSource.id = newId;
	}

	coeffects.updateState({
		operation: 'merge',
		path: 'dataSourceInfos',
		value: { [(dataSource.id: string)]: dataSourceInfo },
		shouldRender: false, // This should be removed/true when we remove dataSourceS and dataSourceInfoS
	});

	/**
	 * Needs to be setted apart because
	 * we need to keep the other "loaded" info unchanged
	 */
	coeffects.updateState({
		operation: 'set',
		path: `isLoadingDataSource[${index}]`,
		value: false,
	});

	coeffects.dispatch(VERIFY_DATA_SOURCE_INFO_IS_LOADED, {
		dataSource,
		dataSourceIndex,
		addNewDataSource,
	});
};

const verifyDataSourceInfosIsLoaded = (coeffects: Coeffects<State, any>) => {
	const loading = Object.values(coeffects.state.isLoadingDataSource);

	if (loading.every(isLoading => !isLoading)) {
		if (!coeffects.state.isInitializingDotWalkFields) {
			return coeffects.dispatch(
				DATA_SOURCE_INFO_LOADED,
				coeffects.action.payload
			);
		}

		/**
		 * In the first load, metadata needs to load before validating the model
		 * DATA_SOURCE_INFO_LOADED is the action that triggers validation
		 * So we need to retrieve field metadata first
		 */
		coeffects.dispatch(
			RETRIEVE_FIELD_METADATA,
			{ fields: getFieldMetadataRequest(coeffects.state.chartModel) },
			{
				actionName: DATA_SOURCE_INFO_LOADED,
				payload: coeffects.action.payload,
			}
		);
	}
};

export const handleRetrieveFieldsRequestedEffect = createGraphQLEffect(
	`query($tableName: String!){
		AppPARConfig_Query {
			tableDetails(name: $tableName) {
				label
				isDatabaseView
				fields {
					label
					name
					type
					internalType
					reference
					canCountDistinct
					canGroupBy
					canSum
					canTrendBy
					isRange
					unit
				}
				result
			}
		}
	}`,
	{
		variableList: ['tableName'],
		successActionType: RETRIEVE_TABLE_INFO_SUCCESS,
		errorActionType: RETRIEVE_TABLE_INFO_FAILURE,
	}
);

const handleRetrieveFieldsBySourceRequested = createGraphQLEffect(
	`query($sourceId: String!){
		AppPARConfig_Query {
			tableDetails(sourceID: $sourceId) {
				label
				name
				isDatabaseView
				fields {
					label
					name
					type
					internalType
					reference
					canCountDistinct
					canGroupBy
					canSum
					canTrendBy
					isRange
					unit
				}
			}
		}
	}`,
	{
		variableList: ['sourceId'],
		successActionType: RETRIEVE_TABLE_INFO_SUCCESS,
		errorActionType: RETRIEVE_TABLE_INFO_FAILURE,
	}
);

const handleRetrieveTableInfoSuccess = (coeffects: Coeffects<State, any>) => {
	const data = get(
		coeffects.action.payload,
		'data.AppPARConfig_Query.tableDetails',
		{ fields: [], label: '' }
	);

	if (data.result === UNREPORTABLE_TABLE || data.result === INVALID_TABLE) {
		coeffects.dispatch(SET_DATA_SOURCE_ERROR, {
			result: data.result,
		});
	}

	const dataSourceInfo: DataSourceInfo = {
		displayLabel: data.label,
		tableFields: data.fields,
		isDatabaseView: data.isDatabaseView,
	};

	const metadata: PayloadAddDataSource = get(coeffects, 'action.meta');

	if (data.name) {
		metadata.dataSource.tableName = data.name;
	}

	setDataSource(coeffects, metadata, dataSourceInfo);
};

const handleRetrieveFieldsFailure = () => {
	//TODO: use some good error-handling strategy
};

export const handleRetrieveIndicatorInfoRequested = createGraphQLEffect(
	`query($indicator: String!, $uuid: String!){
		AppPARConfig_Query {
			breakdowns(uuid: $indicator) {
				values {
					sysId: sysID
					name
				}
				result
			}
			indicatorDetails(sysID: $indicator) {
				name
				frequency {
					value
					label
				}
				type {
					value
				}
				aggregates {
					sysId: sysID
					name
					frequencyValue
				}
				scripted
				aggregate {
					value
				}
				result
				unit {
					sysID
				}
			}
			isUUIDRealtime(uuid: $uuid) {
				value
			}
		}
	}`,
	{
		variableList: ['indicator', 'uuid'],
		successActionType: RETRIEVE_INDICATOR_INFO_SUCCESS,
		errorActionType: RETRIEVE_INDICATOR_INFO_FAILURE,
	}
);

const handleRetrieveIndicatorInfoSuccess = (
	coeffects: Coeffects<State, any>
) => {
	const indicatorDetails = get(
		coeffects.action.payload,
		'data.AppPARConfig_Query.indicatorDetails'
	);

	if (
		indicatorDetails.result === UNREADABLE_INDICATOR ||
		indicatorDetails.result === INVALID_INDICATOR
	) {
		coeffects.dispatch(SET_DATA_SOURCE_ERROR, {
			result: indicatorDetails.result,
		});
	}

	const displayLabel = get(indicatorDetails, 'name', '');
	const aggregates: Aggregate[] = get(indicatorDetails, 'aggregates', []);
	const frequency = get(indicatorDetails, 'frequency.label', '');
	const scripted = get(indicatorDetails, 'scripted', false);
	const frequencyValue = Number(
		get(indicatorDetails, 'frequency.value', FREQUENCY_OPTIONS.DAILY)
	);
	const type = get(indicatorDetails, 'type.value', '');
	const aggregate = get(indicatorDetails, 'aggregate.value', '');
	const unitId = get(indicatorDetails, 'unit.sysID', '');

	const breakdowns: Breakdown[] = get(
		coeffects.action.payload,
		'data.AppPARConfig_Query.breakdowns.values',
		[]
	);

	const isIndicatorRealtime: boolean = get(
		coeffects.action.payload,
		'data.AppPARConfig_Query.isUUIDRealtime.value',
		false
	);

	const dataSourceInfo: DataSourceInfo = {
		displayLabel,
		breakdowns,
		aggregates,
		frequency,
		scripted,
		frequencyValue,
		type,
		aggregate,
		isIndicatorRealtime,
		unitId,
	};

	const metadata: PayloadAddDataSource = get(coeffects, 'action.meta');

	setDataSource(coeffects, metadata, dataSourceInfo);
};

const handleRetrieveIndicatorInfoFailure = () => {
	//TODO: use some good error-handling strategy
};

export const dataSourceInfoActionHandlers = {
	[RETRIEVE_DATA_SOURCE_OPTIONS]: retrieveDataSourceOptions,
	[RETRIEVE_MULTI_DATA_SOURCE_OPTIONS]: retrieveMultipleDataSourceOptions,
	[RETRIEVE_TABLE_INFO_REQUESTED]: effectProxy(
		handleRetrieveFieldsRequestedEffect
	),
	[RETRIEVE_SOURCE_TABLE_INFO_REQUESTED]: handleRetrieveFieldsBySourceRequested,
	[RETRIEVE_TABLE_INFO_SUCCESS]: handleRetrieveTableInfoSuccess,
	[RETRIEVE_TABLE_INFO_FAILURE]: handleRetrieveFieldsFailure,
	[RETRIEVE_INDICATOR_INFO_REQUEST]: effectProxy(
		handleRetrieveIndicatorInfoRequested
	),
	[RETRIEVE_INDICATOR_INFO_SUCCESS]: handleRetrieveIndicatorInfoSuccess,
	[RETRIEVE_INDICATOR_INFO_FAILURE]: handleRetrieveIndicatorInfoFailure,
	[VERIFY_DATA_SOURCE_INFO_IS_LOADED]: verifyDataSourceInfosIsLoaded,
};
