// @flow

import { type FieldDefinition } from '../../common/types/field';
import { isDateField } from './is-date-field';

/**
 * Verify if the field can be used to TrendBy
 * Logic extracted and simplified from service.dotWalkingFilters.js: trendByFilter
 * @param {FieldDefinition} item
 * @return {boolean}
 */
export const canTrendBy = (item: FieldDefinition): boolean => {
	return isDateField(item.internalType);
};
